import { load } from "opentype.js";
import type { ILoader } from "@gdk/core";
import type { Font } from "opentype.js";

export interface IFileType {
	data: Font;
	family?: string;
	filename: string;
}

export interface IStringDictionary<T> {
	[key: string]: T;
}

export class FontLoader implements ILoader {
	// Public members
	public extensions: string[] = ["woff", "ttf", "otf"];

	// Private members
	private _baseUrl: string = "";

	// Static members
	public static fonts: { [index: string]: IFileType } = {};
	public static fontMapNames: IStringDictionary<IStringDictionary<string>> = {};

	/**
	 * Returns the associated font giving a family + sub family
	 * @param fontFamily The font family
	 * @param fontSubfamily The font sub family
	 */
	public static getFontFamily(fontFamily: string, fontSubfamily: string = "Regular"): Font {
		if (!fontFamily) {
			return null;
		}

		const font = FontLoader.fontMapNames[fontFamily];
		if (!font) {
			return null;
		}

		const targetFontName = font[fontSubfamily];
		const targetFont = FontLoader.fonts[targetFontName];

		return targetFont ? targetFont.data : null;
	}

	/**
	 * Returns the base url of the loader
	 */
	public get baseUrl(): string {
		return this._baseUrl;
	}

	/**
	 * Sets the base url of the loader
	 */
	public set baseUrl(url: string) {
		this._baseUrl = url;
	}

	/**
	 * Add the given file to the files to load
	 * @param filename the file to add
	 */
	public add(filename: string, key?: string): void {
		FontLoader.fonts[key || filename] = {
			filename,
			data: null,
		};
	}

	/**
	 * Clears the files to load dictionary
	 */
	public clear(): void {
		FontLoader.fonts = {};
	}

	/**
	 * Returns the resource identified by the given key
	 * @param key the key of the value to get
	 */
	public get<T>(key: string): T {
		if (!key) {
			return null;
		}

		const font = FontLoader.fonts[key];
		if (font) {
			return font.data as any;
		}

		return null;
	}

	/**
	 * Load all files previously added
	 * @param callback called when all files loaded
	 * @param progress called when a file has been loaded
	 * @param error called when an error happens
	 */
	public load(
		callback: () => void,
		progress?: (filename?: string) => void,
		error?: (file: string) => void
	): void {
		const keys = Object.keys(FontLoader.fonts);
		if (keys.length === 0) {
			return callback();
		}

		let count = 0;

		for (const key of keys) {
			const file = FontLoader.fonts[key];
			if (file.data) {
				count++;

				if (count === keys.length) {
					callback();
				}

				continue;
			}

			this._createInstance(
				file.filename,
				key,
				(loadedFile) => {
					count++;
					if (progress) {
						progress(loadedFile);
					}

					if (count === keys.length) {
						callback();
					}
				},
				(filename) => {
					if (error) {
						error(filename);
					}
				}
			);
		}
	}

	// Creates a new instanceof of Howler sound
	private _createInstance(
		filename: string,
		key: string,
		callback: (filename: string) => void,
		error: (filename: string) => void
	): void {
		if (FontLoader.fonts[key].data) {
			return;
		}

		load(this._baseUrl + filename, (err: any, font?: Font) => {
			if (err) {
				return error(filename);
			}

			if (font) {
				FontLoader.fonts[key].data = font;
				FontLoader.fonts[key].family = font.names.postScriptName.en;

				if (!FontLoader.fontMapNames[font.names.fontFamily.en]) {
					FontLoader.fontMapNames[font.names.fontFamily.en] = {};
				}

				FontLoader.fontMapNames[font.names.fontFamily.en][font.names.fontSubfamily.en] =
					font.names.postScriptName.en;

				callback(filename);
			} else {
				throw new Error(`Font ${filename} is not supported.`);
			}
		});
	}
}
