import { interaction } from "pixi.js";
import { tracker, ButtonClick, Tools } from "@gdk/core";
import { isArray } from "util";
import { TweenMax } from "gsap";
import type { WebGLRenderer, CanvasRenderer } from "pixi.js";

import { Timeline } from "../../spine/timeline";
import { TextField } from "..";
import type { ITimelineOptions } from "../../spine/timeline";
import type { TimelineAnimation, ITimelineAnimationOptions } from "../../spine/timelineAnimation";
import type { TimelineAnimationEvent } from "../../spine/timelineEvents";
import type { IStyle, IStyles } from "..";

export interface ISpineButtonParams extends ITimelineOptions {
	label?: {
		style: IStyle | IStyles;
	};
	skin?: string;
	enable: boolean;
	lockOnClick?: boolean;
	animations?: ISpineButtonAnimation[];
	callToAction?: {
		label?: string;
		minDelay: number;
		maxDelay: number;
	};
}

export interface ISpineButtonAnimation extends ITimelineAnimationOptions {
	name: string;
	event?: string | string[];
	next?: string;
	delay?: number;
}

export enum TimelineInteractiveEvent {
	// pointer events
	pointermove = "pointermove",
	pointerdown = "pointerdown",
	pointerleave = "pointerleave",
	pointerenter = "pointerenter",
	pointerover = "pointerover",
	pointercancel = "pointercancel",
	pointerup = "pointerup",
	pointerupoutside = "pointerupoutside",
	pointerout = "pointerout",
	pointertap = "pointertap",
	// mouse events
	mousemove = "mousemove",
	mousedown = "mousedown",
	mouseout = "mouseout",
	mouseover = "mouseover",
	mouseup = "mouseup",
	click = "click",
	rightclick = "rightclick",
	// touch events
	touchstart = "touchstart",
	touchcancel = "touchcancel",
	touchend = "touchend",
	touchmove = "touchmove",
	tap = "tap",
}

export interface IMap<T> {
	[K: string]: T;
}

export class SpineButton extends Timeline {
	// Public members
	public labels: IMap<TextField>;
	public parameters: ISpineButtonParams;

	// Private members
	private _enabled: boolean;
	private _isOver: boolean = false;
	private _isDown: boolean = false;
	private _currentLabel: string = "";
	private _interactionManager: any;
	private ctaTimer: TweenMax = null;
	private renderer: WebGLRenderer | CanvasRenderer;

	// Static members
	public static DEFAULT_STATES: ISpineButtonAnimation[] = [
		// states
		// enter state
		{ name: "enter", label: "enter" },
		{ name: "idleLoop", loop: true },
		// default state :
		{ name: "idle", label: "idle" },
		{ name: "idleLoop", label: "idleLoop", loop: true },
		// pointer/mouse out
		{
			name: "out",
			label: "out",
			event: TimelineInteractiveEvent.pointerout,
		},
		{ name: "idleLoop", loop: true },
		// pointer/touch/mouse down
		{
			name: "down",
			label: "down",
			event: TimelineInteractiveEvent.pointerdown,
		},
		{ name: "downLoop", label: "downLoop", loop: true },
		// pointer/touch/mouse release
		{
			name: "up",
			label: "up",
			event: [
				TimelineInteractiveEvent.pointertap,
				TimelineInteractiveEvent.pointerup,
				TimelineInteractiveEvent.click,
				TimelineInteractiveEvent.tap,
			],
		},
		{ name: "idleLoop", label: "pointerupoutside", loop: true },
		// pointer/mouse over
		{
			name: "over",
			label: "over",
			event: TimelineInteractiveEvent.pointerover,
		},
		{ name: "overLoop", label: "overLoop", loop: true },
		// enabled = false
		{ name: "disable", label: "disable" },
		{ name: "disableLoop", label: "disableLoop", loop: true },
		// call to action (random delay)
		{ name: "calltoaction", label: "calltoaction", next: "idleLoop" },
		// exit state
		{ name: "exit", label: "exit", pauseAtEnd: true },
	];

	/**
	 * Constructor
	 * @param options: spine button options
	 */
	public constructor(options: ISpineButtonParams) {
		super(
			Tools.assign(
				{
					callToAction: null,
					enable: false,
					lockOnClick: true,
					animations: options[`states`] ? options[`states`] : SpineButton.DEFAULT_STATES,
					label: { style: { default: {} } },
				},
				options
			)
		);

		// remove obsolete (and deprecated) states:
		delete this.parameters[`states`];

		this.initLabel();
		this.initInteractionsProxies();
		this.initCTA(this.parameters.callToAction);

		// custom setter, so we need to set it to true before
		this.enabled = true;
		this.enabled = this.parameters.enable !== false;
	}

	/**
	 * Sets if the button is enabled
	 */
	public set enabled(value: boolean) {
		if (value !== this._enabled) {
			if (value) {
				this.resume("idle");
			} else {
				this.resume("disable");
			}
		}

		this._enabled = value;
		this.interactive = value;
	}

	/**
	 * Returns if the button is enabled
	 */
	public get enabled(): boolean {
		return this._enabled;
	}

	/**
	 * Sets the label's text
	 */
	// remove this comment when allow-override-transient-properties is included in typescript
	// @ts-ignore
	public set label(value: string) {
		if (value !== "") {
			this._currentLabel = value;

			for (const labelId in this.labels) {
				if (this.labels[labelId]) {
					const label = this.labels[labelId];
					label.text = value;

					label.x = label.width * -0.5;
					label.y = label.height * -0.5;
				}
			}
		}
	}

	/**
	 * Gets the current label's text
	 */
	public get label(): string {
		return this._currentLabel;
	}

	/**
	 * get pointer down status
	 */
	public get isDown(): boolean {
		return this._isDown;
	}

	/**
	 * get pointer over status
	 */
	public get isOver(): boolean {
		return this._isOver;
	}

	/**
	 * get pointer canceled status
	 */
	public set lockOnClick(value: boolean) {
		this.parameters.lockOnClick = value;
	}

	/**
	 * get pointer canceled status
	 */
	public get lockOnClick(): boolean {
		return this.parameters.lockOnClick;
	}

	// Inits the label of the button
	private initLabel(): void {
		this.labels = {};
		this.spineData.slots.forEach((slot) => {
			if (slot.name === "label") {
				this.buildLabel(slot.name, "default");
			} else if (slot.name.indexOf("label_") === 0) {
				this.buildLabel(slot.name, slot.name.replace("label_", ""));
			}
		});
	}

	// Builds a label which has the given style and attached to the given slot name
	private buildLabel(slotName: string, styleName: string): void {
		const style = this.parameters.label.style[styleName];
		if (style) {
			const label = new TextField("", style);
			label.scaleY = -1;
			this.labels[styleName] = label;
			this.setSlotAttachementByName(slotName, label);
		}
	}

	// Create proxy for all supported interactions
	// See SpineButton.buttonAnimations
	private initInteractionsProxies(): void {
		this.parameters.animations.forEach((i) => this.proxyInteraction(i));

		const onAnimationChangeEvent = (e: TimelineAnimationEvent) => {
			const emitter = e.emitter as TimelineAnimation;
			const currentInteraction = this.getInteractionByName(emitter.name);

			if (currentInteraction) {
				if (currentInteraction.next && this.animationExist(currentInteraction.next)) {
					this.resume(currentInteraction.next);
				}
			}

			if (emitter.name !== "idleLoop") {
				this.resetCTA();
			}
		};

		this.onAnimationLoop(onAnimationChangeEvent);
		this.onAnimationRepeat(onAnimationChangeEvent);
		this.onAnimationComplete(onAnimationChangeEvent);
	}

	// will init the call to action delayed call
	private initCTA(ctaOptions: any): void {
		if (ctaOptions) {
			this.parameters.callToAction.minDelay = ctaOptions.minDelay;
			this.parameters.callToAction.maxDelay = ctaOptions.maxDelay;

			const ctaAnimation = this.animations.find((anim) => {
				return anim.name === "calltoaction" || anim.name === "CTA";
			});

			if (ctaAnimation) {
				this.parameters.callToAction.label = ctaAnimation.name;
				if (this.parameters.callToAction.label) {
					this.resetCTA();
				}
			}
		}
	}

	private playCTA(ctaAnimationName: string): void {
		if (this.enabled && this.currentAnimation.name === "idleLoop") {
			this.resume(ctaAnimationName);
			this.resetCTA();
		}
	}

	// reset the call to action delayed call
	private resetCTA(): void {
		if (this.ctaTimer) {
			this.ctaTimer.kill();
			this.ctaTimer = null;
		}

		if (this.parameters.callToAction) {
			const ctaDelay = this.getRandomBetween(
				this.parameters.callToAction.minDelay,
				this.parameters.callToAction.maxDelay
			);

			this.ctaTimer = TweenMax.delayedCall(ctaDelay, () => {
				this.playCTA(this.parameters.callToAction.label);
			});
		}
	}

	// return a number between min and max values
	private getRandomBetween(min: number, max: number): number {
		return min + Math.random() * (max - min);
	}

	// Returns an interaction by its name
	private getInteractionByName(nameOrLabel: string): ISpineButtonAnimation {
		return this.parameters.animations.find((anim) => {
			return anim.name === nameOrLabel || anim.label === nameOrLabel;
		});
	}

	// Create a proxy for the given interaction
	private proxyInteraction(animation: ISpineButtonAnimation): void {
		const animationExist = this.availableAnimations.indexOf(animation.name) > -1;
		if (animationExist && animation.event !== undefined) {
			if (isArray(animation.event)) {
				animation.event.forEach((event) => {
					this.on(event, (e) => this.onInteractionEvent(e, animation));
				});
			} else {
				this.on(animation.event, (e) => this.onInteractionEvent(e, animation));
			}
		}
	}

	/**
	 * Render text with WebGL
	 * @param renderer the pixi.js renderer (WebGLRenderer)
	 * @override PIXI.Sprite.renderWebGL
	 */
	public renderWebGL(renderer: WebGLRenderer): void {
		super.renderWebGL(renderer);
		if (!this.renderer) {
			this.renderer = renderer;
			this._interactionManager = this.renderer.plugins.interaction;
		}
	}

	/**
	 * Render text with Canvas renderer
	 * @param renderer the pixi.js renderer (CanvasRenderer)
	 * @override PIXI.Sprite._renderCanvas
	 */
	public renderCanvas(renderer: CanvasRenderer): void {
		super.renderCanvas(renderer);
		if (!this.renderer) {
			this.renderer = renderer;
			this._interactionManager = this.renderer.plugins.interaction;
		}
	}

	// Called each interaction
	private onInteractionEvent(
		interactionEvent: interaction.InteractionEvent,
		animation: ISpineButtonAnimation
	): void {
		const eventType: string = interactionEvent.type;

		switch (eventType) {
			case TimelineInteractiveEvent.touchmove:
				break;
			case TimelineInteractiveEvent.pointerover:
			case TimelineInteractiveEvent.pointerenter:
			case TimelineInteractiveEvent.mouseover:
				this._isOver = true;
				break;
			case TimelineInteractiveEvent.pointerout:
			case TimelineInteractiveEvent.pointerleave:
			case TimelineInteractiveEvent.mouseout:
				if (this._isDown && !this._isOver) {
					this.resume("up");
				}
				this._isOver = false;
				break;
			case TimelineInteractiveEvent.pointerdown:
			case TimelineInteractiveEvent.mousedown:
				this._isDown = true;
				break;
			case TimelineInteractiveEvent.pointerup:
			case TimelineInteractiveEvent.pointerupoutside:
			case TimelineInteractiveEvent.mouseup:
				this._isDown = false;
				break;
		}

		this.onUserInteraction(interactionEvent, animation);
	}

	private onUserInteraction(
		interactionEvent: interaction.InteractionEvent,
		animation: ISpineButtonAnimation
	): void {
		const eventType: string = interactionEvent.type;
		const click = eventType === TimelineInteractiveEvent.pointertap;

		if (this._enabled) {
			// update animation
			switch (eventType) {
				case TimelineInteractiveEvent.pointerupoutside:
					this.resume(eventType);
					break;
				default:
					this.resume(animation.name);
					break;
			}

			// trigger click + tracking
			if (click) {
				this.triggerClick(interactionEvent);
			}
		}
	}

	private cloneInteractionEventData(
		interactionEvent: interaction.InteractionEvent,
		newType: string
	): any {
		// clone interaction datas
		const interactionDatas = new interaction.InteractionData();
		interactionDatas.copyEvent(interactionEvent.data.originalEvent as any);

		// reset old event (don't know why but he does it in interaction manager)
		// interactionEvent.reset();

		return {
			currentTarget: interactionEvent.currentTarget,
			data: interactionDatas,
			reset: interactionEvent.reset,
			stopPropagation: interactionEvent.stopPropagation,
			stopped: interactionEvent.stopped,
			target: interactionEvent.target,
			type: newType,
		};
	}

	/**
	 * will track the click, release the interaction, and disable the button, then set the button state to downLoop.
	 * @param e the interaction event
	 */
	private triggerClick(interactionEvent: interaction.InteractionEvent): void {
		// As the interaction 'disable' is fired on button click, track the event before disabling the button
		this.trackClick();

		this._isDown = false;

		// then i can disable.
		if (this.lockOnClick) {
			this._isOver = false;
			this.enabled = false;
			// if the animation exist, set state to downloop
			if (this.animationExist("downLoop")) {
				this.resume("downLoop");
			}
		}
	}

	// track button's click
	private trackClick(): void {
		tracker.track(new ButtonClick(this.getName()));
	}

	/**
	 * Exports the current spine button
	 */
	public export(): any {
		// Do not export labels
		for (const l in this.labels) {
			if (!this.labels.hasOwnProperty(l)) {
				continue;
			}

			this.labels[l].doNotExport = true;
		}

		const parameters = Tools.assign({}, this.parameters, {
			skin: this.skeleton.skin ? this.skeleton.skin.name : undefined,
		});

		return Tools.assign(super.export(), {
			type: "spine.SpineButton",
			label: this._currentLabel,
			parameters,
		});
	}

	/**
	 * Parses the spine button
	 * @param config the serialized data
	 * @param baseUrl the base url of the loader
	 */
	public static parse(config: any, baseUrl: string, spineButton?: SpineButton): SpineButton {
		delete config.parameters.states;
		delete config.parameters.animations;

		if (!spineButton) {
			spineButton = new SpineButton(config.parameters);
		}

		if (config.label) {
			spineButton.label = config.label;
		}

		Timeline.parseSlotContainers(config, baseUrl, spineButton);

		return spineButton;
	}
}
