import { Container } from "pixi.js";
import { Scalar } from "@gdk/core";
import type { Point } from "pixi.js";

import { SVGGraphics } from "../../svg/SVGGraphics";
import type { IDrawStyle } from "../../svg/SVGGraphics";

export interface IScratchPathOptions {
	assetID: string; // the ID of the SVG or it's raw data string.
	resolution?: number; // the resolution of the svg path
	duration: number; // the duration requiered to travel this path.
	drawStyle?: IDrawStyle;
}

export interface IPathPoint {
	x: number;
	y: number;
}

export class ScratchPath extends Container {
	public duration: number; // the duration requiered to travel this path.
	public svg: SVGGraphics; // the svg graphic used to make the path.
	public values: IPathPoint[]; // the values of the points.
	private _width: number;
	private _height: number;
	private _resolution: number;

	/**
	 * will build create an SVG, and extract points arrays from it.
	 * this will be used as a travelable path.
	 * @param options IScratchPathOptions { duration: number, svg: SVGGraphics, values: IPathPoint[] }
	 */
	public constructor(options: IScratchPathOptions) {
		super();

		const drawStyle = options.drawStyle
			? options.drawStyle
			: {
					stroke: { lineWidth: 0, color: 0x00ff00, alpha: 0 },
					fill: { color: 0xff0000, alpha: 0 },
			  };
		this.svg = new SVGGraphics(options.assetID, options.resolution || 0.1, drawStyle);

		this.duration = options.duration || 1;
	}

	/**
	 * define the size of the svg shape and scale all the points.
	 * will be called by the Scratch to fit on it.
	 * @param width the desired width of the shape
	 * @param height the desired height of the shape
	 * @param resolution the scale ratio of the scratch.
	 */
	public setSize(width: number, height: number, resolution: number): void {
		this._width = width;
		this._height = height;
		this._resolution = resolution;
		this.svg.clear();

		this.values = [];
		let firstPoint: IPathPoint = null;

		this.svg.points.forEach((ppath: Point[]) => {
			ppath.forEach((p: Point) => {
				const scaledPoint = {
					x: Scalar.clamp(
						(p.x / 1024) * this._width * this._resolution + this.x,
						this.x,
						this.x + width
					),
					y: Scalar.clamp(
						(p.y / 1024) * this._height * this._resolution + this.y,
						this.y,
						this.y + height
					),
				};

				if (!firstPoint) {
					firstPoint = scaledPoint;
					this.svg.moveTo(firstPoint.x, firstPoint.y);
				} else {
					this.svg.lineTo(firstPoint.x, firstPoint.y);
				}

				this.values.push(scaledPoint);
			});
		});

		this.svg.lineStyle(4, 0xff0000, 1);
		this.svg.drawRect(0, 0, width, height);
		if (firstPoint) {
			this.svg.moveTo(firstPoint.x, firstPoint.y);
		}
	}
}
