import { spine, Graphics, DEG_TO_RAD } from "pixi.js";
import { TimelineMax, Linear } from "gsap";
import { Loader, Logger, Tools, Scalar } from "@gdk/core";
import type { Container, WebGLRenderer, CanvasRenderer, Rectangle } from "pixi.js";

import { ESpineTimelineEvent } from "./types";
import { DockingBounds } from "../container/dockingBounds";
import { ContainerExporter } from "../loading/export/containerExporter";
import { ContainerImporter } from "../loading/import/containerImporter";
import type { ISpineTimelineSequence, ISpineTimelineAnimation } from "./types";

/**
 * @experimental
 * Defines the main spine timeline class used to create, and play user-defined sequences containing
 * all the available animations of the current spine.
 */
export class SpineTimeline extends spine.Spine {
	/**
	 * Defines the minimum update time to apply transforms on spines skeletons.
	 */
	public static minUpdateTime: number = 1 / 120;

	/**
	 * GSAP TimelineMax reference used to control the overall animation instead of using the pixi.js ticker.
	 */
	public timelineMax: TimelineMax = null;
	/**
	 * Keep asset id used to retrieve the spine datas in the file loader.
	 */
	public readonly assetId: string;

	/**
	 * Sequences dictionary. The SpineTimeline is set to work with sequences. Each sequence contains a list of animations
	 * and can be looped. Animations are not intended to be looped but the sequence itself is done to.
	 * @example myTimeline.addSequence("mySequence", { loop: false, animations: [{ name: "enter" }, { name: "idle", repeatCount: 2 }] });
	 * @example myTimeline.play("mySequence", "enter"); // Plays mySequence starting from "enter".
	 * @example myTimeline.play("mySequence"); // Plays mySequence starting from the first animation of the sequence (here "enter").
	 * @example myTimeline.play(); // Plays the "default" sequence starting from the first animation of the default sequence.
	 */
	public sequences: { [index: string]: ISpineTimelineSequence } = {};

	private _currentTime: number = 0; // tweened property in "this" by the timelineMax's children tweens.
	private _lastTime: number = 0;
	private _currentSequence: string = "";
	private _currentAnimation: string = "";
	private _needsUpdate: boolean = false;

	private _stopped: boolean = true;
	private _isDestroyed: boolean = false;

	private _skeletonBounds: spine.core.SkeletonBounds = null;

	private _setUpBounds: Rectangle = null;

	private _stopCallbackRef: () => void = null;
	private _resolveCompleteFn: () => void = null;

	/**
	 * Constructor.
	 * @param assetId the id of the spine datas in the core-loader.
	 */
	public constructor(assetId: string) {
		super(Loader.get<any>(assetId, true).spineData);

		// Configure
		this.assetId = assetId;
		this._initSpine();
	}

	/**
	 * Inits the timeline.
	 */
	private _initSpine(): void {
		// Configure "this"
		this.autoUpdate = false;

		// Configure slots
		this.spineData.slots.forEach((s, index) => (this.slotContainers[index].name = s.name));

		// Setup default pose
		this.skeleton.setToSetupPose();
		this.update(SpineTimeline.minUpdateTime);

		// Configure pivot and bounds
		this._setUpBounds = this.getBounds();
		this.resetToSetupBounds();

		// Pivot and skeleton changed, re-update it
		this.update(SpineTimeline.minUpdateTime);

		// Create GSAP timeline used to contro the overall animation.
		this.createTimeline();

		// Events
		this.state.addListener({
			event: (_, event) => {
				const isReversed = this.timelineMax.reversed();
				if (!isReversed) {
					this.emit(ESpineTimelineEvent.Event, event.data);
				}
			},
		});

		// Create skeleton bounds used to detect hit areas (bounding boxes)
		this._skeletonBounds = new spine.core.SkeletonBounds();
		this._updateHitArea();
	}

	/**
	 * Creates the TimelineMax instance.
	 */
	protected createTimeline(): void {
		// Create timeline
		this.timelineMax = new TimelineMax({
			paused: true,
			repeat: 0,
			onStart: () => this.emit(ESpineTimelineEvent.Start),
			onComplete: () => this._onSequenceComplete(),
			onPause: () => this.emit(ESpineTimelineEvent.Pause),
			onRepeat: () => this.emit(ESpineTimelineEvent.Repeat),
			onUpdate: () => (this._needsUpdate = true),
		});

		// Create default sequences
		this.addSequence("default", {
			animations: this.spineData.animations.map((a) => ({ name: a.name })),
			loop: true,
			doNotExport: true,
		});
		this.addSequence("__direct", { animations: [], loop: false, doNotExport: true });
	}

	/**
	 * Renders the scratch using the WebGL renderer.
	 * @param renderer the webgl renderer reference.
	 */
	public renderWebGL(renderer: WebGLRenderer): void {
		// if the object is not visible or the alpha is 0 then no need to render this element
		if (!this.visible || this.worldAlpha <= 0 || !this.renderable) {
			return;
		}

		// Move the syncSkinning in the afterRender so our elements are ready when the render occurs.
		renderer.once("prerender" as any, () => !this._isDestroyed && this.syncSkinning());
		super.renderWebGL(renderer);
	}

	/**
	 * Renders the scratch using the canvas renderer.
	 * @param renderer the canvas renderer reference.
	 */
	public renderCanvas(renderer: CanvasRenderer): void {
		// if not visible or the alpha is 0 then no need to render this
		if (!this.visible || this.worldAlpha <= 0 || !this.renderable) {
			return;
		}

		// Move the syncSkinning in the afterRender so our elements are ready when the render occurs.
		renderer.once("prerender" as any, () => !this._isDestroyed && this.syncSkinning());
		super.renderCanvas(renderer);
	}

	/**
	 * Syncs the pixi-spine skinning transforms with the current timeline's progress.
	 * @param force specifies if the sync should be forced or tested before applied.
	 */
	protected syncSkinning(force: boolean = false, dt: number = 0): void {
		if (this._needsUpdate || force) {
			this._updateTracksAndTransforms(force, dt);
			this._needsUpdate = false;
		}
	}

	/**
	 * Adds a new sequence to the sequences dictionary.
	 * @param sequenceName the name (identifier) of the sequence to add.
	 * @param animations the array of animations available in the sequence being added.
	 * @see .sequences
	 */
	public addSequence(sequenceName: string, sequence: ISpineTimelineSequence): void {
		this.sequences[sequenceName] = sequence;
	}

	/**
	 * Gets the sequence identified by the given name.
	 * @param sequenceName the name of the sequence to retrieve.
	 */
	public getSequence(sequenceName: string): ISpineTimelineSequence {
		return this.sequences[sequenceName];
	}

	/**
	 * Removes the sequence identified by the given name.
	 * @param sequenceName the name of the sequence to remove.
	 */
	public removeSequence(sequenceName: string): void {
		if (this.sequences[sequenceName]) {
			delete this.sequences[sequenceName];
		}
	}

	/**
	 * Gets the current sequence being used to play its animations.
	 */
	public get currentSequence(): string {
		return this._currentSequence;
	}

	/**
	 * Gets the current animation being played in the current sequence.
	 */
	public get currentAnimation(): string {
		return this._currentAnimation;
	}

	/**
	 * Gets the current timescale of the TimelineMax. Default is 1.
	 */
	public get timeScale(): number {
		return this.timelineMax.timeScale();
	}

	/**
	 * Sets the current timescale of the TimelineMax. Default is 1.
	 */
	public set timeScale(scale: number) {
		this.timelineMax.timeScale(scale);
	}

	/**
	 * Stops the timeline
	 */
	public stop(): void {
		// Set to current animation default pose with the timeline paused.
		this.setPose(this.currentSequence, this.currentAnimation);
	}

	/**
	 * Moves the timeline's track head to the given position.
	 * @param position the position in terms of seconds.
	 */
	public seek(position: number | string): void {
		this._needsUpdate = true;
		this.timelineMax.seek(position, false);
	}

	/**
	 * Plays a direct animation without taking care of the current timeline configuration.
	 * @param animationName the name of the animation to play.
	 * @param loop wether or not the animation should be played looped.
	 * @param reversed defines wether or not the animation is played in reversed mode.
	 * @returns a resolved promise in case of looping. Else, a promise resolved when animation finished.
	 */
	public playDirect(
		animationName: string,
		loop: boolean = false,
		reversed: boolean = false
	): Promise<void> {
		// Configure direct sequence
		this.sequences.__direct.loop = loop;
		this.sequences.__direct.animations = [{ name: animationName }];
		return this.play("__direct", animationName, reversed);
	}

	/**
	 * Plays the timeline.
	 * @param sequenceName the name of the sequence to play. Default value is "default".
	 * @param from the name of the animation in sequence where to start the timeline.
	 * @param reversed defines wether or not the animation is played in reversed mode.
	 */
	public play(sequenceName: string = "default", from?: string, reversed?: boolean): Promise<void> {
		// Check sequence
		if (!this._checkSequence(sequenceName)) {
			return;
		}

		// Get sequence and animation's informations
		const sequence = this.sequences[sequenceName];
		const animationFromLabel = from ? sequence.animations.find((a) => a.label === from) : null;
		const labelName = animationFromLabel ? animationFromLabel.label : from;

		// Check animation
		from = animationFromLabel ? animationFromLabel.name : from;
		if (from && !this.state.hasAnimation(from)) {
			Logger.getLogger().info(`SpineTimeline: animation named "${from}" doesn't exists"`);
			from = undefined;
		}

		// Stop!
		this._stop();

		// Set state and immediately update
		if (reversed) {
			this._currentAnimation = from || sequence.animations[sequence.animations.length - 1].name;
		} else {
			this._currentAnimation = from || sequence.animations[0].name;
		}

		// Change sequence
		this._createLabelsFromSequence(sequenceName, reversed);

		return new Promise<void>((resolve) => {
			this.once(
				ESpineTimelineEvent.Stop,
				(this._stopCallbackRef = () => {
					// Don't keep the resolve function reference anymore.
					this._resolveCompleteFn = null;
					resolve();
				})
			);

			// Keep resolve function reference.
			this._resolveCompleteFn = resolve;

			// Repeat?
			this.timelineMax.repeat(sequence.loop ? -1 : 0);

			// Force sync with rendering
			this._startAnimation(sequence.animations.find((a) => a.name === this._currentAnimation));

			// Play!
			this.timelineMax.seek(this._currentAnimation, true);
			this.timelineMax.play(labelName);

			// Played, so not stopped anymore
			this._stopped = false;
		});
	}

	/**
	 * Reverses the currently playing timeline. If the timeline is paused, it is automatically playing in the reversed mode.
	 * @example typically used when the game needs to rewind an animation linked to an action.
	 */
	public reverse(): void {
		this.timelineMax.reverse();
	}

	/**
	 * Sets wether or not the timeline is reversed.
	 * @param reversed a boolean indicating that the timeline is reversed or not.
	 */
	public reversed(reversed: boolean): void {
		const isReversed = this.timelineMax.reversed();
		if (reversed !== isReversed) {
			this.timelineMax.reversed(reversed);
		}
	}

	/**
	 * Pauses the timeline.
	 */
	public pause(): void {
		this.timelineMax.pause();
		this.emit(ESpineTimelineEvent.Pause);
	}

	/**
	 * Resumes the timeline.
	 */
	public resume(): void {
		this.timelineMax.resume();
		this.emit(ESpineTimelineEvent.Resume);
	}

	/**
	 * Sets the spine to the given animation default pose.
	 * @param sequenceName the name of the sequence containing the given animation.
	 * @param animationName the animation to set its default pose.
	 */
	public setPose(sequenceName: string = "default", animationName?: string): void {
		// Check
		if (!this._checkSequence(sequenceName)) {
			return;
		}
		if (!animationName) {
			animationName = this.sequences[sequenceName].animations[0].name;
		}

		// Stop!
		this._stop();

		// Create labels
		this._createLabelsFromSequence(sequenceName, false);

		// Seek and update
		this.state.setAnimation(0, animationName, false);
		this.timelineMax.pause(animationName);

		this._currentTime = 0;
		this._currentSequence = sequenceName;
		this._currentAnimation = animationName;
		this.syncSkinning(true, SpineTimeline.minUpdateTime);
	}

	/**
	 * Gets the original bounds computed while instantiating spine timeline.
	 */
	public get setupBounds(): Rectangle {
		return this._setUpBounds;
	}

	/**
	 * Resets the current spine to the setup bounds.
	 */
	public resetToSetupBounds(): void {
		this.dockingBounds = new DockingBounds(this._setUpBounds.width, this._setUpBounds.height);
		this.skeleton.x = -this._setUpBounds.x;
		this.skeleton.y = -this._setUpBounds.y;
		this.pivot.set(this.skeleton.x, this.skeleton.y);
	}

	/**
	 * Sets the given skin to the spine.
	 * @param skinName the name of the skin to set.
	 */
	public setSkinByName(skinName: string): void {
		this._removeCurrentSkin();
		this.skeleton.setSkinByName(skinName);

		// Update all transforms. When pixi-spine changes the skin of a skeleton,
		// it tries to re-use already created sprites (SpineSprite) and applies
		// attachment transforms only and only when the sprite is created (x, y, rotation, etc.).
		// If the sprite already exists, then the new transformations of the attachment are not
		// applied as the sprite is no rebuilt.
		// TODO: test with meshes and graphics to see if the problems exists for them.
		this.skeleton.skin.attachments.forEach((attachmentMap) => {
			for (const attachmentKey in attachmentMap) {
				if (!attachmentMap.hasOwnProperty(attachmentKey)) {
					continue;
				}

				const attachment = attachmentMap[attachmentKey];
				if (!(attachment instanceof spine.core.RegionAttachment)) {
					continue;
				}

				const slot = this.skeleton.slots.find((s) => s.data.name === attachmentKey);
				if (!slot?.currentSprite) {
					continue;
				}

				slot.currentSprite.x = attachment.x;
				slot.currentSprite.y = attachment.y;
				slot.currentSprite.rotation = attachment.rotation * DEG_TO_RAD;
				slot.currentSprite.alpha = attachment.color.a;
			}
		});

		if (this.dockingBounds.isEmpty()) {
			// Previous skin was empty, refresh docking bounds.
			const bounds = this.getBounds();
			this.dockingBounds.setBounds(bounds.width, bounds.height);
		}

		this.syncSkinning(true, SpineTimeline.minUpdateTime);
		this._updateHitArea();
	}

	/**
	 * Returns wether or not the current spine has a skin identified by the given name.
	 * @param skinName the name of the skin to test if exists.
	 */
	public hasSkin(skinName: string): boolean {
		const s = this.skeleton.data.findSkin(skinName);
		if (!s) {
			return false;
		}

		return true;
	}

	/**
	 * Returns the first slot identified by the given name.
	 * @param slotName the name of the slot to find.
	 */
	public getSlotContainerByName(slotName: string): Container {
		return this.slotContainers.find((s) => s.name === slotName);
	}

	/**
	 * Destroys the spine timeline. This must be overriden as we need to kill some resources such
	 * as the timeline etc.
	 */
	public destroy(): void {
		if (this._isDestroyed) {
			return;
		}

		this._destroy();
		super.destroy();

		this._isDestroyed = true;
	}

	/**
	 * Destroys the timeline.
	 */
	private _destroy(): void {
		// First, stop and notify awaiters
		this._stop();

		// Clear timeline
		this.timelineMax.clear(true);
		this.timelineMax.kill();
	}

	/**
	 * Updates the spine's tracks and the spine's transforms.
	 * @param force specifies if the tracks and transforms should be updated in any case.
	 * @param dt specifies the delta time between the previous frame and current frame. Used to hack pixi-spine and force update using an Epsilon.
	 */
	private _updateTracksAndTransforms(force: boolean, dt: number): boolean {
		// Check current time
		const elapsedTime = Math.abs(this._currentTime - this._lastTime);
		if (!force && elapsedTime <= SpineTimeline.minUpdateTime) {
			return false;
		}
		this._lastTime = this._currentTime;

		// Update tracks
		this.state.tracks.forEach((t) => {
			if (!t) {
				return;
			}
			t.trackTime = this._currentTime;
		});

		// Update transforms
		this.update(dt);

		return true;
	}

	/**
	 * Creates all the tween max laels.
	 * @param sequenceName the name of the sequence being used.
	 */
	private _createLabelsFromSequence(sequenceName: string, reversed: boolean = false): void {
		// Clear
		this.timelineMax.clear(true);

		// Add labels
		const sequence = this.sequences[sequenceName];
		const animations = reversed ? sequence.animations.slice().reverse() : sequence.animations;

		let lastTime = 0;
		let lastLabel = "";

		animations.forEach((a, index) => {
			const animation = this.spineData.animations.find((sa) => sa.name === a.name);
			if (!animation) {
				return;
			}

			const label = a.label || a.name;
			const repeat = a.repeat || 0;
			const delay = a.delay || 0;
			const repeatDelay = a.repeatDelay || 0;
			const inverted = (a.inverted || false) !== reversed;

			// Add label
			this.timelineMax.addLabel(label, lastLabel ? `${lastLabel}+=${lastTime}` : 0);

			// Add tween
			this.timelineMax.fromTo(
				this,
				animation.duration,
				{
					_currentTime: inverted ? animation.duration : 0,
				},
				{
					immediateRender: true,
					_currentTime: inverted ? 0 : animation.duration,
					repeat,
					repeatDelay,
					delay,
					ease: a.ease || Linear.easeNone,
					onStart: () => {
						// Start animation only if the animation to start is not the same animation as the one already mounted on the state.
						// To ensure synchronization between rendering and tweenmax, the first animation is updated when
						// the .play function is called.
						if (this._getMountedStateAnimation() !== a.name) {
							this._startAnimation(a);
						}
					},
					onComplete: () => this._onAnimationComplete(animation, inverted),
					onRepeat: a.repeat
						? () => (this._currentTime = inverted ? 0 : animation.duration)
						: undefined,
					// ...
				},
				a.name
			);

			lastTime = animation.duration + animation.duration * repeat + delay;
			lastLabel = label;
		});

		// Finish
		this._currentSequence = sequenceName;
	}

	/**
	 * Starts the given animation.
	 */
	private _startAnimation(a: ISpineTimelineAnimation): void {
		// Set state and immediately update
		this.state.setAnimation(0, a.name, false);
		this.syncSkinning(true, SpineTimeline.minUpdateTime);

		this._currentAnimation = a.name;

		// Update hit area and emit
		this._updateHitArea();
		this.emit(ESpineTimelineEvent.StartAnimation, a.name);
	}

	/**
	 * Called on a sequence has been complete (normal or reverse).
	 */
	private _onSequenceComplete(): void {
		// Don't forget to remove the stop listener.
		this.removeListener(ESpineTimelineEvent.Stop as any, this._stopCallbackRef);
		// Stop the timeline.
		this._stop();
		// Resolve the awaiting callers.
		if (this._resolveCompleteFn) {
			this._resolveCompleteFn();
			this._resolveCompleteFn = null;
		}
		// Timeline completed, notify!
		this.emit(ESpineTimelineEvent.Complete);
	}

	/**
	 * Called on an animation finished.
	 */
	private _onAnimationComplete(animation: spine.core.Animation, inverted: boolean): void {
		this._lastTime = 0;
		this._currentTime = inverted ? 0 : animation.duration;
		this.syncSkinning(true, SpineTimeline.minUpdateTime);

		this.emit(ESpineTimelineEvent.AnimationComplete, animation.name);
	}

	/**
	 * Stops the timeline and tracks, notify awaiters and clear waiting events.
	 */
	private _stop(): boolean {
		// Check
		if (this._stopped) {
			return false;
		}

		// Force syncing skinning to ensure the last rendered frame is the last
		// frame available in the spine file.
		this.syncSkinning(true, SpineTimeline.minUpdateTime);

		// Reset timeline to 0 seconds.
		this.timelineMax.pause();

		// Clear spine tracks
		this.state.clearTracks();

		// Stopped!
		this._stopped = true;

		// Notify
		this.emit(ESpineTimelineEvent.Stop);

		return true;
	}

	/**
	 * Returns the name of the current animation being mounted in the state.
	 */
	private _getMountedStateAnimation(layer: number = 0): string {
		return this.state.getCurrent(layer).animation.name;
	}

	/**
	 * Removes the current skin's attachment before the new one is applied.
	 * This function exists only because some spines don't really use the skin system and go further by using
	 * empty slots etc.
	 */
	private _removeCurrentSkin(): void {
		if (!this.skeleton.skin) {
			return;
		}

		this.skeleton.skin.attachments.forEach((attachmentMap) => {
			for (const a in attachmentMap) {
				if (!attachmentMap.hasOwnProperty(a)) {
					continue;
				}

				const attachment = attachmentMap[a];
				const slot = this.skeleton.slots.find((s) => s.attachment === attachment);
				if (slot) {
					slot.setAttachment(null);
				}
			}
		});

		this.skeleton.setSkin(null);
	}

	/**
	 * Checks wether or not the given sequence exists. If doesn't exists, the error is logged and returns false.
	 * Else, retruns true.
	 * @param sequenceName the name of the sequence to check if exists.
	 */
	private _checkSequence(sequenceName: string): boolean {
		if (!this.sequences[sequenceName]) {
			Logger.getLogger().info(
				`SpineTimeline: sequence named "${sequenceName}" doesn't exists. No animations will be played."`,
				this
			);
			return false;
		}

		if (!this.sequences[sequenceName].animations.length) {
			Logger.getLogger().info(
				`SpineTimeline: sequence named "${sequenceName}" contains 0 animations to play. No animations will be played."`,
				this
			);
			return false;
		}

		return true;
	}

	/**
	 * Updates the hit areas to determine if the skeleton bounds should be used or not.
	 */
	private _updateHitArea(): void {
		const bb = this.skeleton.slots.find(
			(s) => s.attachment && s.attachment instanceof spine.core.BoundingBoxAttachment
		);
		if (!bb) {
			this.hitArea = null;
			return;
		}

		this.hitArea = {
			contains: (x, y) => {
				this._skeletonBounds.update(this.skeleton, true);
				if (this._skeletonBounds.containsPoint(x, y)) {
					return true;
				}
				return false;
			},
		};
	}

	/**
	 * Exports the spine timeline.
	 */
	public export(): any {
		// Do not export slot containers
		this.slotContainers.forEach((s) => (s.doNotExport = true));

		return Tools.assign(ContainerExporter.exportContainer(this), {
			type: "spine.SpineTimeline",
			assetId: this.assetId,
			skin: this.skeleton.skin ? this.skeleton.skin.name : undefined,
			children: [],
			slots: this.slotContainers.map((s) => ({
				name: s.name,
				children: s.children
					.filter(
						(c) =>
							!(c instanceof spine.SpineSprite) &&
							!(c instanceof spine.SpineMesh) &&
							!(c instanceof Graphics) &&
							!c.doNotExport &&
							c.name !== null
					)
					.map((c) => ({
						index: s.getChildIndex(c),
						serializationObject: ContainerExporter.export(c as Container),
					})),
			})),
			sequences: Object.keys(this.sequences)
				.filter((k) => !this.sequences[k].doNotExport)
				.map((k) => {
					const sequence = this.sequences[k];
					return {
						name: k,
						loop: sequence.loop,
						animations: sequence.animations.map((a) => ({
							name: a.name,
							repeat: a.repeat,
							repeatDelay: a.repeatDelay,
							delay: a.delay,
							ease: undefined, // TODO: not supported yet.
							label: a.label,
							inverted: a.inverted,
						})),
					};
				}),
		});
	}

	/**
	 * Parses the spine timeline according to the given configuration.
	 * @param config the serialized data.
	 * @param baseUrl the base url of the loader.
	 * @param timeline the existing timeline reference.
	 */
	public static parse(config: any, baseUrl: string, timeline?: SpineTimeline): SpineTimeline {
		// Create timeline
		if (!timeline) {
			timeline = new SpineTimeline(config.assetId);
		}

		// Sequences
		config.sequences.forEach((s) => {
			timeline.addSequence(s.name, { loop: s.loop, animations: s.animations });
		});

		// Skin
		if (config.skin && timeline.hasSkin(config.skin)) {
			timeline.setSkinByName(config.skin);
		}

		// Slots
		config.slots.forEach((s) => {
			const slot = timeline.getSlotContainerByName(s.name);
			if (!slot) {
				return;
			}

			s.children.forEach((c) => {
				const child = ContainerImporter.import(c.serializationObject, baseUrl);
				slot.addChildAt(child, Scalar.clamp(c.index, 0, slot.children.length));
			});
		});

		return timeline;
	}
}
