import type { Container } from "pixi.js";

import { AssetElement } from "./assetElement";
import { ContainerImporter } from "../loading/import/containerImporter";
import type { IAssetsRoot, IProjectContainer } from "../loading/model";
import type { IStyles } from "../ui/baseui/text/textStyle";

export class AssetStore {
	// Public members
	public static prefabs: Array<AssetElement<IProjectContainer>> = [];
	public static styles: Array<AssetElement<IStyles>> = [];

	// Private memnbers
	private static _baseUrl: string = "";

	/**
	 * Returns the asset identified by the given Id
	 * @param id the id of the asset to find
	 */
	public static getAssetById(id: string): AssetElement<any> {
		const prefab = this.prefabs.find((p) => p.id === id);
		if (prefab) {
			return prefab;
		}

		const style = this.styles.find((s) => s.id === id);
		if (style) {
			return style;
		}

		// Etc.

		return null;
	}

	/**
	 * Instancate a prefab identified by the given id
	 * @param name the prefab name in order to retrieve configuration
	 * @param instance the base container instance to configure following the associated prefab configuration
	 */
	public static instanciatePrefab<T extends Container>(name: string, instance?: T): T {
		const prefab = this.prefabs.find((p) => p.name === name);
		if (prefab) {
			return ContainerImporter.import(prefab.data, this._baseUrl, instance) as T;
		}

		return null;
	}

	/**
	 * Exports all the assets to an array of JSONs
	 */
	public static export(): any {
		const result = {
			prefabs: [],
			styles: [],
			// Others...
		};

		this.prefabs.forEach((p) => result.prefabs.push(p.export()));
		this.styles.forEach((s) => result.styles.push(s.export()));

		return result;
	}

	/**
	 * Parses the given assets to store into the assets store
	 * @param assets the assets to parse typically coming from a json project
	 */
	public static parse(assets: IAssetsRoot, baseUrl: string): void {
		this._baseUrl = baseUrl;

		// Prefabs
		if (assets.prefabs) {
			assets.prefabs.forEach((p) => {
				this.prefabs.push(new AssetElement(p.name, p.data, p.id, p.type));
			});
		}

		// Styles
		if (assets.styles) {
			assets.styles.forEach((s) => {
				this.styles.push(new AssetElement(s.name, s.data, s.id, s.type));
			});
		}
	}

	/**
	 * Clears the asset store by removing all assets
	 */
	public static clear(): void {
		this.prefabs = [];
		this.styles = [];
	}
}
