import { Container } from "pixi.js";

export abstract class Scratchable extends Container {
	public static REVEAL_EVENT: string = "Scratchable::reveal";
	public static FULL_REVEAL_EVENT: string = "Scratchable::fullReveal";
	public static AUTO_EVENT: string = "Scratchable::auto";

	public isRevealed: boolean;
	public isAuto: boolean;

	public auto: () => void;
	public getProgress: () => number;
	public reset: () => void;
}
