import { Container, Point } from "pixi.js";
import { TweenMax, Power2 } from "gsap";
import type { WebGLRenderer, CanvasRenderer } from "pixi.js";

import { Helpers } from "./helpers";
import { DraggEvent } from "./draggEvent";

export interface ISliderOptions {
	track: PIXI.mesh.NineSlicePlane; // the slider "background" where the handle will slide onto.
	handle: PIXI.mesh.NineSlicePlane; // the sliding element
	minValue?: number; // the minimum scroll value (default 0)
	maxValue?: number; // the minimum scroll value (default 100)
	decimals?: number; // up decimals to trigger scroll value update more often (default 0)
	vertical?: boolean; // is this a vertical slider
	onValueChange?: () => void; // the onValueChange callback
	onValueChanging?: () => void; // the onValueChanging callback
	value?: number; // start scroll value
}

export class Slider extends Container {
	public vertical: boolean;
	public track: PIXI.mesh.NineSlicePlane;
	public handle: PIXI.mesh.NineSlicePlane;
	public decimals: number;
	public minValue: number;
	public maxValue: number;

	protected pValue: number;

	protected pOnValueChange: (newValue: number) => void;
	protected pOnValueChanging: (newValue: number) => void;

	private _disabled: boolean;
	private _canvas: HTMLCanvasElement;

	protected _width: number = 0;
	protected _height: number = 0;

	private _trackWidth: number = 0;
	private _trackHeight: number = 0;
	private _handleWidth: number = 0;
	private _handleHeight: number = 0;

	// now i store those (easier debugging)
	private _handleDraggEvent: DraggEvent;
	private _trackDraggEvent: DraggEvent;

	/**
	 * create a Slider element with specified options
	 */
	public constructor(options: ISliderOptions) {
		super();

		this.pValue = 0;
		this._disabled = false;

		this.track = options.track;
		this.handle = options.handle;
		this.minValue = options.minValue || 0;
		this.maxValue = options.maxValue || 100;
		this.decimals = options.decimals || 0;
		this.vertical = options.vertical || false;
		this.pOnValueChange = options.onValueChange || null;
		this.pOnValueChanging = options.onValueChanging || null;
		this.value = options.value || 0;

		this._handleWidth = this.handle.width;
		this._handleHeight = this.handle.height;

		this._trackWidth = this.track.width;
		this._trackHeight = this.track.height;

		this._width = this._trackWidth;
		this._height = this._trackHeight;

		this.handle.texture.baseTexture.mipmap = false;
		this.track.texture.baseTexture.mipmap = false;

		this.addChild(this.track);
		this.addChild(this.handle);
	}

	/**
	 * Render text with WebGL
	 * @param renderer the pixi.js renderer (WebGLRenderer)
	 * @override PIXI.Sprite.renderWebGL
	 */
	public renderWebGL(renderer: WebGLRenderer): void {
		super.renderWebGL(renderer);
		if (!this._canvas) {
			this._canvas = renderer.view;
			this.initialize();
		}
	}

	/**
	 * Render text with Canvas renderer
	 * @param renderer the pixi.js renderer (CanvasRenderer)
	 * @override PIXI.Sprite._renderCanvas
	 */
	public renderCanvas(renderer: CanvasRenderer): void {
		super.renderCanvas(renderer);
		if (!this._canvas) {
			this._canvas = renderer.view;
			this.initialize();
		}
	}

	/**
	 * @param value sets the scroll value
	 */
	public set value(val: number) {
		this.pValue = Helpers.Cap(val, this.minValue, this.maxValue);

		if (typeof this.onValueChange === "function") {
			this.onValueChange(this.value);
		}
		if (typeof this.pOnValueChanging === "function") {
			this.pOnValueChanging(this.value);
		}

		this.update();
	}

	/**
	 * gets the scroll value
	 */
	public get value(): number {
		return Helpers.Round(Helpers.Lerp(this.minValue, this.maxValue, this.pValue), this.decimals);
	}

	/**
	 * sets the onValueChange callback.
	 * will be triggered after a scroll value update.
	 */
	public set onValueChange(val: (newValue: number) => void) {
		this.pOnValueChange = val;
	}

	/**
	 * gets the onScrollRatioChange callback.
	 */
	public get onValueChange(): (newValue: number) => void {
		return this.pOnValueChange;
	}

	/**
	 * sets the onValueChanging callback.
	 * will be triggered while scroll value is updated.
	 */
	public set onValueChanging(val: (newValue: number) => void) {
		this.pOnValueChanging = val;
	}

	/**
	 * gets the onValueChanging callback.
	 */
	public get onValueChanging(): (newValue: number) => void {
		return this.pOnValueChanging;
	}

	/**
	 * is the slider disabled (not interactive)
	 */
	public get disabled(): boolean {
		return this._disabled;
	}

	/**
	 * set the slider disable (not interactive) or not
	 */
	public set disabled(val: boolean) {
		if (val !== this._disabled) {
			this._disabled = val;
			this.handle.buttonMode = !val;
			this.handle.interactive = !val;
			this.track.interactive = !val;
		}
	}

	// remove this comment when allow-override-transient-properties is included in typescript
	// @ts-ignore
	public get width(): number {
		return this._width;
	}

	// remove this comment when allow-override-transient-properties is included in typescript
	// @ts-ignore
	public get height(): number {
		return this._height;
	}

	/**
	 * perform an update
	 */
	protected update(soft: boolean = false): void {
		let handleSize;
		let val;
		if (this.vertical) {
			handleSize = this.handle.height;
			val = (this._height - handleSize) * this.pValue; // + (handleSize * 0.5);
			if (soft) {
				TweenMax.to(this.handle, 0.5, { ease: Power2.easeOut, y: val });
			} else {
				TweenMax.set(this.handle, { y: val });
			}
		} else {
			handleSize = this.handle.width;
			val = (this._width - handleSize) * this.pValue; // + (handleSize * 0.5);
			if (soft) {
				TweenMax.to(this.handle, 0.5, { ease: Power2.easeOut, x: val });
			} else {
				TweenMax.set(this.handle, { x: val });
			}
		}
	}

	/**
	 * initialize Events handlers
	 */
	protected initialize(): void {
		let startValue = 0;
		let maxPosition;
		const localMousePosition: Point = new Point();
		let pLastChanging;
		let pLastChange;

		// Handle dragging (stop propagation is on by default)
		this._handleDraggEvent = new DraggEvent({
			target: this.handle,
			canvas: this._canvas,
			dragThreshold: 1,
		});

		this._handleDraggEvent.onDragStart = (event) => {
			startValue = this.pValue;
			maxPosition = this.vertical
				? this._height - this.handle.height
				: this._width - this.handle.width;
		};

		this._handleDraggEvent.onDragMove = (event, offset) => {
			if (!maxPosition) {
				this.pValue = 0;
			} else {
				this.pValue = Math.max(
					0,
					Math.min(1, startValue + (this.vertical ? offset.y : offset.x) / maxPosition)
				);
			}

			triggerScrollRatioChanging();
			this.update(true);
		};

		this._handleDraggEvent.onDragEnd = () => {
			triggerScrollRatioChange();
			this.update(true);
		};

		// Bar pressing/dragging
		this._trackDraggEvent = new DraggEvent({
			target: this.track,
			canvas: this._canvas,
		});

		this._trackDraggEvent.onPress = (event, isPressed) => {
			if (isPressed) {
				updatePositionToMouse(event.data.global, true);
			}
			// stop propagation is on by default
		};

		this._trackDraggEvent.onDragMove = (event, offset, position) => {
			updatePositionToMouse(position, true);
		};

		this._trackDraggEvent.onDragEnd = (e) => {
			triggerScrollRatioChange();
		};

		const updatePositionToMouse = (mousePosition: Point, soft: boolean = false) => {
			this.track.toLocal(mousePosition, null, localMousePosition, true);
			let newPos;
			let maxPos;

			if (this.vertical) {
				newPos = localMousePosition.y - this.handle.height * 0.5;
				maxPos = this._height - this.handle.height;
			} else {
				newPos = localMousePosition.x - this.handle.width * 0.5;
				maxPos = this._width - this.handle.width;
			}

			this.pValue = !maxPos ? 0 : Math.max(0, Math.min(1, newPos / maxPos));

			this.update(soft);
			triggerScrollRatioChanging();
		};

		const triggerScrollRatioChange = () => {
			this.emit("change", this.value);
			if (pLastChange !== this.value) {
				pLastChange = this.value;
				if (typeof this.onValueChange === "function") {
					this.onValueChange(this.value);
				}
			}
		};

		const triggerScrollRatioChanging = () => {
			this.emit("changeing", this.value);
			if (pLastChanging !== this.value) {
				pLastChanging = this.value;
				if (typeof this.pOnValueChanging === "function") {
					this.pOnValueChanging(this.value);
				}
			}
		};
	}
}
