import "pixi-spine";
import { TweenMax, Linear } from "gsap";
import type { spine } from "pixi.js";

import { ETimelineType } from "./timeline";
import {
	ETimelineEventsType,
	TimelineStartEvent,
	TimelineCompleteEvent,
	TimelineLoopEvent,
	TimelineReverseLoopEvent,
	TimelineRepeatEvent,
	TimelineReverseCompleteEvent,
	TimelineUpdateEvent,
	TimelineAnimationEvent,
} from "./timelineEvents";
import type { ITimelineCallback, TimelineEvent } from "./timelineEvents";
import type { Timeline } from "./timeline";

/**
 * Represents the timeline animation options
 */
export interface ITimelineAnimationOptions {
	name: string;
	label?: string;
	layer?: number;
	delay?: number;
	repeatDelay?: number;
	loop?: boolean;
	repeat?: number;
	timeScale?: number;
	paused?: boolean;
	pauseAtEnd?: boolean;
}

export class TimelineAnimation {
	// Public members
	public name: string;
	public label: string;
	public entry: spine.core.TrackEntry;
	public tween: TweenMax;
	public layer: number;
	public repeat: number;
	public delay: number;
	public loop: boolean;
	public repeatDelay: number;
	public pauseAtEnd: boolean;
	public events: spine.core.Event[];
	public startTime: number = 0;
	public endTime: number = 0;
	private _index: number;

	// Private members
	private _animation: spine.core.Animation;
	private _timeline: Timeline;
	private _time: number = 0;
	private _callbacks: ITimelineCallback[] = [];

	/**
	 * Constructor
	 * @param timeline the parent timeline
	 * @param options options for this animation
	 */
	public constructor(timeline: Timeline, options: ITimelineAnimationOptions) {
		this._timeline = timeline;
		this.name = options.name;
		this._animation = this._timeline.stateData.skeletonData.findAnimation(this.name);

		if (!this._animation) {
			throw new Error(`TimelineAnimation: the animation '${this.name}' doesn't exist !`);
		} else {
			this.configure(options);
		}
	}

	/**
	 * Configures the timeline
	 * @param options options for this animation
	 */
	public configure(options: ITimelineAnimationOptions): void {
		for (const timeline of this._animation.timelines) {
			if (timeline instanceof PIXI.spine.core.EventTimeline) {
				this.events = timeline.events;
				break;
			}
		}

		this.time = 0;
		this.layer = options.layer || 0;
		this.delay = (options.delay || 0) / 1000;
		this.repeat = options.repeat > -1 ? options.repeat : 0;
		this.repeatDelay = (options.repeatDelay || 0) / 1000;
		this.pauseAtEnd = options.pauseAtEnd || false;
		this.loop = options.loop === true || options.repeat === -1;

		let repeatCount: number = 0;
		let reverseLoopCount: number = 0;
		let loopCount: number = 0;

		this.tween = new TweenMax(this, this._animation.duration, {
			time: this._animation.duration,
			delay: this.delay,
			repeat: this.repeat,
			repeatDelay: this.repeatDelay,
			ease: Linear.easeNone,
			onStart: () => {
				this._emitEvent(new TimelineStartEvent(this));
			},
			onComplete: () => {
				if (this.loop) {
					this._timeline.timeline.play(this.startTime);
					this._emitEvent(
						new TimelineLoopEvent(this, {
							loop: loopCount++,
						})
					);
				} else if (this.pauseAtEnd || this._timeline.pauseOnAnimationEnd) {
					this._timeline.timeline.pause(this.startTime + this.endTime);
					this._emitEvent(new TimelineCompleteEvent(this));
				} else {
					this._emitEvent(new TimelineCompleteEvent(this));
				}
			},
			onRepeat: () => {
				let repeat: number = this.tween.repeat();
				let remaining: number;

				if (repeatCount >= repeat) {
					repeatCount = 0;
				}

				repeatCount++;
				remaining = repeat - repeatCount;

				if (repeat === -1) {
					repeat = Infinity;
					repeatCount = Infinity;
					remaining = Infinity;
				}

				this._emitEvent(
					new TimelineRepeatEvent(this, {
						repeatCount,
						repeat,
						remaining,
					})
				);
			},
			onReverseComplete: () => {
				if (this.loop) {
					this._timeline.timeline.reverse(this.endTime);
					this._emitEvent(
						new TimelineReverseLoopEvent(this, {
							loop: reverseLoopCount++,
						})
					);
				} else {
					this._emitEvent(new TimelineReverseCompleteEvent(this));
				}
			},
			onUpdate: () => {
				this._emitEvent(new TimelineUpdateEvent(this));
			},
		});

		this.timeScale = options.timeScale || 1;
		this.label = options.label || "";
		this.startTime = this._timeline.timeline.duration();
		this.endTime = this.startTime + this.tween.duration();

		if (this.label !== "") {
			this._timeline.timeline.add(this.label);
			this._timeline.timeline.add(this.tween, this.label);
		} else {
			this._timeline.timeline.add(this.tween);
		}

		if (this.events) {
			this.events.forEach((ev: spine.core.Event) => {
				this.addEventCallback(ev);
			});
		}
	}

	/**
	 * Remove all callback listeners
	 */
	public clearCallbacks(): void {
		this._callbacks = [];
	}

	/**
	 * Returns the current time
	 */
	public get time(): number {
		return this._time;
	}

	/**
	 * Sets the current time
	 * @param value: the current time in seconds
	 */
	public set time(value: number) {
		this._time = value;

		// update entry time from tweenmax time
		if (this.entry) {
			this.entry.trackTime = this.time;
		}
	}

	/**
	 * Set index value
	 * @param value number to set as index
	 */
	public set index(value: number) {
		this._index = value;
	}

	/**
	 * Returns the current time
	 */
	public get index(): number {
		return this._index;
	}

	/**
	 * Returns the animation timeScale
	 */
	public get timeScale(): number {
		return this.tween.timeScale();
	}

	/**
	 * Sets the animation timeScale
	 * @param value: the timeScale ratio
	 */
	public set timeScale(value: number) {
		this.tween.timeScale(value);
	}

	/**
	 * Returns the timeline type
	 */
	public get type(): ETimelineType {
		return ETimelineType.Animation;
	}

	/**
	 * Add a callback for that Animation
	 * @param callback the callback to add
	 */
	public onEvent(callback: (e?: TimelineEvent) => void): TimelineAnimation {
		this._callbacks.push({
			type: ETimelineEventsType.AnimationEvent,
			emitter: ETimelineType.Animation,
			callback,
		});
		return this;
	}

	/**
	 * Add a callback for that Animation Start event.
	 * @param callback the callback to add
	 */
	public onStart(callback: (e?: TimelineEvent) => void): TimelineAnimation {
		this._callbacks.push({
			type: ETimelineEventsType.Start,
			emitter: ETimelineType.Animation,
			callback,
		});
		return this;
	}

	/**
	 * Add a callback for that Animation Complete event.
	 * @param callback the callback to add
	 */
	public onComplete(callback: (e?: TimelineEvent) => void): TimelineAnimation {
		this._callbacks.push({
			type: ETimelineEventsType.Complete,
			emitter: ETimelineType.Animation,
			callback,
		});
		return this;
	}

	/**
	 * Add a callback for that Animation Update event.
	 * @param callback the callback to add
	 */
	public onRepeat(callback: (e?: TimelineEvent) => void): TimelineAnimation {
		this._callbacks.push({
			type: ETimelineEventsType.Repeat,
			emitter: ETimelineType.Animation,
			callback,
		});
		return this;
	}

	/**
	 * Add a callback for that Animation Loop event.
	 * @param callback the callback to add
	 */
	public onLoop(callback: (e?: TimelineEvent) => void): TimelineAnimation {
		this._callbacks.push({
			type: ETimelineEventsType.Loop,
			emitter: ETimelineType.Animation,
			callback,
		});
		return this;
	}

	/**
	 * Add a callback for that Animation Reverse Loop event.
	 * @param callback the callback to add
	 */
	public onReverseLoop(callback: (e?: TimelineEvent) => void): TimelineAnimation {
		this._callbacks.push({
			type: ETimelineEventsType.ReverseLoop,
			emitter: ETimelineType.Animation,
			callback,
		});
		return this;
	}

	/**
	 * Add a callback for that Animation Update event.
	 * @param callback the callback to add
	 */
	public onUpdate(callback: (e?: TimelineEvent) => void): TimelineAnimation {
		this._callbacks.push({
			type: ETimelineEventsType.Update,
			emitter: ETimelineType.Animation,
			callback,
		});
		return this;
	}

	/**
	 * Add a callback for that Animation Reverse Complete event.
	 * @param callback the callback to add
	 */
	public onReverseComplete(callback: (e?: TimelineEvent) => void): TimelineAnimation {
		this._callbacks.push({
			type: ETimelineEventsType.ReverseComplete,
			emitter: ETimelineType.Animation,
			callback,
		});
		return this;
	}

	private addEventCallback(ev: spine.core.Event): void {
		const eventPosition = this.label !== "" ? `${this.label}+=${ev.time}` : ev.time;

		const animationEventParams = {
			animation: this.name,
			name: `${ev.data.name}`,
			floatValue: ev.floatValue,
			intValue: ev.intValue,
			stringValue: ev.stringValue,
			time: ev.time,
		};

		this._timeline.timeline.addCallback(
			(params: any) => {
				const animEvt = new TimelineAnimationEvent(this, animationEventParams);
				this._emitEvent(animEvt);
			},
			eventPosition,
			[],
			this
		);
	}

	// Emits an event
	private _emitEvent(event: TimelineEvent): void {
		if (!this._callbacks) {
			return;
		}

		this._callbacks.forEach((cb) => {
			if (cb.type === event.type || cb.type === ETimelineEventsType.All) {
				cb.callback(event);
			}
		});
	}

	/**
	 * Exports TimelineAnimation.
	 */
	public export(): ITimelineAnimationOptions {
		return {
			name: this.name,
			label: this.label,
			layer: this.layer,
			delay: this.delay,
			repeatDelay: this.repeatDelay,
			loop: this.loop,
			repeat: this.repeat,
			timeScale: this.timeScale,
			pauseAtEnd: this.pauseAtEnd,
		};
	}
}
