import { ContainerImporter } from "./containerImporter";
import { StateImporter } from "./stateImporter";
import type { IProject, IViewMap } from "../model";
import type { LoadedView } from "../../view/loadedView";
import type { GamePixi } from "../../gamePixi";

export class ProjectImporter {
	/**
	 * Import the project from the given configuration
	 * @param game the game reference
	 * @param project the project configuration
	 * @param baseUrl the base url of the loader
	 * @param viewMap the map containing the view constructors
	 */
	public static importProject(
		game: GamePixi,
		project: IProject,
		baseUrl: string,
		viewMap: IViewMap = {}
	): void {
		// Configure stage
		ContainerImporter.importContainer(project.stage, baseUrl, game.stage, project, game);

		// Metadatas
		game.metadata = project.metadata || game.metadata;

		// Import state
		if (!game.state) {
			game.state = StateImporter.importState(project.views, baseUrl, viewMap, project);
		} else {
			// Call .config
			game.state.stateKeys().forEach((k) => {
				// Find view config
				for (const c of project.views) {
					if (c.stateName === k) {
						// Configure view
						const view = game.state.states[k].view as LoadedView;

						if (view.config) {
							view.config(game.loader.baseUrl, c, project);
						} else {
							// we should use logger for that @TODO
							console.warn(
								`ProjectLoader: a view is defined as "${k}" in the project but not defined as LoadedView.`
							);
						}
						return;
					}
				}
			});
		}
	}
}
