import { Container, Point } from "pixi.js";
import { Tools } from "@gdk/core";

import { Timeline } from "../../spine/timeline";
import { Scratch } from "../scratch/scratch";
import { Scratch2 } from "../scratch2";
import { ContainerImporter } from "../../loading/import/containerImporter";
import type { ITimelineOptions } from "../../spine/timeline";
import type { TimelineEvent } from "../../spine/timelineEvents";
import type { ITimelineAnimationOptions } from "../../spine/timelineAnimation";
import type { IScratchOptions } from "../scratch/scratch";
import type { IScratch2Options } from "../scratch2";

/**
 * Define a spineScratch.
 * It extends the ITimelineOptions and add an array of IScratchSlotOptions
 */
export interface ISpineScratchOptions extends ITimelineOptions {
	/**
	 * Define the scratchable areas you want in the SpineScratch.
	 */
	scratchSlotsOptions: IScratchSlotOptions[];
	/**
	 * Define the symbols below the scratchable areas.
	 */
	symbolSlotsOptions: ISymbolSlotOptions[];
}

/**
 * Define a scratchable area in a SpineScratch.
 * It extends the IScratchOptions for the configuration of the scratch.
 */
export interface IScratchSlotOptions {
	/**
	 * Name of the slot which will host the Scratch.
	 */
	slotName: string;
	/**
	 * @deprecated, use scratch2 instead.
	 * Scratch or definition of the scratch to place in the slot.
	 */
	scratch?: IScratchOptions | Scratch;
	/**
	 * Scratch2 or definition of the scratch2 to place in the slot.
	 */
	scratch2?: IScratch2Options | Scratch2;
	/**
	 * When content is a Timeline which contains spine defined hitareas,
	 * the scratch will use them as hitarea.
	 */
	useContentTimelineHitArea?: boolean;
}

/**
 * Define a symbol in a SpineScratch.
 */
export interface ISymbolSlotOptions {
	/**
	 * Name of the slot which will host the Symbol.
	 */
	slotName: string;
	/**
	 * Symbol or definition of the symbol to place in the slot.
	 */
	symbol: ITimelineOptions | Timeline;
}

/**
 * Container of a Scratch.
 */
export interface IScratchSlot {
	slotName: string;
	scratch: Scratch | Scratch2;
	content: Container;
	useContentTimelineHitArea: boolean;
}

/**
 * Container of a symbol.
 */
export interface ISymbolSlot {
	slotName: string;
	symbol: Timeline;
}

export enum ESpineScratchEvent {
	ReadyToScratch = "readytoscratch",
	AllScratchsRevealed = "allscratchsrevealed",
	Exited = "exited",
}

/**
 * @experimental
 * Timeline with scratchable content and timeline symbols.
 */
export class SpineScratch extends Timeline {
	public static DEFAULT_SEQUENCE: ITimelineAnimationOptions[] = [
		{ name: "enter", label: "enter" },
		{ name: "idle", label: "idle", pauseAtEnd: true },
		{ name: "exit", label: "exit", pauseAtEnd: true },
	];

	public scratchSlots: IScratchSlot[] = [];
	public symbolSlots: ISymbolSlot[] = [];

	/**
	 * Instantiate a SpineScratch.
	 * @param parameters ITimelineOptions for the base animation.
	 * @param scratchOptions Array of IScratchSlotOptions to define the scratchables zones in the Timeline.
	 */
	public constructor(parameters: ISpineScratchOptions) {
		super(parameters);

		this._setupScratchs(parameters.scratchSlotsOptions);
		this._setupSymbols(parameters.symbolSlotsOptions);
		this._setupAnimations();
		this.reset();
	}

	/**
	 * Play the "enter" animation of the Timeline.
	 */
	public enter(): void {
		this.play("enter");
	}

	/**
	 * Play the "exit" animation of the Timeline.
	 */
	public exit(): void {
		this.play("exit");
	}

	/**
	 * Call the auto method of each scratch in the SpineScratch.
	 */
	public auto(): void {
		this.scratchSlots.forEach((slot: IScratchSlot) => slot.scratch.auto());
	}

	/**
	 * Stop the animation, hide the container and reset the scratchs.
	 */
	public reset(): void {
		this.pause();
		this.visible = false;
		this._resetScratchs();
		this._listenForScratchsRevealation();
	}

	private _resetScratchs(): void {
		this.scratchSlots.forEach((slot: IScratchSlot) => slot.scratch.reset());
		this.disableScratchs();
	}

	/**
	 * Enable the scratchs.
	 */
	public enableScratchs(): void {
		this.scratchSlots.forEach((slot: IScratchSlot) => (slot.scratch.interactive = true));
	}

	/**
	 * Disable the scratchs.
	 */
	public disableScratchs(): void {
		this.scratchSlots.forEach((slot: IScratchSlot) => (slot.scratch.interactive = false));
	}

	/**
	 * Wait for the SpineScratch to play the enter animation and then to enable the scratchs.
	 */
	public waitToBeReadyToScratch(): Promise<void> {
		return this._waitFor(ESpineScratchEvent.ReadyToScratch);
	}

	/**
	 * Wait for all the scratchs to be revealed.
	 */
	public waitForAllScratchsRevealed(): Promise<void> {
		return this._waitFor(ESpineScratchEvent.AllScratchsRevealed);
	}

	/**
	 * Wait for the SpineScratch to finish the exit animation.
	 */
	public waitForExited(): Promise<void> {
		return this._waitFor(ESpineScratchEvent.Exited);
	}

	private _waitFor(event: ESpineScratchEvent): Promise<void> {
		return new Promise((resolve) => this.once(event, resolve));
	}

	private async _listenForScratchsRevealation(): Promise<void> {
		await Promise.all(
			this.scratchSlots.map((slot: IScratchSlot) => {
				if (!slot.scratch.isRevealed) {
					return new Promise<void>((resolve) => (slot.scratch.onRevealComplete = resolve));
				}
			})
		);
		this.emit(ESpineScratchEvent.AllScratchsRevealed);
	}

	private _setupAnimations(): void {
		this.setAnimations(SpineScratch.DEFAULT_SEQUENCE);
		this.onAnimationStart(this._onAnimationStart.bind(this));
		this.onAnimationComplete(this._onAnimationComplete.bind(this));
	}

	private _onAnimationStart(event: TimelineEvent): void {
		this.visible = true;
		if (event.emitter.label === "exit") {
			this.disableScratchs();
		}
	}

	private _onAnimationComplete(event: TimelineEvent): void {
		if (event.emitter.label === "idle") {
			this.enableScratchs();
			this.emit(ESpineScratchEvent.ReadyToScratch);
		}
		if (event.emitter.label === "exit") {
			this.emit(ESpineScratchEvent.Exited);
		}
	}

	private _setupSymbols(options: ISymbolSlotOptions[]): void {
		options.forEach((each) => this._setupSymbol(each));
	}

	private _setupSymbol(options: ISymbolSlotOptions): void {
		const symbolSlot = {
			slotName: options.slotName,
			symbol: this._getSymbol(options.symbol),
		};
		this.symbolSlots.push(symbolSlot);

		symbolSlot.symbol.pivot.set(symbolSlot.symbol.width * 0.5, symbolSlot.symbol.height * 0.5);
		symbolSlot.symbol.scaleY = -1;
		this.setSlotAttachementByName(options.slotName, symbolSlot.symbol);
	}

	private _getSymbol(symbol: Timeline | ITimelineOptions): Timeline {
		if (symbol instanceof Timeline) {
			return symbol;
		}
		return new Timeline(symbol);
	}

	private _setupScratchs(options: IScratchSlotOptions[]): void {
		options.forEach((each) => this._setupScratch(each));
	}

	private _setupScratch(options: IScratchSlotOptions): void {
		const container = this._prepareScratchSlot(options);
		this.setSlotAttachementByName(options.slotName, container);
	}

	private _prepareScratchSlot(options: IScratchSlotOptions): Container {
		const scratch = options.scratch2
			? this._prepareScratch2(options.scratch2)
			: this._prepareScratch(options.scratch);
		const scratchSlot = {
			scratch,
			content: scratch instanceof Scratch2 ? scratch.content : scratch.renderer.content,
			slotName: options.slotName,
			useContentTimelineHitArea: options.useContentTimelineHitArea || false,
		};
		this.scratchSlots.push(scratchSlot);

		if (scratchSlot.useContentTimelineHitArea) {
			this._setupScratchContentTimelineHitArea(scratchSlot);
		}

		const container = new Container();
		container.addChild(scratch);
		container.scaleY = -1;

		return container;
	}

	private _setupScratchContentTimelineHitArea(scratchSlot: IScratchSlot): void {
		const content = scratchSlot.content;
		if (!(content instanceof Timeline)) {
			throw new Error("Scratch content is not a Timeline.");
		}

		scratchSlot.scratch.hitArea = {
			contains: (x, y) => {
				return content.isContainedInAnimationHitAreas(
					x - scratchSlot.scratch.pivot.x,
					y - scratchSlot.scratch.pivot.y
				);
			},
		};
	}

	/**
	 * @deprecated
	 */
	private _prepareScratch(options: IScratchOptions | Scratch): Scratch {
		const scratch = this._getScratch(options);
		scratch.pivot = scratch.renderer.content.pivot;
		scratch.renderer.content.pivot = new Point(0, 0);
		return scratch;
	}

	private _prepareScratch2(options: IScratch2Options | Scratch2): Scratch2 {
		const scratch = this._getScratch2(options);
		scratch.pivot = scratch.content.pivot;
		scratch.content.pivot = new Point(0, 0);
		return scratch;
	}

	/**
	 * @deprecated
	 */
	private _getScratch(scratch: IScratchOptions | Scratch): Scratch {
		if (scratch instanceof Scratch) {
			return scratch;
		}

		return new Scratch(scratch);
	}

	private _getScratch2(scratch: IScratch2Options | Scratch2): Scratch2 {
		if (scratch instanceof Scratch2) {
			return scratch;
		}

		return new Scratch2(scratch);
	}

	/**
	 * Export SpineScratch component for editor games.
	 */
	public export(): any {
		const scratchSlots = this.scratchSlots.map((slot: IScratchSlot) => {
			slot.scratch.doNotExport = true;
			return {
				slotName: slot.slotName,
				scratch: slot.scratch.export(),
				useContentTimelineHitArea: slot.useContentTimelineHitArea,
			};
		});

		const symbolSlots = this.symbolSlots.map((slot: ISymbolSlot) => {
			slot.symbol.doNotExport = true;
			return {
				slotName: slot.slotName,
				symbol: slot.symbol.export(),
			};
		});

		return Tools.assign(super.export(), {
			type: "spine.SpineScratch",
			scratchSlots,
			symbolSlots,
		});
	}

	/**
	 * Parse SpineScratch component for editor games.
	 * @param config SpineScratch configuration
	 * @param baseUrl the baseUrl
	 * @param spineScratch optionnal instance of the SpineScratch
	 */
	public static parse(config: any, baseUrl: string, spineScratch?: SpineScratch): SpineScratch {
		if (!spineScratch) {
			config.options.scratchSlots = config.scratchSlots.map((each) => {
				return {
					slotName: each.slotName,
					scratch: {
						...each.scratch,
						content: ContainerImporter.import(each.scratch.content, baseUrl),
					},
					useContentTimelineHitArea: each.useContentTimelineHitArea,
				};
			});
			config.options.symbolSlot = config.symbolSlot.map((each) => {
				return {
					slotName: each.slotName,
					symbol: ContainerImporter.import(each.symbol, baseUrl),
				};
			});

			spineScratch = new SpineScratch(config.options);
		}

		return spineScratch;
	}
}
