import { State } from "@gdk/core";

import { ContainerImporter } from "./containerImporter";
import { LoadedView } from "../../view/loadedView";
import type { IProjectView, IViewMap, IProject } from "../model";

export class StateImporter {
	/**
	 * Imports a state from the given configuration
	 * @param viewsConfig the views configurations
	 * @param baseUrl the base Url of the loader
	 * @param viewMap the map containing the view constructors
	 * @param project the overall project configuration
	 */
	public static importState(
		viewsConfig: IProjectView[],
		baseUrl: string,
		viewMap: IViewMap = {},
		project: IProject
	): State {
		const state = {};
		viewsConfig.forEach((c) => {
			state[c.stateName] = { view: new (viewMap[c.stateName] || LoadedView)(baseUrl, c, project) };
		});

		return new State(state);
	}

	/**
	 * Import a view from the given configuration
	 * @param view the view reference to configure
	 * @param config the configuration of the view to import
	 */
	public static importView(
		view: LoadedView,
		config: IProjectView,
		baseUrl: string,
		project: IProject
	): void {
		// Import container's config
		ContainerImporter.importContainer(config, baseUrl, view, project);
		view.trackingName = config.trackingName || "";
	}
}
