import { Transform, Rectangle, Point } from "pixi.js";
import type { Container } from "pixi.js";

import { DataCache } from "../tools/dataCache";
import type { IDockingBounds } from "./dockingBounds";

/**
 * Docking function type alias
 */
export type DockingType = (child: Container) => void;

/**
 * Interface that represents
 */
export interface IDocking {
	/**
	 * X offset according to the current dock
	 */
	x?: number;
	/**
	 * Y offset according to the current dock
	 */
	y?: number;

	/**
	 * Docking type as lambda function
	 */
	dock?: (child: Container) => void;

	/**
	 * compensate pivot position for docking
	 */
	cancelPivot?: boolean;
}

export class Docking implements IDocking {
	/**
	 * The docking offset on axis (X, Y).
	 */
	public position: Point = new Point(0, 0);
	/**
	 * Docking type as lambda function
	 */
	public dock: DockingType;

	/**
	 * Compensate pivot position for docking.
	 */
	public cancelPivot: boolean;

	/**
	 * Used for cache, stores the latest known parent bounds.
	 * @hidden
	 */
	public _lastParentBounds: Rectangle = new Rectangle(undefined, undefined, undefined, undefined);
	/**
	 * Used for cache, stores the lastest bounds of the docked object.
	 * @hidden
	 */
	public _lastBounds: Rectangle = new Rectangle(undefined, undefined, undefined, undefined);
	/**
	 * Used for cache, stores the latest position of the docked object.
	 * @hidden
	 */
	public _lastPosition: Point = new Point(undefined, undefined);
	/**
	 * Used for cache, store the latest docking function of the docked object.
	 * @hidden
	 */
	public _lastDock: DockingType = null;
	/**
	 * Used for cache, stores the latest pivot of the docked object.
	 * @hidden
	 */
	public _lastPivot: Point = new Point(undefined, undefined);
	/**
	 * Used for cache, stores the latest state for cancel pivot.
	 * @hidden
	 */
	public _lastCancelPivot: boolean = false;
	/**
	 * Used for cache, to check cache.
	 * @hidden
	 */
	public _cache: DataCache<Docking> = new DataCache(this);

	/**
	 * Constructor
	 * @param width fixed width of the container
	 * @param height fixed height of the container
	 * @param x x offset of the container
	 * @param y y offset of the container
	 * @param dock the docking function to apply
	 * @param usePivot (default 1) define if we shall use pivot while docking.
	 */
	public constructor(
		dock?: DockingType,
		x: number = 0,
		y: number = 0,
		cancelPivot: boolean = true
	) {
		this.dock = dock;

		this.position.set(x, y);
		this.cancelPivot = cancelPivot;
	}

	/**
	 * Gets X offset according to the current dock.
	 */
	public get x(): number {
		return this.position.x;
	}

	/**
	 * Sets the X offset according to the current dock.
	 */
	public set x(value: number) {
		this.position.x = value;
	}

	/**
	 * Gets the Y offset according to the current dock.
	 */
	public get y(): number {
		return this.position.y;
	}

	/**
	 * Sets the Y offset according to the current dock.
	 */
	public set y(value: number) {
		this.position.y = value;
	}

	/**
	 * Sets the docking position of the container according to the parent
	 * @param x the x offset.
	 * @param y the y offset.
	 * @deprecated please use .position.set instead.
	 */
	public setPosition(x: number, y: number): Docking {
		this.position.set(x, y);

		return this;
	}

	/**
	 * Clones the current docking bounds object
	 */
	public clone(): Docking {
		return Docking.parse(this.export());
	}

	/**
	 * Sets the docking forced dirty. Especially used when implemented custom/animated docking.
	 */
	public setDirty(): void {
		this._cache._forcedDirty = true;
	}

	/**
	 * Returns wether or not the docking is dirty. If yes, the docking function should be applied.
	 * @param parentBounds the potental new parent bounds rectangle.
	 * @param bounds the current bounds of the docked object.
	 */
	public isDirty(child: Container, parentBounds: Rectangle, bounds: Rectangle): boolean {
		this._cache.addCommon("_lastCancelPivot", this.cancelPivot);
		this._cache.addCommon("_lastDock", this.dock);

		this._cache.addPoint("_lastPosition", this.position);
		this._cache.addPoint("_lastPivot", child.pivot);

		this._cache.addRectangle("_lastParentBounds", parentBounds);
		this._cache.addRectangle("_lastBounds", bounds);

		return this._cache.checkDirty();
	}

	/**
	 * Exports the docking configuration
	 */
	public export(): any {
		let dockingFunctionName: string;
		switch (this.dock) {
			case Docking.centerAll:
				dockingFunctionName = "centerAll";
				break;

			case Docking.top:
				dockingFunctionName = "top";
				break;
			case Docking.topLeft:
				dockingFunctionName = "topLeft";
				break;
			case Docking.topRight:
				dockingFunctionName = "topRight";
				break;

			case Docking.bottom:
				dockingFunctionName = "bottom";
				break;
			case Docking.bottomLeft:
				dockingFunctionName = "bottomLeft";
				break;
			case Docking.bottomRight:
				dockingFunctionName = "bottomRight";
				break;

			case Docking.centerVertical:
				dockingFunctionName = "centerVertical";
				break;
			case Docking.centerVerticalRight:
				dockingFunctionName = "centerVerticalRight";
				break;
			case Docking.centerVerticalLeft:
				dockingFunctionName = "centerVerticalLeft";
				break;
			case Docking.centerHorizontal:
				dockingFunctionName = "centerHorizontal";
				break;

			case Docking.topCenterHorizontal:
				dockingFunctionName = "topCenterHorizontal";
				break;
			case Docking.bottomCenterHorizontal:
				dockingFunctionName = "bottomCenterHorizontal";
				break;
		}

		return {
			dock: dockingFunctionName,
			x: this.x,
			y: this.y,
			cancelPivot: this.cancelPivot,
		};
	}

	/**
	 * Parses the given docking configuration
	 * @param config the exported configuration of the docking object
	 */
	public static parse(config: any, ref?: Docking): Docking {
		if (!ref) {
			return new Docking(Docking[config.dock], config.x, config.y, config.cancelPivot);
		}

		ref.dock = Docking[config.dock];
		ref.position.set(config.x, config.y);
		ref.cancelPivot = config.cancelPivot;
		return ref;
	}

	// Static members
	public static _transform: Transform = new Transform();
	public static _parentTransform: Transform = new Transform();
	public static _parentBounds: Rectangle = new Rectangle();
	public static _bounds: IDockingBounds = { width: 0, height: 0 };
	public static _pivotInfluenceRatio: number = 1;
	public static _transformedPivot: Point = new Point(0, 0);

	/**
	 * Docks the element on the center according to the parent
	 */
	public static readonly centerAll = (child: Container) => {
		Docking.centerHorizontal(child);
		Docking.centerVertical(child);
	};

	/**
	 * Docks the element on top left according to the parent
	 */
	public static readonly topLeft = (child: Container) => {
		Docking.top(child);
		Docking.left(child);
	};

	/**
	 * Docks the element on top and horizontaly centered according to the parent
	 */
	public static readonly topCenterHorizontal = (child: Container) => {
		Docking.top(child);
		Docking.centerHorizontal(child);
	};

	/**
	 * Docks the element on top right according to the parent
	 */
	public static readonly topRight = (child: Container) => {
		Docking.top(child);
		Docking.right(child);
	};

	/**
	 * Docks the element verticaly on center and right according to the parent
	 */
	public static readonly centerVerticalRight = (child: Container) => {
		Docking.centerVertical(child);
		Docking.right(child);
	};

	/**
	 * Docks the element on bottom left according to the parent
	 */
	public static readonly bottomLeft = (child: Container) => {
		Docking.left(child);
		Docking.bottom(child);
	};

	/**
	 * Docks the element on left and verticaly centered according to the parent
	 */
	public static readonly centerVerticalLeft = (child: Container) => {
		Docking.centerVertical(child);
		Docking.left(child);
	};

	/**
	 * Docks the element on bottom and horizontaly centered according to the parent
	 */
	public static readonly bottomCenterHorizontal = (child: Container) => {
		Docking.bottom(child);
		Docking.centerHorizontal(child);
	};

	/**
	 * Docks the element on bottom right according to the parent
	 */
	public static readonly bottomRight = (child: Container) => {
		Docking.bottom(child);
		Docking.right(child);
	};

	/**
	 * Docks the element horizontaly on center according to the parent
	 */
	public static readonly centerHorizontal = (child: Container) => {
		(child.transform as Transform).position.x =
			((child.scale.x < 0 ? Docking._bounds.width * Docking._pivotInfluenceRatio : 0) +
				(Docking._parentBounds.width - Docking._bounds.width * Docking._pivotInfluenceRatio) * 0.5 +
				((child.docking.x || 0) / Math.abs(child.scale.x) +
					child.pivot.x * Docking._transformedPivot.x * Docking._pivotInfluenceRatio) *
					Docking._transform.scale.x) /
			Docking._parentTransform.scale.x;
	};

	/**
	 * Docks the element verticaly on center according to the parent
	 */
	public static readonly centerVertical = (child: Container) => {
		(child.transform as Transform).position.y =
			((child.scale.y < 0 ? Docking._bounds.height * Docking._pivotInfluenceRatio : 0) +
				(Docking._parentBounds.height - Docking._bounds.height * Docking._pivotInfluenceRatio) *
					0.5 +
				((child.docking.y || 0) / Math.abs(child.scale.y) +
					child.pivot.y * Docking._transformedPivot.y * Docking._pivotInfluenceRatio) *
					Docking._transform.scale.y) /
			Docking._parentTransform.scale.y;
	};

	/**
	 * Docks the element on left according to the parent
	 */
	public static readonly left = (child: Container) => {
		(child.transform as Transform).position.x =
			((child.scale.x < 0 ? Docking._bounds.width * Docking._pivotInfluenceRatio : 0) +
				((child.docking.x || 0) / Math.abs(child.scale.x) +
					child.pivot.x * Docking._transformedPivot.x * Docking._pivotInfluenceRatio) *
					Docking._transform.scale.x) /
			Docking._parentTransform.scale.x;
	};

	/**
	 * Docks the element on right according to the parent
	 */
	public static readonly right = (child: Container) => {
		(child.transform as Transform).position.x =
			((child.scale.x < 0 ? Docking._bounds.width * Docking._pivotInfluenceRatio : 0) +
				Docking._parentBounds.width -
				Docking._bounds.width * Docking._pivotInfluenceRatio +
				((child.docking.x || 0) / Math.abs(child.scale.x) +
					child.pivot.x * Docking._transformedPivot.x * Docking._pivotInfluenceRatio) *
					Docking._transform.scale.x) /
			Docking._parentTransform.scale.x;
	};

	/**
	 * Docks the element on top according to the parent
	 */
	public static readonly top = (child: Container) => {
		(child.transform as Transform).position.y =
			((child.scale.y < 0 ? Docking._bounds.height * Docking._pivotInfluenceRatio : 0) +
				((child.docking.y || 0) / Math.abs(child.scale.y) +
					child.pivot.y * Docking._transformedPivot.y * Docking._pivotInfluenceRatio) *
					Docking._transform.scale.y) /
			Docking._parentTransform.scale.y;
	};

	/**
	 * Docks the element on bottom according to the parent
	 */
	public static readonly bottom = (child: Container) => {
		(child.transform as Transform).position.y =
			((child.scale.y < 0 ? Docking._bounds.height * Docking._pivotInfluenceRatio : 0) +
				Docking._parentBounds.height -
				Docking._bounds.height * Docking._pivotInfluenceRatio +
				((child.docking.y || 0) / Math.abs(child.scale.y) +
					child.pivot.y * Docking._transformedPivot.y * Docking._pivotInfluenceRatio) *
					Docking._transform.scale.y) /
			Docking._parentTransform.scale.y;
	};
}
