import { Application as PixiApplication, Container, WebGLRenderer, utils } from "pixi.js";
import { tracker, EEventType } from "@gdk/core";
import type { IEvent } from "@gdk/core";

import type { IGamePixiOptions } from "./gamePixi";

export class Application extends PixiApplication {
	/**
	 * Stage being rendered by Pixi.js to draw the lock view
	 */
	public lockStage: Container;
	/**
	 * If the application should render the lock screen (lockStage)
	 */
	public shouldLockScreen: boolean = false;
	/**
	 * Called before rendering the pixi.js main stage
	 * @deprecated
	 */
	public onBeforeRender: () => void;
	/**
	 * Called after rendered the pixi.js main stage
	 * @deprecated
	 */
	public onAfterRender: () => void;
	/**
	 * Emitter used to send events like "beforeRender", etc.
	 * @example: "beforeRender", "afterRender", "start", "stop"
	 */
	public emitter: utils.EventEmitter = new utils.EventEmitter();

	/**
	 * Defines the time in ms to wait before checking the renderer resolution optimizer
	 */
	public waitingTimeBeforeOptimization: number = 5000;
	/**
	 * Defines the resolution ratios (capping) used by the optimizer
	 * @example optimizerResolutionRatios.lowQuality = 0.5; // Means that if FPS <= 30, change to low quality resolution (1)
	 * @example optimizerResolutionRatios.highQuality = 0.75; // Means that if FPS >= 45, change to high quality resolution (window.devicePixelRatio)
	 */
	public optimizerResolutionRatios: { lowQuality: number; highQuality: number } = {
		lowQuality: 0.33,
		highQuality: 0.6,
	};
	/**
	 * Defines wether or not the optimizer should be disabled once the resolution changed.
	 * Typically fell back to lower resolution. To re-enable optimizer, juste set
	 * shouldOptimizeRendererResolution = true.
	 */
	public shouldStopOptimizerOnceFellBack: boolean = true;
	/**
	 * Called once the renderer's resolution has been changed (when optimizing)
	 * @param newResolution the new resolution of the renderer
	 */
	public onRendererResolutionChanged: (newResolution?: number) => void;

	/**
	 * Defines the start time for the optimizer (when the renderer starts: date.now())
	 */
	protected startOptimizerTime: number = Date.now();

	/**
	 * Defines the framerate state:
	 *    * count: number of frames rendered before we compute the optimizer
	 *    * time: number of ms taken to render the ${count} frames
	 */
	protected framerate: { count: number; time: number } = { count: 0, time: 0 };

	/**
	 * Defines wether or not the application should optimize itself by changing resolution.
	 */
	private _shouldOptimizeRendererResolution: boolean = true;

	/**
	 * Constructor
	 * @param width the width in pixels
	 * @param height the height in pixels
	 * @param noWebGL to force no webgl
	 * @param options the application options
	 */
	public constructor(
		width: number,
		height: number,
		noWebGL: boolean,
		options: IGamePixiOptions = {}
	) {
		super(width, height, options, noWebGL);

		// Setup rendering elements
		this.stage.name = "MainStage";

		this.lockStage = new Container();
		this.lockStage.name = "LockScreenStage";
	}

	/**
	 * Gets wether or not the application should optimize itself by changing resolution.
	 */
	public get shouldOptimizeRendererResolution(): boolean {
		return this._shouldOptimizeRendererResolution;
	}

	/**
	 * Sets wether or not the application should optimize itself by changing resolution.
	 */
	public set shouldOptimizeRendererResolution(enabled: boolean) {
		this.startOptimizerTime = Date.now();
		this._shouldOptimizeRendererResolution = enabled;
	}

	/**
	 * Starts the application (ticker)
	 */
	public start(): void {
		this.startOptimizerTime = Date.now();
		super.start();

		// In case of an "autoStart: true", check if emitter exists
		if (this.emitter) {
			this.emitter.emit("start");
		}
	}

	/**
	 * Stops the application (ticker)
	 */
	public stop(): void {
		super.stop();
		this.emitter.emit("stop");
	}

	/**
	 * Renders the application:
	 * - Renders the main stage if not locked
	 * - Else, just render the locked screen view
	 */
	public render(): void {
		// Render lock stage?
		if (this.shouldLockScreen) {
			const t0 = Date.now();
			this.renderer.render(this.lockStage);
			return this._checkOptimizer(Date.now() - t0);
		}

		// Callback before rendering main stage
		this.emitter.emit("beforeRender");
		this.renderer.emit("beforeRender");
		if (this.onBeforeRender) {
			this.onBeforeRender();
		}

		// Render!
		const t1 = Date.now();
		this.renderer.render(this.stage);
		this._checkOptimizer(Date.now() - t1);

		// Callback after rendered main stage
		this.emitter.emit("afterRender");
		this.renderer.emit("afterRender");
		if (this.onAfterRender) {
			this.onAfterRender();
		}
	}

	/**
	 * Resizes the application
	 * @param width the new canvas width
	 * @param height the new canvas height
	 */
	public resize(width: number, height: number): void {
		this.renderer.resize(width, height);
	}

	/**
	 * Computes the optimizer to determine if the renderer should render
	 * in a lower or higher resolution
	 */
	protected computeOptimizer(elapsed: number): void {
		// One more frame
		const framerateCount = (this.framerate.count += 1);
		const framerateTime = (this.framerate.time += elapsed);

		// If the optimizer should check if it has to change the resolution
		if (framerateTime >= this.waitingTimeBeforeOptimization) {
			// Get expected number of rendered frames
			const expected = framerateTime * 0.06;
			const devicePixelRatio = window.devicePixelRatio ?? 1;

			// Get the new resolution according to the new metrics
			let newResolution = this.renderer.resolution;
			if (
				framerateCount <= expected * this.optimizerResolutionRatios.lowQuality &&
				this.renderer.resolution > 1
			) {
				newResolution = Math.max(1, Math.trunc(this.renderer.resolution / 2));
			} else if (
				framerateCount >= expected * this.optimizerResolutionRatios.highQuality &&
				this.renderer.resolution < devicePixelRatio &&
				!this.shouldStopOptimizerOnceFellBack
			) {
				newResolution = Math.min(this.renderer.resolution * 2, devicePixelRatio);
			}

			// Fallback resolution?
			if (this.renderer.resolution !== newResolution) {
				tracker.track({
					eventType: EEventType.Rendering,
					event: "resolution",
					resolutionInformations: { from: this.renderer.resolution, to: newResolution },
				} as IEvent);

				this.renderer.resolution = newResolution;
				this.renderer.plugins.interaction.resolution = newResolution;
				if (this.renderer instanceof WebGLRenderer) {
					(this.renderer as any).rootRenderTarget.resolution = newResolution;
				}

				if (this.onRendererResolutionChanged) {
					this.onRendererResolutionChanged(this.renderer.resolution);
				}
			}

			this.startOptimizerTime = Date.now();
			this.framerate.count = 0;
			this.framerate.time = 0;
		}
	}

	/**
	 * Checks the renderer optimizer.
	 */
	protected _checkOptimizer(elapsed: number): void {
		if (this._shouldOptimizeRendererResolution) {
			this.computeOptimizer(elapsed);
		}
	}
}
