import { Sprite, Texture, Rectangle, Point, settings, Transform, BaseTexture } from "pixi.js";
import i18n from "i18next";
import { Loader, Tools, tracker, LinkClick } from "@gdk/core";
import type OpenType from "opentype.js";
import type { CanvasRenderer, WebGLRenderer, DestroyOptions } from "pixi.js";

import { ContainerExporter } from "../../../loading/export/containerExporter";
import { CommonExporter } from "../../../loading/export/commonExporter";
import {
	EGradientType,
	EGradientBounds,
	IStyle,
	IStyles,
	EAlign,
	ELineJoin,
	ELineCap,
} from "./textStyle";
import { UITools } from "../tools";
import { Tokenizer } from "./tools/tokenizer";
import { Text } from "./text";
import { Line } from "./line";
import { Word } from "./word";
import { Glyph, EGlyphType } from "./glyph";
import { AssetStore } from "../../../assets/assetStore";
import type { IToken } from "./tools/tokenizer";
import type { ILinearGradientStyle, IRadialGradientStyle } from "./textStyle";

export { IStyle, IStyles, EAlign };

export interface IUpdateOptions {
	font?: string;
	style?: IStyle;
	text?: string;
	noUpdate?: boolean;
	noUpdateCanvas?: boolean;
	noUpdateTexture?: boolean;
	yOffset?: number;
	xOffset?: number;
	Text?: Text;
}

export interface ITextFieldOptions {
	align?: EAlign;
	padding?: Rectangle;
	interLine?: number;
	wordWrap?: boolean;
	noUpdate?: boolean;
}

export enum EDrawPass {
	Stroke,
	Fill,
	Debug,
}

export enum EHitAreaLevel {
	Text,
	Line,
	Word,
	Glyph,
}

export interface ILink {
	hitAreas: Rectangle[];
	token: IToken;
}

export class TextField extends Sprite {
	// Public members
	public canvas: HTMLCanvasElement = null;
	public context: CanvasRenderingContext2D = null;
	public hitAreaLevel: EHitAreaLevel = EHitAreaLevel.Text;

	public centerPivotOnceUpdated: boolean = false;

	/**
	 * Sets or gets wether or not the textfield is frozen. If true, the textfield will not be updated
	 * when the .update function is called. Typically used when parsing the textfield.
	 * @hidden
	 */
	public _isFrozen: boolean = false;

	// Protected members
	protected _align: EAlign = EAlign.Left;
	protected _text: string = "";
	protected _styles: IStyles;

	protected _width: number = 0;
	protected _height: number = 0;

	protected fixedWidth: number = 0;
	protected fixedHeight: number = 0;

	protected fonts: { [index: string]: OpenType.Font } = {};

	protected composedText: Text = null;
	protected hitAreas: Rectangle[] = [];
	protected links: { [index: string]: ILink } = {};
	protected hooveredLink: IToken = null;
	protected hasLinks: boolean = false;

	// Private members
	private _resolution: number = settings.RESOLUTION;
	private _options: ITextFieldOptions;
	private _debug: any = {
		pass: -1,
		canvas: null,
		size: { stroke: "rgba(255, 255, 0, 0.25)", strokeWidth: 1 },
		bounds: null,
		glyphBounds: { stroke: "rgba(0, 0, 255, 0.5)", strokeWidth: 1 },
		baseline: { fill: "rgba(255, 0, 255, 0.8)" },
		lines: { fill: "rgba(0, 255, 255, 0.1)" },
		words: { stroke: "rgba(0, 255, 255, 0.5)", strokeWidth: 1 },
	};

	private _hitAreaPoint: Point = new Point();
	private _i18nKey: string | null = null;

	// Used when calculating bounds. As bounds are absolute bounds in pixi.js.
	private _transform: Transform = new Transform();

	private _keepCanvasReference: boolean = false;
	private _waitingBase64Texture: Texture = null;

	// Static members
	public static defaultLineHeight: number = 1.175;
	public static cachedFonts: { [index: string]: OpenType.Font } = {};

	public static defaultTags: IStyles = {
		default: {
			fill: "#000000",
			fontName: "Arial",
			fontSize: 12,
		},
		b: {
			stroke: "#000000",
			strokeWidth: 1,
		},
		a: {
			fill: "blue",
			interactive: true,
			underlineWeight: 2,
			underlineDistance: 4,
		},
	};

	/**
	 * Constructor
	 * @param text the text of the text field
	 * @param style the style of the text field
	 * @param canvas the optional existing canvas
	 */
	public constructor(
		text: string,
		styles: IStyle | IStyles,
		options: ITextFieldOptions = {},
		canvas?: HTMLCanvasElement
	) {
		super();

		this._options = options;
		// Canvas and texture
		this.canvas = canvas;
		if (this.canvas) {
			this.keepCanvasReference = true;
		}
		// Text
		this._text = text;

		// Styles
		this._styles = (styles as IStyles).default
			? (styles as IStyles)
			: ({
					default: styles,
			  } as IStyles);

		// Cache and update
		if (styles) {
			// Font
			this.updateFontCache();

			// Update
			this._align = this._options.align || EAlign.Left;
			this.update({ noUpdate: options.noUpdate });
		}

		this.on("pointertap", (e) => this._onLinkEvent("pointertap", e));
		this.on("pointerover", (e) => this._onLinkEvent("pointerover", e));
		this.on("pointerout", (e) => this._onLinkEvent("pointerout", e));
	}

	/**
	 * Freeze a text to lock update when chaining multiple text changes that launch update
	 */
	public freeze(): void {
		this._isFrozen = true;
	}

	/**
	 * Unfreeze a text and launch the update to get the text result.
	 * @param options defines the optional options to pass to the update function called automatically.
	 */
	public unfreeze(options?: IUpdateOptions): void {
		this._isFrozen = false;
		this.update(options);
	}

	/**
	 * Destroys the textfield
	 */
	public destroy(options?: boolean | DestroyOptions): void {
		// Canvas
		if (!this.keepCanvasReference && this.canvas) {
			try {
				this.canvas.remove();
			} catch (e) {
				/* Try catch for IE */
			}
		}

		// Texture
		const destroyTexture = typeof options === "boolean" ? options : options && options.texture;
		if (!destroyTexture) {
			// Force destroying the texture in the case of TextFields
			this._texture.destroy(true);
		}

		super.destroy(options);
	}

	/**
	 * Update the font cache taking the current applied
	 * styles
	 */
	public updateFontCache(): void {
		for (const style in this._styles) {
			if (!this._styles.hasOwnProperty(style)) {
				continue;
			}
			this.fonts[style] = this.getFont(this._styles[style]);
		}
	}

	/**
	 * Returns wether or not the canvas reference should be kept.
	 */
	public get keepCanvasReference(): boolean {
		return this._keepCanvasReference;
	}

	/**
	 * Sets wether or not the canvas reference shold be kept.
	 */
	public set keepCanvasReference(keep: boolean) {
		if (keep) {
			this._removeCanvas();
		}

		this._keepCanvasReference = keep;

		this.update();
	}

	/**
	 * Returns the text alignment
	 */
	public get align(): EAlign {
		return this._align;
	}

	/**
	 * Sets the text alignment
	 */
	public set align(value: EAlign) {
		this._align = value;
		this.update();
	}

	/**
	 * Returns the current text
	 */
	public get text(): string {
		return this._text;
	}

	/**
	 * Sets the current text
	 */
	public set text(value: string) {
		if (this._text === value) {
			return;
		}

		this._text = value;

		if (this._text.replace(" ", "") === "") {
			// if the provided text is empty, we don't perform any update (to avoid crash),
			// but the textfield need to be hidden because the old texture is still present
			this.renderable = false;
		} else {
			this.renderable = true;
			this.update();
		}
	}

	/**
	 * Returns the text"s width
	 */
	// remove this comment when allow-override-transient-properties is included in typescript
	// @ts-ignore
	public get width(): number {
		return (
			(this.wordWrap ? this.fixedWidth || this._width : this.composedText.width) * this.scale.x
		);
	}

	/**
	 * Sets the width of the text
	 */
	// remove this comment when allow-override-transient-properties is included in typescript
	// @ts-ignore
	public set width(width: number) {
		this.fixedWidth = width;
		this.update();
	}

	/**
	 * Returns the text"s height
	 */
	// remove this comment when allow-override-transient-properties is included in typescript
	// @ts-ignore
	public get height(): number {
		return (this.fixedHeight || this._height) * this.scale.y;
	}

	/*
	 * Sets the fixed height of the text
	 */
	// remove this comment when allow-override-transient-properties is included in typescript
	// @ts-ignore
	public set height(height: number) {
		this.fixedHeight = height;
		this.update();
	}

	/**
	 * Returns the text styles
	 */
	public get styles(): IStyles {
		return this._styles;
	}

	/**
	 * Sets the text styles
	 */
	public set styles(styles: IStyles) {
		this._styles = (styles as IStyles).default
			? (styles as IStyles)
			: ({
					default: styles,
			  } as IStyles);

		// Update
		this.updateFontCache();
		this.update();
	}

	/**
	 * Returns the padding value
	 */
	public get padding(): Rectangle {
		return this._options.padding;
	}

	/**
	 * Sets the padding value
	 */
	public set padding(value: Rectangle) {
		this._options.padding = value;
		this.update();
	}

	/**
	 * Returns the interline value
	 */
	public get interLine(): number {
		return this._options.interLine;
	}

	/**
	 * Sets the interline value
	 */
	public set interLine(value: number) {
		this._options.interLine = value;

		this.update();
	}

	/**
	 * Returns if the text is word-wrapped
	 */
	public get wordWrap(): boolean {
		return this._options.wordWrap;
	}

	/**
	 * Sets if the text is word-wrapped
	 */
	public set wordWrap(enabled: boolean) {
		this._options.wordWrap = enabled;
		this.update();
	}

	/**
	 * Gets the associated i18n key
	 */
	public get i18nKey(): string | null {
		return this._i18nKey;
	}

	/**
	 * Sets the new associated i18n key
	 */
	public set i18nKey(key: string | null) {
		this._i18nKey = key;
		if (key) {
			this.text = i18n.t(key) || "";
		}
	}

	/**
	 * Updates the textfield
	 */
	public update(options: IUpdateOptions = {}): void {
		// Check if the textfield is frozen
		if (this._isFrozen) {
			return;
		}

		const startDate = Date.now();

		// 0 - update all glyphs :
		this.updateGlyphs();

		// 1 - compose text (split in lines / styles...) :
		this.composeText();

		// 2 - Update Textfield size
		this._width = this.composedText.width / this.resolution;
		this._height = this.composedText.height / this.resolution;

		if (options.noUpdate) {
			return;
		}

		// 3 - manage canvas and texture
		if (!this.canvas) {
			this.canvas = document.createElement("canvas");
			this.context = this.canvas.getContext("2d");

			if (this._keepCanvasReference) {
				if (this._texture && this._texture !== Texture.EMPTY && this._texture !== Texture.WHITE) {
					this._texture.destroy(true);
				}

				this.texture = null;
				this.texture = Texture.fromCanvas(this.canvas);
			}
		}

		// 4 - Update canvas size
		if (!options.noUpdateCanvas) {
			const canvasWidth =
				(this.fixedWidth || this.composedText.width) +
				this.composedText.padding.left +
				this.composedText.padding.right;
			const canvasheight =
				(this.fixedHeight || this.composedText.height) +
				this.composedText.padding.top +
				this.composedText.padding.bottom;

			this.canvas.width = UITools.GetPowerOfTwo(canvasWidth * this.resolution);
			this.canvas.height = UITools.GetPowerOfTwo(canvasheight * this.resolution);
		}

		if (this.canvas.width <= 1 || this.canvas.height <= 1) {
			if (!this._keepCanvasReference) {
				// nothing can be drawn at that size, just drop it.
				this.texture = null;
				this.texture = Texture.EMPTY;

				this._removeCanvas();
			}

			return;
		}

		// TODO : double check horizontal centering of my drawing in canvas
		this.composedText.canvasX =
			(this.canvas.width - (this.fixedWidth || this.composedText.width)) * 0.5;

		// 5 - Update hitArea
		if (this.hitAreaLevel === EHitAreaLevel.Text) {
			this.hitAreas = [
				new Rectangle(
					this.composedText.x,
					this.composedText.y,
					this.composedText.width,
					this.composedText.height
				),
			];
		}

		// 6 - render all glyphs
		this._drawPass(0); // stroke
		this._drawPass(1); // fill

		if (this._debug.pass === 2) {
			// debug
			this._drawPass(2);
		}

		// 7 - FINALLY : Update texture (let Pixi know we are done here)
		if (!options.noUpdateTexture) {
			if (this._keepCanvasReference) {
				this.updateTextureSize();
				this._removeCanvas();
			} else {
				const base64 = this.canvas.toDataURL("image/png");
				const texture = Texture.fromImage(base64);

				Texture.removeFromCache(base64);
				BaseTexture.removeFromCache(base64);

				this._waitingBase64Texture = texture;

				texture.baseTexture.once("loaded", () => {
					if (this._keepCanvasReference || this._destroyed) {
						return;
					}

					if (texture !== this._waitingBase64Texture) {
						return texture.destroy(true);
					}

					if (this._texture && this._texture !== Texture.EMPTY && this._texture !== Texture.WHITE) {
						this._texture.destroy(true);
					}

					this.texture = null;
					this.texture = this._waitingBase64Texture;

					this._texture.trim = new Rectangle();

					this.updateTextureSize();
					this._removeCanvas();

					this._waitingBase64Texture = null;
				});
			}
		}

		// 8 - apply utils
		if (this.centerPivotOnceUpdated) {
			this.pivot.x = this.width * 0.5;
			this.pivot.y = this.height * 0.5;
		}

		const endDate = Date.now();
		const elapsed = endDate - startDate;
	}

	// Removes the canvas to save memory.
	private _removeCanvas(): void {
		if (!this._keepCanvasReference) {
			try {
				this.canvas.remove();
			} catch (e) {
				/* Try catch for IE */
			}
			this.canvas = null;
			this.context = null;
		}
	}

	/**
	 * Override pixi calculateBounds() method
	 * because we don"t get our bounds from texture quads but from our composed Text
	 */
	public calculateBounds(): void {
		// use Text bounds
		this._transform.setFromMatrix(this.transform.worldTransform);

		this._bounds.minX = this._transform.position.x + this.composedText.x;
		this._bounds.maxX = this._transform.position.x + this.composedText.width;
		this._bounds.minY = this._transform.position.y + this.composedText.y;
		this._bounds.maxY = this._transform.position.y + this.composedText.height;
	}

	/**
	 * Override pixi containsPoint(point :Point) method
	 * because we might want to get glyph hitTest
	 * @param point: the point to test
	 */
	public containsPoint(point: Point): boolean {
		this.worldTransform.applyInverse(point, this._hitAreaPoint);

		let result = false;

		if (this.hasLinks) {
			if (this.getLinkHit(point)) {
				result = true;
			}
		} else {
			this.hitAreas.forEach((rect) => {
				if (rect.contains(this._hitAreaPoint.x, this._hitAreaPoint.y)) {
					result = true;
				}
			});
		}

		return result;
	}

	/**
	 * Returns the link"s hit area
	 * @param point the point of the click
	 */
	protected getLinkHit(point: Point): ILink {
		this.worldTransform.applyInverse(point, this._hitAreaPoint);

		let result: ILink = null;

		for (const i in this.links) {
			if (!this.links.hasOwnProperty(i)) {
				continue;
			}
			const link = this.links[i];

			link.hitAreas.forEach((rect) => {
				if (rect.contains(this._hitAreaPoint.x, this._hitAreaPoint.y)) {
					result = link;
				}
			});
		}

		return result;
	}

	/**
	 * Get all glyph and save them in this.glyphs
	 */
	protected updateGlyphs(): void {
		const tokenizer = new Tokenizer(this._text);

		this.composedText = new Text();
		this.composedText.interLine = this._options.interLine || 0;
		this.composedText.align = this.align;
		this.hitAreas = [];
		this.links = {};
		this.hasLinks = false;

		let previousGlyph: Glyph = null;
		let previousWord: Word = null;
		let glyph: Glyph = null;

		let word = new Word(null);

		const wordWrap = (this.wordWrap || false) && this.fixedWidth > 0;

		while (!tokenizer.isEnd()) {
			const token = tokenizer.getNextTag();
			const currentTag = token.type || "default";
			const style = Tools.assign(
				{},
				this._styles.default,
				TextField.defaultTags[currentTag] || {},
				this._styles[currentTag] || {}
			);

			if (style.interactive) {
				this.hasLinks = true;
				this.buttonMode = true;
				this.interactive = true;
				this.links[token.content] = {
					token,
					hitAreas: [],
				};
			}

			// search that style font
			this.fonts[currentTag] = this.getFont(style);

			// and store it back in my style
			style.font = this.fonts[currentTag] || this.fonts.default || this.getFont(style);

			for (let i = 0, len = token.content.length; i < len; i++) {
				// create a glyph
				glyph = new Glyph(token.content[i], style, previousGlyph);
				glyph.token = token;

				// new line from char "\n" or "\r"
				if (glyph.type === EGlyphType.NewLine) {
					// WORDWRAP test line lenght
					this.applyWordWrap(wordWrap, previousWord, word);

					word.nextGlyph = glyph;
					this.composedText.pushWord(word);

					// currentLine.height = previousLine.height;
					// currentLine.baseline = previousLine.baseline;
					// currentLine.baselineOffset = previousLine.baselineOffset;

					previousGlyph = null;
					previousWord = null;
					word = new Word(null);
				} else {
					previousGlyph = glyph;

					// this is a space it"s a good time to check WORDWRAP
					if (glyph.type === EGlyphType.Space) {
						previousGlyph = null;
						word.nextGlyph = glyph;

						// WORDWRAP test line lenght
						this.applyWordWrap(wordWrap, previousWord, word);

						// store the word
						previousWord = this.composedText.pushWord(word);

						// start a new word
						word = new Word(previousWord);
					} else {
						// append glyph to word
						word.pushGlyph(glyph);
					}
				}

				// anyway add that glyph to composed text
				this.composedText.pushGlyph(glyph);
			}
		}

		// WORDWRAP test for the last word
		const lineWidth = this.applyWordWrap(wordWrap, previousWord, word);

		// finaly push the last word
		this.composedText.pushWord(word);
	}

	/**
	 * Applies the word wrap on the text
	 * @param wordWrap: if word wrap is enabled
	 * @param previousWord: the previous word (before "word")
	 * @param word: the current word
	 */
	protected applyWordWrap(wordWrap: boolean, previousWord: Word, word: Word): number {
		if (previousWord) {
			const glyph = previousWord.nextGlyph;
			const wordLastGlyph = glyph ? glyph.width : 0;
			const lineWidth = word.x + word.width + wordLastGlyph;

			this.composedText.maxLineWidth = Math.max(
				this.composedText.maxLineWidth,
				word.x + word.width
			);

			if (wordWrap) {
				if (lineWidth >= this.fixedWidth) {
					word.x = 0;
					previousWord.nextGlyph.type = EGlyphType.NewLine;
					this.composedText.maxLineWidth = Math.max(
						this.composedText.maxLineWidth,
						word.x + word.width + wordLastGlyph
					);
					return word.x + word.width;
				}

				return lineWidth;
			} else {
				return previousWord.x + previousWord.width;
			}
		} else {
			return word.x + word.width;
		}
	}

	/**
	 * slit all glyphs into lines / stles and store them in this.lines
	 */
	protected composeText(): void {
		let previousLine: Line = null;
		let currentLine = new Line(this.composedText, null);

		this.composedText.pushLine(currentLine);
		this.composedText.padding = new Rectangle(); // reset padding

		let maxPadding = 0;
		// let previousWord: Word = null;

		this.composedText.words.forEach((word) => {
			// add all word"s glyphs to the line
			word.glyphs.forEach((glyph) => {
				currentLine.leading = Math.max(currentLine.leading, glyph.leading);
				currentLine.baseline = Math.max(glyph.ascender, currentLine.baseline);
				currentLine.height = Math.max(glyph.maxHeight, currentLine.height);
				currentLine.y = Math.max(currentLine.baseline, currentLine.y);
				// currentLine.y = Math.max(glyph.style.fontSize, currentLine.y);
				const maxPaddingStrokeWidth = Math.max(maxPadding, glyph.style.strokeWidth || 0);
				const maxPaddingShadowBlur = Math.max(maxPadding, glyph.style.shadowBlur || 0);
				maxPadding = Math.max(maxPaddingShadowBlur, maxPaddingStrokeWidth);
				currentLine.underline = Math.max(currentLine.underline, glyph.style.underlineDistance || 2);
			});

			// currentLine.height = Math.max(word.maxHeight, currentLine.height);

			if (currentLine.height === 0 && previousLine) {
				// double new line
				currentLine.height = previousLine.height;
				currentLine.baseline = previousLine.baseline;
			}

			// add that word to the current line
			currentLine.pushWord(word);

			if (word.nextGlyph) {
				if (word.nextGlyph.type === EGlyphType.Space) {
					currentLine.width += word.nextGlyph.width;
				} else if (word.nextGlyph.type === EGlyphType.NewLine) {
					// new line
					previousLine = currentLine;

					currentLine = new Line(this.composedText, previousLine);

					// let"s align previous line
					previousLine.x = this._getLineHorizontalAlignement(previousLine);

					if (previousLine.x > 0) {
						this.composedText.minLineX = Math.min(this.composedText.minLineX, previousLine.x);
					}

					// previous line is aligned so i can get it"s x position
					this.composedText.x = Math.min(this.composedText.x, previousLine.x);
					this.composedText.pushLine(currentLine);
				}
			}
		});

		// let"s handle the first line
		const firstLine = this.composedText.lines[0];
		this.composedText.height += Math.ceil(firstLine.height);
		this.composedText.width = Math.ceil(Math.max(this.composedText.width, firstLine.width));
		firstLine.x = this._getLineHorizontalAlignement(firstLine);

		this.composedText.minLineX = Math.min(this.composedText.minLineX, firstLine.x);
		this.composedText.x = Math.min(this.composedText.x, firstLine.x);
		// this.composedText.y = (firstLine.y - firstLine.baseline);
		// firstLine.y -= (firstLine.y - firstLine.baseline);
		this.composedText.leading = firstLine.leading;
		this.composedText.baseline = firstLine.baseline;

		this.composedText.x = Math.max(this.composedText.minLineX, this.composedText.x);

		const lastLine = this.composedText.lines[this.composedText.lines.length - 1];

		if (this._align === EAlign.Justify) {
			// for all the lines exept the last one (length - 2 because we work on i & i+1 in the loop)
			for (let i = 0, len = this.composedText.lines.length - 2; i < len; i++) {
				// if the next line isn't emty
				if (this.composedText.lines[i + 1].width > 0) {
					this._justifyLine(this.composedText.lines[i]);
				}
			}
		}

		// let"s handle last line alignement
		if (this.composedText.lines.length > 1) {
			lastLine.x = this._getLineHorizontalAlignement(lastLine);
			this.composedText.minLineX = Math.min(this.composedText.minLineX, lastLine.x);
			this.composedText.x = Math.min(this.composedText.x, lastLine.x);
			this.composedText.width = Math.ceil(Math.max(this.composedText.width, lastLine.width));
		} else {
			this.composedText.maxLineWidth = this.composedText.lines[0].width;
		}

		// good text height
		this.composedText.height = lastLine.y + lastLine.maxHeight - this.composedText.baseline;

		let paddingTop = maxPadding + this.composedText.leading; // Add leading
		let paddingLeft = maxPadding;
		let paddingRight = maxPadding;
		let paddingBottom = maxPadding; // Remove leading to not change the texture ratio

		if (this._options.padding) {
			paddingLeft += this._options.padding.x;
			paddingTop += this._options.padding.y;
			paddingRight += this._options.padding.width;
			paddingBottom += this._options.padding.height;
		}

		this.composedText.padding = new Rectangle(paddingLeft, paddingTop, paddingRight, paddingBottom);
	}

	// will justify a line (by defining spaces width)
	private _justifyLine(line: any): void {
		const words = line.words;
		// requires 2 words
		if (words.length > 1) {
			// calculate the spaces width for that line
			const spacesWidth = (this.fixedWidth - line.width) / (words.length - 1);
			const lastWord = words[words.length - 1];
			lastWord.x = this.fixedWidth - lastWord.width; // the last word is right aligned;

			for (let i = 1, len = words.length - 1; i < len; i++) {
				const previousWord = words[i - 1];
				// update word x position.
				words[i].x =
					previousWord.x + previousWord.width + spacesWidth + previousWord.nextGlyph.width;
			}
		}
	}

	// perform a draw pass in context
	private _drawPass(pass: number): void {
		this.composedText.lines.forEach((line) => {
			this._drawLine(pass, line);
		});

		if (pass === EDrawPass.Debug) {
			// draw canvas size
			if (this._debug.canvas) {
				const canvasBounds = new Rectangle(
					-(this.composedText.canvasX + this.composedText.padding.left),
					-this.composedText.padding.top,
					this.canvas.width,
					this.canvas.height
				);
				this.drawDebugRectangle(canvasBounds, this._debug.canvas);
			}

			// draw Text size
			if (this._debug.size) {
				this.drawDebugRectangle(
					new Rectangle(
						this.composedText.x,
						this.composedText.y,
						this.composedText.width,
						this.composedText.height
					),
					this._debug.size
				);
			}
		}
	}

	// draw a Line in context
	private _drawLine(pass: number, line: Line): void {
		line.words.forEach((word) => {
			this._drawWord(pass, line, word);
		});

		const lineBounds = new Rectangle(line.x, line.y + line.baselineOffset, line.width, line.height);

		if (this._debug.lines && pass === EDrawPass.Debug) {
			this.drawDebugRectangle(lineBounds, this._debug.lines);
		}

		if (this.hitAreaLevel === EHitAreaLevel.Line) {
			this.hitAreas.push(lineBounds);
		}
	}

	// draw a word in context
	private _drawWord(pass: number, line: Line, word: Word): void {
		word.glyphs.forEach((glyph) => {
			line.baselineOffset = -this.composedText.baseline;

			const wordBounds = new Rectangle(
				line.x + word.x,
				line.y + line.baseline - word.baseline + line.baselineOffset,
				word.width,
				word.maxHeight
			);

			// draw word boundings
			if (this._debug.words && pass === EDrawPass.Debug) {
				this.drawDebugRectangle(wordBounds, this._debug.words);
			}

			if (this.hitAreaLevel === EHitAreaLevel.Word) {
				this.hitAreas.push(wordBounds);
			}

			// draw word"s glyphs
			this.drawGlyph(pass, glyph, line, word);
		});

		// if it"s a space draw word"s next glyph (for underline)
		if (word.nextGlyph) {
			if (word.nextGlyph.type === EGlyphType.Space) {
				this.drawGlyph(pass, word.nextGlyph, line, word);
			}
		}
	}

	// get line horizontal alignement
	private _getLineHorizontalAlignement(line: Line, rounded: boolean = true): number {
		// should we do wordwrap
		const wordWrap = this.fixedWidth === 0 || !this.wordWrap;

		// only one line no need to go further
		if (this.composedText.lines.length <= 1 && wordWrap) {
			return 0;
		}

		// get our text max width
		const textMaxWidth = wordWrap ? this.composedText.maxLineWidth : this.fixedWidth;

		if (rounded) {
			switch (line.align) {
				case EAlign.Center:
					return Math.round((textMaxWidth - line.width) * 0.5);
				case EAlign.Right:
					return Math.round(textMaxWidth - line.width);
				default:
					return 0;
			}
		} else {
			switch (line.align) {
				case EAlign.Center:
					return (textMaxWidth - line.width) * 0.5;
				case EAlign.Right:
					return textMaxWidth - line.width;
				default:
					return 0;
			}
		}
	}

	// draw a gluph in context
	protected drawGlyph(pass: number, glyph: Glyph, line: Line, word: Word): void {
		if (!glyph) {
			return;
		}

		const style = glyph.style;
		const strokeOffsetX = pass === EDrawPass.Stroke ? style.strokeOffsetX | 0 : 0;
		const strokeOffsetY = pass === EDrawPass.Stroke ? style.strokeOffsetY | 0 : 0;
		const glyphInterline = glyph.interLine || 0;
		const lineX = this.composedText.canvasX + line.x + this.composedText.padding.left;
		const lineY =
			line.y +
			line.baseline +
			line.baselineOffset +
			this.composedText.padding.top +
			glyphInterline +
			strokeOffsetY;

		const glyphX = glyph.x + lineX + word.x + strokeOffsetX;
		const path = glyph.openTypeGlyph.getPath(glyphX, lineY, style.fontSize);
		const underlinePosition = lineY + line.underline;

		// define line styles
		this.context.lineJoin = style.lineJoin || ELineJoin.Round;
		this.context.lineCap = style.lineCap || ELineCap.Round;
		this.context.miterLimit = style.miterLimit || 10;

		const glyphHitArea = this._getBoundsByType(EHitAreaLevel.Glyph, line, word, glyph);

		switch (this.hitAreaLevel) {
			case EHitAreaLevel.Glyph:
				this.hitAreas.push(glyphHitArea);
				this.addLink(glyph, glyphHitArea);
				break;
			case EHitAreaLevel.Word:
				this.addLink(
					glyph,
					new Rectangle(
						line.x + word.x + glyph.x,
						line.y + line.baseline - word.baseline + line.baselineOffset,
						glyph.width,
						word.maxHeight
					)
				);
				break;
			case EHitAreaLevel.Text:
			case EHitAreaLevel.Line:
			default:
				this.addLink(
					glyph,
					new Rectangle(
						line.x + word.x + glyph.x,
						line.y + line.baselineOffset,
						glyph.width,
						line.height
					)
				);
				break;
		}

		// draw glyph bounds
		if (this._debug.glyphBounds && pass === EDrawPass.Debug) {
			this.drawDebugRectangle(glyphHitArea, this._debug.glyphBounds);
		}

		// shadow
		if (style.strokeWidth > 0) {
			if (pass === EDrawPass.Stroke) {
				this.drawShadow(style);
			} else if (pass === EDrawPass.Fill) {
				this.drawShadow(null);
			}
		} else {
			if (pass === EDrawPass.Fill) {
				this.drawShadow(style);
			}
		}

		// draw openType path/stroke in context
		if (pass !== EDrawPass.Debug) {
			this.drawPath(path);
		}

		// draw baseline
		if (this._debug.baseline && pass === EDrawPass.Debug) {
			this.drawDebugRectangle(
				new Rectangle(
					line.x + word.x + glyph.x,
					line.y + line.baselineOffset + line.baseline - 1,
					glyph.width,
					4
				),
				this._debug.baseline
			);
		}

		// fill stroke
		if (style.stroke && style.strokeWidth > 0 && pass === EDrawPass.Stroke) {
			if (style.strokeStyle && style.strokeStyle.steps) {
				style.stroke = this._getFillStyle(
					this._getBoundsByType(style.strokeStyle.bounds, line, word, glyph),
					style.strokeStyle
				);
			}

			// apply stroke
			this.applyRenderPassStyle(pass, style);

			// underline stroke
			if (style.underlineWeight > 0) {
				this.drawUnderline(glyphX, underlinePosition, glyph, style);
				this.applyRenderPassStyle(pass, style);
			}
		}

		// fill
		if (style.fill && pass === EDrawPass.Fill) {
			if (style.fillStyle && style.fillStyle.steps) {
				style.fill = this._getFillStyle(
					this._getBoundsByType(style.fillStyle.bounds, line, word, glyph),
					style.fillStyle
				);
			}

			this.applyRenderPassStyle(pass, style);

			// underline fill
			if (style.underlineWeight > 0) {
				this.drawUnderline(glyphX, underlinePosition, glyph, style);
				this.applyRenderPassStyle(pass, style);
			}
		}
	}

	/**
	 * get the bounds rectangle
	 * @param type: type of bounds wanted
	 * @param line: Line element used to compute bounds
	 * @param word: Word element used to compute bounds
	 * @param glyph: Glyph element used to compute bounds
	 * @returns the bounds
	 */
	private _getBoundsByType(
		type: EGradientBounds | EHitAreaLevel,
		line: Line,
		word: Word,
		glyph: Glyph
	): Rectangle {
		let bounds: Rectangle;
		const glyphInterline = glyph.interLine || 0;

		switch (type) {
			case EGradientBounds.Glyph:
				bounds = new Rectangle(
					line.x + word.x + glyph.x,
					line.y + line.baselineOffset + line.baseline - glyph.yMax - 1 + glyphInterline,
					glyph.width,
					glyph.height
				);
				break;
			case EGradientBounds.Word:
				bounds = new Rectangle(
					line.x + word.x,
					line.y + line.baseline - word.baseline + line.baselineOffset,
					word.width,
					word.maxHeight
				);
				break;
			case EGradientBounds.Text:
				bounds = new Rectangle(
					this.composedText.x,
					this.composedText.y,
					this.composedText.width,
					this.composedText.height
				);
				break;
			case EGradientBounds.Line:
			default:
				bounds = new Rectangle(
					line.x,
					line.y + glyphInterline + line.baselineOffset,
					line.width,
					line.height
				);
				break;
		}

		return bounds;
	}

	/**
	 * get the fill style
	 * @param rect: the bounds where that style apply
	 * @param style: the style parametres
	 * @returns the canvasGradient
	 */
	private _getFillStyle(
		rect: Rectangle,
		fillStyle: ILinearGradientStyle | IRadialGradientStyle
	): CanvasGradient {
		let gradient: CanvasGradient;

		// different fillType
		switch (fillStyle.type) {
			case EGradientType.Radial:
				gradient = this._createRadialGradient(rect, fillStyle);
				break;
			case EGradientType.LinearHorizontal:
			case EGradientType.LinearVertical:
			case EGradientType.Linear:
			default:
				gradient = this._createLinearGradient(rect, fillStyle);
				break;
		}

		// add colors
		if (fillStyle.steps) {
			fillStyle.steps.forEach((step) => {
				gradient.addColorStop(step.ratio, step.color);
			});
		}

		return gradient;
	}

	/**
	 * create a linear gradient
	 * @param rect: the gradient bounds
	 * @param style: the gradient style
	 * @returns the canvasGradient
	 */
	private _createLinearGradient(rect: Rectangle, fillStyle: ILinearGradientStyle): CanvasGradient {
		const origin = this._getGradientOrigin(rect);
		let degrees = this._trimAngle(fillStyle.angle | 0);

		// some shortcuts for linear horizontal / vertical angles
		switch (fillStyle.type) {
			case EGradientType.LinearHorizontal:
				degrees = 0;
				break;
			case EGradientType.LinearVertical:
				degrees = 90;
				break;
		}

		// get the hypothenuse
		const hypotenuse = Math.sqrt(rect.width * rect.width + rect.height * rect.height);

		// search for gradient angle
		const hypotenuseAngle = Math.acos(rect.height / hypotenuse);
		const searchedAngle = this._degToRad(degrees - 90) % (Math.PI * 2);
		let lengthCalculAngle: number = 0;
		if (searchedAngle >= 0 && searchedAngle <= Math.PI / 2) {
			lengthCalculAngle = searchedAngle;
		} else if (searchedAngle > Math.PI / 2 && searchedAngle <= Math.PI) {
			lengthCalculAngle = Math.PI / 2 - (searchedAngle - Math.PI / 2);
		} else if (searchedAngle > Math.PI && searchedAngle <= (Math.PI * 3) / 2) {
			lengthCalculAngle = searchedAngle - Math.PI;
		} else {
			lengthCalculAngle = Math.PI * 2 - searchedAngle;
		}

		// calc the gradient length
		const length = hypotenuse * Math.cos(lengthCalculAngle - hypotenuseAngle);
		const halfLength = length * 0.5;
		const center: Point = new Point(origin.x + rect.width * 0.5, origin.y + rect.height * 0.5);

		// find gradient start and end points
		const gradientStartPoint = this._lineToAngle(center, degrees - 180, halfLength);
		const gradientEndPoint = this._lineToAngle(center, degrees, halfLength);

		return this.context.createLinearGradient(
			gradientStartPoint.x,
			gradientStartPoint.y,
			gradientEndPoint.x,
			gradientEndPoint.y
		);
	}

	/**
	 * create a radial gradient
	 * @param rect: the gradient bounds
	 * @param style: the gradient style
	 * @returns the canvasGradient
	 */
	private _createRadialGradient(rect: Rectangle, style: IRadialGradientStyle): CanvasGradient {
		const origin = this._getGradientOrigin(rect);

		const startCenter = {
			x: rect.width * (style.startCenter ? style.startCenter.x : 0.5),
			y: rect.height * (style.startCenter ? style.startCenter.y : 0.5),
		};

		const endCenter = {
			x: rect.width * (style.endCenter ? style.endCenter.x : 0.5),
			y: rect.height * (style.endCenter ? style.endCenter.x : 0.5),
		};

		const diff = {
			x: Math.max(endCenter.x, startCenter.x),
			y: Math.max(endCenter.y, startCenter.y),
		};

		const endRadius = Math.sqrt(diff.x * diff.x + diff.y * diff.y);

		return this.context.createRadialGradient(
			origin.x + startCenter.x,
			origin.y + startCenter.y,
			endRadius * (style.startRatio | 0),
			origin.x + endCenter.x,
			origin.y + endCenter.y,
			endRadius * (style.endRatio | 1)
		);
	}

	/**
	 * get the origin point in textfield for a given rectangle
	 * @param rect: the rectangle to get origin from
	 * @returns the origin point in textfield for that rectangle
	 */
	private _getGradientOrigin(rect: Rectangle): Point {
		return new Point(
			rect.x + this.composedText.canvasX + this.composedText.padding.left,
			rect.y + this.composedText.padding.top + 1
		);
	}

	/**
	 * limit an angle between min and max
	 * @param degrees: angle to limit
	 * @param min: minimum limit
	 * @param max: maximum limit
	 * @returns limited angle
	 */
	private _trimAngle(degrees: number, min: number = 0, max: number = 360): number {
		const rotation = degrees % max;
		if (rotation < min) {
			return this._trimAngle(rotation + max, min, max);
		} else if (rotation > max) {
			return this._trimAngle(rotation - max, min, max);
		} else {
			return rotation;
		}
	}

	/**
	 * convert an angle in degrees to radians
	 * @param degrees: angle to convert in radian
	 * @returns converted radians angle
	 */
	private _degToRad(degrees: number): number {
		return (degrees / 180) * Math.PI;
	}

	/**
	 * move and rotate a point
	 * @param origin {Point} the origin position
	 * @param angle the angle in degrees
	 * @param length the distance to move
	 * @returns {Point} transformed
	 */
	private _lineToAngle(origin: Point, angle: number, length: number): Point {
		const radiant = this._degToRad(angle);
		const x2 = origin.x + Math.cos(radiant) * length;
		const y2 = origin.y + Math.sin(radiant) * length;
		return new Point(x2, y2);
	}

	/**
	 * Adds the given glyph to the availalbe links
	 * @param glyph the glyph to add
	 * @param hitArea the hit area of the glyph
	 */
	protected addLink(glyph: Glyph, hitArea: Rectangle): void {
		if (glyph.style.interactive) {
			this.links[glyph.token.content].hitAreas.push(hitArea);
		}
	}

	/**
	 * Apply a style to the context
	 * @param pass: the pass number
	 * @param style: the style to applied
	 */
	protected applyRenderPassStyle(pass: number, style: IStyle): void {
		switch (pass) {
			case EDrawPass.Stroke:
				this.context.fillStyle = style.stroke as string | CanvasGradient;
				this.context.strokeStyle = style.stroke as string | CanvasGradient;
				this.context.lineWidth = style.strokeWidth * 2;
				this.context.fill();
				this.context.stroke();
				break;
			case EDrawPass.Fill:
				this.context.fillStyle = style.fill as CanvasGradient | string;
				this.context.fill();
				break;
			case EDrawPass.Debug:
				// disable shadow for debug pass
				this.drawShadow();

				if (style.fill) {
					this.context.fillStyle = style.fill as CanvasGradient | string;
					this.context.fill();
				}

				if (style.stroke && style.strokeWidth > 0) {
					this.context.strokeStyle = style.stroke as string | CanvasGradient;
					this.context.lineWidth = style.strokeWidth || 0;
					this.context.stroke();
				}
				break;
			default:
				break;
		}
	}

	/**
	 * Define context shadow style
	 * @param style: the style to take the shadow parameters
	 */
	protected drawShadow(style: IStyle = null): void {
		if (style) {
			this.context.shadowColor = style.shadowColor || null;
			this.context.shadowOffsetX = style.shadowOffsetX || null;
			this.context.shadowOffsetY = style.shadowOffsetY || null;
			this.context.shadowBlur = style.shadowBlur || null;
		} else {
			this.context.shadowColor = null;
			this.context.shadowOffsetX = null;
			this.context.shadowOffsetY = null;
			this.context.shadowBlur = null;
		}
	}

	/**
	 * Draw the underline
	 * @param glyphX the x position
	 * @param glyphY the y position
	 * @param Glyph the glyph to underline
	 * @param style the Style to use
	 */
	protected drawUnderline(glyphX: number, lineY: number, glyph: Glyph, style: IStyle): void {
		const underlineWeight = style.underlineWeight || 2;
		// const underlineDistance = style.underlineDistance || 2;

		const rectangle = new Rectangle(
			Math.floor(glyphX),
			Math.round(lineY),
			Math.ceil(glyph.width),
			underlineWeight
		);

		this._drawRectangle(rectangle);
	}

	/**
	 * Draw a rectangle into context for debug purpose
	 * @param rectangle the Rectangle to draw
	 * @param style the Style to use
	 */
	protected drawDebugRectangle(rect: Rectangle, style: IStyle): void {
		const bounds = new Rectangle(
			rect.x + this.composedText.canvasX + this.composedText.padding.left,
			rect.y + this.composedText.padding.top,
			rect.width,
			rect.height
		);

		// draw the shape
		this._drawRectangle(bounds);

		this.applyRenderPassStyle(EDrawPass.Debug, style);
	}

	/**
	 * Draw a simple rectangle into context
	 * @param rectangle the Rectangle to draw
	 */
	private _drawRectangle(rect: Rectangle): void {
		this.context.beginPath();
		this.context.moveTo(rect.x, rect.y);
		this.context.lineTo(rect.x + rect.width, rect.y);
		this.context.lineTo(rect.x + rect.width, rect.y + rect.height);
		this.context.lineTo(rect.x, rect.y + rect.height);
		this.context.lineTo(rect.x, rect.y);
		this.context.closePath();
	}

	// When the user clicks on a link
	private _onLinkEvent(eventName: string, e: any): void {
		if (eventName === "pointertap") {
			const link = this.getLinkHit(e.data.global);
			if (link) {
				this.emit("link", link.token);
				tracker.track(new LinkClick(this.getName(), link.token.attributes.href || ""));
			}
		}

		/*
		// DON"T WORK ON TOUCH EVENT ???
		if (eventName === "pointerout" && this.hooveredLink) {
			this.emit("link." + eventName, this.hooveredLink);
			this.hooveredLink = null;
		} else {
			const link = this.getLinkHit(e.data.global);
			if (link) {
				if (eventName ==="pointerover")
					this.hooveredLink = link.token;

				if (eventName === "pointertap") {
					this.emit("link", this.hooveredLink);
					tracker.track(new LinkClick(this.getName(), link.token.attributes.href || "));
				}

				this.emit("link." + eventName, this.hooveredLink);
			}
		}*/
	}

	/**
	 * Render text with WebGL
	 * @param renderer the pixi.js renderer (WebGLRenderer)
	 * @override PIXI.Sprite.renderWebGL
	 */
	public renderWebGL(renderer: WebGLRenderer): void {
		this._resolution = renderer.resolution;
		super.renderWebGL(renderer);
	}

	/**
	 * Render text with Canvas renderer
	 * @param renderer the pixi.js renderer (CanvasRenderer)
	 * @override PIXI.Sprite.renderCanvas
	 */
	public renderCanvas(renderer: CanvasRenderer): void {
		this._resolution = renderer.resolution;
		super.renderCanvas(renderer);
	}

	/**
	 * This function is Path.prototype.draw
	 */
	protected drawPath(path: OpenType.Path): void {
		let cmd;
		this.context.beginPath();

		for (let i = 0, len = path.commands.length; i < len; i++) {
			cmd = path.commands[i];
			if (cmd.type === "M") {
				this.context.moveTo(cmd.x, cmd.y);
			} else if (cmd.type === "L") {
				this.context.lineTo(cmd.x, cmd.y);
			} else if (cmd.type === "C") {
				this.context.bezierCurveTo(cmd.x1, cmd.y1, cmd.x2, cmd.y2, cmd.x, cmd.y);
			} else if (cmd.type === "Q") {
				this.context.quadraticCurveTo(cmd.x1, cmd.y1, cmd.x, cmd.y);
			} else if (cmd.type === "Z") {
				this.context.closePath();
			}
		}
	}

	/**
	 * Updates the texture"s size
	 */
	protected updateTextureSize(): void {
		if (!this._texture.trim) {
			this._texture.trim = new Rectangle();
		}

		// TODO: manage device resolution and pixel ratio
		this._texture.baseTexture.hasLoaded = true;
		this._texture.baseTexture.resolution = this.resolution;
		this._texture.baseTexture.realWidth = this.canvas.width;
		this._texture.baseTexture.realHeight = this.canvas.height;
		this._texture.baseTexture.width = this.canvas.width / this.resolution;
		this._texture.baseTexture.height = this.canvas.height / this.resolution;

		this._texture.trim.x =
			(-this.composedText.padding.left - this.composedText.canvasX) / this.resolution;
		this._texture.trim.y = -this.composedText.padding.top / this.resolution;
		this._texture.trim.width = this.canvas.width / this.resolution;
		this._texture.trim.height = this.canvas.height / this.resolution;

		this._texture.orig.width = this.composedText.width / this.resolution;
		this._texture.orig.height = this.composedText.height / this.resolution;

		this._onTextureUpdate();
		this._texture.baseTexture.emit("update", this._texture.baseTexture);
	}

	/**
	 * Updates the cached fonts
	 * @param style the style containing the font name
	 */
	protected getFont(style: IStyle): OpenType.Font {
		const font =
			TextField.cachedFonts[style.fontName] ||
			(TextField.cachedFonts[style.fontName] = Loader.get<OpenType.Font>(style.fontName));

		if (!font && style.fontName) {
			throw new Error(`No font found for ${style.fontName} !`);
		}

		return font;
	}

	/**
	 * Helper to show debug
	 * @param show show debug or not
	 * @param styles the style to applied
	 */
	public showDebug(show: boolean, styles: {} = null): void {
		this._debug.pass = show ? 2 : -1;

		if (styles !== null) {
			for (const prop in styles) {
				if (this._debug[prop]) {
					this._debug[prop] = styles[prop];
				}
			}
		}

		this.update();
	}

	public get resolution(): number {
		// i am almost good for a real resolution BUT
		// if i use the actual resolution i need to use it for every values drawn on canvas
		// so for now, just set it to 1.
		return 1; // this._resolution;
	}

	/**
	 * Returns the number of pixels by unit
	 * @param value the value to convert
	 * @param style the style to use
	 */
	public static unitToPx(value: number, style: IStyle): number {
		return (value / style.font.unitsPerEm) * (style.fontSize || 12);
	}

	/**
	 * Returns the number of unit by pixels
	 * @param value the value to convert
	 * @param style the style to use
	 */
	// TODO : use pxToUnit instead of unitToPx to reduce calculation aproximations
	// meaning working in opentype units for calculations and in pixel for draw only
	public static pxToUnit(value: number, style: IStyle): number {
		return (value * style.font.unitsPerEm) / (style.fontSize || 12);
	}

	/**
	 * Export the textfield
	 */
	public export(): any {
		return Tools.assign(ContainerExporter.exportContainer(this), {
			type: "ui.TextField",

			text: this._i18nKey ?? this.text,
			styles: this.metadata && this.metadata.style ? this.metadata.style.id : this.styles,

			hitAreaLevel: this.hitAreaLevel,
			hitAreaPoint: {
				x: 0,
				y: 0,
			},

			width: this.fixedWidth,
			height: this.fixedHeight,

			options: {
				align: this.align,
				padding: CommonExporter.exportRectangle(this.padding),
				interLine: this.interLine,
				wordWrap: this.wordWrap,
			},

			i18nKey: this._i18nKey,
		});
	}

	/**
	 * Parses the textfield
	 * @param config the serialized data
	 * @param baseUrl the base url of the loader
	 * @param textfield the optional existing textfield instance
	 */
	public static parse(config: any, baseUrl: string, textfield?: TextField): TextField {
		let styles = config.styles;
		if (config.metadata && config.metadata.style) {
			styles = AssetStore.getAssetById(config.metadata.style.id).data;
		}

		const text = textfield || new TextField("", styles, config.options);
		text._isFrozen = true;
		text.text = config.i18nKey ?? config.text;
		text.styles = styles;
		text.wordWrap = config.options.wordWrap;
		text.hitAreaLevel = config.hitAreaLevel;
		text.width = config.width;
		text.height = config.height;
		text.interLine = config.options.interLine || text.interLine;
		text.i18nKey = config.i18nKey;
		text.align = config.options.align;
		text.padding = config.options.padding;
		text._isFrozen = false;
		text.update();

		return text;
	}
}
