import type { Font } from "opentype.js";

export enum ELineJoin {
	Miter = "miter",
	Bevel = "bevel",
	Round = "round",
}

export enum ELineCap {
	Square = "square",
	Butt = "butt",
	Round = "round",
}

export enum EDisplayMode {
	Inline,
	Block,
}

export enum EAlign {
	Left,
	Right,
	Center,
	Justify,
}

export enum EGradientType {
	Linear,
	LinearVertical,
	LinearHorizontal,
	Radial,
}

export interface IGradientSteps {
	ratio: number;
	color: string;
}

export enum EGradientBounds {
	Text,
	Line,
	Word,
	Glyph,
}

export interface ILinearGradientStyle {
	type: EGradientType;
	bounds?: EGradientBounds;
	angle?: number;
	steps: IGradientSteps[];
}

export interface IRadialGradientStyle {
	type: EGradientType;
	bounds?: EGradientBounds;
	startRatio?: number;
	startCenter?: { x: number; y: number };
	endRatio?: number;
	endCenter?: { x: number; y: number };
	steps: IGradientSteps[];
}

export interface ITextStyle {
	// fontFamily?: string;
	// fontSubFamily?: string;
	fontName?: string;
	fontSize?: number;
	fill?: string | CanvasGradient | ILinearGradientStyle | IRadialGradientStyle;
	fillStyle?: ILinearGradientStyle | IRadialGradientStyle;
	stroke?: string | CanvasGradient | ILinearGradientStyle | IRadialGradientStyle;
	strokeStyle?: ILinearGradientStyle | IRadialGradientStyle;
	strokeOffsetX?: number;
	strokeOffsetY?: number;
	strokeWidth?: number;
	// outerStroke?:boolean;
	// lineHeight?: number;
	interLine?: number;
	shadowColor?: string;
	shadowOffsetX?: number;
	shadowOffsetY?: number;
	shadowBlur?: number;

	// stroke style
	lineJoin?: ELineJoin;
	lineCap?: ELineCap;
	miterLimit?: number;

	// underline?: boolean;
	underlineDistance?: number;
	underlineWeight?: number;
}

export interface IStyle extends ITextStyle {
	font?: Font;
	// letterSpacing?: number;
	// display?: DisplayMode;
	interactive?: boolean;
	// interactiveIdle?: Style;
	// interactiveOver?: Style;
}

export interface IStyles {
	default: IStyle;
	[name: string]: IStyle;
}
