import { Rectangle } from "pixi.js";
import { TweenMax } from "gsap";
import { Tools } from "@gdk/core";
import type { mesh } from "pixi.js";

import { EScrollbarAlign } from "./scrollContainer";
import { Slider } from "./slider";
import { ContainerExporter } from "../../../loading/export/containerExporter";
import { CommonExporter } from "../../../loading/export/commonExporter";
import { ContainerImporter } from "../../../loading/import/containerImporter";
import { CommonImporter } from "../../../loading/import/commonImporter";
import type { ISliderOptions } from "./slider";
import type { ScrollContainer } from "./scrollContainer";

export interface IScrollBarOptions extends ISliderOptions {
	margin: Rectangle; // the margins around the scrollbar
	scrollingContainer: ScrollContainer; // the scrollContainer
	align: EScrollbarAlign;
}

export class ScrollBar extends Slider {
	// Public members
	public autohide: boolean = true; // does it hide itself ?
	public toogleVisibilityDuration: number = 0.5; // the duration it take to toogle the alpha.
	public margin: Rectangle; // the margin around this.
	public scrollingContainer: ScrollContainer; // the attached scrolling container
	public scaleRatio: number = 1;

	// Private members
	private _hidden: boolean = false;
	private _align: EScrollbarAlign;

	/**
	 * create a Scrollbar element with specified options
	 * @param options the creation options
	 */
	public constructor(options: IScrollBarOptions) {
		super(options);

		this.margin = options.margin || new Rectangle();
		this.scrollingContainer = options.scrollingContainer;
		this._align =
			options.align === EScrollbarAlign.After ? EScrollbarAlign.After : EScrollbarAlign.Before;

		this._toggleHidden(true, 0);

		if (this.scrollingContainer) {
			this.scrollingContainer.attachScrollbar(this, this._align);
		}
	}

	/**
	 * will align the scrollbar size and position to the scroll-container content size and position.
	 */
	public alignToContainer(): void {
		let newPos: number;
		let size: number;
		const xOrY: string = this.vertical ? "y" : "x";
		const widthOrHeight: string = this.vertical ? "height" : "width";
		const innerSize: number = Math.round(
			this.scrollingContainer.innerBounds[widthOrHeight] * this.scaleRatio
		);
		const scrollingContainerSize: number = Math.round(
			this.scrollingContainer[widthOrHeight] * this.scaleRatio
		);
		const innerPosition: number = Math.round(
			this.scrollingContainer.innerContainer[xOrY] * this.scaleRatio
		);
		const sizeAmt: number = !innerSize ? 1 : scrollingContainerSize / innerSize;

		this[`_${widthOrHeight}`] = scrollingContainerSize;
		const handleMinPos: number = this.vertical ? this.handle.topHeight : this.handle.leftWidth;
		const handleMaxPos: number = this.vertical ? this.handle.bottomHeight : this.handle.rightWidth;
		const handleMinSize: number = handleMinPos + handleMaxPos;

		// update scroll value
		if (!scrollingContainerSize) {
			this.pValue = 0;
		} else {
			this.pValue = -(innerPosition / (innerSize - scrollingContainerSize));
		}

		if (sizeAmt >= 1) {
			size = this[`_${widthOrHeight}`];
			this.handle[xOrY] = size * 0.5;
			this._toggleHidden(true, this.toogleVisibilityDuration);
		} else {
			size = Math.max(handleMinSize, this[`_${widthOrHeight}`] * sizeAmt);
			const sizeDiff = this[`_${widthOrHeight}`] - size;

			if (this.pValue > 1) {
				size = Math.max(handleMinSize, size - sizeDiff * (this.pValue - 1));
				newPos = this[`_${widthOrHeight}`] - size;
			} else if (this.pValue < 0) {
				size = Math.max(handleMinSize, size - sizeDiff * -this.pValue);
				newPos = 0;
			} else {
				newPos = this.pValue * (this[`_${widthOrHeight}`] - size);
			}

			this.handle[xOrY] = Math.floor(newPos);
			this._toggleHidden(false, this.toogleVisibilityDuration);
		}

		this._resizeHandle(widthOrHeight, Math.ceil(size));
		this._resizeTrack(widthOrHeight, this[`_${widthOrHeight}`]);
	}

	/**
	 * Gets the width of the scroll bar
	 */
	public get width(): number {
		return this._width;
	}

	/**
	 * Sets the width of the scrollbar. Then resizes the handle and the track meshes
	 */
	public set width(value: number) {
		if (!this.vertical) {
			this.scaleRatio = value / this.scrollingContainer.width;
		} else {
			this._resizeHandle("width", value);
			this._resizeTrack("width", value);
		}
	}

	/**
	 * Gets the height of the scroll bar
	 */
	public get height(): number {
		return this._height;
	}

	/**
	 * Sets the height of the scrollbar. Then resizes the handle and the track meshes
	 */
	public set height(value: number) {
		if (this.vertical) {
			this.scaleRatio = value / this.scrollingContainer.height;
		} else {
			this._resizeHandle("height", value);
			this._resizeTrack("height", value);
		}
	}

	/**
	 * initialize Events handlers
	 */
	protected initialize(): void {
		super.initialize();

		this.handle.hitArea = new Rectangle(0, 0, this.handle.width, this.handle.height);
		this.track.hitArea = new Rectangle(0, 0, this.track.width, this.track.height);

		this.handle.buttonMode = true;
		this.handle.interactive = true;

		this.track.buttonMode = true;
		this.track.interactive = true;

		this.pOnValueChanging = (val) => {
			const widthOrHeight: string = this.vertical ? "height" : "width";
			const innerContainerSize: number =
				this.scrollingContainer.innerContainer[widthOrHeight] || 0.001;
			const scrollingContainerSize = this.scrollingContainer[widthOrHeight];
			const sizeAmt: number = scrollingContainerSize / innerContainerSize;

			this.scrollingContainer.scrollTo(this.vertical, this.pValue, 0);
		};
	}

	private _resizeTrack(widthOrHeight: string, value: number): void {
		this[`_${widthOrHeight}`] =
			this.track[widthOrHeight] =
			this.track.hitArea[widthOrHeight] =
				value;
	}

	private _resizeHandle(widthOrHeight: string, value: number): void {
		this.handle[widthOrHeight] = this.handle.hitArea[widthOrHeight] = value;
	}

	/**
	 * toogle alpha 0 / 1 at given speed
	 * @param hidden {boolean} will alpha goes to 0 ?
	 * @param duration {number} the duration of the toogle tween.
	 */
	private _toggleHidden(hidden: boolean, duration: number): void {
		if (this.autohide) {
			if (hidden && !this._hidden) {
				TweenMax.to(this, duration, { alpha: 0 });
				this._hidden = true;
			} else if (!hidden && this._hidden) {
				TweenMax.to(this, duration, { alpha: 1 });
				this._hidden = false;
			}
		}
	}

	/**
	 * Exports the scroll bar
	 */
	public export(): any {
		this.track.doNotExport = true;
		this.handle.doNotExport = true;

		return Tools.assign(ContainerExporter.exportContainer(this), {
			type: "ui.ScrollBar",
			track: ContainerExporter.exportNineSlicePlane(this.track),
			handle: ContainerExporter.exportNineSlicePlane(this.handle),
			align: this._align,
			vertical: this.vertical,
			margin: CommonExporter.exportRectangle(this.margin),
		});
	}

	/**
	 * Parses the scroll bar
	 * @param config the serialized data
	 * @param baseUrl the base url of the loader
	 */
	public static parse(config: any, baseUrl: string, scrollContainer?: ScrollContainer): ScrollBar {
		return new ScrollBar({
			align: config.align,
			handle: ContainerImporter.import(config.handle, baseUrl) as mesh.NineSlicePlane,
			track: ContainerImporter.import(config.track, baseUrl) as mesh.NineSlicePlane,
			margin: CommonImporter.importRectangle(config.margin),
			vertical: config.vertical,
			scrollingContainer: scrollContainer,
		});
	}
}
