import { Container, Sprite, Graphics, Rectangle, Point, Transform } from "pixi.js";
import { Cubic } from "gsap";
import { Tools, Vector2 } from "@gdk/core";
import type { WebGLRenderer, CanvasRenderer } from "pixi.js";

import { DockingBounds } from "../../container/dockingBounds";
import { ContainerExporter } from "../../loading/export/containerExporter";
import { ContainerImporter } from "../../loading/import/containerImporter";
import { ScrollBar2, EScrollBarAlign2 } from "./scrollBar2";
import { DragEvent2 } from "../events/dragEvent2";

/**
 * @experimental
 */
export class ScrollContainer2 extends Container {
	/**
	 * Container used to store content of the scroll container.
	 * All scrollable children should be added in this content container.
	 */
	public content: Container = new Container();
	/**
	 * Array of existing scroll bars for the current scroll container.
	 */
	public scrollBars: ScrollBar2[] = [];
	/**
	 * Defines the inertia ratio multiplied by the current deltas.
	 */
	public inertia: number = 0.9;
	/**
	 * Defines the scroll speed. When scrolling, the speed is multiplied by this value.
	 */
	public scrollSpeed: number = 0.1;
	/**
	 * Sets wether or not scrolling inertia is enabled.
	 */
	public inertiaEnabled: boolean = false;

	private _scrollMask: Sprite | Graphics = null;

	private _width: number = 1;
	private _height: number = 1;

	private _mouseWheelListener: (this: Document, ev: WheelEvent) => void = null;

	private _scrollDelta: Vector2 = new Vector2(0, 0);
	private _inertialOffsets: Vector2 = new Vector2(0, 0);
	private _lastFramePosition: Point = new Point(0, 0);
	private _dragEvent: DragEvent2 = new DragEvent2();

	private _scrollBounds: Rectangle = new Rectangle();
	private _lastScrollBounds: Rectangle = new Rectangle();

	private _automaticTrackHeight: boolean = false;
	private _automaticScrollBarVisibility: boolean = false;

	private static _lineHeight: number = 100;
	private static _pageHeight: number = 1;

	/**
	 * Constructor.
	 */
	public constructor() {
		super();

		// Configure
		this.dockingBounds = new DockingBounds();
		this._bindEvents();

		// Content
		this.content.name = "Content";
		this.content.doNotExport = true;
		this.content.hitArea = new Rectangle(0, 0, this._width, this._height);
		this.addChild(this.content);

		// Mask
		this.scrollMask = new Graphics();

		// Set default dimensions to 500,500
		this.setDimensions(500, 500);
	}

	/**
	 * Renders the scratch using the WebGL renderer.
	 * @param renderer the webgl renderer reference.
	 */
	public renderWebGL(renderer: WebGLRenderer): void {
		this._updateInertia();
		this._updateContentMask();

		super.renderWebGL(renderer);
	}

	/**
	 * Renders the scratch using the canvas renderer.
	 * @param renderer the canvas renderer reference.
	 */
	public renderCanvas(renderer: CanvasRenderer): void {
		this._updateInertia();
		this._updateContentMask();

		super.renderCanvas(renderer);
	}

	/**
	 * Sets the new dimensions of the scroll container.
	 * @param width the new width of the container in pixels.
	 * @param height the new height of the container in pixels.
	 */
	public setDimensions(width: number, height: number): void {
		// If sprite, bounds are fixed
		if (this._scrollMask instanceof Sprite) {
			return;
		}

		// Bounds
		this.dockingBounds.setBounds(width, height);

		// Mask
		this._scrollMask.clear();
		this._scrollMask.beginFill(0xffffff, 1);
		this._scrollMask.drawRect(0, 0, width, height);
		this._scrollMask.endFill();

		// Configure
		this._width = width;
		this._height = height;

		// Refresh
		this._refreshContentBounds();
	}

	/**
	 * Gets the mask used to show/hide content in the scroll container. This should be a graphics in order to
	 * work in both WebGL and Canvas modes.
	 */
	public get scrollMask(): Sprite | Graphics {
		return this._scrollMask;
	}

	/**
	 * Sets the mask used to show/hide content in the scroll container. This should be a graphics in order to
	 * work in both WebGL and Canvas modes.
	 */
	public set scrollMask(mask: Sprite | Graphics) {
		if (this._scrollMask === mask) {
			return;
		}
		if (this._scrollMask) {
			this.removeChild(this._scrollMask);
		}

		this._scrollMask = mask;
		this.content.mask = mask;
		this.content.addChildAt(mask, 0);

		if (mask instanceof Graphics) {
			this.setDimensions(this._width, this._height);
		} else {
			this._width = mask.width;
			this._height = mask.height;
			this.dockingBounds.setBounds(this._width, this._height);
		}
	}

	/**
	 * Gets the current width of the container in pixels.
	 */
	// remove this comment when allow-override-transient-properties is included in typescript
	// @ts-ignore
	public get width(): number {
		return this._width;
	}

	/**
	 * Sets the new width of the container in pixels.
	 */
	// remove this comment when allow-override-transient-properties is included in typescript
	// @ts-ignore
	public set width(width: number) {
		this.setDimensions(width, this._height);
	}

	/**
	 * Gets the current height of the container in pixels.
	 */
	// remove this comment when allow-override-transient-properties is included in typescript
	// @ts-ignore
	public get height(): number {
		return this._height;
	}

	/**
	 * Sets the new height of the container in pixels.
	 */
	// remove this comment when allow-override-transient-properties is included in typescript
	// @ts-ignore
	public set height(height: number) {
		this.setDimensions(this._width, height);
	}

	/**
	 * Gets wether or not the height of the track bar should be computed automatically.
	 */
	public get automaticTrackHeight(): boolean {
		return this._automaticTrackHeight;
	}

	/**
	 * Gets wether or not the height of the track bar should be computed automatically.
	 */
	public set automaticTrackHeight(enable: boolean) {
		if (enable === this._automaticTrackHeight) {
			return;
		}

		this._automaticTrackHeight = enable;
		this._refreshTracksHeights();
	}

	/**
	 * Gets wether or not the scroll bars should be automatically visible/unvisible according to the current content's bounds.
	 */
	public get automaticScrollBarVisibility(): boolean {
		return this._automaticScrollBarVisibility;
	}

	/**
	 * Sets wether or not the scroll bars should be automatically visible/unvisible according to the current content's bounds.
	 */
	public set automaticScrollBarVisibility(enable: boolean) {
		if (this._automaticScrollBarVisibility === enable) {
			return;
		}
		this._automaticScrollBarVisibility = enable;

		this._refreshContentBounds();
		this._updateScrollBarsVisibility();
	}

	/**
	 * Scrolls to the given position in pixels or %.
	 * @param x the new position to scroll to. Number expression represents pixels. String expresion represents a ratio in %.
	 * @param duration the duration of the scroll animation. 0 means no animation.
	 * @param ease the easing function used while animating the scroll.
	 */
	public scrollTo(
		x: number | string,
		y: number | string,
		duration: number = 1,
		ease: Cubic = Cubic.easeInOut
	): void {
		const hsbs = this.scrollBars.filter((sb) => sb.alignement === EScrollBarAlign2.Horizontal);
		for (const hsb of hsbs) {
			hsb.scrollTo(x, duration, ease);
		}

		const vsbs = this.scrollBars.filter((sb) => sb.alignement === EScrollBarAlign2.Vertical);
		for (const vsb of vsbs) {
			vsb.scrollTo(y, duration, ease);
		}
	}

	/**
	 * Adds a new scroll bar to the scroll container (handle + track) with the given type (alignment).
	 * @param scrollBar the scroll bar object containing the handle and track objects and the alignment type.
	 */
	public addScrollBar(scrollBar: ScrollBar2, configure: boolean = true): ScrollBar2 {
		this.addChild(scrollBar);
		this.scrollBars.push(scrollBar);

		if (configure) {
			scrollBar.height =
				scrollBar.alignement === EScrollBarAlign2.Vertical ? this._height : this._width;
			scrollBar.name =
				scrollBar.alignement === EScrollBarAlign2.Vertical
					? "Vertical Scroll Bar"
					: "Horizontal Scroll Bar";
		}

		scrollBar.removeAllListeners();
		scrollBar.on("scroll", (ratio) => {
			this._refreshContentBounds();

			switch (scrollBar.alignement) {
				case EScrollBarAlign2.Vertical:
					this.content.y = -this._scrollBounds.height * ratio;
					break;
				case EScrollBarAlign2.Horizontal:
					this.content.x = -this._scrollBounds.width * ratio;
					break;
			}

			this._checkContentLimits();
			this._updateContentMask();
			this._refreshTracksHeights();
		});

		// Refresh
		this._updateScrollBarsVisibility();
		this._checkContentLimits();
		this._updateContentMask();
		this._refreshTracksHeights();

		return scrollBar;
	}

	/**
	 * Removes the given scroll bar from the scroll container.
	 * @param scrollBar the scroll bar to remove.
	 */
	public removeScrollBar(scrollBar: ScrollBar2): boolean {
		const index = this.scrollBars.indexOf(scrollBar);
		if (index === -1) {
			return false;
		}

		this.removeChild(scrollBar);
		this.scrollBars.splice(index, 1);

		return true;
	}

	/**
	 * Adds the given container to the content.
	 * @param container defines the reference to the container to add in the content.
	 */
	public addContent(container: Container): void {
		this.content.addChild(container);
		this._updateScrollBarsVisibility();
		this._checkContentLimits();
		this._updateContentMask();
		this._refreshTracksHeights();
	}

	/**
	 * Removes the given container from the content.
	 * @param container defines the reference to the container to remove from the content.
	 */
	public removeContent(container: Container): void {
		this.content.removeChild(container);
		this._updateScrollBarsVisibility();
		this._checkContentLimits();
		this._updateContentMask();
		this._refreshTracksHeights();
	}

	/**
	 * Updates the scroll bars visibilities according to the current configuration and the content's bounds.
	 */
	private _updateScrollBarsVisibility(): void {
		this.scrollBars.forEach((sb) => {
			if (!this._automaticScrollBarVisibility) {
				sb.visible = true;
				return;
			}

			switch (sb.alignement) {
				case EScrollBarAlign2.Vertical:
					sb.visible = this._scrollBounds.height > 0;
					break;
				case EScrollBarAlign2.Horizontal:
					sb.visible = this._scrollBounds.width > 0;
					break;
			}
		});
	}

	/**
	 * Refreshes the current content's bounds.
	 */
	private _refreshContentBounds(): void {
		const width = this.content.dockingBounds
			? this.content.dockingBounds.width
			: this.content.width;
		const height = this.content.dockingBounds
			? this.content.dockingBounds.height
			: this.content.height;

		this._scrollBounds.x = this.content.x;
		this._scrollBounds.y = this.content.y;
		this._scrollBounds.width = width - this._width;
		this._scrollBounds.height = height - this._height;

		if (
			this._scrollBounds.width !== this._lastScrollBounds.width ||
			this._scrollBounds.height !== this._lastScrollBounds.height
		) {
			this._updateScrollBarsVisibility();
			this._refreshTracksHeights();
		}

		this._lastScrollBounds.copy(this._scrollBounds);

		// Content hit area
		const ha = this.content.hitArea as Rectangle;
		ha.x = -this.content.x;
		ha.y = -this.content.y;
		ha.width = this.dockingBounds.width;
		ha.height = this.dockingBounds.height;
	}

	/**
	 * Refreshes the tracks heights of the scroll bars when "_automaticTrackHeight" is set to true.
	 */
	private _refreshTracksHeights(): void {
		if (this._automaticTrackHeight) {
			this.scrollBars.forEach((sb) => {
				const ratio =
					sb.alignement === EScrollBarAlign2.Vertical
						? this.height / (this._scrollBounds.height + this._height)
						: this._width / (this._scrollBounds.width + this._width);

				sb.track.height = sb._fixedTrackHeight = Math.max(
					sb.handle.height * Math.min(ratio, 1),
					sb.track.texture.height
				);
			});
		} else {
			this.scrollBars.forEach(
				(sb) => (sb.track.height = sb._fixedTrackHeight = sb.track.texture.height)
			);
		}
	}

	/**
	 * Binds the events of the scroll container (typically wheel events).
	 */
	private _bindEvents(): void {
		this.interactive = true;
		this.buttonMode = true;

		this.content.interactive = true;

		this.content.on("pointerover", () => {
			document.addEventListener(
				"wheel",
				(this._mouseWheelListener = (ev) => {
					if (ev.deltaMode) {
						// Firefox
						this._scrollDelta.set(
							ev.deltaX * ScrollContainer2._lineHeight,
							ev.deltaY * ScrollContainer2._lineHeight * this.scrollSpeed
						);
					} else {
						// Good ones
						this._scrollDelta.set(
							ev.deltaX * ScrollContainer2._pageHeight,
							ev.deltaY * ScrollContainer2._pageHeight * this.scrollSpeed
						);
					}

					this.emit("wheel", this._scrollDelta);
				})
			);
		});

		this.content.on("pointerout", () => {
			document.removeEventListener("wheel", this._mouseWheelListener);
		});

		this.on("wheel", (delta: Vector2) => {
			const verticalScrollBars = this.scrollBars.filter(
				(sb) => sb.alignement === EScrollBarAlign2.Vertical
			);
			const horizontalScrollBars = this.scrollBars.filter(
				(sb) => sb.alignement === EScrollBarAlign2.Horizontal
			);

			// Change content's position
			this.content.position.set(
				(this.content.position.x - delta.x) * (horizontalScrollBars.length ? 1.0 : 0.0),
				(this.content.position.y - delta.y) * (verticalScrollBars.length ? 1.0 : 0.0)
			);

			if (this.inertiaEnabled) {
				this._inertialOffsets.addSelf(delta);
			}

			this._checkContentLimits();
			this._updateContentMask();
		});

		this._dragEvent.targetContainer = this.content;
		this._dragEvent.on("drag", (offsets: Point) => {
			this._lastFramePosition.set(this.content.x, this.content.y);
			this.content.position.copy(offsets);

			if (this.inertiaEnabled) {
				this._inertialOffsets.set(
					this._lastFramePosition.x - this.content.x,
					this._lastFramePosition.y - this.content.y
				);
			}

			this._checkContentLimits();
			this._updateContentMask();
		});
		this._dragEvent.on("dragEnd", (offsets: Point) => {
			this._scrollDelta.set(offsets.x, offsets.y);
		});
	}

	/**
	 * Checks the contents limits.
	 */
	private _checkContentLimits(): void {
		if (this._scrollBounds.width <= 0) {
			this.content.x = 0;
		} else if (this.content.position.x > 0) {
			this.content.position.x = 0;
		} else {
			if (this.content.position.x < -this._scrollBounds.width) {
				this.content.position.x = -this._scrollBounds.width;
			}
		}

		if (this._scrollBounds.height <= 0) {
			this.content.y = 0;
		} else if (this.content.position.y > 0) {
			this.content.position.y = 0;
		} else if (this.content.position.y < -this._scrollBounds.height) {
			this.content.position.y = -this._scrollBounds.height;
		}

		const verticalScrollBars = this.scrollBars.filter(
			(sb) => sb.alignement === EScrollBarAlign2.Vertical
		);
		const horizontalScrollBars = this.scrollBars.filter(
			(sb) => sb.alignement === EScrollBarAlign2.Horizontal
		);

		for (const horizontalScrollBar of horizontalScrollBars) {
			horizontalScrollBar.setScrollPosition(Math.abs(this.content.x / this._scrollBounds.width));
		}
		for (const verticalScrollBar of verticalScrollBars) {
			verticalScrollBar.setScrollPosition(Math.abs(this.content.y / this._scrollBounds.height));
		}

		this._refreshContentBounds();
	}

	/**
	 * Updates the inertia.
	 */
	private _updateInertia(): void {
		if (!this.inertiaEnabled) {
			return;
		}

		const offsetx = this.content.position.x - this._inertialOffsets.x;
		const offsety = this.content.position.y - this._inertialOffsets.y;

		const diffy = -offsety - this._scrollBounds.height;
		if (diffy > 0 || diffy < -this._scrollBounds.height) {
			const scrollBars = this.scrollBars.filter(
				(sb) => sb.alignement === EScrollBarAlign2.Vertical
			);
			for (const scrollBar of scrollBars) {
				this._checkThumbHeight(scrollBar);
			}
		}

		const diffx = -offsetx - this._scrollBounds.width;
		if (diffx > 0 || diffx < -this._scrollBounds.width) {
			const scrollBars = this.scrollBars.filter(
				(sb) => sb.alignement === EScrollBarAlign2.Horizontal
			);
			for (const scrollBar of scrollBars) {
				this._checkThumbHeight(scrollBar);
			}
		}

		if (this._dragEvent.dragDown) {
			return;
		}

		this.content.position.set(offsetx, offsety);
		this._checkContentLimits();

		this._inertialOffsets.multiplySelf(new Vector2(this.inertia, this.inertia));

		if (this._inertialOffsets.x >> 0 === 0) {
			this._inertialOffsets.x = 0;
		}
		if (this._inertialOffsets.y >> 0 === 0) {
			this._inertialOffsets.y = 0;
		}
	}

	/**
	 * Updates the content's mask.
	 */
	private _updateContentMask(): void {
		// Content mask
		this._scrollMask.x = -this.content.x;
		this._scrollMask.y = -this.content.y;
	}

	/**
	 * Updates the thumb height iOS style.
	 */
	private _checkThumbHeight(scrollBar: ScrollBar2): void {
		if (!scrollBar) {
			return;
		}

		const offset =
			scrollBar.alignement === EScrollBarAlign2.Vertical
				? this._inertialOffsets.y
				: this._inertialOffsets.x;
		scrollBar.track.height = Math.max(
			scrollBar.track.texture.height * (this._automaticTrackHeight ? 1.0 : 0.5),
			scrollBar._fixedTrackHeight - Math.abs(offset)
		);
	}

	/**
	 * Gets wether or not the scroll container is in debug mode in order to deactivate the mask and see all its content.
	 */
	public get debugMode(): boolean {
		return this.content.mask === null && this._scrollMask !== null;
	}

	/**
	 * Sets wether or not the scroll container is in debug mode in order to deactivate the mask and see all its content.
	 */
	public set debugMode(enable: boolean) {
		if (enable === this.debugMode || !this._scrollMask) {
			return;
		}
		this.content.mask = enable ? null : this._scrollMask;
	}

	/**
	 * Export the scroll container.
	 */
	public export(): any {
		this._scrollMask.doNotExport = true;

		this.content.doNotExport = false;
		const content = ContainerExporter.export(this.content);
		this.content.doNotExport = true;

		this.scrollBars.forEach((sb) => (sb.doNotExport = false));
		const scrollBars = this.scrollBars.map((sb) => sb.export());
		this.scrollBars.forEach((sb) => (sb.doNotExport = true));

		return Tools.assign(ContainerExporter.exportContainer(this), {
			type: "ui.ScrollContainer2",
			width: this._width,
			height: this._height,
			content,
			scrollBars,
			automaticTrackHeight: this._automaticTrackHeight,
			automaticScrollBarVisibility: this._automaticScrollBarVisibility,
			scrollSpeed: this.scrollSpeed,
			inertiaEnabled: this.inertiaEnabled,
			inertia: this.inertia,
		});
	}

	/**
	 * Parses the scroll container.
	 * @param config the serialized data.
	 * @param baseUrl the base url of the loader.
	 * @param container the existing scroll container 2.
	 */
	public static parse(
		config: any,
		baseUrl: string,
		container?: ScrollContainer2
	): ScrollContainer2 {
		if (!container) {
			container = new ScrollContainer2();
		}

		container.setDimensions(config.width, config.height);

		ContainerImporter.import(config.content, baseUrl, container.content);
		container._refreshContentBounds();

		config.scrollBars.forEach((sb) => container.addScrollBar(ScrollBar2.parse(sb, baseUrl), false));
		container.automaticTrackHeight = config.automaticTrackHeight;
		container.automaticScrollBarVisibility = config.automaticScrollBarVisibility;
		container.scrollSpeed = config.scrollSpeed;
		container.inertiaEnabled = config.inertiaEnabled;
		container.inertia = config.inertia;

		container.scrollTo(0, 0, 0);

		return container;
	}
}
