import { loaders } from "pixi.js";
import { Logger } from "@gdk/core";
import type { ILoader } from "@gdk/core";

export class PixiLoader implements ILoader {
	// Public members
	public extensions: string[] = ["png", "jpg", "json", "gdk", "svg"];
	public instance: loaders.Loader;
	public overrideOnMultipleLoads: boolean = false;

	/**
	 * Constructor
	 */
	public constructor(baseUrl?: string) {
		this.instance = new loaders.Loader(baseUrl);
	}

	public get baseUrl(): string {
		return this.instance.baseUrl;
	}

	public set baseUrl(url: string) {
		this.instance.baseUrl = url;
	}

	/**
	 * Add the given file to the files to load
	 * @param filename the file to add
	 */
	public add(filename: string, key?: string, options?: any): void {
		try {
			this.instance.add(key || filename, filename, options);
		} catch (e) {
			if (!this.overrideOnMultipleLoads) {
				throw e;
			} else {
				Logger.getLogger().warn(`Asset "${filename}" with key "${key}" is already loaded.`);
			}
		}
	}

	/**
	 * Removes the given entry identified by the given key
	 * @param key the key used to identify the entry to remove
	 */
	public remove(key: string): void {
		if (this.instance.resources[key]) {
			delete this.instance.resources[key];
		}
	}

	/**
	 * Load all files previously added
	 * @param callback called when all files loaded
	 */
	public load(callback: () => void, progress?: () => void, error?: (file: string) => void): void {
		this.instance.onProgress.add((loader: loaders.Loader, resource) => {
			const fromJson = resource.name.indexOf(".json_image") === resource.name.lastIndexOf(".");
			if (progress && !fromJson) {
				progress();
			}
		});

		this.instance.onError.add((err) => {
			if (error) {
				error(err.message);
			}

			throw err;
		});

		this.instance.load(() => {
			callback();
		});
	}

	/**
	 * Clears the resources previously loaded
	 */
	public clear(): void {
		this.instance.reset();
	}

	/**
	 * Returns the resource identified by the given key
	 * @param key the key of the value to get
	 */
	public get?<T>(key: string, raw?: boolean): T {
		if (raw) {
			return (this.instance.resources[key] as any) || null;
		}

		const value = this.instance.resources[key];
		if (value) {
			return value.data;
		}

		return null;
	}
}
