import {
	RenderTexture,
	SCALE_MODES,
	Container,
	Sprite,
	Texture,
	settings,
	Graphics,
	Polygon,
} from "pixi.js";
import { EEventType, Logger, Tools, tracker } from "@gdk/core";
import type { Howl } from "howler";
import type { IEvent, ITrackableObject } from "@gdk/core";
import type { WebGLRenderer, CanvasRenderer, CanvasRenderTarget, Point } from "pixi.js";

import { CustomPointer } from "../baseui/ui/customPointer";
import { ScratchPresets } from "../scratch/scratchPresets";
import { Scratch2MaskFilter } from "./maskFilter";
import {
	ParticlesScratchRenderer,
	DefaultScratchRenderer,
	OneClickScratchRenderer,
} from "./renderers";
import { Scratch2Zone } from "./zone";
import { Scratch2Presets } from "./scratch2Presets";
import { ContainerExporter } from "../../loading/export/containerExporter";
import { ContainerImporter } from "../../loading/import/containerImporter";
import type { IEmitterConfig } from "../../proton/parser2";
import type { IScratch2Options, AbstractScratch2Renderer } from "./types";
import type { IPointerEvent } from "../baseui/ui/customPointer";
import type { ScratchPath } from "../scratch/scratchPath";

export class Scratch2 extends Container implements ITrackableObject {
	/**
	 * Defines the optional name of the object to be tracked.
	 * If unset, a default value will be taken by the tracker. For example,
	 * the name of the object (button, scratch, etc.).
	 */
	public trackingName: string = "";

	/**
	 * The scratchable content container being rendered over the container to be hidden by the scratch.
	 */
	public content: Container;
	/**
	 * The current scratch options used to configure/customize the scratch.
	 */
	public options: IScratch2Options;
	/**
	 * The renderer being used to render the scratch mask.
	 */
	public renderer: AbstractScratch2Renderer = null;
	/**
	 * Sets wether or not the optimizations should be disabled. Configuration will not be exported and must be set carefuly.
	 */
	public disableOptimizations: boolean = false;
	/**
	 * Defines the increment value while traversing the scratch's pixels to determine if it should be revealed or not.
	 */
	public pixelIncrementValue: number = 4;
	/**
	 * Defines the zones to reveal. This is used mainly to save performances (draw calls). Only one scratch that can define multiple zones.
	 */
	public zones: Scratch2Zone[] = [];
	/**
	 * Defines if the scratch component should be able to scratch outside of the zones.
	 */
	public scratchOnlyZones: boolean = true;
	/**
	 * Defines if the global auto-scratch should be triggered once all zones have been revealed complete.
	 * Means that once all zones are revealed complete, the global auto-scratch will not be triggered if property is set to "true".
	 * The basic steps for scratch that contains zones is:
	 * 	- scratch each zone
	 * 	- auto reveal zone once reveal ratio is reached for the zone
	 * 	- once all zones have been auto-revealed, auto-reveal the overall scratch.
	 * Setting this property to "true", removes the last step.
	 */
	public enableAutoScratchOnlyOnZones: boolean = false;
	/**
	 * Sets wether or not the scratch should force reveal the revealed zone on the auto-mode has finished.
	 */
	public forceRevealZoneOnAutoDone: boolean = false;
	/**
	 * Optional callback called once the scratch component is ready to be rendered.
	 */
	public onReady: () => void = null;
	/**
	 * Optional callback called on the scratch is being revealed.
	 */
	public onReveal: () => void = null;
	/**
	 * Optional callback called on the given scratch zone is being revealed.
	 * @param zone in case of multiple zones, the given zone reference represents the zone being revealed.
	 */
	public onRevealZone: (zone: Scratch2Zone) => void = null;
	/**
	 * Optional callback called once the scratch has been completely revealed.
	 */
	public onRevealComplete: () => void = null;
	/**
	 * Optional callback called once the given scratch zone has been completely revealed.
	 * @param zone in case of multiple zones, the given zone reference represents the zone that has been revealed.
	 */
	public onRevealCompleteZone: (zone: Scratch2Zone) => void = null;
	/**
	 * Optional callback called on the user is scratching the component
	 * @param zone in case of multiple zones, the given zone reference represents the zone being scratched.
	 */
	public onScratch: (zone?: Scratch2Zone) => void = null;
	/**
	 * Defines the optional callback called on the ratio of scratched content changed on the scratch.
	 * @param oldRatio defines the old ratio before update.
	 * @param newRatio: defines the new ratio of the scratched content.
	 */
	public onRatioChange: (oldRatio: number, newRatio: number) => void = null;
	/**
	 * The render texture used to store the scratch's mask data (pixels).
	 */
	public scratchRenderTexture: RenderTexture;
	/**
	 * Defines the auto path that renderers will use to scratch the content when in auto mode.
	 * Typically used by the @ParticlesScratchRenderer and @DefaultScratchRenderer.
	 */
	public autoPath: ScratchPath = ScratchPresets.DEFAULT_PATH;
	/**
	 * Defines the auto path that renderers will use to scratch the content of a zone when in auto mode.
	 * Typically used by the @ParticlesScratchRenderer and @DefaultScratchRenderer.
	 */
	public zoneAutoPath: ScratchPath = null;

	/**
	 * The render texture used to store the content's data (pixels)
	 * Only used while using the canvas renderer to perform composite operations.
	 */
	protected contentRenderTexture: RenderTexture;
	/**
	 * Custom pointer reference used to calculate pointer position, direction etc.
	 */
	protected pointer: CustomPointer = new CustomPointer({
		element: this,
		allowGlobalInteractionEvents: false,
		onMove: (target, datas) => this.onMove(target, datas),
		onDown: (target, datas) => this.onMove(target, datas),
	});

	private _firstRender: boolean = true;
	private _isReady: boolean = false;
	private _isWebGL: boolean = false;
	private _isDirty: boolean = true;
	private _shouldBeCleared: boolean = true;
	private _shouldCheckComplete: boolean = true;
	private _pixiRenderer: CanvasRenderer | WebGLRenderer;

	private _maskSprite: Sprite = null;
	private _maskFilter: Scratch2MaskFilter = null;

	private _maskCanvas: HTMLCanvasElement = null;
	private _maskContext: CanvasRenderingContext2D = null;

	private _progress: number = 0;
	private _oldProgress: number = 0;
	private _isComplete: boolean = false;
	private _lastCompleteTime: number = 0;

	private _clampingEnabled: boolean = true;
	private _lastPerformanceTime: number = 0;
	private _performanceAverageTime: number = 0;
	private _performanceElapsedTime: number = 0;
	private _performanceFramesCount: number = 0;

	private _isAuto: boolean = false;

	private _defaultZone: Scratch2Zone = new Scratch2Zone(0, 0, 1, 1);
	private _debugZonesGraphics: Graphics[];
	private _debugZonesColors: number[];
	private _zonesToForceReveal: Scratch2Zone[] = [];

	private _scratchPreset: IEmitterConfig = Scratch2Presets.FDJ_SCRATCH_BIG;
	private _autoPreset: IEmitterConfig = Scratch2Presets.FDJ_SCRATCH_BIG;
	private _autoZonePreset: IEmitterConfig = Scratch2Presets.FDJ_SCRATCH_BIG;

	private _cursor: string = null;

	private static _autoZones: Scratch2Zone[] = [];

	/**
	 * Sound reference used to be played on the user is scratching a Scratch2 component.
	 */
	public static scratchSound: Howl = null;
	/**
	 * Sound reference used to be played on a Scratch2 component is being revealed.
	 */
	public static revealSound: Howl = null;

	private static _scratchSoundTimeoutId: number = null;

	/**
	 * Constructor.
	 * @param options the options used to configure/customize the newly scratch being created.
	 */
	public constructor(options: IScratch2Options) {
		super();

		// Validate options
		options.resolution = options.resolution || 0.5;
		options.clampValue = options.clampValue || 0.0;
		options.revealRatio = options.revealRatio || 0.7;
		options.disableCaching = options.disableCaching || false;
		options.padding = options.padding || 0;
		options.inverted = options.inverted || false;
		options.fallback = options.fallback || {};
		if (!options.content) {
			throw new Error("A content container must be provided to render the scratch");
		}

		// Misc.
		this.options = options;
		this.interactive = true;
		this.buttonMode = true;

		// Pointer
		this.pointer.hideNativePointer = false;
		this.pointer.addTarget(this);
		this.addChild(this.pointer);

		// Add content
		this.content = options.content;
		this.addChild(this.content);

		// Renderer
		this.setRenderer(this.options.renderer);

		// Create render texture
		this.scratchRenderTexture = RenderTexture.create(
			options.content.width + this.options.padding,
			options.content.height + this.options.padding,
			SCALE_MODES.LINEAR,
			this.options.resolution
		);

		this._defaultZone.rectangle.width = this.scratchRenderTexture.width;
		this._defaultZone.rectangle.height = this.scratchRenderTexture.height;
	}

	/**
	 * Gets the scratch preset that particles renderer (optimal scratch renderer) uses when scratching.
	 * @see Scratch2Presets to change scratch preset.
	 */
	public get scratchPreset(): IEmitterConfig {
		return this._scratchPreset;
	}

	/**
	 * Sets the scratch preset that particles renderer (optimal scratch renderer) uses when scratching.
	 * @see Scratch2Presets to change scratch preset.
	 */
	public set scratchPreset(preset: IEmitterConfig) {
		this._scratchPreset = preset;
		if (this.renderer instanceof ParticlesScratchRenderer) {
			this.renderer.scratchPreset = preset;
			this.renderer.reset();
		}
	}

	/**
	 * Gets the auto preset that particles renderer (optimal scratch renderer) uses when auto-scratching.
	 * @see Scratch2Presets to change scratch preset.
	 */
	public get autoPreset(): IEmitterConfig {
		return this._autoPreset;
	}

	/**
	 * Sets the auto preset that particles renderer (optimal scratch renderer) uses when auto-scratching.
	 * @see Scratch2Presets to change preset.
	 */
	public set autoPreset(preset: IEmitterConfig) {
		this._autoPreset = preset;
		if (this.renderer instanceof ParticlesScratchRenderer) {
			this.renderer.autoPreset = preset;
			this.renderer.reset();
		}
	}

	/**
	 * Gets the auto preset that particles renderer (optimal scratch renderer) uses when auto-scratching a zone.
	 * @see Scratch2Presets to change preset.
	 */
	public get autoZonePreset(): IEmitterConfig {
		return this._autoZonePreset;
	}

	/**
	 * Gets the auto preset that particles renderer (optimal scratch renderer) uses when auto-scratching a zone.
	 * @see Scratch2Presets to change preset.
	 */
	public set autoZonePreset(preset: IEmitterConfig) {
		this._autoZonePreset = preset;
		if (this.renderer instanceof ParticlesScratchRenderer) {
			this.renderer.autoZonePreset = preset;
			this.renderer.reset();
		}
	}

	/**
	 * Sets the new cursor being used by the scratch.
	 */
	public setCursor(cursor: string): void {
		this._cursor = cursor;
	}

	/**
	 * Renders the scratch using the WebGL renderer.
	 * @param renderer the webgl renderer reference.
	 */
	public renderWebGL(renderer: WebGLRenderer): void {
		// Is WebGL and renderer
		this._isWebGL = true;
		this._pixiRenderer = renderer;

		// First render
		if (this._firstRender) {
			// Mask
			if (this._maskSprite) {
				this._maskSprite.destroy();
			}
			this._maskSprite = new Sprite(this.scratchRenderTexture);
			this._maskSprite.renderable = false;
			this.content.addChild(this._maskSprite);

			// Create filter
			this._maskFilter = new Scratch2MaskFilter(
				renderer.resolution,
				this.options.padding,
				this._maskSprite,
				this.options.clampValue,
				this.options.inverted
			);
			if (this.options.inverted) {
				this.content.filters = [this._maskFilter];
			}
			this._firstRender = false;

			// Re-call render
			this.renderWebGL(renderer);
		} else {
			// Get the current time. If will be used to compute optimizer by calculating the needed time to compute the renderWebGL function
			const lastRenderWebGLTime = Date.now();

			// Set filter area to fit properly the content except when being in cached mode.
			if (!this.content.cacheAsBitmap) {
				this.content.filterArea = this._maskSprite.getBounds(true);
				this.content.filterArea.width = this.content.filterArea.width >> 0;
				this.content.filterArea.height = this.content.filterArea.height >> 0;
			}

			// Super render
			super.renderWebGL(renderer);

			// Get the final rendering duration for the component
			this._lastPerformanceTime = Date.now() - lastRenderWebGLTime;

			// Render scratch renderer using canvas renderer
			renderer.once("afterRender" as any, () => {
				// Get the current time. If will be used to compute optimizer by calculating the needed time to compute the render target
				const lastRenderTargetTime = Date.now();

				this.renderer.onRender();

				// Check zones that must be instant revealed.
				if (this._zonesToForceReveal.length > 0) {
					// Add all graphics
					this._zonesToForceReveal.forEach(
						(z) =>
							z._waitingForceRevealGraphics && this.renderer.addChild(z._waitingForceRevealGraphics)
					);
					// Apply filter only on first scratch. Not necessary before.
					if (this._isWebGL && !this.content.filters) {
						this.content.filters = [this._maskFilter];
					}
				}

				// Check is dirty. If not dirty, it is not necessary to render the scratch, just cache as bitmap.
				if (!this._isDirty) {
					// If not cached as bitmap, cache as bitmap to avoid rendering the content's filter.
					if (!this.options.disableCaching && !this.content.cacheAsBitmap) {
						this.content.filterArea = null;
						this.content.cacheAsBitmap = true;
					}

					// Optimizer
					this._lastPerformanceTime += Date.now() - lastRenderTargetTime;
					this.computeOptimizer();

					this.renderer.onPostRender();
					return;
				}

				// If cached as bitmap, just uncache.
				if (!this.options.disableCaching && this.content.cacheAsBitmap) {
					this.content.cacheAsBitmap = false;
				}

				renderer.render(
					this.renderer,
					this.scratchRenderTexture,
					this._shouldBeCleared,
					null,
					false
				);
				this._shouldBeCleared = false;

				this.renderer.onPostRender();

				// Notify ready
				if (!this._isReady) {
					if (this.onReady) {
						this.onReady();
					}
					this._isReady = true;
				}

				// Check complete
				this.isComplete(renderer);

				// Not dirty anymore?
				this._isDirty = this._zonesToForceReveal.length > 0;

				// Clear graphics used to instant reveal zones
				this._clearZonesToForceReveal();

				// Optimizer
				this._lastPerformanceTime += Date.now() - lastRenderTargetTime;
				this.computeOptimizer();
			});
		}
	}

	/**
	 * Renders the scratch using the canvas renderer.
	 * @param renderer the canvas renderer reference.
	 */
	public renderCanvas(renderer: CanvasRenderer): void {
		// Is WebGL and renderer
		this._isWebGL = false;
		this._pixiRenderer = renderer;

		// Render super
		if (!this._firstRender) {
			super.renderCanvas(renderer);
		}

		// Render scratch renderer using canvas renderer
		renderer.once("afterRender" as any, () => {
			// Get the current time. If will be used to compute optimizer by calculating the needed time to compute the renderWebGL function
			const lastRenderCanvasTime = Date.now();

			// Inverted? Hide the first frame to avoid glitches.
			if (this.options.inverted) {
				this.content.visible = !this._firstRender;
			}

			// First render
			if (this._firstRender) {
				// Create content render target
				this.contentRenderTexture = RenderTexture.create(
					this.scratchRenderTexture.width + this.options.padding,
					this.scratchRenderTexture.height + this.options.padding,
					SCALE_MODES.LINEAR,
					this.options.resolution
				);

				// Mask canvas
				this._maskCanvas = document.createElement("canvas");
				this._maskCanvas.width = this.scratchRenderTexture.width * this.options.resolution;
				this._maskCanvas.height = this.scratchRenderTexture.height * this.options.resolution;

				this._maskContext = this._maskCanvas.getContext("2d");

				// Mask
				this._maskSprite = new Sprite(Texture.fromCanvas(this._maskCanvas));
				this._maskSprite.scaleXY /= this.options.resolution;
				this.addChild(this._maskSprite);

				this._firstRender = false;
			}

			// Check zones that must be instant revealed.
			if (this._zonesToForceReveal.length > 0) {
				// Add all graphics
				this._zonesToForceReveal.forEach(
					(z) =>
						z._waitingForceRevealGraphics && this.renderer.addChild(z._waitingForceRevealGraphics)
				);
			}

			// Render scratch
			const canvasScratchRenderTarget = (this.scratchRenderTexture.baseTexture as any)
				._canvasRenderTarget as CanvasRenderTarget;
			if (this._shouldBeCleared && canvasScratchRenderTarget) {
				canvasScratchRenderTarget.clear();
				this._shouldBeCleared = false;
			}

			this.renderer.onRender();
			if (this._isDirty) {
				renderer.render(
					this.renderer,
					this.scratchRenderTexture,
					this._shouldBeCleared,
					null,
					false
				);
			}

			// Render content
			this.content.renderable = true;
			const canvasContentRenderTarget = (this.contentRenderTexture.baseTexture as any)
				._canvasRenderTarget as CanvasRenderTarget;
			if (canvasContentRenderTarget) {
				canvasContentRenderTarget.clear();
			}

			renderer.render(this.content, this.contentRenderTexture, true, null, false);

			// Composite operation
			if (canvasContentRenderTarget && canvasScratchRenderTarget) {
				this.content.renderable = false;

				// Base
				const imgData = canvasScratchRenderTarget.context.getImageData(
					0,
					0,
					canvasScratchRenderTarget.canvas.width,
					canvasScratchRenderTarget.canvas.height
				);

				// Clamp?
				if (this.options.clampValue && this._clampingEnabled) {
					const pixels = imgData.data;

					for (let i = 0, len = pixels.length; i < len; i += 4) {
						pixels[i] =
							pixels[i + 1] =
							pixels[i + 2] =
								pixels[i + 3] <= this.options.clampValue ? 0.0 : 1.0;
					}
				}

				this._maskContext.putImageData(
					imgData,
					0,
					0,
					0,
					0,
					this._maskCanvas.width,
					this._maskCanvas.height
				);

				// Source out scratch
				this._maskContext.globalCompositeOperation = this.options.inverted
					? "source-in"
					: "source-out";
				this._maskContext.drawImage(this.contentRenderTexture.baseTexture.source, 0, 0);

				// Update texture
				this._maskSprite.texture.update();

				// Notify ready
				if (!this._isReady) {
					if (this.onReady) {
						this.onReady();
					}
					this._isReady = true;
				}

				// Check complete
				this.isComplete(renderer);

				// Not dirty anymore?
				this._isDirty = this._zonesToForceReveal.length > 0;

				// Clear graphics used to instant reveal zones
				this._clearZonesToForceReveal();

				// Optimizer
				this._lastPerformanceTime = Date.now() - lastRenderCanvasTime;
				this.computeOptimizer();
			}
		});
	}

	/**
	 * Stops the scratch component. Typically used to pause in auto-mode etc.
	 */
	public stop(): void {
		this.renderer.onStop();
		if (Scratch2.scratchSound) {
			Scratch2.scratchSound.pause();
		}

		Scratch2._autoZones = [];

		this._isAuto = false;

		// Force update progress and don't notify user event if scratch is over the reveal ratio.
		this.isComplete(this._pixiRenderer, true, true);

		// Dirty states
		this._isDirty = false;
		this.zones.forEach((z) => {
			if (!z.revealedComplete) {
				z.revealed = false;
			}
			z._isDirty = false;
		});
	}

	/**
	 * Sets the new renderer of the scratch component.
	 * @param renderer the new renderer to use while rendering scratch.
	 */
	public setRenderer(renderer: AbstractScratch2Renderer): void {
		if (this.renderer) {
			this.renderer.onDestroy();
		}

		this.renderer = renderer;
		this.renderer.onActivate(this);
	}

	/**
	 * Resizes the scratch renderer being used as a mask for the content.
	 * @param width the new width of the scratch (default: content.width).
	 * @param height the new height of the scrach (default: content.height).
	 */
	public resizeMask(
		width: number = this.content.width,
		height: number = this.content.height
	): void {
		if (
			this.scratchRenderTexture.width !== Math.ceil(width) ||
			this.scratchRenderTexture.height !== Math.ceil(height)
		) {
			if (this._isWebGL) {
				// re-create render texture
				this.scratchRenderTexture.destroy(true);
				this.scratchRenderTexture = RenderTexture.create(
					width,
					height,
					SCALE_MODES.LINEAR,
					this.options.resolution
				);

				// Set first render
				this._firstRender = true;

				// Reset renderer
				this.renderer.reset();
			} else {
				this.contentRenderTexture.resize(width, height);
				this._maskCanvas.width = width;
				this._maskCanvas.height = height;
			}
		}

		// Update default zone
		this._defaultZone.rectangle.width = this.scratchRenderTexture.width;
		this._defaultZone.rectangle.height = this.scratchRenderTexture.height;
	}

	/**
	 * Gets wether or not the scratch component has been revealed.
	 */
	public get isRevealed(): boolean {
		return this._isComplete && this._progress === 1;
	}

	/**
	 * Gets wether or not the scratch component is in auto mode.
	 */
	public get isAuto(): boolean {
		return this._isAuto;
	}

	/**
	 * Gets the current progression of the scratch component in the interval [0,1].
	 * 0 = never scratched
	 * 1 = fully scratched (revealed)
	 */
	public get progress(): number {
		if (this.zones.length) {
			return this.zones.reduce((a, b) => a + b.progress, 0) / this.zones.length;
		}
		return this._progress;
	}

	/**
	 * Reveals the result by hiding the scratchable content.
	 */
	public reveal(): void {
		if (!this._isAuto) {
			return;
		}

		const unRevealedZone = this.zones.find((z) => !z.revealedComplete);
		if (!unRevealedZone) {
			this.forceReveal();
		}
	}

	/**
	 * Forces the reveal by hiding the scratchable content.
	 * @param zone the optional zone that must be instant revealed.
	 */
	public forceReveal(zone?: Scratch2Zone): void {
		if (zone) {
			zone._waitingForceRevealGraphics = new Graphics();
			zone._waitingForceRevealGraphics.beginFill(0xffffff, 1);
			zone._waitingForceRevealGraphics.drawRect(zone.x, zone.y, zone.width, zone.height);
			zone._waitingForceRevealGraphics.endFill();
			this._zonesToForceReveal.push(zone);
			this.setDirty(zone);
			zone.progress = 1;

			if (!zone.revealedComplete) {
				if (this.onRevealCompleteZone) {
					this.onRevealCompleteZone(zone);
				}
				if (zone.onRevealComplete) {
					zone.onRevealComplete();
				}
			}

			zone.revealed = zone.revealedComplete = true;

			// Check that all zones have been revealed
			if (!this.zones.find((z) => !z.revealedComplete)) {
				if (this.onRevealComplete) {
					this.onRevealComplete();
				}
			}
		} else {
			// Already forced reveal?
			if (this._progress === 1) {
				return;
			}

			if (this.options.inverted) {
				// Force reveal using a force reveal graphics
				this._defaultZone._waitingForceRevealGraphics = new Graphics();
				this._defaultZone._waitingForceRevealGraphics.beginFill(0xffffff, 1);
				this._defaultZone._waitingForceRevealGraphics.drawRect(
					this._defaultZone.x,
					this._defaultZone.y,
					this._defaultZone.width,
					this._defaultZone.height
				);
				this._defaultZone._waitingForceRevealGraphics.endFill();
				this._zonesToForceReveal.push(this._defaultZone);
			} else {
				this.content.visible = false;
			}

			this._isComplete = true;
			this.setDirty();
			this._progress = 1;

			if (this.onRevealComplete) {
				this.onRevealComplete();
			}

			// Track reveal complete
			tracker.track({
				eventType: EEventType.Scratch,
				event: "scratchComplete",
				name: this.trackingName || this.name,
			} as IEvent);
		}
	}

	/**
	 * Automatically reveals the scratch component by animating the scratch movement.
	 * @param zone the zone to automatically reveal. @see .zones.
	 */
	public auto(zone?: Scratch2Zone): Promise<void> {
		// Apply filter in the scratch hasn't been scratched.
		if (this._isWebGL && !this.content.filters) {
			this.content.filters = [this._maskFilter];
		}

		// Zone
		if (!zone) {
			this._isAuto = true;
			if (this.onReveal) {
				this.onReveal();
			}

			// Track auto-reveal
			tracker.track({
				eventType: EEventType.Scratch,
				event: "scratchReveal",
				name: this.trackingName || this.name,
			} as IEvent);

			// Mark all revealed and generate default zone for the auto path.
			this.zones.forEach((z) => {
				if (!z.revealed && z.onReveal) {
					z.onReveal();
				}
				z.revealed = true;
			});
			zone = this._defaultZone;
		} else {
			if (this.onRevealZone) {
				this.onRevealZone(zone);
			}
			if (zone.onReveal) {
				zone.onReveal();
			}

			zone.revealed = true;
		}

		const isAuto = this._isAuto;

		return new Promise<void>((resolve) => {
			// Add this zone to referenced auto zones
			Scratch2._autoZones.push(zone);

			const path = (zone !== this._defaultZone && this.zoneAutoPath) || this.autoPath;

			this.renderer.onAuto(this, zone, path, () => {
				// Remove from auto zones
				const autoZoneIndex = Scratch2._autoZones.indexOf(zone);
				if (autoZoneIndex !== -1) {
					Scratch2._autoZones.splice(autoZoneIndex, 1);
				}

				// Mark complete
				zone.revealedComplete = true;

				// At least one unrevealed zone?
				const unRevealedZone = this.zones.find((oz) => !oz.revealedComplete);

				if (this.onRevealCompleteZone) {
					this.onRevealCompleteZone(zone);
				}
				if (zone.onRevealComplete) {
					zone.onRevealComplete();
				}

				// Reveal complete
				if (!unRevealedZone && !isAuto) {
					if (this.enableAutoScratchOnlyOnZones) {
						this._forceRevealAndMarkZones();
						resolve();
					} else {
						// Resolve only when all scratch complete
						this.auto().then(resolve);
					}
				} else if (isAuto) {
					this._forceRevealAndMarkZones();
					// Resolve scratch complete
					resolve();
				} else {
					// Resolve zone complete
					if (zone !== this._defaultZone && this.forceRevealZoneOnAutoDone) {
						this.forceReveal(zone);
					}
					resolve();
				}
			});
		});
	}

	/**
	 * Resets the scratch component
	 */
	public reset(): void {
		// Reset utils
		this._shouldBeCleared = true;

		this._progress = 0;
		this._oldProgress = 0;

		this._isComplete = false;
		this._lastCompleteTime = 0;

		this._isReady = false;

		this._isAuto = false;

		// Dirty
		this.setDirty();

		// Pointer
		this.pointer.reset();

		// Reset optimizer
		this._performanceElapsedTime = 0;
		this._performanceAverageTime = 0;
		this._lastPerformanceTime = 0;
		this._performanceFramesCount = 0;

		// Reset rendering
		this.content.visible = true;

		this.renderer.reset();

		// Reset all zones
		this.zones.forEach((z) => z.reset());
		this._defaultZone.reset();

		if (this._isWebGL) {
			// Remove filter
			if (!this.options.inverted) {
				this.content.filterArea = null;
				this.content.filters = null;
			}

			// Dont cache as bitmap anymore
			this.content.cacheAsBitmap = false;

			// Hack, we need to clear the render target textures while using the WebGL renderer.
			const renderTarget = (this.scratchRenderTexture.baseTexture as any)._glRenderTargets[0];
			if (renderTarget) {
				renderTarget.clear([0, 0, 0, 255]);
			}
		}
	}

	/**
	 * Returns if the scratch is complete according to the reveal ratio.
	 * @param pixels the pixels array to test
	 * @param force wether or not the check is forced. Typically used by the .stop function.
	 * @param doNotNotify wether or not the check should notify if the scratch is complete or not. (typically used by the .stop function).
	 */
	public isComplete(
		renderer: CanvasRenderer | WebGLRenderer,
		force?: boolean,
		doNotNotify?: boolean
	): void {
		// Is the renderer dirty?
		if (!force && (!this._shouldCheckComplete || this._isAuto || !this._isReady)) {
			return;
		}
		if (this.isRevealed) {
			return;
		}

		// Check time interval
		const now = Date.now();
		if (!force && now - this._lastCompleteTime < 250) {
			return;
		}
		this._lastCompleteTime = now;

		// Get pixels
		const pixels = renderer.extract.pixels(this.scratchRenderTexture);

		// Update default zone
		this._defaultZone.rectangle.width = this.scratchRenderTexture.width;
		this._defaultZone.rectangle.height = this.scratchRenderTexture.height;

		// Zones
		const zones = this.zones.length === 0 ? [this._defaultZone] : this.zones;

		// Misc.
		const textureWidth = (this.scratchRenderTexture.width * this.options.resolution) >> 0;

		// Compute average of each zone
		zones.forEach((z) => {
			if (!force && (z.revealed || !z.isActive || !z._isDirty)) {
				return;
			}

			let average = 0;

			if (z === this._defaultZone) {
				// This is more optimized as we avoid interpolating the (x,y) coordinates.
				const length = pixels.length;
				const toDraw = length / 4;

				for (let i = 0; i < length; i += 4) {
					if (pixels[i] > 128) {
						average++;
					}
				}
				average /= toDraw;
			} else {
				// Check average for the given zone by interpolating the (x,y) coordinates.
				const zoneWidth = (z.width * this.options.resolution) >> 0;
				const zoneHeight = (z.height * this.options.resolution) >> 0;

				const xs = (z.x * this.options.resolution) >> 0;
				const xe = (xs + zoneWidth) >> 0;

				const ys = (z.y * this.options.resolution) >> 0;
				const ye = (ys + zoneHeight) >> 0;

				const increment = this.pixelIncrementValue * this.options.resolution;

				for (let x = xs; x < xe; x += increment) {
					for (let y = ys; y < ye; y += increment) {
						if (pixels[(y * textureWidth + x) * 4] > 128) {
							average += increment;
						}
					}
				}

				average /= (zoneWidth * zoneHeight) / increment;
			}

			z.progress = average;

			// Is complete or not
			if (!doNotNotify && average > (z.revealRatio || this.options.revealRatio)) {
				z.revealed = true;

				// Set complete
				this._isComplete = !this.zones.find((oz) => !oz.revealed);

				// Auto and sound
				if (Scratch2.revealSound) {
					Scratch2.revealSound.play();
				}
				this.auto(z === this._defaultZone ? null : z);
			}

			// Set zone not dirty anymore
			z._isDirty = false;
		});

		this._progress = zones.reduce((p, c) => p + c.progress, 0) / zones.length;

		// if (this._progress !== this._oldProgress) {
		this.renderer.onRatioChanged(this, this._oldProgress, this._progress);
		if (this.onRatioChange) {
			this.onRatioChange(this._oldProgress, this._progress);
		}

		this._oldProgress = this._progress;
		// }

		// Tested, should not test anymore
		this._shouldCheckComplete = false;
	}

	/**
	 * Flags the scratch as dirty.
	 * Setting as dirty will enable the completion test and disable caching to update the frame.
	 */
	public setDirty(zone?: Scratch2Zone): void {
		this._isDirty = true;
		this._defaultZone._isDirty = true;

		if (!zone) {
			this.zones.forEach((z) => (z._isDirty = true));
		} else {
			zone._isDirty = true;
		}

		this._shouldCheckComplete = true;
	}

	/**
	 * Returns the current clamping value. If 0.0, then clamping is disabled.
	 */
	public get clampValue(): number {
		return this.options.clampValue;
	}

	/**
	 * Sets the clamp value. If 0.0, then clamping is disabled.
	 */
	public set clampValue(value: number) {
		if (this.options.clampValue === value) {
			return;
		}
		this.options.clampValue = value;

		this._resetFilter();
	}

	/**
	 * Gets wether or not the scratch is in inverted mode.
	 */
	public get inverted(): boolean {
		return this.options.inverted;
	}

	/**
	 * Sets wether or not the scratch is in inverted mode.
	 */
	public set inverted(inverted: boolean) {
		if (this.options.inverted === inverted) {
			return;
		}
		this.options.inverted = inverted;

		this._resetFilter();
	}

	/**
	 * Resets the filter.
	 */
	private _resetFilter(): void {
		if (this._isWebGL) {
			const resolution = this._maskFilter
				? this._maskFilter.resolution
				: settings.FILTER_RESOLUTION;
			this.content.filters = [
				(this._maskFilter = new Scratch2MaskFilter(
					resolution,
					this.options.padding,
					this._maskSprite,
					this.options.clampValue,
					this.options.inverted
				)),
			];
		}
	}

	/**
	 * Gets the default zone of the scratch component.
	 * @see .zones
	 */
	public get defaultZone(): Scratch2Zone {
		return this._defaultZone;
	}

	/**
	 * Returns the reference of the first found zone that is indentified by the given identifier.
	 * @param id defines the identifier of the zone to get.
	 */
	public getZoneById(id: string): Scratch2Zone {
		return this.zones.find((z) => z.id === id);
	}

	/**
	 * Plays the scratch sound. Typically used by the renderers.
	 * @hidden
	 */
	public _playScratchSound(): void {
		if (Scratch2._scratchSoundTimeoutId !== null) {
			return;
		}

		if (Scratch2.scratchSound && !Scratch2.scratchSound.playing()) {
			Scratch2.scratchSound.play();
			Scratch2._scratchSoundTimeoutId = setTimeout(
				() => (Scratch2._scratchSoundTimeoutId = null),
				100
			) as unknown as number;
		}
	}

	/**
	 * Called once the user scratchs the scratch component.
	 * @param target the target container where pointer is moving.
	 * @param datas object that contains all the pointer event values (distance, etc.).
	 */
	protected onMove(target: Container, datas: IPointerEvent): void {
		if (target !== this) {
			return;
		}

		// Get zone which is bellow the cursor and call the onScratch callback is the user pressed/clicked on the zone
		let zone: Scratch2Zone = null;
		if (
			this.zones.length === 0 &&
			this._zoneContainsCoordinates(this._defaultZone, datas.localPosition)
		) {
			zone = this._defaultZone;
		} else {
			zone = this.zones.find((z) => this._zoneContainsCoordinates(z, datas.localPosition));
		}

		if (!zone && this.scratchOnlyZones) {
			if (!this._cursor) {
				this.buttonMode = false;
			} else {
				this.cursor = this._pixiRenderer.plugins.interaction.cursorStyles.default;
			}
			return;
		} else {
			if (!this._cursor) {
				this.buttonMode = true;
			} else {
				this.cursor = this._cursor;
			}
		}

		// Depending on the device, the "datas.data.button" can be 1 (mobile) or 0 (web) or -1 for Edge/Chrome that only trigger the input Down
		const isScratching = datas.data.button <= 1 && datas.data.buttons === 1;

		// The user is not scratching
		if (!isScratching || this._isAuto) {
			return;
		}

		if (!zone) {
			zone = this._defaultZone;
		}

		if (!zone.isActive) {
			return;
		}

		if (this.onScratch) {
			this.onScratch(zone);
		}
		if (zone.onScratch) {
			zone.onScratch();
		}

		this._playScratchSound();

		// Apply filter only on first scratch. Not necessary before.
		if (this._isWebGL && !this.content.filters) {
			this.content.filters = [this._maskFilter];
		}

		this.renderer.onScratch(this, datas, zone);
		this.setDirty(zone);
	}

	/**
	 * Returns wether or not the given zone contains the given pointer coordinates (x,y)
	 * @param zone the zone to check
	 * @param coordinates the pointer coordinates
	 */
	private _zoneContainsCoordinates(zone: Scratch2Zone, coordinates: Point): boolean {
		if (!zone.hitArea) {
			return zone.rectangle.contains(coordinates.x, coordinates.y);
		}
		if (zone.hitArea instanceof Polygon) {
			return zone.hitArea.contains(coordinates.x - zone.x, coordinates.y - zone.y);
		} else {
			return zone.hitArea.contains(coordinates.x, coordinates.y);
		}
	}

	/**
	 * Computes the optimizer which switches from
	 */
	protected computeOptimizer(): void {
		// Don't compute if disabled
		if (this.disableOptimizations) {
			return;
		}

		// Don't compute optimizer if renderer is a one click renderer.
		if (this.renderer instanceof OneClickScratchRenderer) {
			return;
		}

		if (!this._performanceElapsedTime) {
			this._performanceElapsedTime = Date.now();
			return;
		}

		this._performanceFramesCount++;
		this._performanceAverageTime += this._lastPerformanceTime;
		if (Date.now() - this._performanceElapsedTime < 5000) {
			return;
		} // 5 seconds
		this._performanceElapsedTime = 0;

		// Should optimize?
		if (this._performanceAverageTime / this._performanceFramesCount <= 8) {
			this._performanceElapsedTime = 0;
			this._performanceAverageTime = 0;
			this._performanceFramesCount = 0;
			return;
		}

		this._performanceElapsedTime = 0;
		this._performanceAverageTime = 0;
		this._performanceFramesCount = 0;

		// Clamping
		if (!this._isWebGL && this._clampingEnabled && this.options.clampValue) {
			Logger.getLogger().warn(
				`Performances fallback: disabling clamping for scratch "${this.name}"`
			);
			this._trackPerformancesDrop("Scratch2: Disabled Clamping due to a performances drop.");
			this._clampingEnabled = false;
			return;
		}

		// Default renderer
		if (!(this.renderer instanceof DefaultScratchRenderer)) {
			Logger.getLogger().warn(
				`Performances fallback: using default renderer for scratch "${this.name}"`
			);
			this._trackPerformancesDrop(
				"Scratch2: Switched to DefaultRenderer due to a performances drop."
			);
			return this.setRenderer(
				this.options.fallback.defaultRenderer || new DefaultScratchRenderer()
			);
		}

		// One click
		if (!(this.renderer instanceof OneClickScratchRenderer)) {
			Logger.getLogger().warn(
				`Performances fallback: using one click renderer for scratch "${this.name}"`
			);
			this._trackPerformancesDrop(
				"Scratch2: Switched to OneClickRenderer due to a performances drop."
			);
			return this.setRenderer(
				this.options.fallback.oneClickRenderer || new OneClickScratchRenderer()
			);
		}
	}

	/**
	 * Sends the tracking event for scratch performances switch.
	 */
	private _trackPerformancesDrop(infos: string): void {
		tracker.track({
			eventType: EEventType.Scratch,
			event: "scratchPerformances",
			name: this.trackingName || this.name,
			infos,
		} as IEvent);
	}

	/**
	 * Only for debug purpose, shows the zones by drawing some graphics.
	 */
	public _showZones(): void {
		if (!this._debugZonesGraphics) {
			this._debugZonesGraphics = [];
		}
		if (!this._debugZonesColors) {
			this._debugZonesColors = [];
		}

		this._hideZones();

		this.zones.forEach((z, index) => {
			if (this._debugZonesColors.length === index) {
				this._debugZonesColors.push(0xffffff * Math.random());
			}

			const g = new Graphics();
			g.beginFill(this._debugZonesColors[index], 0.5);
			g.drawRect(0, 0, z.width, z.height);
			g.endFill();
			g.position.set(z.x, z.y);
			g.doNotExport = true;

			this.addChild(g);
			this._debugZonesGraphics.push(g);
		});
	}

	/**
	 * Only for debug purpose, hides the zones by drawing some graphics.
	 */
	public _hideZones(): void {
		if (this._debugZonesGraphics) {
			this._debugZonesGraphics.forEach((dzg) => dzg.destroy());
			this._debugZonesGraphics = [];
		}
	}

	/**
	 * Clears all the graphics used to instant reveal zones.
	 */
	private _clearZonesToForceReveal(): void {
		this._zonesToForceReveal.forEach(
			(z) => z._waitingForceRevealGraphics && z._waitingForceRevealGraphics.destroy()
		);
		this._zonesToForceReveal = [];
	}

	/**
	 * Forces reveal the scratch and marks all zones reveal complete.
	 */
	private _forceRevealAndMarkZones(): void {
		this.forceReveal();
		this.zones.forEach((z) => {
			if (!z.revealedComplete && z.onRevealComplete) {
				z.onRevealComplete();
			}
			z.revealedComplete = true;
		});
	}

	/**
	 * Exports the current scratch2 configuration
	 */
	public export(): any {
		// Don't export everything
		this.renderer.doNotExport = true;
		this.pointer.doNotExport = true;
		if (this._maskSprite) {
			this._maskSprite.doNotExport = true;
		} // Not created before first render.

		const scratch = Tools.assign(ContainerExporter.exportContainer(this), {
			type: "ui.Scratch2",
			scratch: {
				content: ContainerExporter.export(this.content),
				renderer: this.renderer.export(),
				resolution: this.options.resolution,
				disableCaching: this.options.disableCaching,
				revealRatio: this.options.revealRatio,
				clampValue: this.options.clampValue,
				inverted: this.options.inverted,
				fallback: {
					defaultRenderer: this.options.fallback.defaultRenderer
						? this.options.fallback.defaultRenderer.export()
						: undefined,
					oneClickRenderer: this.options.fallback.oneClickRenderer
						? this.options.fallback.oneClickRenderer.export()
						: undefined,
				},
				padding: this.options.padding,
				zones: this.zones.map((z) => z.export()),
				scratchOnlyZones: this.scratchOnlyZones,
				enableAutoScratchOnlyOnZones: this.enableAutoScratchOnlyOnZones,
			},
			// Must not have children
			children: [],

			trackingName: this.trackingName,
		});

		return scratch;
	}

	/**
	 * Parses the given scratch configuration to return a new scratch component
	 * @param config the configuration of the scratch component being created
	 * @param baseUrl the base url of the loader
	 */
	public static parse(config: any, baseUrl: string): Scratch2 {
		const scratch = new Scratch2({
			content: ContainerImporter.import(config.scratch.content, baseUrl),
			renderer: new ParticlesScratchRenderer(),
			resolution: config.scratch.resolution,
			disableCaching: config.scratch.disableCaching,
			revealRatio: config.scratch.revealRatio,
			clampValue: config.scratch.clampValue,
			inverted: config.scratch.inverted,
			padding: config.scratch.padding,
		});

		if (config.scratch.zones) {
			config.scratch.zones.forEach((z) => {
				const zone = Scratch2Zone.parse(z);
				scratch.zones.push(zone);
			});
		}

		scratch.scratchOnlyZones = config.scratch.scratchOnlyZones || scratch.scratchOnlyZones;
		scratch.enableAutoScratchOnlyOnZones =
			config.scratch.enableAutoScratchOnlyOnZones || scratch.enableAutoScratchOnlyOnZones;

		scratch.trackingName = config.trackingName || "";

		return scratch;
	}
}
