import Proton from "proton-js";

import type { Scratch } from "./scratch";

export class PixiFollowEmitter extends Proton.Emitter {
	/**
	 * The FollowEmitter class inherits from Proton.Emitter
	 *
	 * use the FollowEmitter will emit particle when mousemoving
	 *
	 * @class Proton.FollowEmitter
	 * @constructor
	 * @param {Element} mouseTarget mouseevent"s target;
	 * @param {Object} pObj the parameters object;
	 */

	public p: Proton.Vector2D;
	public v: Proton.Vector2D;
	public mouseDown: boolean = false;
	public resolution: number = 1;

	/**
	 * Called on the "this" emitter is emitting particles
	 */
	public onEmitting: () => void;

	private _mouseTarget: Scratch;
	private _minDistance: number = 4;
	private _maxParticules: number = 50;
	private _AllowEmitting: boolean;
	private _direction: Proton.Vector2D;
	private _normalized: Proton.Vector2D;
	private _magnitude: number;

	private _pointermoveHandler: (e: PIXI.interaction.InteractionEvent) => void;
	private _pointerdownHandler: (e: PIXI.interaction.InteractionEvent) => void;
	private _pointerupHandler: (e: PIXI.interaction.InteractionEvent) => void;

	public constructor(mouseTarget: Scratch, ease?: number, pObj?: any) {
		super(pObj);

		this._mouseTarget = mouseTarget;

		this._AllowEmitting = false;
		this.p.x = -1;
		this.p.y = -1;
		this._initEventHandler();
	}

	/**
	 * start emit particle
	 * @method emit
	 */
	public emit(force?: boolean): void {
		this._AllowEmitting = true;
		if (force) {
			super.emit("once");
		}
	}

	/**
	 * stop emitting
	 * @method stop
	 */
	public stop(): void {
		this._AllowEmitting = false;
	}

	/**
	 * Destroy this Emitter
	 * @method destroy
	 */
	public destroy(): void {
		super.destroy();
		this._mouseTarget.off("pointerdown", this._pointerdownHandler);
		this._mouseTarget.off("pointerup", this._pointerupHandler);
		this._mouseTarget.off("pointermove", this._pointermoveHandler);
	}

	/**
	 * Remove all particles
	 */
	public removeAllParticles(): void {
		super.removeAllParticles();
	}

	/**
	 * Add initialize to emitter
	 * @param thing initalize to add
	 */
	public addInitialize(thing: any): void {
		super.addInitialize(thing);
	}

	/**
	 * Add behaviour to emitter
	 * @param thing behavior to add
	 */
	public addBehaviour(thing: any): void {
		super.addBehaviour(thing);
	}

	private _initEventHandler(): void {
		this._pointermoveHandler = (e) => this._pointermove.call(this, e);
		this._pointerdownHandler = (e) => this._pointerdown.call(this, e);
		this._pointerupHandler = (e) => this._pointerup.call(this, e);

		this._mouseTarget.on("pointerdown", this._pointerdownHandler);
		this._mouseTarget.on("pointerup", this._pointerupHandler);
		this._mouseTarget.on("pointermove", this._pointermoveHandler);
	}

	private _pointerdown(e: PIXI.interaction.InteractionEvent): void {
		this._pointermove(e);
	}

	private _pointerup(e: PIXI.interaction.InteractionEvent): void {
		this.mouseDown = false;
	}

	private _pointermove(e: PIXI.interaction.InteractionEvent): void {
		this.mouseDown = e.data.buttons > 0; // detect mouse down outside of target
		if (!this.mouseDown) {
			return;
		}
		this._emitAtPosition(e.data.getLocalPosition(this._mouseTarget));
	}

	private _emitAtPosition(pos: Proton.Vector2D): void {
		const newPosition = new Proton.Vector2D(pos.x * this.resolution, pos.y * this.resolution);
		this._direction = new Proton.Vector2D(newPosition.x - this.p.x, newPosition.y - this.p.y);
		this._magnitude = Math.sqrt(
			this._direction.x * this._direction.x + this._direction.y * this._direction.y
		);

		if (!this._mouseTarget.renderer.oneClick) {
			if (this._mouseTarget.particulesCount < this._maxParticules) {
				if (this._magnitude > this._minDistance * this.resolution) {
					this._normalized = new Proton.Vector2D(
						this._direction.x / this._magnitude,
						this._direction.y / this._magnitude
					);

					this.p.x = newPosition.x;
					this.p.y = newPosition.y;

					this.v.x = this._normalized.x;
					this.v.y = this._normalized.y;

					super.emit("once");
				}
			}
		} else {
			const dist = this._magnitude > 0 ? this._magnitude : Number.MAX_VALUE;
			this._mouseTarget.renderer.updateFrame(dist);
			this._mouseTarget.renderer.onScratch();
		}

		// Notify that the emitter is emitting
		if (this.onEmitting) {
			this.onEmitting();
		}
	}
}
