import { Rectangle } from "pixi.js";
import type OpenType from "opentype.js";

import { TextField } from "./textField";
import type { IStyle } from "./textStyle";
import type { Word } from "./word";
import type { IToken } from "./tools/tokenizer";

export enum EGlyphType {
	Null = 0,
	Character = 1,
	Space = 2,
	NewLine = 3,
}

export class Glyph {
	public x: number = 0; // x position in the line.
	public interLine: number = 0;
	public width: number = 0; // glyph real width
	public height: number = 0; // glyph real height
	public yMax: number = 0;
	public yMin: number = 0;
	public ascender: number = 0;
	public maxHeight: number = 0;
	public leading: number = 0;
	public kerning: number = 0; // calculated from previous glyph in line (or 0 if first glyph)
	public openTypeGlyph: OpenType.Glyph = null; // the openType glyph itself
	public style: IStyle = null; // that glyph style reference
	public text: string = "";
	public word: Word = null;
	public type: EGlyphType = EGlyphType.Null;
	public token: IToken = null;

	/**
	 * Constructor
	 * @param char: the glyph's char
	 * @param style the glyph's style
	 * @param previousGlyph: the glyph just before "this"
	 */
	public constructor(char: string, style: IStyle, previousGlyph: Glyph) {
		const openTypeGlyph = style.font.charToGlyph(char);
		const metrics = openTypeGlyph.getMetrics();

		const kerning: number = previousGlyph
			? TextField.unitToPx(
					style.font.getKerningValue(openTypeGlyph, previousGlyph.openTypeGlyph),
					style
			  )
			: 0;

		const yMax = TextField.unitToPx(metrics.yMax, style);
		const yMin = TextField.unitToPx(metrics.yMin, style);

		const glyphX: number = previousGlyph ? previousGlyph.x + previousGlyph.width : 0;
		const glyphWidth = TextField.unitToPx(openTypeGlyph.advanceWidth, style);
		const glyphHeight = TextField.unitToPx(metrics.yMax - metrics.yMin, style);
		const ascender = TextField.unitToPx(style.font.ascender, style);
		const descender = TextField.unitToPx(style.font.descender, style);
		const maxHeight = TextField.unitToPx(
			Math.abs(style.font.ascender - style.font.descender),
			style
		);
		const leading = style.fontSize * 2 + (style.interLine || 0) - maxHeight;

		if (char === "\n" || char === "\r") {
			this.type = EGlyphType.NewLine;
		} else if (char === " ") {
			this.type = EGlyphType.Space;
		} else {
			this.type = EGlyphType.Character;
		}

		this.x = glyphX;
		this.interLine = style.interLine | 0;
		this.openTypeGlyph = openTypeGlyph;
		this.kerning = kerning;
		this.style = style;
		this.width = glyphWidth;
		this.ascender = ascender;
		this.yMax = yMax;
		this.maxHeight = maxHeight;
		this.yMin = yMin;
		this.height = glyphHeight;
		this.leading = leading;
		this.text = char;
	}

	/**
	 * Returns the glyph's bounds
	 */
	public getBounds(): Rectangle {
		return new Rectangle(
			this.word.line.x + this.word.x + this.x,
			this.word.line.y + this.word.y - this.yMax + this.interLine,
			this.width,
			this.height
		);
	}
}
