import { Texture } from "pixi.js";

import { TextureExporter } from "../../../loading/export/textureExporter";
import { TextureImporter } from "../../../loading/import/textureImporter";
import type { IButtonStyle, IButtonTextStates } from "./button";
import { Button } from "./button";
import type { IStyles, IStyle } from "../text/textArea";

export interface IToggleButtonStyle extends IButtonStyle<IToggleButtonTextStates> {
	overClickedTexture?: string | Texture;
}

export interface IToggleButtonTextStates extends IButtonTextStates {
	hoverClicked?: IStyle | IStyles;
}

const STATE_TO_TEXTSTATE = {
	overClicked: "hoverClicked",
};

export class ToggleButton extends Button {
	protected static instances: ToggleButton[] = [];

	/**
	 * Texture displayed when mouse over the button, and button is checked.
	 */
	public overClickedTexture?: Texture = null;

	protected _groupName: null | string;
	protected _checked: boolean;

	public constructor(
		style: IToggleButtonStyle = {},
		textStyle: IStyle | IStyles = {},
		text: string = ""
	) {
		super(style, textStyle, text);

		this._checked = false;
		this._groupName = null;

		this.overClickedTexture =
			typeof style.overClickedTexture === "string"
				? Texture.from(style.overClickedTexture)
				: style.overClickedTexture;

		ToggleButton.instances.push(this);

		const onAddedOrRemoved = this.onAddedOrRemoved.bind(this);
		this.on("added", onAddedOrRemoved);
		this.on("removed", onAddedOrRemoved);
		this.on("destroyed", onAddedOrRemoved);
	}

	/**
	 * Change this button state (checked -> not checked and vice versa)
	 */
	public toggle(): void {
		this.checked = !this.checked;
	}

	/**
	 * Change the state status of this button.
	 */
	public set checked(checked: boolean) {
		if (this._checked === checked) {
			return;
		}

		this._checked = checked;
		this.state = checked ? "clicked" : "idle";

		// in radio mode, this button cannot be deselected by itself
		if (this._groupName !== null) {
			this.buttonMode = this.interactive = !checked;
		}

		this.emit("changed", this, checked);
		if (checked) {
			this.emit("checked", this);
		}

		if (this._groupName !== null) {
			ToggleButton.enforceRadioConstraints(this.groupName, this);
		}
	}

	/**
	 * Return the checked status of this button.
	 */
	public get checked(): boolean {
		return this._checked;
	}

	// On mouse over this
	protected _onPointerOver(): void {
		if (this._enabled) {
			this.state = this.checked ? "overClicked" : "over";
		}
	}

	// On mouse out of this
	protected _onPointerOut(): void {
		if (this._enabled) {
			this.state = this.checked ? "clicked" : "idle";
		}
	}

	// On mouse/touch on this
	protected _onPointerDown(): void {
		if (this._enabled) {
			this.state = this.checked ? "idle" : "clicked";
		}
	}

	protected _onMouseUp(): void {
		if (this._enabled) {
			this.toggle();
			this.state = this.checked ? (this._groupName === null ? "overClicked" : "clicked") : "over";
		}
	}

	// On touch end
	protected _onTouchEnd(): void {
		if (this._enabled) {
			this.toggle();
			this.state = this.checked ? "clicked" : "idle";
		}
	}

	// On mouse/touch up outside
	protected _onPointerUpOutside(): void {
		if (this._enabled) {
			this.state = this.checked ? "clicked" : "idle";
		}
	}

	protected onAddedOrRemoved(): void {
		if (this.groupName !== null) {
			ToggleButton.enforceRadioConstraints(this.groupName);
		}
	}

	protected stateToTextStyle(state: string): IStyle | IStyles {
		return state in STATE_TO_TEXTSTATE
			? this.textStates[STATE_TO_TEXTSTATE[state]]
			: super.stateToTextStyle(state);
	}

	/**
	 * Serialize this component.
	 */
	public export(): any {
		const data = super.export();

		data.type = "ui.ToggleButton";
		data.style.overClickedTexture = TextureExporter.exportTexture(this.overClickedTexture);
		data.groupName = this.groupName;

		return data;
	}

	/**
	 * Unserialize a toggle button
	 *
	 * @param config the serialized data
	 * @param baseUrl the base url of the loader
	 * @param button the already created ToggleButton
	 */
	public static parse(config: any, baseUrl: string, button?: ToggleButton): ToggleButton {
		if (button === null || button === undefined) {
			button = new ToggleButton({}, config.textStyle, config.text);
		}

		button = Button.parse(config, baseUrl, button) as ToggleButton;
		button.overClickedTexture = TextureImporter.importTexture(
			config.style.overClickedTexture,
			baseUrl
		);
		button.groupName = config.groupName === "" ? null : config.groupName ?? null;

		return button;
	}

	/**
	 * Destroy this component.
	 */
	public destroy(): void {
		super.destroy();

		const index = ToggleButton.instances.indexOf(this);
		if (index >= 0) {
			ToggleButton.instances.splice(index, 1);
		}

		this.onAddedOrRemoved();
	}

	/**
	 * Set the group name of this button (null if no group).
	 */
	public set groupName(groupName: string | null) {
		if (this._groupName === groupName) {
			return;
		}

		const previousGroupName = this._groupName;

		this._groupName = groupName;

		if (previousGroupName !== null) {
			ToggleButton.enforceRadioConstraints(previousGroupName);
		}
		if (groupName !== null) {
			ToggleButton.enforceRadioConstraints(groupName);
		}
	}

	/**
	 * Return the group name of this button (null if no group).
	 */
	public get groupName(): null | string {
		return this._groupName;
	}

	protected isAccountableAsRadio = (): boolean => {
		return this.visible && this.alpha > 0 && this.parent && this.enabled;
	};

	/**
	 * Apply rules related to radio group to the given group.
	 * @param groupName: name of the group of which we check constraints
	 * @param checked: if set, keep this button as checked in case several buttons are checked
	 */
	public static enforceRadioConstraints(groupName: string, checked?: ToggleButton): void {
		const instances = ToggleButton.instances.filter((button) => {
			return button.groupName === groupName && button.isAccountableAsRadio();
		});

		if (instances.length === 0) {
			return;
		}

		const checkedInstances = instances.filter((button) => button.checked);

		if (checkedInstances.length === 0) {
			instances[0].checked = true;
		} else if (checkedInstances.length > 1) {
			checked = checked ?? checkedInstances[0];
			for (const button of instances) {
				button.checked = button === checked;
			}
		}
	}

	/**
	 * Return the instance of ToggleButton that is checked for the given group.
	 * @returns: checked button if one, null if no button checked
	 */
	public static getChecked(groupName: string): null | ToggleButton {
		const checkedButton = ToggleButton.instances.find((button) => {
			return button.groupName === groupName && button.isAccountableAsRadio() && button.checked;
		});

		return checkedButton ?? null;
	}
}
