import type { Rectangle, Point } from "pixi.js";

/**
 * @hidden
 */
export interface ICacheDictionary<T> {
	[index: string]: T;
}

/**
 * Used to help managing/checking cache.
 * @see Docking for more usage examples.
 * @see Resize for more usage examples.
 */
export class DataCache<T> {
	/**
	 * The base object reference being used to check cache.
	 */
	public object: T = null;

	/**
	 * @hidden
	 */
	public _rectangles: ICacheDictionary<Rectangle> = {};
	/**
	 * @hidden
	 */
	public _points: ICacheDictionary<Point> = {};
	/**
	 * @hidden
	 */
	public _commons: ICacheDictionary<number | boolean | string | ((...args: any[]) => any)> = {};
	/**
	 * @hidden
	 */
	public _isDirty: boolean = false;
	/**
	 * @hidden
	 */
	public _forcedDirty: boolean = false;

	/**
	 * Constructor.
	 * @param object the base object reference being used to check cache.
	 * @see Docking for more usage examples.
	 * @see Resize for more usage examples.
	 */
	public constructor(object: T) {
		this.object = object;
	}

	/**
	 * Adds a new rectangle to check values.
	 * @param name the name of the rectangle property in the base class.
	 * @param newRectangle the new rectangle reference to test.
	 */
	public addRectangle(name: string, newRectangle: Rectangle): void {
		this._rectangles[name] = newRectangle;
	}

	/**
	 * Adds a new point to check values.
	 * @param name the name of the point property in the base class.
	 * @param newPoint the new point reference to test.
	 */
	public addPoint(name: string, newPoint: Point): void {
		this._points[name] = newPoint;
	}

	/**
	 * Adds a new common value to check.
	 * @param name the name of the value property in the base class.
	 * @param newValue the new value to test.
	 */
	public addCommon(
		name: string,
		newValue: number | boolean | string | ((...args: any[]) => any)
	): void {
		this._commons[name] = newValue;
	}

	/**
	 * Returns wether or not the cache is dirty. If dirty, the cache is updated.
	 */
	public checkDirty(): boolean {
		this._isDirty = this._forcedDirty;
		this._forcedDirty = false;

		// Rectangles
		for (const r in this._rectangles) {
			if (!this._rectangles.hasOwnProperty(r)) {
				continue;
			}
			const r1 = this._rectangles[r];
			const r2 = this.object[r] as Rectangle;

			if (r1.x !== r2.x || r1.y !== r2.y || r1.width !== r2.width || r1.height !== r2.height) {
				r2.copy(r1);
				this._isDirty = true;
			}
		}

		// Points
		for (const r in this._points) {
			if (!this._points.hasOwnProperty(r)) {
				continue;
			}
			const p1 = this._points[r];
			const p2 = this.object[r] as Point;

			if (!p1.equals(p2)) {
				p2.copy(p1);
				this._isDirty = true;
			}
		}

		// Common
		for (const c in this._commons) {
			if (!this._commons.hasOwnProperty(c)) {
				continue;
			}
			const c1 = this._commons[c];
			const c2 = this.object[c];

			if (c1 !== c2) {
				this.object[c] = c1;
				this._isDirty = true;
			}
		}

		return this._isDirty;
	}
}
