import { Rectangle, Point } from "pixi.js";

import type { IProjectRectangle, IProjectPoint } from "../model";

export class CommonImporter {
	/**
	 * Import a rectangle from the given configuration
	 * @param config the rectangle's configuration
	 */
	public static importRectangle(config: IProjectRectangle): Rectangle {
		if (!config) {
			return null;
		}

		return new Rectangle(config.x, config.y, config.width, config.height);
	}

	/**
	 * Import a point from the given configuration
	 * @param config the point's configuration
	 */
	public static importPoint(config: IProjectPoint): Point {
		return new Point(config.x, config.y);
	}
}
