export enum ETokenType {
	EndOfInput = 0,
	Other = 1,
	TagOpen = 4,
	TagClose = 5,
}

export interface IToken {
	type: string;
	content: string;
	attributes: {
		[index: string]: string;
	};
}

export class Tokenizer {
	// Public members
	public toParse: string;
	public pos: number = 0;
	public size: number;

	public currentString: string = "";

	// Private members
	private _canBeTag: RegExp = /^[<>]+$/;

	/**
	 * Constructor
	 * @param str the string to tokenize
	 */
	public constructor(str: string) {
		this.toParse = str;
		this.size = str.length;
	}

	/**
	 * Returns the next character
	 */
	public read(): string {
		return this.toParse[this.pos++];
	}

	/**
	 * Peeks the current character
	 */
	public peek(): string {
		return this.toParse[this.pos];
	}

	/**
	 * Increment position in string to parse
	 */
	public forward(): void {
		this.pos++;
	}

	/**
	 * Returns if at end of string to parse
	 */
	public isEnd(): boolean {
		return this.pos >= this.size;
	}

	/**
	 * Returns the next token (tag or other)
	 */
	public getNextToken(): ETokenType {
		if (this.isEnd()) {
			return ETokenType.EndOfInput;
		}

		let c = this.read();

		switch (c) {
			case "<":
				return ETokenType.TagOpen;
			case ">":
				return ETokenType.TagClose;
			default:
				this.currentString = c;

				while (!this.isEnd() && !this._canBeTag.test((c = this.peek()))) {
					this.currentString += c;
					this.forward();
				}

				return ETokenType.Other;
		}
	}

	/**
	 * Returns the next tag
	 */
	public getNextTag(): IToken {
		if (this.isEnd()) {
			return null;
		}

		let token = this.getNextToken();
		let tag: string = null;
		let content = "";
		const attributes: { [index: string]: string } = {};

		// Get tag type + content
		if (token === ETokenType.TagOpen) {
			// We know that it's a string
			token = this.getNextToken();
			tag = this.currentString;

			token = this.getNextToken();

			const split = tag.split(" ");
			if (split.length > 1) {
				tag = split[0];

				for (let i = 1, len = split.length; i < len; i++) {
					const attributeSplit = split[i].split("=");
					const attributeName = attributeSplit[0];
					const attributeData = attributeSplit[1];
					attributes[attributeName] = attributeData.substr(1, attributeData.length - 2);
				}
			}
		}

		if (token === ETokenType.TagClose) {
			token = this.getNextToken();
		}

		if (token === ETokenType.Other) {
			content = this.currentString;
			if (tag) {
				token = this.getNextToken();
			}
		}

		// Get tag close
		if (tag && token === ETokenType.TagOpen) {
			token = this.getNextToken();
		}

		if (tag && token === ETokenType.Other && this.currentString.indexOf(tag) === 1) {
			token = this.getNextToken();
		}

		return {
			type: tag || "default",
			content,
			attributes,
		};
	}
}
