import { Container } from "pixi.js";
import { TweenMax, Cubic } from "gsap";
import { Tools } from "@gdk/core";
import type { mesh, interaction, Point } from "pixi.js";

import { ContainerExporter } from "../../loading/export/containerExporter";
import { ContainerImporter } from "../../loading/import/containerImporter";
import { DockingBounds } from "../../container/dockingBounds";
import { DragEvent2 } from "../events/dragEvent2";

/**
 * @experimental
 */
export enum EScrollBarAlign2 {
	/**
	 * Defines a scroll bar being vertical (scroll on Y axis).
	 */
	Vertical = 0,
	/**
	 * Defines a scroll bar being horizontal (scroll on the X axis).
	 */
	Horizontal,
}

/**
 * @experimental
 */
export class ScrollBar2 extends Container {
	/**
	 * The handle element used to draw scroll bar's background.
	 */
	public handle: mesh.NineSlicePlane;
	/**
	 * The tracking element used to change the scroll bar's ratio.
	 */
	public track: mesh.NineSlicePlane;

	/**
	 * Used by specialized components. Mainly used to keep the original track height while computing iOS style thumb height while scrolling.
	 * @see ScrollContainer2.prototype._refreshTracksHeights for more informations.
	 * @hidden
	 */
	public _fixedTrackHeight: number = 0;

	private _alignement: EScrollBarAlign2;

	private _width: number = 1;
	private _height: number = 1;

	private _dragEvent: DragEvent2 = new DragEvent2();

	/**
	 * Constructor.
	 * @param handle The handle element used to draw scroll bar's background.
	 * @param track The tracking element used to change the scroll bar's ratio.
	 * @param alignement The alignement type of the scroll bar (vertical or horizontal).
	 */
	public constructor(
		handle: mesh.NineSlicePlane,
		track: mesh.NineSlicePlane,
		alignement: EScrollBarAlign2
	) {
		super();

		// Configure
		this.handle = handle;
		this.track = track;
		this.dockingBounds = new DockingBounds(this.handle.width, this.handle.height);
		this.alignement = alignement;

		this.handle.interactive = true;
		this.handle.name = "Handle";
		this.addChild(this.handle);

		this.track.interactive = true;
		this.track.buttonMode = true;
		this.track.name = "Track";
		this.addChild(this.track);

		// Events
		this._bindEvents();
	}

	/**
	 * Gets the current width of the scroll bar.
	 */
	// remove this comment when allow-override-transient-properties is included in typescript
	// @ts-ignore
	public get width(): number {
		return this._width;
	}

	/**
	 * Sets the current width of the scroll bar.
	 */
	// remove this comment when allow-override-transient-properties is included in typescript
	// @ts-ignore
	public set width(width: number) {
		if (this._width === width) {
			return;
		}

		this._width = width;
		this.handle.width = width;
		this.track.width = width;
		this.dockingBounds.width = width;
	}

	/**
	 * Gets the current height of the scroll bar.
	 */
	// remove this comment when allow-override-transient-properties is included in typescript
	// @ts-ignore
	public get height(): number {
		return this._height;
	}

	/**
	 * Sets the current height of the scroll bar.
	 */
	// remove this comment when allow-override-transient-properties is included in typescript
	// @ts-ignore
	public set height(height: number) {
		if (this._height === height) {
			return;
		}

		this._height = height;
		this.handle.height = height;
		this.dockingBounds.height = height;
	}

	/**
	 * Gets the scroll bar alignment type (verticla or horizontal).
	 */
	public get alignement(): EScrollBarAlign2 {
		return this._alignement;
	}

	/**
	 * Sets the scroll bar alignment type (verticla or horizontal).
	 */
	public set alignement(align: EScrollBarAlign2) {
		if (this._alignement === align) {
			return;
		}

		this._alignement = align;
		switch (align) {
			case EScrollBarAlign2.Vertical:
				this.rotation = 0;
				break;
			case EScrollBarAlign2.Horizontal:
				this.rotation = -Math.PI * 0.5;
				break;
		}

		this.dockingBounds.setBounds(this.dockingBounds.height, this.dockingBounds.width);
	}

	/**
	 * Scrolls to the given position.
	 * @param position the new position to scroll to. Number expression represents pixels. String expresion represents a ratio in %.
	 * @param duration the duration of the scroll animation. 0 means no animation.
	 * @param ease the easing function used while animating the scroll.
	 */
	public scrollTo(
		position: number | string,
		duration: number = 1,
		ease: Cubic = Cubic.easeInOut
	): void {
		// Target
		let target = 0;
		if (typeof position === "number") {
			target = position;
		} else {
			const ratio = parseFloat(position.split("%")[0]) / 100;
			target = this._width * ratio;
		}

		// Set position
		if (!duration) {
			this.track.y = target;
			this._refreshScroll();
			return;
		}

		TweenMax.to(this.track, duration, {
			y: target,
			ease,
			onUpdate: () => this._refreshScroll(),
		});
	}

	/**
	 * Sets the new scroll position.
	 * @param ratio the position ratio to set the tracking element to.
	 */
	public setScrollPosition(ratio: number): void {
		const maxy = this._height - this.track.height;
		this.track.y = maxy * ratio;
	}

	/**
	 * Emits the scroll event.
	 */
	private _refreshScroll(): void {
		const maxy = this._height - this.track.height;
		this._checkLimits(maxy);

		this.emit("scroll", this.track.y / maxy);
	}

	/**
	 * Checks the limits of the tracking element according to the current handle height.
	 */
	private _checkLimits(maxy: number): void {
		if (this.track.y < 0) {
			this.track.y = 0;
		}
		if (this.track.y > maxy) {
			this.track.y = maxy;
		}
	}

	/**
	 * Binds the events of the overall scroll bar.
	 */
	private _bindEvents(): void {
		// Handle
		this.handle.removeAllListeners();
		this.handle.on("pointertap", (ev: interaction.InteractionEvent) => {
			const localPosition = ev.data.getLocalPosition(this.handle);
			this.scrollTo(localPosition.y - this.track.height * 0.5);
		});

		this._dragEvent.targetContainer = this.track;
		this._dragEvent.on("drag", (offsets: Point) => {
			this.track.y = this._alignement === EScrollBarAlign2.Vertical ? offsets.y : offsets.x;
			this._refreshScroll();
		});
	}

	/**
	 * Export the scroll bar.
	 */
	public export(): any {
		this.handle.doNotExport = false;
		this.track.doNotExport = false;

		const handle = ContainerExporter.export(this.handle);
		const track = ContainerExporter.export(this.track);

		this.handle.doNotExport = true;
		this.track.doNotExport = true;

		return Tools.assign(ContainerExporter.exportContainer(this), {
			type: "ui.ScrollBar2",
			width: this._width,
			height: this._height,
			alignement: this._alignement,
			handle,
			track,
		});
	}

	/**
	 * Parses the scroll bar.
	 * @param config the serialized data.
	 * @param baseUrl the base url of the loader.
	 * @param scrollBar the existing scroll bar.
	 */
	public static parse(config: any, baseUrl: string, scrollBar?: ScrollBar2): ScrollBar2 {
		if (!scrollBar) {
			scrollBar = new ScrollBar2(
				ContainerImporter.import(config.handle, baseUrl) as mesh.NineSlicePlane,
				ContainerImporter.import(config.track, baseUrl) as mesh.NineSlicePlane,
				config.alignement
			);
		}

		scrollBar.width = config.width;
		scrollBar.height = config.height;

		ContainerImporter.importContainer(config, baseUrl, scrollBar);

		return scrollBar;
	}
}
