import { Container } from "pixi.js";

import { CSSLikeMargin } from "./cssLikeMargin";
import { TextField } from "../text/textField";

export interface ITableCellOptions<T extends Container> {
	content?: T;
	margin?: CSSLikeMargin;
	width?: number;
	height?: number;
	fixedWidth?: number;
	fixedHeight?: number;
}

export class TableCell<T extends Container> extends Container {
	protected content: T;
	protected margin: CSSLikeMargin;
	protected cellWidth: number;
	protected cellHeight: number;
	protected fixedWidth: number;
	protected fixedHeight: number;

	public constructor(options: ITableCellOptions<T>) {
		super();

		if (!options.margin) {
			options.margin = new CSSLikeMargin();
		}

		this.updateOptions(options);
		this.draw();
	}

	/**
	 * Update the cell options
	 * @param options
	 */
	public updateOptions(options: ITableCellOptions<T>): void {
		this.content = options.content ? options.content : this.content;
		this.margin = options.margin ? options.margin : this.margin;
		this.cellWidth = options.width ? options.width : this.cellWidth;
		this.cellHeight = options.height ? options.height : this.cellHeight;
		this.fixedWidth = options.fixedWidth
			? options.fixedWidth
			: this.fixedWidth
			? this.fixedWidth
			: this.cellWidth + this.margin.horizontal;
		this.fixedHeight = options.fixedHeight
			? options.fixedHeight
			: this.fixedHeight
			? this.fixedHeight
			: this.cellHeight + this.margin.vertical;

		this.cellWidth = 0;
		this.cellHeight = 0;

		if (this.content) {
			this.addChild(this.content);

			if (this.content instanceof TextField) {
				this.content.freeze();
				this.content.wordWrap = true;
				this.content.width = this.fixedWidth - this.margin.horizontal;
				this.content.unfreeze();
			}

			this.cellWidth = this.content.dockingBounds
				? this.content.dockingBounds.width
				: this.content.width;
			this.cellHeight = this.content.dockingBounds
				? this.content.dockingBounds.height
				: this.content.height;
		}

		this.cellHeight += this.margin.top + this.margin.bottom;
		this.cellWidth += this.margin.left + this.margin.right;
	}

	/**
	 * get the cell content
	 */
	public getContent(): T {
		return this.content;
	}

	/**
	 * get the cell width
	 */
	public getWidth(): number {
		return this.cellWidth;
	}

	/**
	 * get the cell height
	 */
	public getHeight(): number {
		return this.cellHeight;
	}

	/**
	 * Get the cell fixed Width
	 */
	public getFixedWidth(): number {
		return this.fixedWidth;
	}

	/**
	 * get The cell fixed height
	 */
	public getFixedHeight(): number {
		return this.fixedHeight;
	}

	/**
	 * Draw the cell
	 */
	public draw(): void {
		this.updateOptions({});

		if (this.content) {
			this.content.x =
				(this.fixedWidth -
					(this.content.dockingBounds ? this.content.dockingBounds.width : this.content.width) -
					this.margin.horizontal) /
				2;
			this.content.x += this.margin.left;
			this.content.y =
				(this.fixedHeight -
					(this.content.dockingBounds ? this.content.dockingBounds.height : this.content.height) -
					this.margin.vertical) /
				2;
			this.content.y += this.margin.top;
		}
	}
}
