import { Transform, utils } from "pixi.js";

import { ContainerUtils } from "./utils";

export const accessibilityUpdate = function (): void {
	if (!this.renderer.renderingToScreen) {
		return;
	}

	// update children...
	this.updateAccessibleObjects(this.renderer._lastObjectRendered);

	const rect = this.renderer.view.getBoundingClientRect();
	const sx = (rect.width / this.renderer.width) * this.renderer.resolution;
	const sy = (rect.height / this.renderer.height) * this.renderer.resolution;

	let div = this.div;

	div.style.left = `${rect.left}px`;
	div.style.top = `${rect.top}px`;
	div.style.width = `${this.renderer.width / this.renderer.resolution}px`;
	div.style.height = `${this.renderer.height / this.renderer.resolution}px`;

	for (let i = 0; i < this.children.length; i++) {
		const child = this.children[i];

		if (child.renderId !== this.renderId) {
			child._accessibleActive = false;

			utils.removeItems(this.children, i, 1);
			this.div.removeChild(child._accessibleDiv);
			this.pool.push(child._accessibleDiv);
			child._accessibleDiv = null;

			i--;

			if (this.children.length === 0) {
				this.deactivate();
			}
		} else {
			// map div to display..
			div = child._accessibleDiv;
			let hitArea = child.hitArea;
			const wt = child.worldTransform;

			if (child.hitArea) {
				div.style.left = `${(wt.tx + hitArea.x * wt.a) * sx}px`;
				div.style.top = `${(wt.ty + hitArea.y * wt.d) * sy}px`;

				div.style.width = `${hitArea.width * wt.a * sx}px`;
				div.style.height = `${hitArea.height * wt.d * sy}px`;
			} else {
				// hitArea = child.getBounds();
				const t = new Transform();
				t.setFromMatrix(child.worldTransform);
				hitArea = ContainerUtils.refreshBounds(child, t);

				this.capHitArea(hitArea);

				div.style.left = `${hitArea.x * sx}px`;
				div.style.top = `${hitArea.y * sy}px`;

				div.style.width = `${hitArea.width * sx}px`;
				div.style.height = `${hitArea.height * sy}px`;

				// update button titles and hints if they exist and they've changed
				if (div.title !== child.accessibleTitle && child.accessibleTitle !== null) {
					div.title = child.accessibleTitle;
				}
				if (
					div.getAttribute("aria-label") !== child.accessibleHint &&
					child.accessibleHint !== null
				) {
					div.setAttribute("aria-label", child.accessibleHint);
				}
			}
		}
	}

	// increment the render id..
	this.renderId++;
};

export const activateUpdate = function (): void {
	// Added feature: sends accessibility event to window
	const event = new Event("EnableAccessibility");
	window.dispatchEvent(event);

	// Untouched PIXI code below
	if (this.isActive) {
		return;
	}
	this.isActive = true;
	window.document.addEventListener("mousemove", this._onMouseMove, true);
	window.document.addEventListener("touchmove", this.deactivate.bind(this), true);
	window.removeEventListener("keydown", this._onKeyDown, false);
	this.renderer.on("postrender", this.update, this);
	if (this.renderer.view.parentNode) {
		this.renderer.view.parentNode.appendChild(this.div);
	}
};

export const deactivateUpdate = function (): void {
	if (!this.isActive || this.isMobileAccessibility) {
		return;
	}

	this.isActive = false;

	window.document.removeEventListener("mousemove", this._onMouseMove, true);
	window.document.removeEventListener("touchmove", this.deactivate, true);
	window.addEventListener("keydown", this._onKeyDown, false);

	this.renderer.off("postrender", this.update);

	if (this.div.parentNode) {
		this.div.parentNode.removeChild(this.div);
	}
};
