import * as PIXI from "pixi.js";
import {
	Container,
	Texture,
	extras,
	mesh,
	SHAPES,
	Rectangle,
	RoundedRectangle,
	Ellipse,
	Circle,
	Polygon,
} from "pixi.js";
import { Tools, Loader } from "@gdk/core";
import type { Sprite, Graphics } from "pixi.js";

import { CommonImporter } from "./commonImporter";
import { TextureImporter } from "./textureImporter";
import { Docking } from "../../container/docking";
import { DockingBounds } from "../../container/dockingBounds";
import { Resize } from "../../container/resize";
import { Layout } from "../../container/layout";
import { editorStore } from "../../decorators/editor";
import { loaderStore } from "../../decorators/loader";
import type { IEditorDecoratorObject } from "../../decorators/editor";
import type { ILoaderDecoratorObject } from "../../decorators/loader";
import type {
	IProjectContainer,
	IProjectSprite,
	IProjectAnimatedSprite,
	IProjectTilingSprite,
	IProject,
	IProjectNineSlicePlane,
	IProjectGraphics,
	IProjectRoundedRectangle,
	IProjectRectangle,
	IProjectEllipse,
	IProjectCircle,
	IProjectPolygon,
	IProjectContainerTransform,
} from "../model";
import type { GamePixi } from "../../gamePixi";

export enum EContainerImporterMode {
	/**
	 * Typically used by the editor.
	 */
	Dev = "dev",
	/**
	 * Production mode when importing a container.
	 */
	Prod = "prod",
}

/**
 * Defines the template used by
 */
const scriptTemplate = `
var require = SystemJS.amdRequire;
var exports = exports || { };
{{code}}
SystemJS.set("{{id}}", SystemJS.newModule(exports));
`;

export class ContainerImporter {
	/**
	 * Custom container creator. Should be use to create custom
	 * containers with custom types like ui.TextField etc.
	 */
	public static customContainerCreator: (
		config: IProjectContainer,
		container?: Container,
		project?: IProject
	) => Container;

	/**
	 * Defines the mode when importing a container. Prod = import scripts etc. Dev = just create a new container.
	 */
	public static mode: EContainerImporterMode = EContainerImporterMode.Prod;

	/**
	 * Custom container modeler. Once the container is created, just call
	 * this function to customize more the container (metadatas, etc.)
	 */
	public static customContainerCustomizer: (
		config: IProjectContainer,
		container: Container
	) => void;

	/**
	 * Imports the given config by checking the type
	 * @param config the container configuration
	 * @param baseUrl the base url of the loader
	 * @param container the container reference. If not exists, will be created
	 */
	public static import<T extends Container>(
		config: IProjectContainer,
		baseUrl: string,
		container?: Container,
		project?: IProject
	): Container {
		// If no container, let's try to create from a script if attached (starting from Editor vNext).
		if (!container) {
			container = this.getContainerFromScript(config, baseUrl);
		}

		// If no container, let's create it.
		if (!container) {
			switch (config.type) {
				case "extras.AnimatedSprite":
					container = new extras.AnimatedSprite([Texture.WHITE]);
					break;
				case "extras.TilingSprite":
					container = new extras.TilingSprite(Texture.WHITE, 1, 1);
					break;
				case "mesh.NineSlicePlane":
					container = new mesh.NineSlicePlane(Texture.WHITE, 0, 0, 0, 0);
					break;

				case "Container":
				case "Sprite":
				case "Graphics":
					container = new PIXI[config.type]();
					break;

				default:
					if (this.customContainerCreator) {
						container = this.customContainerCreator(config, container, project);
					}

					if (!container) {
						throw new Error(`The container type ${config.type} is unknown`);
					}
			}
		} else {
			// If exists, call parse for custom container creators
			if (this.customContainerCreator) {
				this.customContainerCreator(config, container, project);
			}
		}

		if (config.type === "extras.AnimatedSprite") {
			return this.importAnimatedSprite(config as IProjectAnimatedSprite, baseUrl, container as any);
		}

		if (config.type === "extras.TilingSprite") {
			return this.importTilingSprite(config as IProjectTilingSprite, baseUrl, container as any);
		}

		if (config.type === "Sprite") {
			return this.importSprite(config as IProjectSprite, baseUrl, container as any);
		}

		if (config.type === "mesh.NineSlicePlane") {
			return this.importNineSlicePlane(config as IProjectNineSlicePlane, baseUrl, container as any);
		}

		if (config.type === "Graphics") {
			return this.importGraphics(config as IProjectGraphics, baseUrl, container as any);
		}

		return this.importContainer(config as IProjectContainer, baseUrl, container, project);
	}

	/**
	 * Import a container from the given configuration
	 * @param config the container configuration
	 * @param baseUrl the base url of the loader
	 * @param container the container reference. If not exists, will be created
	 */
	public static importContainer<T extends Container>(
		config: IProjectContainer,
		baseUrl: string,
		container: Container,
		project?: IProject,
		game?: GamePixi
	): T {
		// Configure the container
		this.configureContainer(config, container, baseUrl);

		// Configure script properties, if attached, for the editor.
		if (this.mode !== EContainerImporterMode.Prod) {
			this._configureScriptProperties(container, baseUrl);
		}

		const decoratorTarget = game || container;
		const ctor = Tools.getConstructorName(decoratorTarget);

		// Children
		config.children.forEach((c) => {
			let target: Container = null;
			if (editorStore[ctor]) {
				const store = editorStore[ctor].find((s) => s.path === c.name);
				if (store) {
					target = container[store.propertyKey];
				}
			}

			if (!target) {
				target = container[c.name];
			}

			const child = this.import(
				c,
				baseUrl,
				target instanceof Container ? target : undefined,
				project
			);

			// The user precised the component as null, then assign it
			if (target === null) {
				container[c.name] = child;
			}

			// The game has been precised, typically while importing the stage
			// then, assign the child if null to the game instance
			if (game && game[c.name] === null) {
				game[c.name] = child;
			}

			container.addChild(child);
		});

		// Customize
		if (this.customContainerCustomizer) {
			this.customContainerCustomizer(config, container);
		}

		// Layouts
		if (config.layouts) {
			container.layouts = Layout.parse(container, config.layouts);
		}

		// @fromLoader decorators
		if (loaderStore[ctor]) {
			this.getFromLoaderDecorators(loaderStore[ctor], container, decoratorTarget);
		}

		// @fromEditor decorators
		if (editorStore[ctor]) {
			this.getFromEditorDecorators(editorStore[ctor], container, decoratorTarget);
		}

		// Notify parsed
		if (container.onLoaded) {
			container.onLoaded();
		}

		return container as T;
	}

	/**
	 * Gets the references from the loader for the given decorated properties of the given target.
	 * @param store the store containing the decorated properties informations.
	 * @param container the container being parsed.
	 * @param target the target object to put reference in it. (typically a game or a container).
	 */
	public static getFromLoaderDecorators(
		store: ILoaderDecoratorObject[],
		container: Container,
		target: GamePixi | Container
	): void {
		const ctor = Tools.getConstructorName(target);

		store.forEach((s) => {
			const name = s.name.toString();
			const data = Loader.get(name);
			if (!data) {
				// Notify user that his "@fromLoader(name)" is wrong.
				console.warn(
					`Retrieved data from loader "${name}" in class ${ctor} has value ${data}. Please verify the key in the files.json file.`
				);
			} else {
				target[s.propertyKey] = data;
			}
		});
	}

	/**
	 * Gets the container references for the given decorated properties of the given target.
	 * @param store the store containing the decorated properties informations.
	 * @param container the container being parsed.
	 * @param target the target object to put reference in it. (typically a game or a container).
	 */
	public static getFromEditorDecorators(
		store: IEditorDecoratorObject[],
		container: Container,
		target: GamePixi | Container
	): void {
		const ctor = Tools.getConstructorName(target);

		store.forEach((s) => {
			const path = s.path.toString();
			const split = path.split(".");

			let effectiveContainer = container;
			for (let i = 0, len = split.length; i < len; i++) {
				effectiveContainer = effectiveContainer.getChildByName(split[i]);
				if (!effectiveContainer) {
					// Notify user that his "@fromEditor(path)" is wrong.
					console.warn(
						`Failed to retrieve container in "${ctor}" with path ${path}. Please verify the hierarchy in your editor project.`
					);
					break;
				}
			}

			target[s.propertyKey] = effectiveContainer;
		});
	}

	/**
	 * Returns the newly created container in case a script is attached to.
	 * Starting from Editor vNext.
	 * @param config the container's configuration.
	 * @param baseUrl the base url of the loader
	 */
	public static getContainerFromScript(config: IProjectContainer, baseUrl: string): Container {
		// Check if the script should be injected
		if (this.mode !== EContainerImporterMode.Prod) {
			return;
		}

		const script = (config.metadata || {}).script;
		if (!script) {
			return null;
		}

		// Inject script and configure container
		const scriptName = script.name.replace(`.${Tools.getExtension(script.name)}`, ".js");
		const content = Loader.get<string>(script.name);
		if (!content) {
			return null;
		}

		Tools.injectScript(
			scriptTemplate.replace("{{code}}", content).replace("{{id}}", scriptName),
			scriptName,
			"scripts"
		);
		const exports = SystemJS.get(scriptName);
		if (!exports.default) {
			return null;
		}

		const container = new exports.default();
		if (!container) {
			return null;
		}

		// Set properties
		container.metadata = config.metadata;
		this._configureScriptProperties(container, baseUrl);

		for (const propertyKey in script.properties) {
			if (!script.properties.hasOwnProperty(propertyKey)) {
				continue;
			}
			container[propertyKey] = script.properties[propertyKey];
		}

		delete container.metadata.script;
		return container;
	}

	/**
	 * Configures the properties of the script attached to the container.
	 * @param config the container's configuration.
	 * @param baseUrl the base url of the loader
	 */
	private static _configureScriptProperties(
		container: Container | IProjectContainer,
		baseUrl: string = ""
	): void {
		const script = (container.metadata || {}).script;
		if (!script) {
			return null;
		}

		// Configure properties
		for (const p in script.properties) {
			if (!script.properties.hasOwnProperty(p)) {
				continue;
			}

			const property = script.properties[p];
			switch (property.type) {
				case "point":
					script.properties[p] = CommonImporter.importPoint(property.value);
					break;
				case "rectangle":
					script.properties[p] = CommonImporter.importRectangle(property.value);
					break;
				case "texture":
					script.properties[p] = TextureImporter.importTexture(property.value, baseUrl);
					break;
				default:
					script.properties[p] = property.value;
					break;
			}
		}
	}

	/**
	 * Configures the given container according to the given configuration
	 * @param config the container configuration
	 * @param container the container to configure using the config object
	 * @param baseUrl the base url of the loader
	 */
	public static configureContainer(
		config: IProjectContainer,
		container: Container,
		baseUrl?: string
	): void {
		// Transforms
		this.configureContainerTransform(config, container);

		// Container data
		Tools.assign(container, {
			name: config.name,

			visible: config.visible,

			alpha: config.alpha,
			worldAlpha: config.worldAlpha,

			buttonMode: config.buttonMode,
			interactive: config.interactive,

			metadata: config.metadata,
		});
	}

	/**
	 * Configures the given container according to the given transform configuration.
	 * @param config the container's transformation configuration.
	 * @param container the container to configure using the configuration object.
	 */
	public static configureContainerTransform(
		config: IProjectContainerTransform,
		container: Container
	): void {
		Tools.assign(container, {
			x: config.x || container.x,
			y: config.y || container.y,

			rotation: config.rotation,

			pivot: { x: config.pivot.x, y: config.pivot.y },
		});
		if (config.scaleX) {
			Object.assign(container, { scaleX: config.scaleX });
		}
		if (config.scaleY) {
			Object.assign(container, { scaleY: config.scaleY });
		}

		// Resize
		if (config.resize) {
			container.resize = Resize.parse(config.resize, container.resize);
		}

		// Docking
		if (config.dockingBounds) {
			container.dockingBounds = DockingBounds.parse(config.dockingBounds, container.dockingBounds);
		}

		if (config.docking) {
			container.docking = Docking.parse(config.docking, container.docking);
		}
	}

	/**
	 * Import a sprite from the given configuration
	 * @param config the sprite configuration
	 * @param baseUrl the base url of the loader
	 * @param sprite the sprite reference
	 */
	public static importSprite(config: IProjectSprite, baseUrl: string, sprite?: Sprite): Sprite {
		sprite = this.importContainer<Sprite>(config, baseUrl, sprite);
		sprite.texture = TextureImporter.importTexture(config.sprite.texture, baseUrl, sprite);

		return sprite;
	}

	/**
	 * Import an animated sprite from the given configuration
	 * @param config the animated sprite configuration
	 * @param baseUrl the base url of the loader
	 * @param sprite the animated sprite reference
	 */
	public static importAnimatedSprite(
		config: IProjectAnimatedSprite,
		baseUrl: string,
		sprite?: extras.AnimatedSprite
	): extras.AnimatedSprite {
		sprite = this.importContainer<extras.AnimatedSprite>(config, baseUrl, sprite);

		if (config.animatedSprite.textures.length === 0) {
			sprite.textures = [Texture.WHITE];
			// we should use logger for that @TODO
			console.warn(`animatedSprite "${config.name}" had no texture. Used 16x16 white texture.`);
		} else if (config.animatedSprite.durations) {
			sprite.textures = config.animatedSprite.textures.map((t, index) => ({
				texture: TextureImporter.importTexture(t, baseUrl),
				time: config.animatedSprite.durations[index],
			}));
		} else {
			sprite.textures = config.animatedSprite.textures.map((t) =>
				TextureImporter.importTexture(t, baseUrl)
			);
		}

		if (config.animatedSprite.animationSpeed !== undefined) {
			sprite.animationSpeed = config.animatedSprite.animationSpeed;
		}

		if (config.animatedSprite.loop !== undefined) {
			sprite.loop = config.animatedSprite.loop;
		}

		return sprite;
	}

	/**
	 * Import a tiling sprite from the given configuration
	 * @param config the tiling sprite configuration
	 * @param baseUrl the base url of the loader
	 * @param sprite the tiling sprite reference
	 */
	public static importTilingSprite(
		config: IProjectTilingSprite,
		baseUrl: string,
		sprite?: extras.TilingSprite
	): extras.TilingSprite {
		sprite = this.importContainer<extras.TilingSprite>(config, baseUrl, sprite);
		sprite.texture = TextureImporter.importTexture(config.tilingSprite.texture, baseUrl);

		Tools.assign(sprite, {
			width: config.tilingSprite.width,
			height: config.tilingSprite.height,
			uvRespectAnchor: config.tilingSprite.uvRespectAnchor,
			clampMargin: config.tilingSprite.clampMargin,
			tileScale: config.tilingSprite.tileScale,
			tilePosition: config.tilingSprite.tilePosition,
		});

		return sprite;
	}

	/**
	 * Import a nine slice plane from the given configuration
	 * @param config the nine slice plane configuration
	 * @param baseUrl the base url of the loader
	 * @param nineSlicePlane the nine slice plane reference
	 */
	public static importNineSlicePlane(
		config: IProjectNineSlicePlane,
		baseUrl: string,
		nineSlicePlane?: mesh.NineSlicePlane
	): mesh.NineSlicePlane {
		nineSlicePlane = this.importContainer<mesh.NineSlicePlane>(config, baseUrl, nineSlicePlane);
		nineSlicePlane.texture = TextureImporter.importTexture(
			config.nineSlicePlane.texture,
			baseUrl,
			nineSlicePlane
		);

		Tools.assign(nineSlicePlane, {
			width: config.nineSlicePlane.width || 1,
			height: config.nineSlicePlane.height || 1,
			leftWidth: config.nineSlicePlane.leftWidth,
			rightWidth: config.nineSlicePlane.rightWidth,
			topHeight: config.nineSlicePlane.topHeight,
			bottomHeight: config.nineSlicePlane.bottomHeight,
		});

		return nineSlicePlane;
	}

	/**
	 * Import a graphics from the given configuration
	 * @param config the graphics configuration
	 * @param baseUrl the base url of the loader
	 * @param graphics the graphics reference
	 */
	public static importGraphics(
		config: IProjectGraphics,
		baseUrl: string,
		graphics?: Graphics
	): Graphics {
		graphics = this.importContainer<Graphics>(config, baseUrl, graphics);

		// Graphics datas
		config.graphicsDatas.forEach((gd) => {
			// Unsupported?
			if (!gd.shape) {
				return;
			}

			graphics.beginFill(gd.fillColor, gd.fillAlpha);
			switch (gd.shape.type) {
				case SHAPES.RECT:
					const rect = gd.shape as IProjectRectangle;
					graphics.drawShape(new Rectangle(rect.x, rect.y, rect.width, rect.height));
					break;
				case SHAPES.RREC:
					const rrect = gd.shape as IProjectRoundedRectangle;
					graphics.drawShape(
						new RoundedRectangle(rrect.x, rrect.y, rrect.width, rrect.height, rrect.radius)
					);
					break;
				case SHAPES.ELIP:
					const elip = gd.shape as IProjectEllipse;
					graphics.drawShape(new Ellipse(elip.x, elip.y, elip.width, elip.height));
					break;
				case SHAPES.CIRC:
					const circ = gd.shape as IProjectCircle;
					graphics.drawShape(new Circle(circ.x, circ.y, circ.radius));
					break;
				case SHAPES.POLY:
					const poly = gd.shape as IProjectPolygon;
					graphics.drawShape(new Polygon(poly.points));
					break;
			}
			graphics.endFill();
		});

		return graphics;
	}
}
