import { Container, Sprite, mesh } from "pixi.js";
import { Tools } from "@gdk/core";
import type { Texture } from "pixi.js";

import { Docking } from "../../../container/docking";
import { ContainerExporter } from "../../../loading/export/containerExporter";
import { TextureExporter } from "../../../loading/export/textureExporter";
import { TextureImporter } from "../../../loading/import/textureImporter";
import { ContainerImporter } from "../../../loading/import/containerImporter";
import { Button } from "./button";
import { DockingBounds } from "../../../container/dockingBounds";
import type { IButtonStyle } from "./button";

export interface IPopupStyle {
	/**
	 * The background texture reference or the texture name
	 */
	backgroundTexture?: string | Texture;
	/**
	 * The close button reference of the style of the button
	 */
	closeButtonStyle?: IButtonStyle | Button;
}

export class Popup extends Container {
	/**
	 * The background sprite reference when the popup's background is using a sprite
	 */
	public backgroundSprite: Sprite = null;
	/**
	 * The background nine slice reference when the popup's background is using a nine slice plane
	 */
	public backgroundNineSlice: mesh.NineSlicePlane = null;
	/**
	 * The top-right button used to close the popup
	 */
	public closeButton: Button;

	/**
	 * Gets wether or not the popup's background is using a nine slice plane
	 */
	protected _isNineSlice: boolean = false;

	/**
	 * Constructor
	 * @param style the style of the popup
	 */
	public constructor(style: IPopupStyle = {}) {
		super();

		// Background
		this.backgroundSprite =
			typeof style.backgroundTexture === "string"
				? Sprite.from(style.backgroundTexture)
				: new Sprite(style.backgroundTexture);
		this.background.name = "Background";
		this.background.doNotExport = true;
		this.addChild(this.background);

		// Close button
		this.closeButton =
			style.closeButtonStyle instanceof Button
				? style.closeButtonStyle
				: new Button(style.closeButtonStyle || {});
		this.closeButton.docking = new Docking(Docking.topRight);
		this.closeButton.doNotExport = true;
		this.addChild(this.closeButton);

		// Misc.
		this.dockingBounds = new DockingBounds(
			this.background.texture.width,
			this.background.texture.height
		);
	}

	/**
	 * Gets the sprite background when button.isNineSlice is set to "false" and returns the nine slice background
	 * when button.isNineSlice is set to "true"
	 */
	public get background(): Sprite | mesh.NineSlicePlane {
		return this._isNineSlice ? this.backgroundNineSlice : this.backgroundSprite;
	}

	/**
	 * Gets the current texture of the background
	 */
	public get backgroundTexture(): Texture {
		return this.background.texture;
	}

	/**
	 * Sets the current texture of the background
	 */
	public set backgroundTexture(texture: Texture) {
		this.background.texture = texture;
	}

	/**
	 * Gets wether or not the button is set to nine slice mode
	 */
	public get isNineSlice(): boolean {
		return this._isNineSlice;
	}

	/**
	 * Sets wether or not the button is set to nine slice mode
	 */
	public set isNineSlice(enabled: boolean) {
		if (enabled === this._isNineSlice) {
			return;
		}

		this._isNineSlice = enabled;

		// Keep background position in this.children
		const currentIndex = this.getChildIndex(this.backgroundSprite || this.backgroundNineSlice);
		const config = ContainerExporter.exportContainer(
			this.backgroundSprite || this.backgroundNineSlice
		);

		// Build
		if (enabled) {
			this.backgroundNineSlice = new mesh.NineSlicePlane(this.backgroundSprite.texture);
			this.backgroundNineSlice.doNotExport = true;
			this.backgroundNineSlice.id = this.backgroundSprite.id;

			this.backgroundSprite.destroy();
			this.backgroundSprite = null;
		} else {
			this.backgroundSprite = new Sprite(this.backgroundNineSlice.texture);
			this.backgroundSprite.doNotExport = true;
			this.backgroundSprite.id = this.backgroundNineSlice.id;

			this.backgroundNineSlice.destroy();
			this.backgroundNineSlice = null;

			this.dockingBounds.setBounds(this.backgroundSprite.width, this.backgroundSprite.height);
		}

		// Configure
		ContainerImporter.configureContainer(config, this.backgroundSprite || this.backgroundNineSlice);

		// Add child
		this.addChildAt(this.backgroundSprite || this.backgroundNineSlice, currentIndex);
	}

	/**
	 * Gets the current width of the popup
	 */
	// remove this comment when allow-override-transient-properties is included in typescript
	// @ts-ignore
	public get width(): number {
		return this.background.width;
	}

	/**
	 * Sets the current width of the popup. Should be used only when isNineSlice is set to "true"
	 */
	// remove this comment when allow-override-transient-properties is included in typescript
	// @ts-ignore
	public set width(width: number) {
		if (!this.backgroundNineSlice) {
			return;
		}
		this.backgroundNineSlice.width = width;
		this.dockingBounds.width = width;
	}

	/**
	 * Gets the current height of the popup
	 */
	// remove this comment when allow-override-transient-properties is included in typescript
	// @ts-ignore
	public get height(): number {
		return this.background.height;
	}

	/**
	 * Sets the current height of the popup. Should be used only when isNineSlice is set to "true"
	 */
	// remove this comment when allow-override-transient-properties is included in typescript
	// @ts-ignore
	public set height(height: number) {
		if (!this.backgroundNineSlice) {
			return;
		}
		this.backgroundNineSlice.height = height;
		this.dockingBounds.height = height;
	}

	/**
	 * Export the popup
	 */
	public export(): any {
		const background = this._isNineSlice
			? ContainerExporter.exportNineSlicePlane(this.backgroundNineSlice)
			: ContainerExporter.exportSprite(this.backgroundSprite);

		return Tools.assign(ContainerExporter.exportContainer(this), {
			type: "ui.Popup",
			isNineSlice: this._isNineSlice,
			background,
			style: {
				backgroundTexture: TextureExporter.exportTexture(
					(this.backgroundNineSlice || this.backgroundSprite).texture
				),
				closeButtonStyle: this.closeButton ? this.closeButton.export() : undefined,
			},
		});
	}

	/**
	 * Parses the textfield
	 * @param config the serialized data
	 * @param baseUrl the base url of the loader
	 * @param popup the already created popup
	 */
	public static parse(config: any, baseUrl: string, popup?: Popup): Popup {
		if (!popup) {
			popup = new Popup();
		}

		popup.backgroundSprite.texture = TextureImporter.importTexture(
			config.style.backgroundTexture,
			baseUrl
		);
		popup.dockingBounds = new DockingBounds(popup.background.width, popup.background.height);

		if (config.style.closeButtonStyle) {
			popup.closeButton = ContainerImporter.import(
				config.style.closeButtonStyle,
				baseUrl,
				popup.closeButton
			) as Button;
		}

		if (config.isNineSlice) {
			popup.isNineSlice = true;
			ContainerImporter.import(config.background, baseUrl, popup.backgroundNineSlice);
			popup.dockingBounds.setBounds(popup.width, popup.height);
		} else if (config.background) {
			ContainerImporter.import(config.background, baseUrl, popup.backgroundSprite);
		}

		return popup;
	}
}
