import {
	Circle,
	Ellipse,
	extras,
	Graphics,
	mesh,
	Polygon,
	Rectangle,
	RoundedRectangle,
	Sprite,
} from "pixi.js";
import { Tools } from "@gdk/core";
import type { Container, Texture, GraphicsData } from "pixi.js";

import { ContainerView } from "../../view/containerView";
import { LoadedView } from "../../view/loadedView";
import { TextureExporter } from "./textureExporter";
import { CommonExporter } from "./commonExporter";
import type {
	IProjectAnimatedSprite,
	IProjectContainer,
	IProjectContainerTransform,
	IProjectGraphics,
	IProjectNineSlicePlane,
	IProjectSprite,
	IProjectTilingSprite,
} from "../model";

export class ContainerExporter {
	/**
	 * Export the given container
	 * @param container the container to export
	 * @param noChildren if to not export children
	 */
	public static export(container: Container, noChildren: boolean = false): IProjectContainer {
		if (
			container.doNotExport ||
			(noChildren && (container instanceof ContainerView || container instanceof LoadedView))
		) {
			return null;
		}

		// Has export function ?
		if (container.export) {
			return container.export();
		}

		// Animated Sprite
		if (container instanceof extras.AnimatedSprite) {
			return this.exportAnimatedSprite(container, noChildren);
		}

		// Tiling Sprite
		if (container instanceof extras.TilingSprite) {
			return this.exportTilingSprite(container, noChildren);
		}

		// Sprite
		if (container instanceof Sprite) {
			return this.exportSprite(container, noChildren);
		}

		// Nine slice plane
		if (container instanceof mesh.NineSlicePlane) {
			return this.exportNineSlicePlane(container, noChildren);
		}

		// Graphivs
		if (container instanceof Graphics) {
			return this.exportGraphics(container, noChildren);
		}

		return this.exportContainer(container, noChildren);
	}

	/**
	 * Exports the given container
	 * @param container the container to export
	 * @param noChildren if to not export children
	 */
	public static exportContainer(
		container: Container,
		noChildren: boolean = false
	): IProjectContainer {
		// Children
		const children: IProjectContainer[] = [];

		container.children.forEach((c) => {
			const child = this.export(c as Container, noChildren);
			if (child) {
				children.push(child);
			}
		});

		// Common
		const script = container.metadata ? container.metadata.script : null;
		if (script) {
			delete container.metadata.script;
		}

		// Apply default layout
		if (container.layouts) {
			container.layouts.saveCurrentLayoutConfiguration();
		}

		// Export container
		const result = Tools.assign(this.exportContainerConfiguration(container), {
			children,
			layouts: container.layouts ? container.layouts.export() : undefined,
			metadata: Tools.assign({}, container.metadata),
		});

		// If script attached, convert properties
		if (script) {
			container.metadata.script = script;
			result.metadata.script = {
				name: script.name,
				properties: {},
			};

			for (const p in container.metadata.script.properties) {
				if (!container.metadata.script.properties.hasOwnProperty(p)) {
					continue;
				}

				const value = container.metadata.script.properties[p];
				const type = Tools.getConstructorName(value).toLowerCase();

				switch (type) {
					case "point":
						result.metadata.script.properties[p] = {
							type,
							value: CommonExporter.exportPoint(value),
						};
						break;
					case "rectangle":
						result.metadata.script.properties[p] = {
							type,
							value: CommonExporter.exportRectangle(value),
						};
						break;
					case "texture":
						result.metadata.script.properties[p] = {
							type,
							value: TextureExporter.exportTexture(value),
						};
						break;
					default:
						result.metadata.script.properties[p] = { type, value };
						break;
				}
			}
		}

		// Interactive and button mode
		if (!container.metadata) {
			return result;
		}

		if (container.metadata.interactive !== undefined) {
			result.interactive = container.metadata.interactive;
		}

		if (container.metadata.buttonMode !== undefined) {
			result.buttonMode = container.metadata.buttonMode;
		}

		return result;
	}

	/**
	 * Exports the given container's configuration.
	 * @param container the container to export its configuration.
	 */
	public static exportContainerConfiguration(container: Container): IProjectContainer {
		return Tools.assign(this.exportContainerTransforms(container), {
			type: "Container",

			visible: container.visible,

			alpha: container.alpha,
			worldAlpha: container.worldAlpha,

			buttonMode: container.buttonMode,
			interactive: container.interactive,

			layouts: undefined,
			metadata: undefined,
		}) as IProjectContainer;
	}

	/**
	 * Exports the transformation configuration of the given container.
	 * @param container the container to export its transformtion configuration.
	 */
	public static exportContainerTransforms(container: Container): IProjectContainerTransform {
		return {
			name: container.name,

			x: container.docking ? undefined : container.x,
			y: container.docking ? undefined : container.y,

			rotation: container.rotation,

			scaleX: container.resize ? 1 : container.scaleX,
			scaleY: container.resize ? 1 : container.scaleY,

			pivot: { x: container.pivot.x, y: container.pivot.y },

			dockingBounds: container.dockingBounds ? container.dockingBounds.export() : undefined,
			docking: container.docking ? container.docking.export() : undefined,
			resize: container.resize ? container.resize.export() : undefined,
		};
	}

	/**
	 * Exports the given sprite
	 * @param sprite the sprite to export
	 * @param noChildren if to not export children
	 */
	public static exportSprite(sprite: Sprite, noChildren: boolean = false): IProjectSprite {
		return Tools.assign(this.exportContainer(sprite, noChildren), {
			type: "Sprite",
			sprite: {
				texture: TextureExporter.exportTexture(sprite.texture),
			},
		});
	}

	/**
	 * Exports the given animated sprite
	 * @param sprite the animated sprite to export
	 * @param noChildren if to not export children
	 */
	public static exportAnimatedSprite(
		sprite: extras.AnimatedSprite,
		noChildren: boolean = false
	): IProjectAnimatedSprite {
		return Tools.assign(this.exportContainer(sprite, noChildren), {
			type: "extras.AnimatedSprite",
			animatedSprite: {
				loop: sprite.loop,
				animationSpeed: sprite.animationSpeed,
				textures: (sprite.textures as Texture[]).map((t) => TextureExporter.exportTexture(t)),
				durations: (sprite as any)._durations,
			},
		});
	}

	/**
	 * Exports the given tiling sprite
	 * @param sprite the tiling sprite to export
	 * @param noChildren if to not export children
	 */
	public static exportTilingSprite(
		sprite: extras.TilingSprite,
		noChildren: boolean = false
	): IProjectTilingSprite {
		return Tools.assign(this.exportContainer(sprite, noChildren), {
			type: "extras.TilingSprite",
			tilingSprite: {
				width: (sprite as any)._width,
				height: (sprite as any)._height,
				texture: TextureExporter.exportTexture(sprite.texture),
				uvRespectAnchor: sprite.uvRespectAnchor,
				clampMargin: sprite.clampMargin,
				tileScale: CommonExporter.exportPoint(sprite.tileScale),
				tilePosition: CommonExporter.exportPoint(sprite.tilePosition),
			},
		});
	}

	/**
	 * Exports the given nine slice plane
	 * @param nineSlicePlane the nine slice plane to export
	 * @param noChildren if to not export children
	 */
	public static exportNineSlicePlane(
		nineSlicePlane: mesh.NineSlicePlane,
		noChildren: boolean = false
	): IProjectNineSlicePlane {
		return Tools.assign(this.exportContainer(nineSlicePlane, noChildren), {
			type: "mesh.NineSlicePlane",
			nineSlicePlane: {
				texture: TextureExporter.exportTexture(nineSlicePlane.texture),
				width: nineSlicePlane.width,
				height: nineSlicePlane.height,
				leftWidth: nineSlicePlane.leftWidth,
				rightWidth: nineSlicePlane.rightWidth,
				topHeight: nineSlicePlane.topHeight,
				bottomHeight: nineSlicePlane.bottomHeight,
			},
		});
	}

	/**
	 * Exports the given graphics
	 * @param graphics the graphics to export
	 * @param noChildren if to not export children
	 */
	public static exportGraphics(graphics: Graphics, noChildren: boolean = false): IProjectGraphics {
		return Tools.assign(this.exportContainer(graphics, noChildren), {
			type: "Graphics",
			graphicsDatas: (graphics as any).graphicsData.map((d: GraphicsData) => ({
				lineWidth: d.lineWidth,
				lineColor: d.lineColor,
				lineAlpha: d.lineAlpha,
				fillColor: d.fillColor,
				fillAlpha: d.fillAlpha,
				fill: d.fill,
				nativeLines: d.nativeLines,
				shape:
					d.shape instanceof Rectangle
						? CommonExporter.exportRectangle(d.shape)
						: d.shape instanceof RoundedRectangle
						? CommonExporter.exportRoundedRectangle(d.shape)
						: d.shape instanceof Ellipse
						? CommonExporter.exportEllipse(d.shape)
						: d.shape instanceof Circle
						? CommonExporter.exportCircle(d.shape)
						: d.shape instanceof Polygon
						? CommonExporter.exportPolygon(d.shape)
						: null,
			})),
		});
	}
}
