import { Container } from "pixi.js";
import { Tween, TweenMax, Linear, Power2 } from "gsap";
import type { Ease } from "gsap";

import { StackedContainer } from "./stackedContainer";

/**
 * Interface for Stackable component initialization.
 * @property {Container[]} stack   An exisiting container Array to transfer to the Stackable stack.
 * @property {string} direction         The direction to where the stacks grows.
 * @property {number} spacing           The amount of space in pixels between stack elements.
 */
export interface IStackableOptions<T extends Container> {
	stack?: T[];
	direction?: "up" | "down" | "left" | "right";
	spacing?: number;
	operationTween?: IStackableTween;
	operationOffset?: IStackableOffset;
}

export interface IStackableTween {
	duration: number;
	ease: Ease;
	fade: boolean;
}

export interface IStackableOffset {
	parralel: number;
	perpendicular: number;
}

/**
 * @class A stack of containers that updates itself when changed.
 */
export class Stackable<T extends Container> extends Container {
	private _stack: Array<StackedContainer<T>>;
	private _spacing: number;
	private _direction: "up" | "down" | "left" | "right";
	public operationTween: IStackableTween;
	public operationOffset: IStackableOffset;

	private static DEFAULT_TWEEN: IStackableTween = {
		duration: 0,
		ease: Linear.easeNone,
		fade: false,
	};

	/**
	 * Constructs a new Stackable component.
	 * @param {IStackableOptions} options Options object to initialize.
	 */
	public constructor(options: IStackableOptions<T> = {}) {
		super();
		this._stack = new Array<StackedContainer<T>>();
		this._direction = options.direction || "up";
		this._spacing = options.spacing || 0;
		this.operationOffset = options.operationOffset || { parralel: 0, perpendicular: 0 };
		this.operationTween = options.operationTween || Stackable.DEFAULT_TWEEN;

		if (options.stack) {
			options.stack.forEach((el) => {
				this.insertAt(el, 0, false);
			});
		}
	}

	/**
	 * Re-stack the elements together.
	 * Called by public functions that modify the stack.
	 */
	private _update(tween: boolean = true): void {
		let offset = 0;
		for (const element of this._stack) {
			element.offset = offset;
			offset += this._getGrowthDimension(element.container);
		}

		const mult = this._getGrowthDirection();

		this._stack.forEach((el, index) => {
			TweenMax.to(el.container, tween ? this.operationTween.duration : 0, {
				x: (el.offset + this._spacing * index) * mult.x,
				y: (el.offset + this._spacing * index) * mult.y,
				ease: this.operationTween.ease,
			});
		});
	}

	private _getGrowthDimension(container: T): number {
		if (this.direction === "up" || this.direction === "down") {
			return container.height;
		}
		return container.width;
	}

	private _getGrowthDirection(): { x: number; y: number } {
		switch (this._direction) {
			case "up":
				return { x: 0, y: -1 };
			case "down":
				return { x: 0, y: 1 };
			case "left":
				return { x: -1, y: 0 };
			case "right":
				return { x: 1, y: 0 };
		}
	}

	private _getPerpendicularDirection(): { x: number; y: number } {
		switch (this._direction) {
			case "up":
				return { x: 1, y: 0 };
			case "down":
				return { x: -1, y: 0 };
			case "left":
				return { x: 0, y: -1 };
			case "right":
				return { x: 0, y: 1 };
		}
	}

	private _offsetToVector(offset: IStackableOffset): { x: number; y: number } {
		const vector = { x: 0, y: 0 };

		const parralelDirection = this._getGrowthDirection();
		vector.x = parralelDirection.x * offset.parralel;
		vector.y = parralelDirection.y * offset.parralel;

		const perpendicularDirection = this._getPerpendicularDirection();
		vector.x += perpendicularDirection.x * offset.perpendicular;
		vector.y += perpendicularDirection.y * offset.perpendicular;

		return vector;
	}

	/**
	 * Pushes a container on top of the stack.
	 * @param container An existing container to push on the stack.
	 */
	public push(container: T, tween?: boolean, cb?: (T: T) => void): void {
		this.insertAt(container, this._stack.length, tween, cb);
	}

	/**
	 * Removes the last container of the stack and returns it.
	 * @returns The container that was removed.
	 */
	public pop(tween?: boolean, cb?: (T: T) => void): T {
		return this.removeAt(this._stack.length - 1, tween, cb);
	}

	/**
	 * Insert a new container at the specified index.
	 * @param container An existing container to insert in the stack.
	 * @param index The index to where the container is inserted.
	 */
	public insertAt(container: T, index: number, tween?: boolean, cb?: (T: T) => void): void {
		const sc = new StackedContainer<T>(container);
		sc.container.alpha = 0;
		TweenMax.to(sc.container, this.operationTween.fade ? this.operationTween.duration : 0, {
			alpha: 1,
			ease: this.operationTween.ease,
		});

		let targetContainer: T;
		if (index > 0) {
			targetContainer = this._stack[index - 1].container;
			sc.container.x =
				targetContainer.position.x +
				this._offsetToVector(this.operationOffset).x +
				this._getGrowthDirection().x * (this._getGrowthDimension(targetContainer) + this._spacing);
			sc.container.y =
				targetContainer.position.y +
				this._offsetToVector(this.operationOffset).y +
				this._getGrowthDirection().y * (this._getGrowthDimension(targetContainer) + this._spacing);
		} else {
			sc.container.x = this._offsetToVector(this.operationOffset).x;
			sc.container.y = this._offsetToVector(this.operationOffset).y;
		}
		this._stack.splice(index, 0, sc);
		this.addChild(sc.container);
		this._update(tween);
		if (cb !== undefined) {
			cb(container);
		}
	}

	/**
	 * Removes a container from the stack at the specified index.
	 * @param index The index from where the container is removed.
	 * @returns The removed container.
	 */
	public removeAt(index: number, tween?: boolean, cb?: (T: T) => void): T {
		const removed = this._stack.splice(index, 1)[0];
		this._update(tween);
		TweenMax.to(removed.container, this.operationTween.fade ? this.operationTween.duration : 0, {
			alpha: 0,
			ease: this.operationTween.ease,
			x: removed.container.position.x + this._offsetToVector(this.operationOffset).x * -1,
			y: removed.container.position.y + this._offsetToVector(this.operationOffset).y * -1,
			onComplete: () => {
				this.removeChild(removed.container);
			},
		});
		if (cb !== undefined) {
			cb(removed.container);
		}
		return removed.container;
	}

	/**
	 * Returns the element from the stack from the specified index.
	 * @param index
	 */
	public getElementAt(index: number): T {
		return this._stack[index].container;
	}

	/**
	 * Returns the stack of containers.
	 * @returns
	 */
	public get stack(): T[] {
		const ret = new Array<T>();

		this._stack.forEach((sc) => {
			ret.push(sc.container);
		});

		return ret;
	}

	/**
	 * Sets a new stack of containers, replacing the existing one.
	 * @param stack
	 */
	public set stack(stack: T[]) {
		this._stack.forEach((el) => {
			this._stack.pop();
		});
		stack.forEach((el) => {
			this.insertAt(el, 0);
		});
	}

	/**
	 * Sets the direction to where the stack grows.
	 * @param direction
	 */
	public set direction(direction: "up" | "down" | "left" | "right") {
		this._direction = direction;
		this._update();
	}

	/**
	 * Gets the direction to where the stack grows.
	 * @returns
	 */
	public get direction(): "up" | "down" | "left" | "right" {
		return this._direction;
	}

	/**
	 * Sets the spacing between each element of the stack.
	 * @param value
	 */
	public set spacing(value: number) {
		this._spacing = value;
		this._update();
	}

	/**
	 * Gets the spacing between each element of the stack.
	 * @returns
	 */
	public get spacing(): number {
		return this._spacing;
	}
}
