import Proton from "proton-js";

import { ProtonUtils } from "./utils";
import type { IEmitterConfig } from "./types";

export class EmitterParser2 {
	/**
	 * Parses the given emitters datas and returns an array containing the parsed emitters.
	 * @param datas the emitter datas to parse.
	 * @param system defines the reference to the particles system to configure.
	 */
	public static parse(datas: IEmitterConfig[], system?: Proton): Proton.Emitter[] {
		return datas.map((d) => this.parseEmitter(d, system));
	}

	/**
	 * Parses the given emitter data and returns the new emitter reference.
	 * @param data the data of the emitter to parse.
	 */
	public static parseEmitter(data: IEmitterConfig, system?: Proton): Proton.Emitter {
		const emitter = new Proton.Emitter();

		// Rate
		if (data.Rate) {
			emitter.rate = ProtonUtils.getProperty("Rate", data.Rate);
		}

		// Initializers
		for (const i in data.initializers) {
			if (!data.initializers.hasOwnProperty(i)) {
				continue;
			}
			emitter.addInitialize(ProtonUtils.getProperty(i, data.initializers[i]));
		}

		// Behaviors
		for (const b in data.behaviors) {
			if (!data.behaviors.hasOwnProperty(b)) {
				continue;
			}
			emitter.addBehaviour(ProtonUtils.getProperty(b, data.behaviors[b]));
		}

		// Id & name
		if (data.id) {
			emitter.id = data.id;
		}
		if (data.name) {
			emitter.name = data.name;
		}

		// Add emitter to system?
		if (system) {
			system.addEmitter(emitter);
		}

		return emitter;
	}
}
