import { Container, Sprite, Texture, Rectangle, mesh, Point } from "pixi.js";
import { tracker, ButtonClick, Tools } from "@gdk/core";
import type { ITrackableObject } from "@gdk/core";

import { Docking } from "../../../container/docking";
import { DockingBounds } from "../../../container/dockingBounds";
import { ContainerExporter } from "../../../loading/export/containerExporter";
import { CommonExporter } from "../../../loading/export/commonExporter";
import { TextureExporter } from "../../../loading/export/textureExporter";
import { TextureImporter } from "../../../loading/import/textureImporter";
import { ContainerImporter } from "../../../loading/import/containerImporter";
import { CommonImporter } from "../../../loading/import/commonImporter";
import { TextArea } from "../text/textArea";
import type { IStyles, IStyle } from "../text/textArea";

export interface IButtonStyle<ITextStates extends IButtonTextStates = IButtonTextStates> {
	idleTexture?: string | Texture;
	overTexture?: string | Texture;
	clickedTexture?: string | Texture;
	disabledTexture?: string | Texture;
	textStates?: ITextStates;
}

export interface IButtonTextStates {
	idle?: IStyle | IStyles;
	hover?: IStyle | IStyles;
	down?: IStyle | IStyles;
	highlighted?: IStyle | IStyles;
	disabled?: IStyle | IStyles;
}

const STATE_TO_TEXTSTATE = {
	over: "hover",
	clicked: "down",
};

export class Button extends Container implements ITrackableObject {
	/**
	 * Defines the optional name of the object to be tracked.
	 * If unset, a default value will be taken by the tracker. For example,
	 * the name of the object (button, scratch, etc.).
	 */
	public trackingName: string = "";

	public idleTexture?: Texture = null;
	public overTexture?: Texture = null;
	public clickedTexture?: Texture = null;
	public disabledTexture?: Texture = null;

	public backgroundSprite: Sprite = null;
	public backgroundNineSlice: mesh.NineSlicePlane = null;
	public text: TextArea;
	public textStates: IButtonTextStates;

	public nineSliceMinSize: Point = new Point(0, 0);
	public nineSlicePadding: Rectangle = new Rectangle(0, 0, 0, 0);

	protected textStyle: IStyles;

	protected onPointerOverCallback: () => void = null;
	protected onPointerOutCallback: () => void = null;
	protected onPointerDownCallback: () => void = null;
	protected onMouseUpCallback: () => void = null;
	protected onTouchEndCallback: () => void = null;
	protected onPointerUpOutsideCallback: () => void = null;
	protected onPointerTapCallback: () => void = null;

	protected _enabled: boolean = true;
	protected _isNineSlice: boolean = false;

	protected _state: string;

	/**
	 * Constructor
	 * @param style: the button style
	 * @param textStyle: (optional) style applied to the RichText field
	 * @param text: (optional) Text to display in the richtext field
	 */
	public constructor(
		style: IButtonStyle = {},
		textStyle: IStyle | IStyles = {},
		text: string = ""
	) {
		super();
		this._state = "idle";

		// Create textures
		this.idleTexture =
			typeof style.idleTexture === "string" ? Texture.from(style.idleTexture) : style.idleTexture;
		this.overTexture =
			typeof style.overTexture === "string" ? Texture.from(style.overTexture) : style.overTexture;
		this.clickedTexture =
			typeof style.clickedTexture === "string"
				? Texture.from(style.clickedTexture)
				: style.clickedTexture;

		if (style.disabledTexture) {
			this.disabledTexture =
				typeof style.disabledTexture === "string"
					? Texture.from(style.disabledTexture)
					: style.disabledTexture;
		}

		// Create sprite
		this.backgroundSprite = new Sprite(this.idleTexture);
		this.backgroundSprite.name = "Background";
		this.backgroundSprite.doNotExport = true;
		this.addChild(this.backgroundSprite);

		// Hit area
		this.hitArea = new Rectangle(0, 0, this.backgroundSprite.width, this.backgroundSprite.height);

		// Viewport
		if (this.idleTexture) {
			this.dockingBounds = new DockingBounds(this.idleTexture.width, this.idleTexture.height);
		} else {
			this.dockingBounds = new DockingBounds(1, 1);
		}

		// Save text style
		this.textStyle = Tools.assign(
			{},
			(textStyle as any).default
				? textStyle
				: {
						default: textStyle,
				  }
		) as IStyles;

		// Save the original textOptions as a clone, as it will be modified in the textField
		// and create text
		this.textStates = Tools.merge(style.textStates || {}, {});
		this.text = new TextArea(text, textStyle || {});
		this.text.docking = new Docking(Docking.centerAll);
		this.text.doNotExport = true;
		this.text.name = "Label";
		this.addChild(this.text);

		// Bind events
		this.interactive = true;
		this.buttonMode = true;
		this.bindEvents();
	}

	public set state(state: string) {
		if (this._state === state) {
			return;
		}

		if ([this._state, state].includes("disabled")) {
			this._enabled = this.interactive = this.buttonMode = state !== "disabled";
		}

		this._state = state;

		const texture = this.stateToTexture(state);
		if (texture !== null) {
			this._updateTexture(texture);
		}

		this._updateTextStyle(this.stateToTextStyle(state));
	}

	public get state(): string {
		return this._state;
	}

	protected stateToTextStyle(state: string): IStyle | IStyles {
		const key = STATE_TO_TEXTSTATE[state] ?? state;
		return this.textStates[key];
	}

	protected stateToTexture(state: string): Texture | null {
		return this[`${state}Texture`] ?? null;
	}

	/**
	 * Gets the current text of the button's textfield
	 */
	public get label(): string {
		return this.text.text;
	}

	/**
	 * Sets the current text of the button's textfield
	 */
	public set label(label: string) {
		this.text.text = label;

		// If nineslice, update width
		if (!this._isNineSlice) {
			return;
		}

		const tempWidth = this.text.width + this.nineSlicePadding.left + this.nineSlicePadding.right;
		this.backgroundNineSlice.width =
			tempWidth > this.nineSliceMinSize.x ? tempWidth : this.nineSliceMinSize.x;

		const tempHeight = this.text.height + this.nineSlicePadding.top + this.nineSlicePadding.bottom;
		this.backgroundNineSlice.height =
			tempHeight > this.nineSliceMinSize.y ? tempHeight : this.nineSliceMinSize.y;

		this.dockingBounds.setBounds(this.backgroundNineSlice.width, this.backgroundNineSlice.height);
		this.hitArea = new Rectangle(
			0,
			0,
			this.backgroundNineSlice.width,
			this.backgroundNineSlice.height
		);
	}

	/**
	 * Gets the sprite background when button.isNineSlice is set to "false" or
	 * gets the nine slice background when button.isNineSlice is set to "true"
	 */
	public get background(): Sprite | mesh.NineSlicePlane {
		return this._isNineSlice ? this.backgroundNineSlice : this.backgroundSprite;
	}

	/**
	 * Returns the current background's texture
	 */
	public get backgroundTexture(): Texture {
		return this.background.texture;
	}

	/**
	 * Sets the current background's texture
	 */
	public set backgroundTexture(texture: Texture) {
		this.background.texture = texture;
	}

	/**
	 * Returns if the button is disabled or not
	 */
	public get enabled(): boolean {
		return this._enabled;
	}

	/**
	 * Sets if the button is disabled
	 */
	public set enabled(enabled: boolean) {
		this.state = enabled ? "idle" : "disabled";
	}

	/**
	 * Gets wether or not the button is set to nine slice mode
	 */
	public get isNineSlice(): boolean {
		return this._isNineSlice;
	}

	/**
	 * Sets wether or not the button is set to nine slice mode
	 */
	public set isNineSlice(enabled: boolean) {
		if (enabled === this._isNineSlice) {
			return;
		}

		this._isNineSlice = enabled;

		// Keep background position in this.children
		const currentIndex = this.getChildIndex(this.backgroundSprite || this.backgroundNineSlice);
		const config = ContainerExporter.exportContainer(
			this.backgroundSprite || this.backgroundNineSlice
		);

		// Build
		if (enabled) {
			this.backgroundNineSlice = new mesh.NineSlicePlane(this.idleTexture);
			this.backgroundNineSlice.doNotExport = true;
			this.backgroundNineSlice.id = this.backgroundSprite.id;

			this.backgroundSprite.destroy();
			this.backgroundSprite = null;
		} else {
			this.backgroundSprite = new Sprite(this.idleTexture);
			this.backgroundSprite.doNotExport = true;
			this.backgroundSprite.id = this.backgroundNineSlice.id;

			this.backgroundNineSlice.destroy();
			this.backgroundNineSlice = null;
		}

		// Configure
		ContainerImporter.configureContainer(config, this.backgroundSprite || this.backgroundNineSlice);

		// Add child
		this.addChildAt(this.backgroundSprite || this.backgroundNineSlice, currentIndex);
	}

	/**
	 * Binds the events to the button (hover, clicked, etc.)
	 */
	public bindEvents(): void {
		this.onPointerOverCallback = () => this._onPointerOver();
		this.onPointerOutCallback = () => this._onPointerOut();
		this.onPointerDownCallback = () => this._onPointerDown();
		this.onMouseUpCallback = () => this._onMouseUp();
		this.onTouchEndCallback = () => this._onTouchEnd();
		this.onPointerUpOutsideCallback = () => this._onPointerUpOutside();
		this.onPointerTapCallback = () => this._onPointerTap();

		this.on("pointerover", this.onPointerOverCallback);
		this.on("pointerout", this.onPointerOutCallback);
		this.on("pointerdown", this.onPointerDownCallback);
		this.on("mouseup", this.onMouseUpCallback);
		this.on("touchend", this.onTouchEndCallback);
		this.on("pointerupoutside", this.onPointerUpOutsideCallback);
		this.on("pointertap", this.onPointerTapCallback);
	}

	/**
	 * Unbinds the events
	 */
	public unBindEvents(): void {
		this.off("pointerover", this.onPointerOverCallback);
		this.off("pointerout", this.onPointerOutCallback);
		this.off("pointerdown", this.onPointerDownCallback);
		this.off("mouseup", this.onMouseUpCallback);
		this.off("touchend", this.onTouchEndCallback);
		this.off("pointerupoutside", this.onPointerUpOutsideCallback);
		this.off("pointertap", this.onPointerTapCallback);

		this.onPointerOverCallback = null;
		this.onPointerOutCallback = null;
		this.onPointerDownCallback = null;
		this.onMouseUpCallback = null;
		this.onTouchEndCallback = null;
		this.onPointerUpOutsideCallback = null;
		this.onPointerTapCallback = null;
	}

	// On mouse over this
	protected _onPointerOver(): void {
		if (this._enabled) {
			this.state = "over";
		}
	}

	// On mouse out of this
	protected _onPointerOut(): void {
		if (this._enabled) {
			this.state = "idle";
		}
	}

	// On mouse/touch on this
	protected _onPointerDown(): void {
		if (this._enabled) {
			this.state = "clicked";
		}
	}

	// On mouse up
	protected _onMouseUp(): void {
		if (this._enabled) {
			this.state = "over";
		}
	}

	// On touch end
	protected _onTouchEnd(): void {
		if (this._enabled) {
			this.state = "idle";
		}
	}

	// On mouse/touch up outside
	protected _onPointerUpOutside(): void {
		if (this._enabled) {
			this.state = "idle";
		}
	}

	// on tap
	protected _onPointerTap(/* ev: interaction.InteractionEvent */): void {
		// // Other button than left button for mouse?
		// if (ev.data && ev.data.pointerType === "mouse" && ev.data.button > 0) {
		// 	return;
		// }

		if (this._enabled) {
			tracker.track(new ButtonClick(this.trackingName || this.getName()));
		}
	}

	// update text style according to the selected state
	protected _updateTextStyle(state: IStyle | IStyles): void {
		if (!state) {
			return;
		}

		this.text.styles = (
			(state as any).default
				? Tools.assign({}, this.textStyle, state)
				: Tools.assign({}, this.textStyle, { default: state })
		) as IStyles;
	}

	// Updates the current texture of the current background (sprite or nineslice)
	protected _updateTexture(texture: Texture): void {
		if (this._isNineSlice) {
			this.backgroundNineSlice.texture = texture;
		} else {
			this.backgroundSprite.texture = texture;
		}
	}

	/**
	 * Export the button
	 */
	public export(): any {
		const background = this._isNineSlice
			? ContainerExporter.exportNineSlicePlane(this.backgroundNineSlice)
			: ContainerExporter.exportSprite(this.backgroundSprite);

		return Tools.assign(ContainerExporter.exportContainer(this, false), {
			type: "ui.Button",
			text: this.text.i18nKey ?? this.text.text,
			textStyle: this.textStyle,
			background,
			isNineSlice: this._isNineSlice,
			nineSlicePadding: CommonExporter.exportRectangle(this.nineSlicePadding),
			nineSliceMinSize: CommonExporter.exportPoint(this.nineSliceMinSize),
			style: {
				idleTexture: TextureExporter.exportTexture(this.idleTexture),
				overTexture: TextureExporter.exportTexture(this.overTexture),
				clickedTexture: TextureExporter.exportTexture(this.clickedTexture),
				disabledTexture: TextureExporter.exportTexture(this.disabledTexture),
				textStates: this.textStates,
			},

			textfield: this.text.export(),

			trackingName: this.trackingName,
		});
	}

	/**
	 * Parses the textfield
	 * @param config the serialized data
	 * @param baseUrl the base url of the loader
	 * @param button the already created button
	 */
	public static parse(config: any, baseUrl: string, button?: Button): Button {
		if (!button) {
			button = new Button({}, config.textStyle, config.text);
		}

		// Configure button
		button.idleTexture = TextureImporter.importTexture(
			config.style.idleTexture,
			baseUrl,
			button,
			"idleTexture"
		);
		button.overTexture = TextureImporter.importTexture(
			config.style.overTexture,
			baseUrl,
			button,
			"overTexture"
		);
		button.clickedTexture = TextureImporter.importTexture(
			config.style.clickedTexture,
			baseUrl,
			button,
			"clickedTexture"
		);
		button.disabledTexture = TextureImporter.importTexture(
			config.style.disabledTexture,
			baseUrl,
			button,
			"disabledTexture"
		);

		button.textStates = config.style.textStates;
		button.backgroundSprite.texture = button.idleTexture;

		ContainerImporter.configureContainer(config, button);

		// Configure text
		TextArea.parse(config.textfield, baseUrl, button.text);
		ContainerImporter.configureContainer(config.textfield, button.text);

		// Nine slice
		if (config.nineSlicePadding) {
			button.nineSlicePadding = CommonImporter.importRectangle(config.nineSlicePadding);
		}
		if (config.nineSliceMinSize) {
			button.nineSliceMinSize = CommonImporter.importPoint(config.nineSliceMinSize);
		}

		// Hit area
		if (config.isNineSlice) {
			button.isNineSlice = true;
			ContainerImporter.import(config.background, baseUrl, button.backgroundNineSlice);
		} else {
			button.hitArea = new Rectangle(
				0,
				0,
				button.backgroundSprite.width,
				button.backgroundSprite.height
			);
			if (config.background) {
				// Still keep backward compatibility
				ContainerImporter.import(config.background, baseUrl, button.backgroundSprite);
			}
		}

		// Label
		button.label = button.label;

		// Restore texture to idle
		button.background.texture = button.idleTexture;

		button.trackingName = config.trackingName || "";

		return button;
	}
}
