import { TweenMax } from "gsap";
import type { Container } from "pixi.js";

import { ContainerImporter, EContainerImporterMode } from "../loading/import/containerImporter";
import { ContainerExporter } from "../loading/export/containerExporter";
import type { IProjectContainerTransform } from "../loading/model";

export interface ILayoutConfiguration {
	/**
	 * Defines the name of the layout.
	 */
	name: string;
	/**
	 * Defines the ratio under the one the layout should be applied.
	 */
	ratio: number;
	/**
	 * Defines the configuration of the layout.
	 */
	config: IProjectContainerTransform;
	/**
	 * Sets wether or not the transition should be animated.
	 */
	animateTransition: boolean;
}

/**
 * Defines the main class used to store, apply, etc. layouts on a container.
 */
export class Layout {
	/**
	 * Defines the list of all available layouts.
	 */
	public layoutConfigurations: ILayoutConfiguration[];
	/**
	 * Defines the default configuration of the container.
	 */
	public defaultLayoutConfiguration: ILayoutConfiguration;

	private _container: Container;
	private _currentLayoutConfiguration: ILayoutConfiguration = null;

	private _lastWidth: number = null;
	private _lastHeight: number = null;

	/**
	 * Constructor.
	 * @param container the reference to the container to apply the layouts.
	 * @param layouts defines the list of all available layouts for a container.
	 */
	public constructor(
		container: Container,
		layouts: ILayoutConfiguration[] = [],
		defaultLayout?: ILayoutConfiguration
	) {
		this._container = container;
		this.layoutConfigurations = layouts;

		this.defaultLayoutConfiguration = defaultLayout ?? {
			name: "None",
			ratio: null,
			config: this._exportContainer(container),
			animateTransition: true,
		};

		this.sortLayoutConfigurations();

		this._applyLayoutConfiguration(false, this.defaultLayoutConfiguration.config, container);
		this._currentLayoutConfiguration = this.defaultLayoutConfiguration;
	}

	/**
	 * Returns the name of the current layout.
	 */
	public get currentLayoutConfiguration(): ILayoutConfiguration {
		return this._currentLayoutConfiguration;
	}

	/**
	 * Returns the layout configuration identified by the given name.
	 * @param name the name of the layout configuration to retrieve.
	 */
	public getLayoutConfigurationByName(name: string): ILayoutConfiguration {
		return this.layoutConfigurations.find((l) => l.name === name);
	}

	/**
	 * Adds a new layout configuration to the current list.
	 * @param name the name of the layout configuration.
	 * @param ratio the ratio under the one the layout is applied.
	 */
	public addLayoutConfiguration(name: string, ratio: number): ILayoutConfiguration {
		const layoutConfiguration = {
			name,
			ratio,
			animateTransition: true,
			config: this._exportContainer(this._container),
		} as ILayoutConfiguration;

		this.layoutConfigurations.push(layoutConfiguration);
		this._applyFromLastDimensions();

		return layoutConfiguration;
	}

	/**
	 * Removes the given layout configuration from the list.
	 * @param layoutConfiguration the layout configuration to remove from the list.
	 */
	public removeLayoutConfiguration(layoutConfiguration: ILayoutConfiguration): boolean {
		if (!layoutConfiguration) {
			return false;
		}

		const index = this.layoutConfigurations.indexOf(layoutConfiguration);
		if (index !== -1) {
			this.layoutConfigurations.splice(index, 1);
			return true;
		}

		this._applyFromLastDimensions();
		return true;
	}

	/**
	 * Sorts the current list of layout configurations.
	 */
	public sortLayoutConfigurations(): void {
		this.layoutConfigurations = this.layoutConfigurations.sort((a, b) => {
			if (a.ratio < b.ratio) {
				return -1;
			}
			if (a.ratio > b.ratio) {
				return 1;
			}

			return 0;
		});
	}

	/**
	 * Applies the layout according to the given parent's dimensions.
	 * @param width the width of the parent container.
	 * @param height the height of the parent container.
	 */
	public apply(width: number, height: number): void {
		if (ContainerImporter.mode === EContainerImporterMode.Dev) {
			return;
		}

		const ratio = width / height;

		this._lastWidth = width;
		this._lastHeight = height;

		for (const layoutConfiguration of this.layoutConfigurations) {
			if (ratio < layoutConfiguration.ratio) {
				return this.applyLayoutConfiguration(layoutConfiguration);
			}
		}

		// Apply default
		this.applyLayoutConfiguration(this.defaultLayoutConfiguration);
	}

	/**
	 * Applies the given layout configuration.
	 * @param layoutConfiguration the layout configuration to apply.
	 */
	public applyLayoutConfiguration(layoutConfiguration: ILayoutConfiguration): void {
		if (!layoutConfiguration || layoutConfiguration === this._currentLayoutConfiguration) {
			return;
		}

		this._container.emit("layoutChange", layoutConfiguration);
		this._applyLayoutConfiguration(
			layoutConfiguration.animateTransition,
			layoutConfiguration.config,
			this._container
		);
		this._container.emit("layoutChanged", layoutConfiguration);

		// Ensure sync
		this._container.resize.resize(this._container);
		this._currentLayoutConfiguration = layoutConfiguration;
	}

	/**
	 * Saves the current layout configurations.
	 */
	public saveCurrentLayoutConfiguration(): void {
		if (this.currentLayoutConfiguration === this.defaultLayoutConfiguration) {
			this.defaultLayoutConfiguration.config = this._exportContainer(this._container);
		} else {
			this.currentLayoutConfiguration.config = this._exportContainer(this._container);
		}
	}

	/**
	 * Applies the layout according to last saved parent's dimensions.
	 */
	private _applyFromLastDimensions(): void {
		if (this._lastWidth !== null && this._lastHeight !== null) {
			this.apply(this._lastWidth, this._lastHeight);
		}
	}

	/**
	 * Applies the layout configuration.
	 */
	private _applyLayoutConfiguration(
		animated: boolean,
		config: IProjectContainerTransform,
		root: Container
	): void {
		ContainerImporter.configureContainerTransform(config, root);

		if (!config.children) {
			return;
		}
		config.children.forEach((c) => {
			const child = root.getChildByName(c.name);
			if (!child) {
				return;
			}

			if (!animated || !child.transform) {
				return this._applyLayoutConfiguration(animated, c, child);
			}

			const tweens = TweenMax.getTweensOf(child)
				.concat(TweenMax.getTweensOf(child.docking))
				.concat(TweenMax.getTweensOf(child.scale))
				.concat(TweenMax.getTweensOf(child.dockingBounds));
			tweens.forEach((t) => {
				// Stop animating positions.
				delete (t.vars as any).x;
				delete (t.vars as any).y;
				delete (t.vars as any).rotation;
				delete (t.vars as any).width;
				delete (t.vars as any).height;
			});

			const x = child.docking?.x ?? child.x ?? null;
			const y = child.docking?.y ?? child.y ?? null;

			const rotation = child.rotation ?? null;

			const scaleX = child.scale?.x ?? null;
			const scaleY = child.scale?.y ?? null;

			const width = child.dockingBounds?.width ?? null;
			const height = child.dockingBounds?.height ?? null;

			this._applyLayoutConfiguration(animated, c, child);

			if (x !== null && y !== null) {
				TweenMax.fromTo(
					child.docking ?? child,
					0.3,
					{ x, y },
					{
						x: child.docking?.x ?? child.x,
						y: child.docking?.y ?? child.y,
					}
				);
			}

			if (rotation !== null) {
				TweenMax.fromTo(
					child,
					0.3,
					{ rotation },
					{
						rotation: child.rotation,
					}
				);
			}

			if (scaleX !== null && scaleY !== null) {
				TweenMax.fromTo(
					child.scale,
					0.3,
					{ scaleX, scaleY },
					{
						x: child.scale.x,
						y: child.scale.y,
					}
				);
			}

			if (width !== null && height !== null) {
				TweenMax.fromTo(
					child.dockingBounds,
					0.3,
					{ width, height },
					{
						width: child.dockingBounds.width,
						height: child.dockingBounds.height,
					}
				);
			}
		});
	}

	/**
	 * Exports the given container's configuration with its children's configuration, recursively.
	 * @hidden
	 */
	public _exportContainer(container: Container): IProjectContainerTransform {
		const result = ContainerExporter.exportContainerTransforms(container);
		if (container === this._container) {
			result.resize = undefined;
		}

		result.children = [];
		container.children.forEach((c) => {
			result.children.push(this._exportContainer(c as Container));
		});

		return result;
	}

	/**
	 * Exports the current layouts configuration to a JSON representation.
	 */
	public export(): any {
		return {
			defaultLayout: this.defaultLayoutConfiguration,
			layouts: this.layoutConfigurations.slice(),
		};
	}

	/**
	 * Parses the given layout configuration.
	 * @param container the reference to the container to apply the layouts.
	 * @param config defines the layout's configuration as a JSON representation.
	 */
	public static parse(container: Container, config: any): any {
		return new Layout(container, config.layouts, config.defaultLayout);
	}
}
