import type { Rectangle } from "pixi.js";

import { EAlign } from "./textField";
import type { Word } from "./word";
import type { Line } from "./line";
import type { Glyph } from "./glyph";

export class Text {
	public align: EAlign = EAlign.Left;
	public lines: Line[] = []; // all lines that compose that text
	public words: Word[] = [];
	public glyphs: Glyph[] = []; // all glyphs that compose that text
	public x: number = Number.MAX_VALUE; // block text horizontal position
	public y: number = 0; // block text vertical position
	public width: number = 0; // block text width
	public height: number = 0; // block text height
	public interLine: number = 0; // distance between lines;
	public padding: Rectangle = null;
	public leading: number = 0;
	public canvasX: number = 0;
	public canvasY: number = 0;
	public maxLineWidth: number = 0;
	public minLineX: number = Number.MAX_VALUE;
	public baseline: number = 0;

	/**
	 * Constructor
	 */
	// constructor() { }

	/**
	 * Add the given line to the text
	 * @param line the line to add
	 */
	public pushLine(line: Line): void {
		this.lines.push(line);
	}

	/**
	 * Add the given word to the text
	 * @param word the word to add
	 */
	public pushWord(word: Word): Word {
		this.words.push(word);
		return word;
	}

	/**
	 * Add the given glyph to the text
	 * @param glyph the glyph to add
	 */
	public pushGlyph(glyph: Glyph): void {
		this.glyphs.push(glyph);
	}
}
