import type { DisplayObject } from "pixi.js";

export function elementToString(el: DisplayObject): string {
	return el.name ?? `<${Object.getPrototypeOf(el).constructor.name}>`;
}

export function elementToPath(el: DisplayObject): string {
	let path = `<${Object.getPrototypeOf(el).constructor.name}>`;

	for (const key of ["name", "label"]) {
		if (typeof (el as any)[key] === "string") {
			path += `[ ${key} = "${(el as any)[key]}"]`;
			break;
		}
	}

	let _parent = el.parent;
	while (_parent) {
		path = `${elementToString(_parent)}/${path}`;
		_parent = _parent.parent;
	}
	return path;
}
