import type { Texture } from "pixi.js";

import type { IProjectTexture } from "../model";

export class TextureExporter {
	/**
	 * Exports the given texture
	 * @param texture the texture to export
	 */
	public static exportTexture(texture: Texture): IProjectTexture {
		if (!texture) {
			return null;
		}

		return {
			imageUrl: texture.textureCacheIds[0] || texture.baseTexture.imageUrl,
		};
	}
}
