import { Rectangle, Transform, Sprite, Graphics, Point } from "pixi.js";
import type { Container, SystemRenderer } from "pixi.js";

export class ContainerUtils {
	/**
	 * This is used to avoid creating new rectangles when calling .getLocalBounds
	 */
	public static tempRectangle: Rectangle = new Rectangle(0, 0, 0, 0);
	/**
	 * This is used to avoid creating new rectangles each time the function is applied.
	 */
	public static parentBounds: Rectangle = new Rectangle(0, 0, 0, 0);
	/**
	 * This is used to avoid creating new rectangles each time the function is applied.
	 */
	public static bounds: Rectangle = new Rectangle(0, 0, 0, 0);

	/**
	 * Returns the current parent bounds according to its configuration (bounds, no bounds, etc.).
	 * @param parent the parent reference.
	 * @param parentTransform the parent transform reference configured from its world transform.
	 * @param result the result where to store the bounds.
	 */
	public static refreshParentBounds(
		parent: any,
		parentTransform: Transform,
		result: Rectangle = this.parentBounds
	): Rectangle {
		if (parent.dockAsPoint) {
			result.width = parentTransform.scale.x;
			result.height = parentTransform.scale.y;
		} else if (parent._width && parent._height) {
			result.width = parent._width;
			result.height = parent._height;
		} else if (parent.dockingBounds) {
			result.width = parent.dockingBounds.width * parentTransform.scale.x;
			result.height = parent.dockingBounds.height * parentTransform.scale.y;
		} else {
			result.copy(parent.getBounds(true, this.tempRectangle));
		}

		result.x = Math.round(parentTransform.position.x);
		result.y = Math.round(parentTransform.position.y);
		result.width = Math.round(result.width) || 1;
		result.height = Math.round(result.height) || 1;

		return result;
	}

	/**
	 * Returns the current container's bounds according to its configuration (bounds, no bounds, etc.).
	 * @param container the container reference.
	 * @param transform the container transform reference configured from its world transform.
	 * @param result the result where to store the bounds.
	 */
	public static refreshBounds(
		container: any,
		transform: Transform,
		result: Rectangle = this.bounds
	): Rectangle {
		// Get the real width / height
		if (container.dockAsPoint) {
			result.width = transform.scale.x;
			result.height = transform.scale.y;
		} else if (container.dockingBounds) {
			result.width = container.dockingBounds.width * transform.scale.x;
			result.height = container.dockingBounds.height * transform.scale.y;
		} else {
			result.width = container._width;
			result.height = container._height;

			if (container instanceof Sprite || container instanceof Graphics) {
				result.width = (container.width * transform.scale.x) / container.scale.x;
				result.height = (container.height * transform.scale.y) / container.scale.y;
			}

			result.width = Math.abs(result.width);
			result.height = Math.abs(result.height);
		}

		result.x = Math.round(transform.position.x);
		result.y = Math.round(transform.position.y);
		result.width = Math.round(result.width) || 1;
		result.height = Math.round(result.height) || 1;

		return result;
	}

	/**
	 * Recursively updates the transform of each container, needed to synchronize docking/resize with children.
	 * @param root the root container to update and update its children.
	 */
	public static recursivelyUpdateTransform(root: Container): void {
		root.transform.updateTransform(root.parent.transform);
		root.children.forEach(
			(c) =>
				c.visible &&
				c.alpha > 0 &&
				c.worldAlpha >= 0 &&
				this.recursivelyUpdateTransform(c as Container)
		);
	}

	/**
	 * Get the real dimension of the container based (not relative to its children)
	 * The container must have docking bounds to work
	 * @param renderer: The renderer system provided by PIXIGame
	 * @param container: The actual container to get information about scale
	 * @return Point which represents the real dimension of the container in x axis and y axis
	 */
	public static getContainerDimensions(renderer: SystemRenderer, container: Container): Point {
		const transform = new Transform();
		transform.setFromMatrix(container.worldTransform);

		const screenWidth: number = Math.abs(
			renderer.width / (transform.scale.x * renderer.resolution)
		);
		const screenHeight: number = Math.abs(
			renderer.height / (transform.scale.y * renderer.resolution)
		);

		return new Point(screenWidth, screenHeight);
	}
}
