import { Container, Sprite } from "pixi.js";

import { Scratchable } from "./scratchable";

export type TextureID = string;

export interface IOneClickScratchOptions {
	revealTextureID: TextureID;
	scratchTextureID: TextureID;
}

export class OneClickScratch extends Container implements Scratchable {
	public isRevealed: boolean;
	public isAuto: boolean;
	protected revealSprite: Sprite;
	protected scratchSprite: Sprite;

	public constructor(options: IOneClickScratchOptions) {
		super();

		this.interactive = true;

		this.revealSprite = Sprite.fromFrame(options.revealTextureID);
		this.scratchSprite = Sprite.fromFrame(options.scratchTextureID);
		this.addChild(this.revealSprite);
		this.addChild(this.scratchSprite);

		this.reset();
	}

	/**
	 * Automatically scratch element
	 */
	public auto(): void {
		this._onPointerTapHandler(null);
		this.emit(Scratchable.AUTO_EVENT);
	}

	/**
	 * Get the progress of the scratch
	 */
	public getProgress(): number {
		return this.isRevealed ? 1 : 0;
	}

	/**
	 * Reset the scratch
	 */
	public reset(): void {
		this.scratchSprite.visible = true;
		this.isRevealed = false;
		this.on("pointertap", this._onPointerTapHandler, this);
	}

	private _onPointerTapHandler(event: PIXI.interaction.InteractionEvent): void {
		if (this.isRevealed) {
			return;
		}

		this.scratchSprite.visible = false;
		this.isRevealed = true;
		this.off("pointertap", this._onPointerTapHandler, this);
		this.emit(Scratchable.FULL_REVEAL_EVENT);
	}
}
