import { Tools } from "@gdk/core";
import type { interaction } from "pixi.js";

import { SpineTimelineButton } from "./button";
import { ESpineTimelineEvent } from "./types";
import type { IStyles } from "../ui";

/**
 * @experimental
 * Defines the new SpineButton working as a push button.
 * On pointer up, an event named "push" is emitted.
 * @example myPushButton.on("push", () => { ... });
 */
export class SpineTimelinePushButton extends SpineTimelineButton {
	private _pushed: boolean = false;
	private _clickEvent: interaction.InteractionEvent = null;

	/**
	 * Constructor.
	 * @param assetId the id of the spine datas in the core-loader.
	 * @param styles the styles of the buttons.
	 */
	public constructor(assetId: string, styles: IStyles) {
		super(assetId, styles);
	}

	/**
	 * Resets the button.
	 */
	public reset(): void {
		super.reset();

		if (this._pushed) {
			this._pushed = false;
			this.interactive = true;
			this.buttonMode = true;
			this.emit("unpush");
			this.emit("unpushed");
		}
	}

	/**
	 * Registers the event on pointer down.
	 * @override
	 */
	protected onPointerDown(): void {
		this.on("pointerdown", () => {
			this._isDown = true;

			this.playAnimation([
				{ name: this.animationsNames.down },
				{ name: this.animationsNames.downLoop, repeat: this.loopOnDown ? -1 : 0 },
			]);
			this.resetCallToAction();
		});
	}

	/**
	 * Registers the event on pointer up.
	 */
	protected onPointerUp(): void {
		this.on("pointerup", (e: interaction.InteractionEvent) => {
			if (!this._isDown) {
				return;
			}
			this._isDown = false;
			this._clickEvent = e;

			this.trackClick();

			this.pushed = true;

			this.resetCallToAction();
		});
	}

	/**
	 * Registers the event on pointer up outside.
	 */
	protected onPointerUpOutside(): void {
		this.on("pointerupoutside", () => {
			this._isDown = false;

			this.playAnimation([
				{ name: this.animationsNames.up },
				{ name: this.animationsNames.idleLoop, repeat: this.loopOnIdle ? -1 : 0 },
			]);
			this.resetCallToAction();
		});
	}

	/**
	 * Gets wether or not the pushbutton is pushed.
	 */
	public get pushed(): boolean {
		return this._pushed;
	}

	/**
	 * Sets wether or not the pushbutton is pushed.
	 */
	public set pushed(pushed: boolean) {
		if (this._pushed === pushed) {
			return;
		}
		this._setPushed(pushed);
	}

	/**
	 * Sets the button's state (pushed or unpushed).
	 * @param pushed wether or not the button is pushed.
	 */
	private _setPushed(pushed: boolean): void {
		this._pushed = pushed;

		this.interactive = !pushed;
		this.buttonMode = !pushed;

		if (pushed) {
			this.emit("push", this._clickEvent);

			if (
				this.currentAnimation !== this.animationsNames.down &&
				this.currentAnimation !== this.animationsNames.downLoop
			) {
				this.once(ESpineTimelineEvent.AnimationComplete, (a) => {
					if (a === this.animationsNames.down) {
						this.emit("pushed", this._clickEvent);
						this._clickEvent = null;
					}
				});

				this.playAnimation([
					{ name: this.animationsNames.down },
					{ name: this.animationsNames.downLoop, repeat: this.loopOnDown ? -1 : 0 },
				]);
			} else {
				this.emit("pushed", this._clickEvent);
				this._clickEvent = null;
			}
		} else {
			// We need to free the trackedPointers object to avoid old "pointerout" events.
			if (this._trackPointerId !== null) {
				delete this.trackedPointers[this._trackPointerId];
				this._trackPointerId = null;
			}

			this.emit("unpush");

			this.once(ESpineTimelineEvent.AnimationComplete, (a) => {
				if (a === this.animationsNames.up) {
					this._isDown = false;
					this.emit("unpushed");
				}
			});
			this.playAnimation([
				{ name: this.animationsNames.up },
				{ name: this.animationsNames.idleLoop, repeat: this.loopOnIdle ? -1 : 0 },
			]);
		}
	}

	/**
	 * Exports the spine timeline push button.
	 */
	public export(): any {
		return Tools.assign(super.export(), {
			type: "spine.SpineTimelinePushButton",
		});
	}

	/**
	 * Parses the spine timeline push button according to the given configuration.
	 * @param config the serialized data.
	 * @param baseUrl the base url of the loader.
	 * @param button the existing button reference.
	 */
	public static parse(
		config: any,
		baseUrl: string,
		button?: SpineTimelinePushButton
	): SpineTimelinePushButton {
		// Create button
		if (!button) {
			button = new SpineTimelinePushButton(config.assetId, config.styles);
		}

		SpineTimelineButton.parse(config, baseUrl, button);

		return button;
	}
}
