import { Logger, Amount, Tools } from "@gdk/core";
import type { Container } from "pixi.js";

import { SpineTimeline } from "./timeline";
import { TextField } from "../ui";
import type { IStyles } from "../ui";
import type { ISpineTimelineSequence } from "./types";

/**
 * @experimental
 * Defines the new SpineLogo options that uses the newly created SpineTimeline features.
 */
export interface ISpineTimelineLogoOptions {
	/**
	 * The name of the slot container in the spine that will contain the stake's textfield.
	 */
	stakeContainerName?: string;
	/**
	 * The styles to apply to the stake's textfield.
	 */
	stakeStyles?: IStyles;
	/**
	 * The value of the stake to draw.
	 */
	stakeValue?: number;
}

/**
 * @experimental
 * Defines the new SpineLogo that uses the newly created SpineTimeline features.
 */
export class SpineTimelineLogo extends SpineTimeline {
	/**
	 * The default sequence available for the spine logo.
	 */
	public static defaultSequence: ISpineTimelineSequence = {
		loop: false,
		animations: [{ name: "enter" }, { name: "idle", repeat: 1 }, { name: "exit" }],
	};

	/**
	 * The spine logo's options reference.
	 */
	public options: ISpineTimelineLogoOptions;
	public stakeTextField: TextField = null;

	private _stakeSlotContainer: Container = null;
	private _stake: string = "";
	private _showStake: boolean = true;

	/**
	 * Constructor.
	 * @param assetId the id of the spine datas in the core-loader.
	 */
	public constructor(assetId: string, options: ISpineTimelineLogoOptions = {}) {
		super(assetId);

		// Apply default options
		options.stakeContainerName = options.stakeContainerName || "stake";
		options.stakeValue = options.stakeValue || 0;
		options.stakeStyles = options.stakeStyles || {
			default: {
				fontName: "Arial",
				fontSize: 24,
				fill: "#000000",
			},
		};
		this.options = options;

		this._initLogo();
	}

	/**
	 * Inits the spine timeline.
	 */
	private _initLogo(): void {
		this.addSequence("logo", SpineTimelineLogo.defaultSequence);
		this._initStake();
	}

	/**
	 * Shows the logo by playing the logo's sequence.
	 */
	public show(): Promise<void> {
		return this.play("logo");
	}

	/**
	 * Gets the current stake text.
	 */
	public get stake(): string {
		return this._stake;
	}

	/**
	 * Sets the current stake text.
	 */
	public set stake(value: string) {
		if (value === this._stake) {
			return;
		}

		this._stake = value;
		this.stakeTextField.text = value;
		this.updateStake();
	}

	/**
	 * Updates the stake textfield by setting pivot centered etc.
	 * Should be called once the stake's options changed (text, style, etc.).
	 */
	public updateStake(): void {
		this.stakeTextField.text = this._stake;
		this.stakeTextField.styles = this.options.stakeStyles;
		this.stakeTextField.pivot.set(
			this.stakeTextField.width * 0.5,
			-this.stakeTextField.height * 0.5
		);
	}

	/**
	 * Gets wether or not the stake should be shown.
	 */
	public get showStake(): boolean {
		return this._showStake;
	}

	/**
	 * Sets wether or not the stake should be shown.
	 */
	public set showStake(value: boolean) {
		this._showStake = value;
		if (this._stakeSlotContainer) {
			this._stakeSlotContainer.renderable = value;
		}
	}

	/**
	 * Gets the current stake container.
	 */
	public get stakeContainer(): string {
		return this.options.stakeContainerName;
	}

	/**
	 * Sets the current stake container.
	 * If a stake container already exists, the textfield's styles will be copied.
	 */
	public set stakeContainer(name: string) {
		if (name === this.options.stakeContainerName) {
			return;
		}

		this.options.stakeContainerName = name;

		let stakeStyles: IStyles = null;
		if (this.stakeTextField) {
			stakeStyles = this.stakeTextField.styles;
			this.stakeTextField.destroy();
			this.stakeTextField = null;
		}

		this._initStake();

		if (this.stakeTextField && stakeStyles) {
			this.stakeTextField.styles = stakeStyles;
		}
	}

	/**
	 * Inits the stake by retrieving container, creating textfield etc.
	 */
	private _initStake(): void {
		this._stakeSlotContainer = this.getSlotContainerByName(this.options.stakeContainerName);
		if (!this._stakeSlotContainer) {
			Logger.getLogger().warn(
				`SpineTimelineLogo: no slot container named "${this.options.stakeContainerName}" to put the stake's textfield`
			);
			return;
		}

		this.stakeTextField = new TextField("", this.options.stakeStyles);
		this.stakeTextField.scale.y = -1;
		this.stakeTextField.doNotExport = true;
		this.stakeTextField.name = "stake";
		this.stake = new Amount(0).toString({ minimumFractionDigits: 0 });

		this._stakeSlotContainer.addChild(this.stakeTextField);
	}

	/**
	 * Exports the spine timeline logo.
	 */
	public export(): any {
		if (this.stakeTextField) {
			this.stakeTextField.doNotExport = true;
		}

		this.sequences.logo.doNotExport = true;

		return Tools.assign(super.export(), {
			type: "spine.SpineTimelineLogo",
			showStake: this._showStake,
			stake: this._stake,
			options: this.options,
		});
	}

	/**
	 * Parses the spine timeline logo according to the given configuration.
	 * @param config the serialized data.
	 * @param baseUrl the base url of the loader.
	 * @param logo the existing timeline reference.
	 */
	public static parse(config: any, baseUrl: string, logo?: SpineTimelineLogo): SpineTimelineLogo {
		// Create timeline
		if (!logo) {
			logo = new SpineTimelineLogo(config.assetId, config.options);
		}

		SpineTimeline.parse(config, baseUrl, logo);

		logo.showStake = config.showStake;
		logo.stake = config.stake;

		return logo;
	}
}
