import { Graphics, Point } from "pixi.js";
import { Loader, Tools } from "@gdk/core";

export interface IDrawStyle {
	fill?: { color?: number; alpha?: number }; // the style of the fill
	stroke?: { lineWidth?: number; color?: number; alpha?: number }; // the style of the stroke
}

export class SVGGraphics extends Graphics {
	public polygons: Point[][] = []; // all polygons points in one array.
	public paths: Point[][] = []; // all path points in one array.
	public polylines: Point[][] = []; // all polylines points in one array.
	public points: Point[][] = []; // all points in one array.
	public drawStyle: IDrawStyle; // the drawing style of this graphics.
	public svg: Document; // the actual SVG document in the parsed by the DOMParser.
	public src: string; // the raw string.

	private quality: number;

	/**
	 * create a Graphics from a svg file
	 * @param svgAssetID the ID of your SVG file
	 * @param qualityRatio the quality ratio (0 to 1) used to draw path elements
	 * @param drawStyle the style used to draw path elements :
	 *  fill?: { color?: number, alpha?: number };
	 *  stroke?: { lineWidth?: number, color?: number, alpha?: number };
	 */
	public constructor(svgAssetID: string, qualityRatio: number = 0.5, drawStyle?: IDrawStyle) {
		super();

		this.quality = Math.min(1, Math.max(0, qualityRatio));
		// give it a collor (by convention green)
		this.drawStyle = Tools.assign(
			{
				stroke: { lineWidth: 1, color: 0x00ff00, alpha: 1 },
				fill: { color: 0xff0000, alpha: 0 },
			},
			drawStyle
		);

		const parser = new DOMParser();
		this.src = Loader.get(svgAssetID) || svgAssetID;
		this.svg = parser.parseFromString(this.src, "image/svg+xml");

		// get all paths element points in my svg
		this.paths = this.getPaths(this.svg.getElementsByTagName("path"));
		// get all polygon element points in my svg
		this.polygons = this.getPolygons(this.svg.getElementsByTagName("polygon"));
		// get all polyline element points in my svg
		this.polylines = this.getPolygons(this.svg.getElementsByTagName("polyline"));

		if (
			(this.drawStyle.stroke.lineWidth > 0 && this.drawStyle.stroke.alpha > 0) ||
			this.drawStyle.fill.alpha > 0
		) {
			// draw all points
			this.drawPoints(this.paths, this.drawStyle);
			this.drawPoints(this.polygons, this.drawStyle);
		}
	}

	/**
	 * convert an SVGPolygonElement to a point array.
	 * @param polygons a list of SVGPolygonElement or SVGPolylineElement
	 */
	private getPolygons(
		polygons:
			| NodeListOf<SVGPolygonElement>
			| NodeListOf<SVGPolylineElement>
			| HTMLCollectionOf<SVGPolygonElement>
			| HTMLCollectionOf<SVGPolylineElement>
	): Point[][] {
		const results = [];
		for (let i: number = 0, len = polygons.length; i < len; i++) {
			const pointsList: SVGPointList = polygons[i].points;
			const points = [];
			results[i] = [];
			for (let j: number = 0; j < pointsList.numberOfItems; j++) {
				const point: SVGPoint = pointsList.getItem(j);
				const p = new Point(point.x, point.y);
				results[i].push(p);
				points.push(p);
			}
			this.points.push(points);
		}
		return results;
	}

	/**
	 * convert an SVGPathElement to a point array.
	 * @param paths a list of SVGPathElement
	 */
	private getPaths(
		paths: NodeListOf<SVGPathElement> | HTMLCollectionOf<SVGPathElement>
	): Point[][] {
		const results = [];
		for (let i: number = 0, len = paths.length; i < len; i++) {
			const path: SVGPathElement = paths[i];
			const pathLength = path.getTotalLength();
			const numberOfPoints = Math.round(pathLength * this.quality);
			const points = [];
			results[i] = [];
			for (let j: number = 0; j < numberOfPoints; j++) {
				const pointRatio = j / numberOfPoints;
				const point = path.getPointAtLength(pointRatio * pathLength);
				const p = new Point(point.x, point.y);
				results[i].push(p);
				points.push(p);
			}
			this.points.push(points);
		}

		return results;
	}

	/**
	 * will draw all points
	 * @param points the points arrays to draw
	 * @param style	the style to use
	 */
	private drawPoints(points: Point[][], style: IDrawStyle): void {
		if (style.fill) {
			this.beginFill(style.fill.color, style.fill.alpha);
		}
		if (style.stroke) {
			this.lineStyle(style.stroke.lineWidth, style.stroke.color, style.stroke.alpha);
		}

		this.pointsToNumbersArray(points).forEach((path: number[]) => {
			this.drawPolygon(path);
		});
	}

	/**
	 * convert points arrays to number arrays.
	 * @param points the point to convert to number arrays
	 */
	private pointsToNumbersArray(points: Point[][]): number[][] {
		const paths: number[][] = [];
		for (let i = 0; i < points.length; i++) {
			const path = points[i];
			paths[i] = [];

			path.forEach((point: Point) => {
				paths[i].push(point.x);
				paths[i].push(point.y);
			});
		}

		return paths;
	}
}
