import { Container } from "pixi.js";

import { CSSLikeMargin } from "./cssLikeMargin";
import { TableCell } from "./tableCell";
import { DockingBounds } from "../../../container/dockingBounds";

export interface ITableOptions<T extends Container> {
	margin?: CSSLikeMargin;
	rows?: T[][];
	fixedWidth?: number;
	fixedColumnsWidth?: number[];
	fixedHeight?: number;
	fixedRowsHeight?: number[];
}

export class Table<T extends Container> extends Container {
	protected margin: CSSLikeMargin;
	protected cells: Array<Array<TableCell<T>>>;
	protected column: number;
	protected row: number;
	protected fixedWidth: number;
	protected fixedColumnsWidth: number[];
	protected fixedHeight: number;
	protected fixedRowsHeight: number[];

	public constructor(options?: ITableOptions<T>) {
		super();

		this.margin = options && options.margin ? options.margin : new CSSLikeMargin();
		this.fixedWidth = options && options.fixedWidth ? options.fixedWidth : null;
		this.fixedColumnsWidth = options && options.fixedColumnsWidth ? options.fixedColumnsWidth : [];
		this.fixedHeight = options && options.fixedHeight ? options.fixedHeight : null;
		this.fixedRowsHeight = options && options.fixedRowsHeight ? options.fixedRowsHeight : [];
		this.cells = [];

		const rows = options && options.rows ? options.rows : [];
		for (const row of rows) {
			const cellRow: Array<TableCell<T>> = [];
			for (const element of row) {
				cellRow.push(
					new TableCell({
						content: element,
						margin: this.margin.clone(),
					})
				);
			}
			this.cells.push(cellRow);
		}

		this.draw();
	}

	/**
	 * Set the fixed width
	 * @param fixedWidth
	 */
	public setFixedWidth(fixedWidth: number): void {
		this.fixedWidth = fixedWidth;
	}

	/**
	 * Set the fixed columns width
	 * @param fixedColumnsWidth
	 */
	public setFixedColumnsWidth(fixedColumnsWidth: number[]): void {
		this.fixedColumnsWidth = fixedColumnsWidth;
	}

	/**
	 * Set the fixed rows height
	 * @param fixedRowsHeight
	 */
	public setFixedRowsHeight(fixedRowsHeight: number[]): void {
		this.fixedRowsHeight = fixedRowsHeight;
	}

	/**
	 * Push new rows to the table
	 * @param rows
	 */
	public push(...rows: T[][]): void {
		const cellRows: Array<Array<TableCell<T>>> = [];
		for (const row of rows) {
			const cellRow: Array<TableCell<T>> = [];
			for (const element of row) {
				cellRow.push(
					new TableCell({
						content: element,
						margin: this.margin.clone(),
					})
				);
			}
			cellRows.push(cellRow);
		}
		this.cells.push(...cellRows);
		this.draw();
	}

	/**
	 * Add new rows at the top of the table
	 * @param rows
	 */
	public unshift(...rows: T[][]): void {
		const cellRows: Array<Array<TableCell<T>>> = [];
		for (const row of rows) {
			const cellRow: Array<TableCell<T>> = [];
			for (const element of row) {
				cellRow.push(
					new TableCell({
						content: element,
						margin: this.margin.clone(),
					})
				);
			}
			cellRows.push(cellRow);
		}
		this.cells.unshift(...cellRows);
		this.draw();
	}

	/**
	 * Pop the last row
	 */
	public pop(): Array<TableCell<T>> {
		const result = this.cells.pop();
		for (const element of result) {
			this.removeChild(element);
		}
		this.draw();
		return result;
	}

	/**
	 * shift the first row
	 */
	public shift(): Array<TableCell<T>> {
		const result = this.cells.shift();
		for (const element of result) {
			this.removeChild(element);
		}
		this.draw();
		return result;
	}

	/**
	 * Get a part of the table rows
	 * @param start
	 * @param end
	 */
	public slice(start?: number, end?: number): Array<Array<TableCell<T>>> {
		const result = this.cells.slice(start, end);
		this.draw();
		return result;
	}

	/**
	 * Replace a part of the table rows
	 * @param start
	 * @param end
	 * @param rows
	 */
	public splice(start?: number, end?: number, ...rows: T[][]): Array<Array<TableCell<T>>> {
		const cellRows: Array<Array<TableCell<T>>> = [];
		for (const row of rows) {
			const cellRow: Array<TableCell<T>> = [];
			for (const element of row) {
				cellRow.push(
					new TableCell({
						content: element,
						margin: this.margin.clone(),
					})
				);
			}
			cellRows.push(cellRow);
		}
		const result = this.cells.splice(start, end, ...cellRows);
		for (const row of result) {
			for (const element of row) {
				this.removeChild(element);
			}
		}
		this.draw();
		return result;
	}

	/**
	 * Get a column
	 * @param id
	 */
	public getColumn(id: number): Array<TableCell<T>> {
		const result: Array<TableCell<T>> = [];

		if (id < this.column) {
			for (const row of this.cells) {
				result.push(row[id]);
			}
		}

		return result;
	}

	/**
	 * Get a row
	 * @param id
	 */
	public getRow(id: number): Array<TableCell<T>> {
		return this.cells[id];
	}

	/**
	 * Draw the table
	 */
	public draw(): void {
		this.updateSize();

		let x = 0;
		let y = 0;
		for (const row of this.cells) {
			x = 0;

			for (const cell of row) {
				cell.x = x;
				cell.y = y;

				x += cell.getFixedWidth();

				cell.draw();
				this.addChild(cell);
			}

			y += Math.max(
				...row.map((element) => {
					return element ? element.getFixedHeight() : 0;
				})
			);
		}

		this.dockingBounds = new DockingBounds(x, y);
	}

	private updateSize(): void {
		this.row = this.cells.length;
		this.column = Math.max(
			...this.cells.map((row) => {
				return row.length;
			}),
			0
		);

		if (this.fixedWidth) {
			let freeColumns: number = 0;
			let usedSpace: number = 0;
			for (let i = 0; i < this.column; i++) {
				if (this.fixedColumnsWidth[i]) {
					usedSpace += this.fixedColumnsWidth[i];
				} else {
					freeColumns++;
				}
			}

			const freeColumnsWidth = (this.fixedWidth - usedSpace) / freeColumns;

			for (let i = 0; i < this.column; i++) {
				if (!this.fixedColumnsWidth[i]) {
					this.fixedColumnsWidth[i] = freeColumnsWidth;
				}
			}
		}

		if (this.fixedHeight) {
			let freeRows: number = 0;
			const usedSpace: number = this.fixedRowsHeight.length
				? this.fixedRowsHeight.reduce((previous, current) => {
						if (current) {
							return previous + current;
						} else {
							freeRows++;
							return previous;
						}
				  })
				: 0;

			freeRows += this.row - this.fixedRowsHeight.length;
			const freeRowsHeight = (this.fixedHeight - usedSpace) / freeRows;

			for (let i = 0; i < this.row; i++) {
				if (!this.fixedRowsHeight[i]) {
					this.fixedRowsHeight[i] = freeRowsHeight;
				}
			}
		}

		const columnWidth: number[] = [];
		for (let i = 0; i < this.column; i++) {
			columnWidth.push(
				this.fixedColumnsWidth[i]
					? this.fixedColumnsWidth[i]
					: Math.max(
							...this.getColumn(i).map((element) => {
								return element ? element.getWidth() : 0;
							})
					  )
			);
		}

		for (let i = 0; i < this.row; i++) {
			const rowHeight = this.fixedRowsHeight[i]
				? this.fixedRowsHeight[i]
				: Math.max(
						...this.cells[i].map((element) => {
							return element ? element.getHeight() : 0;
						})
				  );
			for (let j = 0; j < this.column; j++) {
				if (!this.cells[i][j]) {
					this.cells[i].push(
						new TableCell({
							margin: this.margin.clone(),
							fixedWidth: columnWidth[j],
							fixedHeight: rowHeight,
						})
					);
				} else {
					this.cells[i][j].updateOptions({
						fixedWidth: columnWidth[j],
						fixedHeight: rowHeight,
					});
				}
			}
		}
	}
}
