import { ContainerExporter } from "./containerExporter";
import { StateExporter } from "./stateExporter";
import type { GamePixi } from "../../gamePixi";
import type { ContainerView } from "../../view/containerView";
import type { IProject, IViewConfig } from "../model";
import type { LoadedView } from "../../view/loadedView";

export class ProjectExporter {
	/**
	 * Export the project
	 * @param game the game reference
	 */
	public static exportProject(game: GamePixi): IProject {
		const views: IViewConfig[] = [];
		Object.keys(game.state.states)
			.sort((a, b) => this._getViewIndex(game, a) - this._getViewIndex(game, b))
			.forEach((k) => views.push({ name: k, view: game.state.states[k].view as ContainerView }));

		return {
			stage: ContainerExporter.export(game.stage, true),
			views: views.map((v) => StateExporter.exportView(v)),
			assets: {},
			metadata: game.metadata,
		};
	}

	/**
	 * Returns the index of the view in its parent.
	 */
	private static _getViewIndex(game: GamePixi, stateKey: string): number {
		const view = game.state.states[stateKey].view as ContainerView | LoadedView;
		return view.parent?.getChildIndex(view) ?? 0;
	}
}
