import { Texture, Sprite } from "pixi.js";
import { TweenMax } from "gsap";

import { AbstractScratch2Renderer } from "../types";
import { ScratchAssets } from "../../scratch/scratchAssets";
import { TextureExporter } from "../../../loading/export/textureExporter";
import { TextureImporter } from "../../../loading/import/textureImporter";
import type { Scratch2 } from "../scratch2";
import type { Scratch2Zone } from "../zone";
import type { ScratchPath } from "../../scratch/scratchPath";
import type { IPointerMoveEvent } from "../../baseui/ui/customPointer";

export interface ISpriteZone {
	/**
	 * The zone reference having the sprite fit on.
	 */
	zone: Scratch2Zone;
	/**
	 * The sprite associated to the zone.
	 */
	sprite: Sprite;
}

export class OneClickScratchRenderer extends AbstractScratch2Renderer {
	/**
	 * The textures array used to keep textures
	 */
	public textures: Texture[];
	/**
	 * The array containing all the sprites associated to their zones.
	 */
	public spriteZones: ISpriteZone[] = [];

	private _distance: number = 0;

	private _autoTweens: TweenMax[] = [];
	private _autoSprites: Sprite[] = [];

	/**
	 * Constructor
	 * @param textures the textures array being used.
	 */
	public constructor(textures?: Texture[]) {
		super();

		// Init scratch assets cache
		ScratchAssets.initCache();

		// Textures
		this.textures = textures || [
			Texture.fromFrame("scratch/grattage_mask_00.png"),
			Texture.fromFrame("scratch/grattage_mask_01.png"),
			Texture.fromFrame("scratch/grattage_mask_02.png"),
			Texture.fromFrame("scratch/grattage_mask_03.png"),
			Texture.WHITE,
		];
	}

	/**
	 * Called on the scratch has been reset.
	 */
	public reset(): void {
		this.spriteZones.forEach((sz) => sz.sprite.destroy());
		this.spriteZones = [];

		this._autoSprites.forEach((as) => as.destroy());
		this._autoSprites = [];

		this._autoTweens.forEach((at) => at.kill());
		this._autoTweens = [];
	}

	/**
	 * Called on the user wants to stop the renderer. Typically to pause in auto mode.
	 */
	public onStop(): void {
		this._autoTweens.forEach((at) => at.kill());
		this._autoTweens = [];
	}

	/**
	 * Called once the user scratchs the scratch component.
	 * @param scratch the scratch component reference.
	 * @param event object that contains all the pointer event values (distance, etc.).
	 * @param zone the zone reference being scratched.
	 */
	public onScratch(scratch: Scratch2, event: IPointerMoveEvent, zone: Scratch2Zone): void {
		if (!event.distance) {
			return;
		}

		this._distance += event.distance;
		if (this._distance < 50) {
			return;
		}

		const spriteZone = this._getSpriteZone(zone);
		this._switchTexture(spriteZone.sprite, zone);
		this._distance = 0;
	}

	/**
	 * Called on the user wants to auto-reveal the scratch component.
	 * @param scratch the scratch component reference.
	 * @param zone the zone to automatically reveal. If no zone provided in the scratch component, the zone represents the overall scratch component.
	 * @param path the path used while animating the auto mode.
	 * @param done called once the auto animation has been completed.
	 */
	public onAuto(scratch: Scratch2, zone: Scratch2Zone, path: ScratchPath, done: () => void): void {
		let count = 0;

		// Get sprite zone
		const spriteZone = this._getSpriteZone(zone);

		// Create auto sprite
		const autoSprite = spriteZone.sprite.clone() as Sprite;
		autoSprite.texture = null;
		autoSprite.position.set(zone.x, zone.y);
		autoSprite.width = zone.width;
		autoSprite.height = zone.height;
		this.addChild(autoSprite);
		this._autoSprites.push(autoSprite);

		const autoTween = TweenMax.to(this, 0.1, {
			repeat: -1,
			onStart: () => (autoSprite.visible = true),
			onRepeat: () => {
				count++;
				if (count === this.textures.length) {
					this._autoTweens.splice(this._autoTweens.indexOf(autoTween), 1);
					autoTween.kill();

					done();
				}

				this._switchTexture(autoSprite, zone);
				scratch.setDirty();
				scratch._playScratchSound();
			},
		});

		this._autoTweens.push(autoTween);
	}

	private _switchTexture(sprite: Sprite, zone: Scratch2Zone): void {
		const textureIndex = this.textures.indexOf(sprite.texture);
		if (textureIndex < this.textures.length) {
			sprite.texture = this.textures[textureIndex + 1];
		}

		sprite.width = zone.width;
		sprite.height = zone.height;
	}

	private _getSpriteZone(zone: Scratch2Zone): ISpriteZone {
		let spriteZone = this.spriteZones.find((sz) => sz.zone === zone);
		if (!spriteZone) {
			const sprite = new Sprite();
			sprite.position.set(zone.x, zone.y);
			this.addChild(sprite);

			spriteZone = { zone, sprite };
			this.spriteZones.push(spriteZone);
		}

		return spriteZone;
	}

	/**
	 * Exports the renderer configuration mainly used by the editor or cloning method
	 */
	public export(): any {
		return {
			type: "OneClickScratchRenderer",
			textures: this.textures.map((t) => TextureExporter.exportTexture(t)),
		};
	}

	/**
	 * Parses the given scratch renderer configuration to return a new renderer component
	 * @param config the configuration of the scratch component being created
	 * @param baseUrl the base url of the loader
	 */
	public static parse(config: any, baseUrl: string): AbstractScratch2Renderer {
		// Init scratch assets cache
		ScratchAssets.initCache();

		return new OneClickScratchRenderer(
			config.textures.map((t) => TextureImporter.importTexture(t, baseUrl))
		);
	}
}
