import { Container, Graphics, DisplayObject, Transform, Point } from "pixi.js";
import { Vector2, Tools } from "@gdk/core";
import type { interaction } from "pixi.js";

import { ContainerUtils } from "./utils";
import { Docking } from "./docking";
import { Resize } from "./resize";
import { ContainerExporter } from "../loading/export/containerExporter";
import { ContainerImporter } from "../loading/import/containerImporter";
import type { Layout } from "./layout";

/**
 * Define extra properties for PIXI.Container
 */
Object.defineProperty(Container.prototype, "resize", {
	get(): number {
		return this._resize;
	},
	set(value: number): void {
		if (this._resize && this._resize.resize === Resize.fitContain) {
			delete this._width;
			delete this._height;
		}

		this._resize = value;
	},
});

// ScaleXY
Object.defineProperty(Container.prototype, "scaleXY", {
	get(): number {
		return Math.max(this.scale.x, this.scale.y);
	},
	set(value: number): void {
		this.scale.set(value, value);
	},
});

// ScaleX
Object.defineProperty(Container.prototype, "scaleX", {
	get(): number {
		return this.scale.x;
	},
	set(value: number): void {
		this.scale.x = value;
	},
});

// ScaleY
Object.defineProperty(Container.prototype, "scaleY", {
	get(): number {
		return this.scale.y;
	},
	set(value: number): void {
		this.scale.y = value;
	},
});

// Slide
Object.defineProperty(Container.prototype, "slide", {
	get(): number {
		return this._slide;
	},
	set(value: number): void {
		// Values
		this._isOver = false;
		this._slidePoint = null;

		if (this._slide === undefined) {
			// Over
			this._pointerOver = (ev: interaction.InteractionEvent) => {
				this._isOver = true;
				this._slidePoint = ev.data.getLocalPosition(this);
			};

			// Out
			this._pointerOut = () => (this._isOver = false);

			// Move
			this._pointerMove = (ev: interaction.InteractionEvent) => {
				const newPoint = ev.data.getLocalPosition(this);

				if (!this._isOver || ev.data.buttons <= 0) {
					this._slidePoint = newPoint;
					return;
				}

				const distance = Vector2.Distance(Vector2.from(this._slidePoint), Vector2.from(newPoint));
				this._currentSlide -= distance;

				if (this._currentSlide <= 0) {
					this.emit("slide", ev);
					this._currentSlide = 0;

					this._isOver = false;
					this._slidePoint = null;
				}

				this._slidePoint = newPoint;
			};

			this.on("pointerover", this._pointerOver);
			this.on("touchmove", this._pointerOver);

			this.on("pointerout", this._pointerOut);
			this.on("touchend", this._pointerOut);
			this.on("touchendoutside", this._pointerOut);

			this.on("pointermove", this._pointerMove);
			this.on("touchmove", this._pointerMove);
		}

		this._currentSlide = value;
		this._slide = value;
	},
});

Container.prototype.updateDebugBoundingBox = function (): void {
	if (this.debugBoundingBox) {
		this.debugBoundingBox.clear();
	} else {
		this.debugBoundingBox = new Graphics();
		this.debugBoundingBox.doNotExport = true;
		this.debugBoundingBox.interactive = false;
		let root = this;
		while (root.parent) {
			root = root.parent;
		}
		root.addChild(this.debugBoundingBox);
	}

	this.debugBoundingBox.transform.setFromMatrix(this.worldTransform);

	const bounds = this.getLocalBounds();
	const halfBounds = new Point(bounds.width * 0.5, bounds.height * 0.5);

	// bounds
	this.debugBoundingBox.lineStyle(2, 0xff0000, 0.5);
	this.debugBoundingBox.drawRect(0, 0, bounds.width, bounds.height);
	// center
	this.debugBoundingBox.lineStyle(2, 0x0000ff, 0.5);
	this.debugBoundingBox.moveTo(halfBounds.x, 0);
	this.debugBoundingBox.lineTo(halfBounds.x, bounds.height);
	this.debugBoundingBox.moveTo(0, halfBounds.y);
	this.debugBoundingBox.lineTo(bounds.width, halfBounds.y);
	// pivot
	this.debugBoundingBox.lineStyle(2, 0x00ff00, 0.5);
	this.debugBoundingBox.drawCircle(this.pivot.x, this.pivot.y, 5);
};

Object.defineProperty(Container.prototype, "showDebugBoundingBox", {
	set(value: boolean): void {
		if (value) {
			this.updateDebugBoundingBox();
		}
		this.debugBoundingBox.visible = value;
	},
	get(): boolean {
		return Boolean(this.debugBoundingBox) && this.debugBoundingBox.visible;
	},
});

// Set name of display object
DisplayObject.prototype.setName = function (name: string): DisplayObject {
	this.name = name;
	return this;
};

// Get name of display object. if name is "null", then the constructor name is returned
DisplayObject.prototype.getName = function (): string {
	return this.name || Tools.getConstructorName(this);
};

// Clone method
Container.prototype.clone = function (noChildren?: boolean): Container {
	return ContainerImporter.import(ContainerExporter.export(this, noChildren), "");
};

/**
 * Override updateTransform() function of PIXI.Container's prototype
 */
const updateTransformFunc = Container.prototype.updateTransform; // Original updateTransform function of container.
const thisTransform = new Transform(); // Keep transform reference containing all transform informations of the current container being updated.
const parentTransform = new Transform(); // Keep transform reference containing all transform informations of the current container's parent being updated.

const updateTransform = function (): void {
	this.emit("pretransform");

	// Force to use base update transform?
	// Else, check if "this" is the stage of the application. The parent of the stage has 0 child.
	if (this._mustReturn || !this.parent.children || this.parent.children.length === 0) {
		return updateTransformFunc.call(this);
	}

	// No dock and no resize ?
	if ((!this.docking && !this.resize) || !this.scale.x || !this.scale.y) {
		// anyway, let update the bounds in all cases to keep bounds synchronized
		if (
			!this.dockingBounds &&
			!this.dockAsPoint &&
			this._boundsID !== this._lastBoundsID &&
			!this.cacheAsBitmap &&
			!this.pluginName
		) {
			// Check at least on child has docking or resize
			for (let i = 0, len = this.children.length; i < len; i++) {
				const child = this.children[i];
				if (!child.docking && !child._resize) {
					continue;
				}

				this._mustReturn = true;
				this.getLocalBounds(ContainerUtils.tempRectangle);
				this._mustReturn = false;

				return updateTransformFunc.call(this);
			}
		}

		return updateTransformFunc.call(this);
	}

	// Check availability to perform docking and resize
	if (!this.parent.calculateBounds || !this.transform.position) {
		return updateTransformFunc.call(this);
	}

	// Get world transforms of this and parent
	thisTransform.setFromMatrix(this.worldTransform);
	parentTransform.setFromMatrix(this.parent.worldTransform);

	ContainerUtils.refreshBounds(this, thisTransform);
	ContainerUtils.refreshParentBounds(this.parent, parentTransform);

	// Apply resize
	if (
		this._resize &&
		this._resize.resize &&
		this._resize.isDirty(ContainerUtils.parentBounds, ContainerUtils.bounds)
	) {
		Resize._transform = thisTransform;
		Resize._parentTransform = parentTransform;
		Resize._parentBounds = ContainerUtils.parentBounds;
		Resize._bounds = ContainerUtils.bounds;
		this._resize.resize(this);

		if (this.layouts) {
			(this.layouts as Layout).apply(
				ContainerUtils.parentBounds.width,
				ContainerUtils.parentBounds.height
			);
		}
	}

	// Apply docking
	if (
		this.docking &&
		this.docking.dock &&
		this.docking.isDirty(this, ContainerUtils.parentBounds, ContainerUtils.bounds)
	) {
		Docking._transform = thisTransform;
		Docking._parentTransform = parentTransform;
		Docking._parentBounds = ContainerUtils.parentBounds;
		Docking._bounds = ContainerUtils.bounds;
		Docking._pivotInfluenceRatio = this.docking.cancelPivot ? 1 : 0;
		Docking._transformedPivot.set(this.scale.x < 0 ? -1 : 1, this.scale.y < 0 ? -1 : 1);

		this.docking.dock(this);
	}

	// Update transform of "this" and each child to sync transforms
	ContainerUtils.recursivelyUpdateTransform(this);

	// Update
	updateTransformFunc.call(this);

	// Emit post-transform event
	this.emit("posttransform");
};

export default updateTransform;
