import { Texture, Sprite } from "pixi.js";
import { createHash } from "crypto";
import { Tools } from "@gdk/core";

import { elementToPath } from "../../../tools";
import { ContainerExporter } from "../../../loading/export/containerExporter";
import { TextureExporter } from "../../../loading/export/textureExporter";
import type { IProjectContainer, IProjectSymbolSprite } from "../../../loading/model";
import { TextureImporter } from "../../../loading/import/textureImporter";
import { ContainerImporter } from "../../../loading/import/containerImporter";

export class SymbolSpriteError extends Error {}

export class SymbolSprite extends Sprite {
	public static readonly instances: SymbolSprite[] = [];

	protected readonly _frames: { [frameId: string]: Texture } = {};
	protected _defaultFrameId?: string;
	protected _frameId?: string;
	protected _sealed: boolean = false;

	constructor(
		frames?: { [frameId: string]: Texture },
		sealed: boolean = false,
		defaultFrameId?: string
	) {
		super();

		for (let key in frames) {
			this.add(key, frames[key]);
		}
		if (defaultFrameId !== undefined) {
			this.defaultFrameId = defaultFrameId;
			this.goto(defaultFrameId);
		}
		if (sealed) {
			this.seal();
		}
		SymbolSprite.instances.push(this);
	}

	/**
	 * Position this SymbolSprite to the specified frame.
	 * Raise a SymbolSpriteError if frame is not registered.
	 */
	public goto(frameId: string): void {
		this.texture = this.getFrame(frameId);
		this._frameId = frameId;
	}

	public getFrame(frameId: string): Texture {
		this.throwIfNoFrame(frameId);
		return this.frames[frameId];
	}

	protected throwIfNoFrame(frameId: string): void {
		if (this.frames[frameId] === undefined) {
			throw new SymbolSpriteError(
				`${elementToPath(this)}: could not find any frame named "${frameId}"`
			);
		}
	}

	/**
	 * Register a texture
	 */
	public add(key: string, texture: Texture): void {
		this.throwIfSealed();
		this._frames[key] = texture;
		if (this._defaultFrameId === undefined) {
			this.defaultFrameId = key;
			this.goto(key);
		}
	}

	public remove(frameId: string) {
		this.throwIfSealed();
		this.throwIfNoFrame(frameId);
		delete this._frames[frameId];
		if (this.defaultFrameId === frameId) {
			this._defaultFrameId = Object.keys(this.frames)[0];
		}

		if (this.frameId === frameId) {
			if (this.defaultFrameId !== undefined) {
				this.goto(this.defaultFrameId);
			} else {
				this.texture = Texture.WHITE;
				this._frameId = undefined;
			}
		}
	}

	protected throwIfSealed() {
		if (this._sealed) {
			throw new SymbolSpriteError(`${elementToPath(this)}: cannot update a sealed SymbolSprite`);
		}
	}

	/**
	 * Return registered frames, by id.
	 */
	public get frames(): { [frameId: string]: Texture } {
		return { ...this._frames };
	}

	public get defaultFrameId(): string | undefined {
		return this._defaultFrameId;
	}

	public set defaultFrameId(frameId: string) {
		this.throwIfSealed();
		this.throwIfNoFrame(frameId);
		this._defaultFrameId = frameId;
	}

	public get frameId(): string | undefined {
		return this._frameId;
	}

	/**
	 * Return symbol signature, a sha256 signature of ordered frame ids.
	 */
	public get signature(): string {
		return createHash("sha256")
			.update(
				(this.defaultFrameId ?? "") +
					Object.keys(this._frames)
						.sort()
						.map((frameId) => frameId + String(frameId.length))
						.join()
			)
			.digest("hex");
	}

	public get sealed(): boolean {
		return this._sealed;
	}

	public seal() {
		this._sealed = true;
	}

	public export(): IProjectContainer & IProjectSymbolSprite {
		const frames = {};
		for (const frameId in this.frames) {
			frames[frameId] = TextureExporter.exportTexture(this.frames[frameId]);
		}
		return Tools.assign(ContainerExporter.exportContainer(this, false), {
			type: "ui.SymbolSprite",
			symbolSprite: {
				frames,
				defaultFrameId: this.defaultFrameId,
			},
		});
	}

	public static parse(
		config: IProjectSymbolSprite,
		baseUrl: string,
		sprite?: SymbolSprite
	): SymbolSprite {
		sprite = ContainerImporter.importContainer<SymbolSprite>(config, baseUrl, new SymbolSprite());

		const frames = config.symbolSprite.frames;
		for (const frameId in frames) {
			sprite.add(frameId, TextureImporter.importTexture(frames[frameId], baseUrl));
		}

		const defaultFrameId = config.symbolSprite.defaultFrameId;
		if (defaultFrameId !== undefined) {
			sprite.defaultFrameId = defaultFrameId;
			sprite.goto(defaultFrameId);
		}

		return sprite;
	}
}
