import Proton from "proton-js";

import type {
	IPosition,
	IMass,
	IBody,
	ILife,
	IMinMaxSpan,
	IVelocity,
	IRotate,
	IGravity,
	IAlpha,
	ICrossZone,
	IAttraction,
	ICollision,
	IColor,
	IForce,
	IGravityWell,
	IRandomDrift,
	IRepulsion,
	IScale,
	IRadius,
	IRate,
	IBlendMode,
	IBancking,
	ILineZone,
	IPointZone,
	ICircleZone,
	IRectZone,
} from "./parseInterfaces";

export class EmitterParser {
	/**
	 * Parse Proton JSON
	 * @param  {Array<any>} json
	 * @returns Proton.Emitter
	 */
	public static parse(json: any[]): Proton.Emitter {
		const emitter = new Proton.Emitter();

		for (const i in json) {
			if (!json.hasOwnProperty(i)) {
				continue;
			}
			const obj = json[i];

			try {
				const func = this[obj.type];
				func(emitter, obj.props);
			} catch (e) {
				// we should use the logger for that @TODO
				console.error(`Error in parser, check type in json : ${obj.type}  || ${e}`);
			}
		}

		return emitter;
	}

	private static valueOrSpan(value: number | IMinMaxSpan | string): number | Proton.Span {
		if (typeof value === "object") {
			return new Proton.Span(value.min, value.max, value.center);
		} else {
			return value;
		}
	}

	private static body(emitter: Proton.Emitter, props: IBody): void {
		if (!props.image || !props.width || !props.height) {
			return;
		}
		emitter.addInitialize(new Proton.Body(props.image, props.width, props.height));
	}

	private static life(emitter: Proton.Emitter, props: ILife): void {
		if (!props.value) {
			return;
		}
		emitter.addInitialize(new Proton.Life(EmitterParser.valueOrSpan(props.value)));
	}

	private static mass(emitter: Proton.Emitter, props: IMass): void {
		if (!props.value) {
			return;
		}
		emitter.addInitialize(new Proton.Mass(EmitterParser.valueOrSpan(props.value)));
	}

	private static position(emitter: Proton.Emitter, props: IPosition): void {
		if (!props.value) {
			return;
		}

		let zone = null;
		switch (props.type) {
			case "rect": {
				const value = props.value as IRectZone;
				zone = new Proton.RectZone(value.x, value.y, value.width, value.height);
				break;
			}
			case "line": {
				const value = props.value as ILineZone;
				zone = new Proton.LineZone(value.x, value.y, value.x2, value.y2, value.direction);
				break;
			}
			case "circle": {
				const value = props.value as ICircleZone;
				zone = new Proton.CircleZone(value.x, value.y, value.radius);
				break;
			}
			case "point":
			default: {
				const value = props.value as IPointZone;
				zone = new Proton.PointZone(value.x, value.y);
				break;
			}
		}
		emitter.addInitialize(new Proton.Position(zone));
	}

	private static radius(emitter: Proton.Emitter, props: IRadius): void {
		if (!props.value) {
			return;
		}
		emitter.addInitialize(new Proton.Radius(EmitterParser.valueOrSpan(props.value)));
	}

	private static rate(emitter: Proton.emitter, props: IRate): void {
		if (!props.end || !props.start) {
			return;
		}
		emitter.rate = new Proton.Rate(
			EmitterParser.valueOrSpan(props.start),
			EmitterParser.valueOrSpan(props.end)
		);
	}

	private static velocity(emitter: Proton.Emitter, props: IVelocity): void {
		if (!props.angle || !props.strengh || !props.type) {
			return;
		}
		emitter.addInitialize(
			new Proton.Velocity(
				EmitterParser.valueOrSpan(props.strengh),
				EmitterParser.valueOrSpan(props.angle),
				props.type
			)
		);
	}

	private static alpha(emitter: Proton.Emitter, props: IAlpha): void {
		if (!props.start) {
			return;
		}
		emitter.addBehaviour(
			new Proton.Alpha(
				EmitterParser.valueOrSpan(props.start),
				EmitterParser.valueOrSpan(props.end),
				props.life,
				props.easeing
			)
		);
	}

	private static attraction(emitter: Proton.Emitter, props: IAttraction): void {
		if (!props.target || !props.f || !props.radius) {
			return;
		}
		emitter.addBehaviour(
			new Proton.Attraction(
				new Proton.PointZone(props.target.x, props.target.y),
				props.f,
				props.radius,
				props.life,
				props.easeing
			)
		);
	}

	private static collision(emitter: Proton.Emitter, props: ICollision): void {
		emitter.addBehaviour(new Proton.Collision(emitter, props.mass));
	}

	private static color(emitter: Proton.Emitter, props: IColor): void {
		if (!props.value) {
			return;
		}

		emitter.addBehaviour(new Proton.Color(props.value, props.type, props.life, props.easeing));
	}

	private static crossZone(emitter: Proton.Emitter, props: ICrossZone): void {
		emitter.addBehaviour(
			new Proton.CrossZone(
				new Proton.RectZone(props.rect.x, props.rect.y, props.rect.width, props.rect.height),
				props.type,
				props.life,
				props.easeing
			)
		);
	}

	private static force(emitter: Proton.Emitter, props: IForce): void {
		if (!props.f) {
			return;
		}
		emitter.addBehaviour(new Proton.Force(props.f.x, props.f.y, props.life, props.easeing));
	}

	private static gravity(emitter: Proton.Emitter, props: IGravity): void {
		if (!props.g) {
			return;
		}
		emitter.addBehaviour(new Proton.Gravity(props.g, props.life, props.easeing));
	}

	private static gravityWell(emitter: Proton.Emitter, props: IGravityWell): void {
		if (!props.target) {
			return;
		}
		emitter.addBehaviour(
			new Proton.GravityWell(
				new Proton.PointZone(props.target.x, props.target.y),
				props.force,
				props.life,
				props.easeing
			)
		);
	}

	private static randomDrift(emitter: Proton.Emitter, props: IRandomDrift): void {
		if (!props.target || !props.delay) {
			return;
		}
		emitter.addBehaviour(
			new Proton.RandomDrift(props.target.x, props.target.y, props.delay, props.life, props.easeing)
		);
	}

	private static repulsion(emitter: Proton.Emitter, props: IRepulsion): void {
		if (!props.target || !props.force || !props.radius) {
			return;
		}
		emitter.addBehaviour(
			new Proton.Repulsion(
				new Proton.PointZone(props.target.x, props.target.y),
				props.force,
				props.radius,
				props.life,
				props.easeing
			)
		);
	}

	private static bancking(emitter: Proton.Emitter, props: IBancking): void {
		if (props.value) {
			emitter.addBehaviour(new Proton.Rotate());
		}
	}

	private static rotate(emitter: Proton.Emitter, props: IRotate): void {
		emitter.addBehaviour(
			new Proton.Rotate(
				EmitterParser.valueOrSpan(props.start),
				EmitterParser.valueOrSpan(props.end),
				props.style,
				props.life,
				props.easeing
			)
		);
	}

	private static scale(emitter: Proton.Emitter, props: IScale): void {
		emitter.addBehaviour(
			new Proton.Scale(
				EmitterParser.valueOrSpan(props.start),
				EmitterParser.valueOrSpan(props.end),
				props.life,
				props.easeing
			)
		);
	}

	private static blendMode(emitter: Proton.Emitter, props: IBlendMode): Proton.Behaviour {
		if (!props.mode) {
			return;
		}

		const behaviour: Proton.Behaviour = new Proton.Behaviour();
		behaviour.initialize = (particle) => {
			particle.blend = props.mode;
		};

		behaviour.applyBehaviour = (particle, time, index) => {
			particle.blend = props.mode;
		};

		emitter.addBehaviour(behaviour);
	}
}
