import { Container, extras as Extra, Texture } from "pixi.js";

import { Docking } from "../container/docking";
import { DockingBounds } from "../container/dockingBounds";

export interface IClipOptionsOptional {
	docking?: Docking;
	speed?: number;
	anchor?: number;
	play?: boolean;
	loop?: boolean;
}

export interface IClipFramesOptions extends IClipOptionsOptional {
	frameNames: string[];
	duration: number;
}

export interface IClipOptions extends IClipOptionsOptional {
	prefix: string;
	suffix: string;
	in: number;
	out: number;
	duration: number;
}

export interface IAnimatedSpriteTextureTimeObject {
	texture: Texture;
	time: number;
}

export type textureTimedCollection = IAnimatedSpriteTextureTimeObject[];

export class Clip extends Container {
	// Public members
	public base: Extra.AnimatedSprite;

	// Private members
	private _frames: textureTimedCollection = [];
	private _frmIx: number = 0;
	private _opts: IClipOptions | IClipFramesOptions;

	// Static members
	private static _zero: string = "0";
	private static _frameNamesKey: string = "frameNames";

	/**
	 * Constructor
	 * @param options refer to IClipOptions
	 */
	public constructor(options: IClipOptions | IClipFramesOptions) {
		super();

		this._opts = options;
		this._setFrames();
		this.base = new Extra.AnimatedSprite(this._frames);
		this.base.docking = options.docking || new Docking(Docking.centerAll);
		const anchorValue = options.anchor ? 0 : options.anchor;
		this.base.anchor.set(anchorValue);
		this.base.animationSpeed = options.speed ? options.speed : 1;
		this.addChild(this.base);

		if (Boolean(options.loop) && options.loop) {
			this.base.loop = true;
		}
		if (Boolean(options.play) && options.play) {
			this.play();
		}

		this.dockingBounds = new DockingBounds(this.base.width, this.base.height);
	}

	/**
	 * play the base clip
	 */
	public play(): void {
		if (!this.base.playing) {
			this.base.play();
		}
	}

	/**
	 * stop the base clip
	 */
	public stop(): void {
		if (this.base.playing) {
			this.base.stop();
		}
	}

	/**
	 * go to required frameNumber and play if withPlay true or go to it and stop
	 *
	 * @param frameNumber set frame index
	 * @param withPlay let clip playing ot not
	 *
	 */
	public goto(frameNumber: number, withPlay?: boolean): void {
		if (Boolean(withPlay) && withPlay) {
			this.base.gotoAndPlay(frameNumber);
		} else {
			this.base.gotoAndStop(frameNumber);
		}
	}

	/**
	 * play a sequence from frameNumberIn to frameNumberOut then stop
	 *
	 * @param frameNumberIn set frame index to start from
	 * @param frameNumberOut set frame to play to
	 * @param cb set on complete callback
	 *
	 */
	public playSequence(frameNumberIn: number, frameNumberOut: number, cb?: () => void): void {
		this.base.onFrameChange = (currentFrame: number) => {
			if (currentFrame === frameNumberOut) {
				this.base.onFrameChange = undefined;
				this.base.stop();
				if (cb) {
					cb();
				}
			}
		};
		this.base.gotoAndPlay(frameNumberIn);
	}

	/**
	 * returns the frame duration in ms
	 * calculated for the global duration option and the number of frames
	 * defined in options
	 */
	protected get frameDuration(): number {
		const durationParts: number = this._hasFrameNames
			? (this._opts as IClipFramesOptions).frameNames.length
			: (this._opts as IClipOptions).out - (this._opts as IClipOptions).in;
		return (this._opts.duration * 1000) / durationParts;
	}

	/**
	 * hasFrameNames
	 * return true if frameNames is defined in options
	 *
	 * @returns boolean
	 */
	private get _hasFrameNames(): boolean {
		return this._isIClipFramesOptions(this._opts);
	}

	/**
	 * isIClipFramesOptions
	 *
	 * return true if opts matches IClipFramesOptions interface
	 *
	 * @param any
	 * @returns boolean
	 */
	private _isIClipFramesOptions(object: any): object is IClipFramesOptions {
		return Clip._frameNamesKey in object;
	}

	/**
	 * load timed textures into frames
	 *
	 * @returns void
	 */
	private _setFrames(): void {
		this._frames = [];
		if (this._hasFrameNames) {
			const frameLenght: number = (this._opts as IClipFramesOptions).frameNames.length;
			for (this._frmIx = 0; this._frmIx < frameLenght; this._frmIx++) {
				this._frames.push({
					texture: Texture.fromFrame((this._opts as IClipFramesOptions).frameNames[this._frmIx]),
					time: this.frameDuration,
				});
			}
		} else {
			const firstFrame: number = (this._opts as IClipOptions).in;
			const lastFrame: number = (this._opts as IClipOptions).out;
			for (this._frmIx = firstFrame; this._frmIx <= lastFrame; this._frmIx++) {
				const name: string =
					(this._opts as IClipOptions).prefix +
					this._padTwo(this._frmIx) +
					(this._opts as IClipOptions).suffix;
				this._frames.push({
					texture: Texture.fromFrame(name),
					time: this.frameDuration,
				});
			}
		}
	}

	/**
	 * padTwo
	 *
	 * return left 0 padded 2 digits number as string
	 *
	 * @param number
	 * @returns string
	 */
	private _padTwo(n: number): string {
		return n < 10 ? Clip._zero + n : n.toString();
	}
}
