import { Container } from "pixi.js";
import { Tools } from "@gdk/core";
import type { IView } from "@gdk/core";

import { StateImporter } from "../loading/import/stateImporter";
import { DockingBounds } from "../container/dockingBounds";
import { ContainerExporter } from "../loading/export/containerExporter";
import type { IProjectContainer } from "../loading/model";

export class LoadedView extends Container implements IView {
	/**
	 * Defines the optional name of the object to be tracked.
	 * If unset, a default value will be taken by the tracker. For example,
	 * the name of the object (button, scratch, etc.).
	 */
	public trackingName: string = "";

	// Private members
	private _baseUrl: string;
	private _viewConfig: any;
	private _project: any;

	/**
	 * Constructor
	 * @param baseUrl the base url of the loader
	 * @param viewConfig the view configuration JSON
	 * @param project the editor project
	 */
	public constructor(baseUrl?: string, viewConfig?: any, project?: any) {
		super();

		// Misc.
		this._baseUrl = baseUrl;
		this._viewConfig = viewConfig;
		this._project = project;

		// Create a default viewport (1, 1)
		// will not trigger lock screen as the ratio === 1
		this.dockingBounds = new DockingBounds(1, 1);
	}

	/**
	 * Configure the view before loading
	 * @param baseUrl the base url of the loader
	 * @param viewCode the view configuration JSON
	 */
	public config(baseUrl: string, viewConfig: any, project: any): void {
		this._baseUrl = baseUrl;
		this._viewConfig = viewConfig;
		this._project = project;
	}

	/**
	 * On the view will appear
	 */
	public onWillAppear(done?: () => void): void;
	public onWillAppear(): void {
		this.visible = true;
	}

	/**
	 * On the view will disappear
	 */
	public onWillDisappear(done?: () => void): void;
	public onWillDisappear(): void {
		this.visible = false;
	}

	/**
	 * On load the view
	 * @param done callback once loading done
	 */
	public onLoad(): void {
		// Load the view here
		StateImporter.importView(this, this._viewConfig, this._baseUrl, this._project);

		// A view is not visible by default
		this.visible = false;
	}

	/**
	 * Returns a child container by name. Tries to find recursively
	 * @param name the name of the container to find
	 * @param root the root container for traversing children (default is "this" view)
	 */
	public getContainerByName<T extends Container>(name: string, root: Container = this): T {
		for (const c of root.children) {
			if (c.name === name) {
				return c as T;
			}

			const result = this.getContainerByName(name, c as Container);
			if (result) {
				return result as T;
			}
		}
	}

	public export(): IProjectContainer {
		return Tools.assign(ContainerExporter.exportContainer(this, false), {
			visible: false,
		});
	}
}
