import Proton from "proton-js";
import { Tools } from "@gdk/core";

import { ESerializedEmitterComponentType } from "./types";
import type { ISerializedEmitterComponent, ISerializedEmitter } from "./types";

export class ProtonSerializer {
	/**
	 * Serializes the given emitter.
	 * @param emitter defines the reference to the emitter to export.
	 */
	public static serializeEmitter(emitter: Proton.Emitter): ISerializedEmitter {
		const initializers: ISerializedEmitterComponent[] = [];
		const behaviors: ISerializedEmitterComponent[] = [];

		emitter.initializes.forEach((i) => {
			const type = Tools.getConstructorName(i);
			switch (type) {
				case "Body":
					initializers.push({ type, arguments: [i.image._arr[0], i.w, i.h] });
					break;
				case "Life":
					initializers.push({
						type,
						arguments: [
							{ type: ESerializedEmitterComponentType.Span, arguments: [i.lifePan.a, i.lifePan.b] },
						],
					});
					break;
				case "Mass":
					initializers.push({
						type,
						arguments: [
							{ type: ESerializedEmitterComponentType.Span, arguments: [i.massPan.a, i.massPan.b] },
						],
					});
					break;
				case "Radius":
					initializers.push({
						type,
						arguments: [
							{ type: ESerializedEmitterComponentType.Span, arguments: [i.radius.a, i.radius.b] },
						],
					});
					break;
				case "Velocity":
					initializers.push({
						type,
						arguments: [
							{ type: ESerializedEmitterComponentType.Span, arguments: [i.rPan.a, i.rPan.b] },
							{ type: ESerializedEmitterComponentType.Span, arguments: [i.thaPan.a, i.thaPan.b] },
							i.type,
						],
					});
					break;
				case "Position":
					const zoneType = Tools.getConstructorName(i.zone);
					const positionArgs: Array<number | string> = [];

					switch (zoneType) {
						case "PointZone":
							positionArgs.push(i.zone.x, i.zone.y);
							break;
						case "RectZone":
							positionArgs.push(i.zone.x, i.zone.y, i.zone.width, i.zone.height);
							break;
						case "CircleZone":
							positionArgs.push(i.zone.x, i.zone.y, i.zone.radius);
							break;
						case "LineZone":
							positionArgs.push(i.zone.x1, i.zone.y1, i.zone.x2, i.zone.y2, i.zone.direction);
							break;
					}

					initializers.push({ type, arguments: [{ type: zoneType, arguments: positionArgs }] });
					break;
			}
		});

		emitter.behaviours.forEach((b) => {
			const type = Tools.getConstructorName(b);
			switch (type) {
				case "Color":
					behaviors.push({ type, arguments: [b.a._arr[0], b.b._arr[0]] });
					break;
				case "Rotate":
					behaviors.push({ type, arguments: [null, null, b.style] });
					break;
				case "Gravity":
					behaviors.push({ type, arguments: [b.force.y / Proton.MEASURE] });
					break;
				case "Alpha":
					behaviors.push({
						type,
						arguments: [
							{ type: "Span", arguments: [b.a.a, b.a.b] },
							{ type: "Span", arguments: [b.b.a, b.b.b] },
						],
					});
					break;
				case "Scale":
					behaviors.push({
						type,
						arguments: [
							{ type: "Span", arguments: [b.a.a, b.a.b] },
							{ type: "Span", arguments: [b.b.a, b.b.b] },
						],
					});
					break;
				case "Force":
					const denormalized = b.force.divideScalar(Proton.MEASURE);
					behaviors.push({ type, arguments: [denormalized.x, denormalized.y] });
					break;
				case "Attraction":
				case "Repulsion":
					behaviors.push({
						type,
						arguments: [
							{
								type: ESerializedEmitterComponentType.Vector2D,
								arguments: [b.targetPosition.x, b.targetPosition.y],
							},
							(b.force * -1.0) / Proton.MEASURE,
							b.radius,
						],
					});
					break;
				case "RandomDrift":
					behaviors.push({
						type,
						arguments: [b.panFoce.x / Proton.MEASURE, b.panFoce.y / Proton.MEASURE, b.delay],
					});
					break;
				case "CrossZone":
					const zoneType = Tools.getConstructorName(b.zone);
					const positionArgs: Array<number | string> = [];

					switch (zoneType) {
						case "PointZone":
							positionArgs.push(b.zone.x, b.zone.y);
							break;
						case "RectZone":
							positionArgs.push(b.zone.x, b.zone.y, b.zone.width, b.zone.height);
							break;
						case "CircleZone":
							positionArgs.push(b.zone.x, b.zone.y, b.zone.radius);
							break;
						case "LineZone":
							positionArgs.push(b.zone.x1, b.zone.y1, b.zone.x2, b.zone.y2, b.zone.direction);
							break;
					}

					behaviors.push({ type, arguments: [{ type: zoneType, arguments: positionArgs }] });
					break;
			}
		});

		return {
			name: emitter.name,
			id: emitter.id,
			rate: {
				type: "Rate",
				arguments: [
					{ type: "Span", arguments: [emitter.rate.numPan.a, emitter.rate.numPan.b] },
					{ type: "Span", arguments: [emitter.rate.timePan.a, emitter.rate.timePan.b] },
				],
			},
			damping: emitter.damping,
			behaviors,
			initializers,
		};
	}

	/**
	 * Serializes the given particle system.and returns its JSON representation.
	 * @param system defines the reference to the particles system to serialize.
	 */
	public static serializeSystem(system: Proton): ISerializedEmitter[] {
		return system.emitters?.map((e) => this.serializeEmitter(e)) ?? [];
	}

	/**
	 * Parses the emitters according the given list of JSON representations.
	 * @param config defines the array of JSON representatons of the Proton emitters.
	 */
	public static parseEmitters(config: ISerializedEmitter[], system?: Proton): Proton.Emitter[] {
		return config.map((c) => this.parseEmitter(c, system));
	}

	/**
	 * Parses the emitter according to the given JSON configuration.
	 * @param config defines the JSON representation of the Proton emitter.
	 */
	public static parseEmitter(config: ISerializedEmitter, system?: Proton): Proton.Emitter {
		const emitter = new Proton.Emitter();

		emitter.id = config.id;
		emitter.name = config.name;
		emitter.damping = config.damping;
		emitter.rate = this.parseComponent(config.rate);

		// Initializers
		config.initializers.forEach((i) => {
			const instance = this.parseComponent(i);
			if (instance) {
				emitter.addInitialize(instance);
			}
		});

		// Behaviors
		config.behaviors.forEach((b) => {
			const instance = this.parseComponent(b);
			if (instance) {
				emitter.addBehaviour(instance);
			}
		});

		if (system) {
			system.addEmitter(emitter);
		}

		return emitter;
	}

	/**
	 * Parses the span according to the given JSON representation.
	 * @param span defines the JSON representation of the span.
	 */
	public static parseSpan(span: ISerializedEmitterComponent): Proton.Span {
		const a = span.arguments[0];
		const b = span.arguments[1];
		const c = span.arguments[2];

		if (a === undefined) {
			return new Proton.Span(0, 1);
		}

		if (b === undefined) {
			return new Proton.Span(a);
		}

		if (c === undefined) {
			return new Proton.Span(a, b);
		}

		return new Proton.Span(a, b, c);
	}

	/**
	 * Parses the component according to the given JSON representation.
	 * @param component defines the JSON representation of the component.
	 */
	public static parseComponent<T = any>(component: ISerializedEmitterComponent): T {
		if (!component) {
			return null;
		}

		if (component.type === "Span") {
			return this.parseSpan(component);
		}

		const ctor = Proton[component.type];
		if (!ctor) {
			return null;
		}

		const args: any[] = [];
		(component.arguments ?? []).forEach((a) => {
			if (typeof a === "object") {
				args.push(this.parseComponent(a));
			} else {
				args.push(a);
			}
		});

		return new ctor(...args);
	}
}
