import { Texture, Rectangle } from "pixi.js";
import Proton from "proton-js";
import { TimelineMax, TweenMax, Linear } from "gsap";
import { Vector2 } from "@gdk/core";
import type { Sprite } from "pixi.js";

import { AbstractScratch2Renderer } from "../types";
import { Scratch2Presets } from "../scratch2Presets";
import { ScratchAssets } from "../../scratch/scratchAssets";
import { TextureExporter } from "../../../loading/export/textureExporter";
import { TextureImporter } from "../../../loading/import/textureImporter";
import { EmitterParser2 } from "../../../proton/parser2/emitterParser";
import type { ScratchPath } from "../../scratch/scratchPath";
import type { IPointerMoveEvent } from "../../baseui/ui/customPointer";
import type { Scratch2Zone } from "../zone";
import type { Scratch2 } from "../scratch2";
import type { IEmitterConfig } from "../../../proton/parser2";

export class ParticlesScratchRenderer extends AbstractScratch2Renderer {
	/**
	 * Defines the texture reference used to render the particles.
	 */
	public texture: Texture;
	/**
	 * Defines the scratch preset used while scratching.
	 */
	public scratchPreset: IEmitterConfig = Scratch2Presets.FDJ_SCRATCH_BIG;
	/**
	 * Defines the auto preset used while auto-scratching.
	 */
	public autoPreset: IEmitterConfig = Scratch2Presets.FDJ_SCRATCH_BIG;
	/**
	 * Defines the auto preset used while auto-scratching a zone.
	 */
	public autoZonePreset: IEmitterConfig = Scratch2Presets.FDJ_SCRATCH_BIG;

	private _renderer: Proton.PIXIRenderer = null;
	private _base: Proton = null;
	private _emitter: Proton.Emitter = null;
	private _zone: Proton.RectZone = null;

	private _scratch: Scratch2 = null;

	private _stopped: boolean = false;

	private _autoEmitters: Proton.Emitter[] = [];
	private _autoTimelines: TimelineMax[] = [];

	/**
	 * Constructor
	 * @param texture the texture being used
	 */
	public constructor(texture?: Texture) {
		super();

		// Init scratch assets cache
		ScratchAssets.initCache();

		// Texture
		this.texture = texture || Texture.fromFrame("scratch/scratch_0.png");

		// Renderer
		this._renderer = new Proton.PixiRenderer(this);
		this._renderer.setColor = true;

		// On particle update
		const updateParticleUpdateFn = this._renderer.onParticleUpdate;
		this._renderer.onParticleUpdate = (particle) => {
			if (!this._scratch.zones.length) {
				this._scratch.setDirty();
			} else {
				const body = particle.body as Sprite;
				const particleRectangle = new Rectangle(
					particle.p.x,
					particle.p.y,
					body.width,
					body.height
				);

				this._scratch.zones.forEach(
					(z) =>
						this._testRectangleIntersection(z.rectangle, particleRectangle) &&
						this._scratch.setDirty(z)
				);
			}

			this._scratch._playScratchSound();

			updateParticleUpdateFn.call(this._renderer, particle);
		};

		// Finish
		this.reset();
	}

	/**
	 * Called on the scratch has been reset.
	 */
	public reset(): void {
		// Base
		if (this._base) {
			this._base.destroy();
		}
		this._base = new Proton();
		this._base.addRenderer(this._renderer);

		// Main emitter
		if (this._emitter) {
			this._clearParticles(this._emitter);
			this._emitter.destroy();
		}
		this._emitter = this._createEmitter(this.scratchPreset);
		if (this._zone) {
			this._emitter.addBehaviour(new Proton.CrossZone(this._zone, "dead"));
		}

		// Auto emitters
		this._autoEmitters.forEach((ae) => {
			this._base.removeEmitter(ae);
			this._clearParticles(ae);
			ae.destroy();
		});
		this._autoEmitters = [];

		// Auto
		this._autoTimelines.forEach((at) => at.kill());
		this._autoTimelines = [];

		if (this._scratch) {
			this._zone.width = this._scratch.scratchRenderTexture.width;
			this._zone.height = this._scratch.scratchRenderTexture.height;
		}
	}

	/**
	 * Called once the renderer is activated by the scratch component.
	 * @param scratch the scratch component reference.
	 */
	public onActivate(scratch: Scratch2): void {
		this._scratch = scratch;

		this._zone = new Proton.RectZone(0, 0, scratch.content.width, scratch.content.height);
		this._emitter.addBehaviour(new Proton.CrossZone(this._zone, "dead"));
	}

	/**
	 * Called on the renderer is being rendered.
	 */
	public onRender(): void {
		if (!this._stopped) {
			this._base.update();
		}
	}

	/**
	 * Called once the renderer has been destroyed.
	 */
	public onDestroy(): void {
		this._base.destroy();
		this._emitter.destroy();
		super.onDestroy();
	}

	/**
	 * Called on the user wants to stop the renderer. Typically to pause in auto mode.
	 */
	public onStop(): void {
		this._autoEmitters.forEach((ae) => {
			this._base.removeEmitter(ae);
			this._clearParticles(ae);
			ae.destroy();
		});
		this._autoEmitters = [];

		this._autoTimelines.forEach((at) => at.kill());
		this._autoTimelines = [];

		this._stopped = true;
	}

	/**
	 * Called once the user scratchs the scratch component.
	 * @param event object that contains all the pointer event values (distance, etc.)
	 */
	public onScratch(scratch: Scratch2, event: IPointerMoveEvent): void {
		// Direction
		const p1 = new Vector2(event.position.x, event.position.y);
		const p2 = new Vector2(this._emitter.p.x, this._emitter.p.y);
		const direction = p1.subtract(p2).normalize();

		this._emitter.p.set(event.position.x, event.position.y);
		this._emitter.v.set(direction.x, direction.y);

		this._emitter.emit("once");

		this._stopped = false;
	}

	/**
	 * Called on the user wants to auto-reveal the scratch component.
	 * @param scratch the scratch component reference.
	 * @param zone the zone to automatically reveal. If no zone provided in the scratch component, the zone represents the overall scratch component.
	 * @param path the path used while animating the auto mode.
	 * @param done called once the auto animation has been completed.
	 */
	public onAuto(scratch: Scratch2, zone: Scratch2Zone, path: ScratchPath, done: () => void): void {
		// Don't stop anymore
		this._stopped = false;

		// Path
		if (!path) {
			return done();
		}

		path.position.set(zone.x, zone.y);
		path.setSize(zone.width, zone.height, 1.0);
		scratch.addChild(path);

		// Create auto emitter
		const emitter = this._createEmitter(
			zone !== scratch.defaultZone ? this.autoZonePreset : this.autoPreset
		);
		this._autoEmitters.push(emitter);

		// Timeline
		const previousPosition = new Vector2(zone.x, zone.y);
		emitter.p.set(zone.x, zone.y);

		const autoTimeline = new TimelineMax({
			onUpdate: () => {
				const normalized = new Vector2(
					emitter.p.x - previousPosition.x,
					emitter.p.y - previousPosition.y
				).normalize();

				emitter.v.x = normalized.x;
				emitter.v.y = normalized.y;

				emitter.emit("once");

				path.svg.lineTo(emitter.p.x, emitter.p.y);

				previousPosition.x = emitter.p.x;
				previousPosition.y = emitter.p.y;

				// Set dirty
				scratch.setDirty(zone);
			},
			onComplete: () => {
				// Remove path from children
				scratch.removeChild(path);

				// Remove emitter
				this._autoEmitters.splice(this._autoEmitters.indexOf(emitter), 1);
				this._clearParticles(emitter);
				emitter.destroy();

				// Force reveal and done
				scratch.reveal();
				done();
			},
		});

		// Bezier
		const besier = TweenMax.to(emitter.p, path.duration, {
			ease: Linear.easeNone,
			bezier: {
				type: "Cubic",
				values: path.values,
				autoRotate: true,
			},
		});
		autoTimeline.add(besier);

		// Play !
		autoTimeline.play();

		// Push
		this._autoTimelines.push(autoTimeline);
	}

	// Creates an emitter an returns its reference
	private _createEmitter(preset: any): Proton.Emitter {
		const emitter = EmitterParser2.parseEmitter(preset);
		this._base.addEmitter(emitter);

		return emitter;
	}

	// Clears all the particles of the given emitter
	private _clearParticles(emitter: Proton.Emitter): void {
		emitter.particles.forEach((p) => p.body && p.body.destroy());
	}

	/**
	 * Returns wether or not the "bb" rectangle intersects the "ab" rectangle.
	 */
	private _testRectangleIntersection(ab: Rectangle, bb: Rectangle): boolean {
		return (
			ab.x + ab.width > bb.x &&
			ab.x < bb.x + bb.width &&
			ab.y + ab.height > bb.y &&
			ab.y < bb.y + bb.height
		);
	}

	/**
	 * Exports the renderer configuration mainly used by the editor or cloning method
	 */
	public export(): any {
		return {
			type: "ParticlesScratchRenderer",
			texture: TextureExporter.exportTexture(this.texture),
		};
	}

	/**
	 * Parses the given scratch renderer configuration to return a new renderer component
	 * @param config the configuration of the scratch component being created
	 * @param baseUrl the base url of the loader
	 */
	public static parse(config: any, baseUrl: string): AbstractScratch2Renderer {
		// Init scratch assets cache
		ScratchAssets.initCache();

		return new ParticlesScratchRenderer(TextureImporter.importTexture(config.texture, baseUrl));
	}
}
