import { Tools } from "@gdk/core";

import { ContainerExporter } from "./containerExporter";
import type { GamePixi } from "../../gamePixi";
import type { ContainerView } from "../../view/containerView";
import type { LoadedView } from "../../view/loadedView";
import type { IProjectView } from "../model";

export interface IViewConfig {
	view: ContainerView | LoadedView;
	name: string;
}

export class StateExporter {
	/**
	 * Exports all available viewss
	 * @param game the game reference
	 */
	public static exportViews(game: GamePixi): IProjectView[] {
		const views: IProjectView[] = [];
		Object.keys(game.state.states).forEach((k) => {
			views.push(this.exportView({ name: k, view: game.state.states[k].view as ContainerView }));
		});

		return views;
	}

	/**
	 * Export the given view
	 * @param view the view to export
	 */
	public static exportView(view: IViewConfig): IProjectView {
		return Tools.assign(ContainerExporter.export(view.view), {
			stateName: view.name,
			trackingName: view.view.trackingName,
		});
	}
}
