const DEFAULT_MARGIN = 10;

export type CSSLikeMarginOptions =
	| [number]
	| [number, number]
	| [number, number, number]
	| [number, number, number, number];

export class CSSLikeMargin extends Array<number> {
	public constructor(options?: CSSLikeMarginOptions) {
		super(4);
		Object.setPrototypeOf(this, Object.create(CSSLikeMargin.prototype));

		if (!options) {
			this.fill(DEFAULT_MARGIN);
		} else {
			switch (options.length) {
				case 1:
					this.fill(options[0]);
					break;
				case 2:
					this[0] = this[2] = options[0];
					this[1] = this[3] = options[1];
					break;
				case 3:
					this.push(...options, options[1]);
					this[0] = options[0];
					this[2] = options[2];
					this[1] = this[3] = options[1];
					break;
				default:
					break;
			}
		}
	}

	/**
	 * Get the top margin
	 */
	public get top(): number {
		return this[0];
	}

	/**
	 * Set the top margin
	 */
	public set top(value: number) {
		this[0] = value;
	}

	/**
	 * Get the right margin
	 */
	public get right(): number {
		return this[1];
	}

	/**
	 * Set the right margin
	 */
	public set right(value: number) {
		this[1] = value;
	}

	/**
	 * Get the bottom margin
	 */
	public get bottom(): number {
		return this[2];
	}

	/**
	 * Set the bottom margin
	 */
	public set bottom(value: number) {
		this[2] = value;
	}

	/**
	 * Get the left margin
	 */
	public get left(): number {
		return this[3];
	}

	/**
	 * Set the left margin
	 */
	public set left(value: number) {
		this[3] = value;
	}

	/**
	 * Get the total horizontal margin
	 */
	public get horizontal(): number {
		return this[1] + this[3];
	}

	/**
	 * Get the total vertical margin
	 */
	public get vertical(): number {
		return this[0] + this[2];
	}

	/**
	 * Clone the margin
	 */
	public clone(): CSSLikeMargin {
		return new CSSLikeMargin(this.slice(0, 3) as [number, number, number, number]);
	}
}
