import { Container, Sprite, Texture, Graphics, Point } from "pixi.js";
import Proton from "proton-js";
import { TweenMax, TimelineMax } from "gsap";
import { Tools, tracker, EEventType } from "@gdk/core";
import type { IEvent } from "@gdk/core";
import type { CanvasRenderer, WebGLRenderer } from "pixi.js";

import { PixiFollowEmitter } from "./pixiFollowEmitter";
import { ScratchSolver, ESounds } from "./scratchSolver";
import { ParticlesMaskRenderer } from "./particlesMaskRenderer";
import { EmitterParser } from "../../proton/parser/emitterParser";
import { ScratchAssets } from "./scratchAssets";
import { ContainerExporter } from "../../loading/export/containerExporter";
import { ContainerImporter } from "../../loading/import/containerImporter";
import { ScratchPresets } from "./scratchPresets";
import type { Scratchable } from "./scratchable";
import type { IScratchSounds } from "./scratchSolver";
import type { ScratchPath } from "./scratchPath";

/**
 * simple clone because "@gdk/game-pixi/proton" doesn"t export that interface.
 */
interface IBody {
	image?: string;
	width?: number;
	height?: number;
}

/**
 * the options available for this scratch component.
 */
export interface IScratchOptions {
	content: Sprite | Container; // the content on which the mask/reveal is applying.
	invert?: boolean; // when inverted particles ERASE content, otherwhise they make content APPEAR.
	clamp?: boolean; // will make mask black/white instead of grayscale. (hard mask)
	revealRatio?: number; // ratio (default 0.7) where we consider our element scratched enough to reveal auto.
	revealCompleteRatio?: number; // ratio (default 0.97) where we consider our element scratched completely.
	autoPath?: ScratchPath; // the path particules will follow in auto mode
	emitterDatas?: any[]; // scratching particles (PROTON) settings
	emitterRevealDatas?: any[]; // reveal auto particles (PROTON) settings
	sounds?: IScratchSounds; // sounds parametres
	resolution?: number; // resolution ratio (default 0.5) of the mask canvas
	disableCacheAsBitmap?: boolean; // specifies if the scratch should disable its own optimization (cacheAsBitmap when needed)
	alwaysUpdateTransform?: boolean; // specifies if, in canvas renderer mode, the transforms should be updated each frame. To be "true" once a scratchable element is animated
}

export class Scratch extends Container implements Scratchable {
	public renderer: ParticlesMaskRenderer;
	public stopped: boolean;
	public options: IScratchOptions;

	// callback called when first render is complete.
	public onReady: () => void;
	// callback called when reveal ratio is reached.
	public onReveal: () => void;
	// callback called when reveal auto/full is complete.
	public onRevealComplete: () => void;
	// callback called when scratch is being scratched.
	public onScratch: () => void;

	private _proton: Proton;
	// private content: Container | Sprite;
	private _scratchEmitter: PixiFollowEmitter;
	private _revealEmitter: Proton.Emitter;
	private _isAuto: boolean;
	private _rectZone: Proton.RectZone;
	private _autoRevealTimeline: TimelineMax = null;
	private _renderedThatFrame: boolean = false;
	private _pixiRenderer: WebGLRenderer | CanvasRenderer;
	private _isWebgl: boolean = false;

	/**
	 * will build a Scratch Container
	 * @param props scratch settings
	 */
	public constructor(props: IScratchOptions) {
		super();

		this.renderable = true;

		this.options = props;
		this.options.revealRatio = props.revealRatio || 0.7;
		this.options.revealCompleteRatio = props.revealCompleteRatio || 0.97;
		this.options.clamp = props.clamp !== false;
		this.options.invert = props.invert !== false;
		this.options.resolution = props.resolution || 0.5;
		this.options.alwaysUpdateTransform = props.alwaysUpdateTransform || false;

		// Disable auto cache as bitmap feature as the canvas renderer generates a bug with the used pixi.js versions.
		this.options.disableCacheAsBitmap = props.disableCacheAsBitmap || false;

		this.renderer = new ParticlesMaskRenderer({
			content: this.options.content,
			revealRatio: this.options.revealRatio,
			revealCompleteRatio: this.options.revealCompleteRatio,
			invert: this.options.invert,
			clamp: this.options.clamp,
			resolution: this.options.resolution,
			alwaysUpdateTransform: this.options.alwaysUpdateTransform,
		});
		(this.renderer as any).doNotExport = true;

		this._rectZone = new Proton.RectZone(
			0,
			0,
			this.options.content.width,
			this.options.content.height
		);

		this.addChild(this.renderer.content);

		this.addChild(this.renderer.output);

		// show mask while scratch composant is not fully loaded
		// not working in Canvas we need to find another way
		/*if (this._options.invert) {
			this.childContent = true;
			this.addChild(this._options.content);
		}*/

		// this will wait for the first render
		this.renderer.onFirstRenderComplete = () => {
			this.renderable = true;
			/*if (this.childContent) {
				this.childContent = false;
				this.removeChild(this._options.content);
			}*/
			if (!this._isWebgl) {
				this.hideContent();
			}

			if (this.onReady) {
				this.onReady.call(this);
			}
		};

		// init emitter
		this.reset();

		if (this.options.sounds) {
			ScratchSolver.registerSounds(this.options.sounds);
		}

		this.renderer.onReveal = () => {
			this.auto();
			ScratchSolver.playSound(ESounds.Reveal);
			if (this.onReveal) {
				this.onReveal.call(this);
			}

			// Track auto-reveal
			tracker.track({
				eventType: EEventType.Game,
				event: "scratchReveal",
				name: this.name,
			} as IEvent);
		};

		this.renderer.onRevealComplete = () => {
			this.stop();

			if (!this.options.disableCacheAsBitmap) {
				this.renderer.cacheAsBitmap = true;
			}

			if (this.onRevealComplete) {
				this.onRevealComplete.call(this);
			}

			// Track reveal complete
			tracker.track({
				eventType: EEventType.Game,
				event: "scratchComplete",
				name: this.name,
			} as IEvent);
		};

		this.renderer.onScratch = () => {
			ScratchSolver.playSound(ESounds.Scratch);
			if (this.onScratch) {
				this.onScratch.call(this);
			}
		};

		// register to solver
		ScratchSolver.instance.add(this);
	}

	private oncePreRender(): void {
		// do nothing yet
	}

	private oncePostRender(): void {
		if (!this._isWebgl && this.options.invert) {
			this.renderer.renderOutput();
		}
	}

	private hideContent(): void {
		let root = this;
		while (root.parent) {
			root = root.parent as any;
		}

		const hiddenContainer = new Container();
		hiddenContainer.renderable = false;
		hiddenContainer.addChild(this.renderer.content);
		root.addChild(hiddenContainer);
	}

	private onPreRender(): void {
		// do nothing yet
	}

	private onPostRender(): void {
		if (!this.renderer.isDirty || this._isAuto || this.options.disableCacheAsBitmap) {
			return;
		}

		this.renderer.cacheAsBitmap = true;
	}

	/**
	 * switch the scratch in performance mode (frame by frame mask).
	 */
	public modePerformance(): void {
		// stop scratch emitter
		this.stopScratchEmitter();
		// stop reveal emitter
		this.stopRevealEmitter();
		this.renderer.oneClick = true;

		// Track new mode
		tracker.track({
			eventType: EEventType.Game,
			event: "scratchPerformances",
			name: this.name,
			infos: "Switched to OneClick due to a performances drop",
		} as IEvent);
	}

	/**
	 * return true if this scratch was updated by mouse/touch or by particle update
	 */
	public get active(): boolean {
		return (
			this.globalTimeScale > 0.01 &&
			(this._renderedThatFrame || this.renderer.isDirty) &&
			(this._scratchEmitter.mouseDown || this.renderer.updated || this._isAuto) &&
			(!this.stopped || !this.renderer.revealComplete)
		);
	}

	/**
	 * return ther TweenMax globalTimeScale
	 */
	public get globalTimeScale(): number {
		return (TweenMax as any).globalTimeScale();
	}

	/**
	 * stops the particles system
	 */
	public stop(): void {
		// stop scratch emitter
		this.stopScratchEmitter();
		// stop reveal emitter
		this.stopRevealEmitter();
		// Auto?
		if (this._isAuto) {
			if (this._autoRevealTimeline) {
				this._autoRevealTimeline.kill();
				this._autoRevealTimeline = null;
			}
		}
		// disable
		this.enabled = false;
		this.stopped = true;
		this.renderer.stop();
	}

	private stopScratchEmitter(): void {
		this._scratchEmitter.stop();
		this._scratchEmitter.removeAllParticles();
	}

	private stopRevealEmitter(): void {
		this._revealEmitter.stop();
		this._revealEmitter.removeAllParticles();
	}

	/**
	 * start auto reveal.
	 */
	public auto(): void {
		// disable optimizations
		if (!this.options.disableCacheAsBitmap && this.renderer.cacheAsBitmap) {
			this.renderer.cacheAsBitmap = false;
		}

		this._isAuto = true;
		// disable touch/mouse
		this.enabled = false;
		// start renderer
		this.renderer.stopped = false;
		// start reveal emitter
		const resolution = this.options.resolution || 1;
		const invResolution = 1 / resolution;
		const previousPosition: Proton.Vector2D = new Proton.Vector2D();

		if (this.options.autoPath) {
			if (!this._autoRevealTimeline) {
				const path = this.options.autoPath;
				path.setSize(this._rectZone.width, this._rectZone.height, resolution);
				this.addChild(path);

				this._autoRevealTimeline = new TimelineMax({
					onUpdate: () => {
						const normalized = new Proton.Vector2D(
							this._revealEmitter.p.x - previousPosition.x,
							this._revealEmitter.p.y - previousPosition.y
						).normalize();

						this._revealEmitter.v.x = normalized.x;
						this._revealEmitter.v.y = normalized.y;

						this._revealEmitter.emit("once");

						path.svg.lineTo(
							this._revealEmitter.p.x * invResolution,
							this._revealEmitter.p.y * invResolution
						);

						previousPosition.x = this._revealEmitter.p.x;
						previousPosition.y = this._revealEmitter.p.y;
					},
					onComplete: () => {
						this.renderer.forceReveal();
					},
				});

				const besier = TweenMax.to(this._revealEmitter.p, path.duration, {
					bezier: {
						type: "Cubic",
						values: path.values,
						autoRotate: true,
					},
				});

				this._autoRevealTimeline.add(besier);
				this._autoRevealTimeline.play();
			}
		} else {
			this._revealEmitter.p.x = this._rectZone.width * 0.5 * resolution;
			this._revealEmitter.p.y = this._rectZone.height * 0.5 * resolution;
			this._revealEmitter.emit();
		}
	}

	/**
	 * get reveal ratio
	 */
	public getProgress(): number {
		return this.renderer.revealRatio;
	}

	/**
	 * reset scratch.
	 */
	public reset(): void {
		this._isAuto = false;

		// disable cache as bitmap so "this" is properly resetted.
		if (!this.options.disableCacheAsBitmap) {
			this.renderer.cacheAsBitmap = false;
		}

		if (this._autoRevealTimeline) {
			this._autoRevealTimeline.kill();
		}
		this._autoRevealTimeline = null;

		// reset renderer
		this.renderer.reset();

		// reset proton
		this._initParticules();

		// enable
		this.enabled = true;
		this.stopped = false;

		// this will be called on the scratch is emitting particles
		this._scratchEmitter.onEmitting = () => {
			if (!this.options.disableCacheAsBitmap && this.renderer.cacheAsBitmap) {
				this.renderer.cacheAsBitmap = false;
			}

			this.renderer.stopped = false;
		};

		// if rendered at least one frame and reseted, setup the optimizations
		if (!this.options.disableCacheAsBitmap && this.renderable) {
			this.renderer.cacheAsBitmap = false;
		}
	}

	/**
	 * perform a proton particle update()
	 */
	public update(): void {
		this._proton.update();
		this._renderedThatFrame = false;
	}

	/**
	 * catch (canvas) renderer
	 */
	public renderCanvas(renderer: CanvasRenderer): void {
		if (!this._pixiRenderer) {
			this._isWebgl = false;
			this._pixiRenderer = renderer;
			renderer.once("beforeRender" as any, () => this.oncePreRender());
			renderer.once("afterRender" as any, () => this.oncePostRender());
		}

		renderer.once("beforeRender" as any, () => this.onPreRender());
		renderer.once("afterRender" as any, () => this.onPostRender());

		super.renderCanvas(renderer);

		this._renderedThatFrame = true;
		this.renderer.renderCanvas(renderer);
	}

	/**
	 * catch (WebGL) renderer
	 */
	public renderWebGL(renderer: WebGLRenderer): void {
		if (!this._pixiRenderer) {
			this._isWebgl = true;
			this._pixiRenderer = renderer;
			renderer.once("beforeRender" as any, () => this.oncePreRender());
			renderer.once("afterRender" as any, () => this.oncePostRender());
		}
		renderer.once("beforeRender" as any, () => this.onPreRender());
		renderer.once("afterRender" as any, () => this.onPostRender());
		super.renderWebGL(renderer);
		this._renderedThatFrame = true;
		this.renderer.renderWebGL(renderer);
	}

	/**
	 * EmitterParser.parse() temporary fix.
	 */
	private _parse(emitter: Proton.Emitter, json: object): Proton.Emitter {
		for (const i in json) {
			const obj = json[i];
			try {
				if (obj.type === "body") {
					this._createParticuleSprite(emitter, obj.props);
				} else if (obj.type === "scale") {
					this._addScaleBehavior(emitter, obj.props);
				} else if (obj.type === "rotation") {
					emitter.addBehaviour(this._addRotateInitializer(obj.props));
				} else if (obj.type === "radius") {
					emitter.addBehaviour(this._addCustumRadiusBehavior(obj.props));
				} else {
					const func = EmitterParser[obj.type];
					func(emitter, obj.props);
				}
			} catch (e) {
				console.error("Error in parser, check type in json : " + obj.type + "  || " + e);
			}
		}
		return emitter;
	}

	/**
	 * inits proton and particules emiters
	 */
	private _initParticules(): void {
		if (this._proton) {
			this._proton.destroy();
			delete this._proton;
		}
		if (this._revealEmitter) {
			this._revealEmitter.destroy();
			delete this._revealEmitter;
		}
		if (this._scratchEmitter) {
			this._scratchEmitter.destroy();
			delete this._scratchEmitter;
		}

		this._proton = new Proton();
		this._proton.addRenderer(this.renderer);

		this.emitterDatas = this.options.emitterDatas;

		this.revealEmitterDatas = this.options.emitterRevealDatas;
	}

	/**
	 * add a proton custom behavior for radius property
	 */
	private _addScaleBehavior(emitter: Proton.Emitter, props: any): void {
		if (props.value) {
			emitter.addBehaviour(
				new Proton.Scale(this._valueOrSpan(props.value), props.life, props.easeing)
			);
		} else if (props.start && props.end) {
			emitter.addBehaviour(
				new Proton.Scale(
					this._valueOrSpan(props.start),
					this._valueOrSpan(props.end),
					props.life,
					props.easeing
				)
			);
		}
	}

	/**
	 * add a proton custom behavior for radius property
	 */
	private _addCustumRadiusBehavior(props: any): any {
		const radiusBehavior = {
			span: new Proton.Span(props.value.min, props.value.max, true),
			initialize: (particle) => {
				particle.p.x += radiusBehavior.span.getValue(true);
				particle.p.y += radiusBehavior.span.getValue(true);
				return;
			},
			applyBehaviour: (particle) => {
				return;
			},
		};

		return radiusBehavior;
	}

	private _addRotateInitializer(props: any): any {
		const rotateInitializer = {
			span: new Proton.Span(props.value.min, props.value.max, true),
			initialize: (particle) => {
				particle.rotation = rotateInitializer.span.getValue(true);
				return;
			},
			applyBehaviour: (particle) => {
				return;
			},
		};

		return rotateInitializer;
	}

	private _valueOrSpan(value: any): number | Proton.Span {
		const resolution = this.options.resolution || 1;
		if (typeof value === "object") {
			return new Proton.Span(value.min * resolution, value.max * resolution, value.center);
		} else {
			return value * resolution;
		}
	}

	/**
	 * get Proton.Body from texture
	 */
	private _createParticuleSprite(emitter: Proton.Emitter, props: IBody): void {
		if (!props.image) {
			return;
		}
		const texture = ScratchAssets.get(props.image);
		const particleSprite = new Sprite(texture);
		const resolution = 1 / (this.options.resolution || 1);
		const width = resolution * (props.width || particleSprite.width);
		const height = resolution * (props.height || particleSprite.height);

		emitter.addInitialize(
			new Proton.Body(particleSprite.texture.baseTexture.source, width, height)
		);
	}

	/**
	 * gets the number of active particules
	 */
	public get particulesCount(): number {
		return this._proton.getCount();
	}

	/**
	 * Sets if the scratch is enabled
	 */
	public set enabled(value: boolean) {
		this.buttonMode = value;
		this.interactive = value;
	}

	/**
	 * gets if the scratch is enabled
	 */
	public get enabled(): boolean {
		return this.buttonMode && this.interactive;
	}

	/**
	 * gets if the scratch is revealed
	 */
	public get isRevealed(): boolean {
		return this.renderer.revealed;
	}

	/**
	 * gets if the scratch is in auto mode
	 */
	public get isAuto(): boolean {
		return this._isAuto;
	}

	/**
	 * sets the revealed pixel ratio to trigger auto mode
	 * @param value {number} the ratio
	 */
	public set revealRatio(value: number) {
		this.options.revealRatio = this.renderer[`_revealRatio`] = value;
	}

	/**
	 * return the revealed pixel ratio to trigger auto mode
	 */
	public get revealRatio(): number {
		return this.options.revealRatio;
	}

	/**
	 * sets the revealed pixel ratio to trigger auto mode completion
	 * @param value {number} the ratio
	 */
	public set revealCompleteRatio(value: number) {
		this.options.revealCompleteRatio = this.renderer[`_revealCompleteRatio`] = value;
	}

	/**
	 * return the revealed pixel ratio to trigger auto mode completion
	 */
	public get revealCompleteRatio(): number {
		return this.options.revealCompleteRatio;
	}

	/**
	 * sets the auto scratch path
	 * @param path {ScratchPath} the path
	 */
	public set autoPath(path: ScratchPath) {
		this.options.autoPath = path;
	}

	/**
	 * return the auto scratch path
	 */
	public get autoPath(): ScratchPath {
		return this.options.autoPath;
	}

	/**
	 * sets the emitter datas from JSON to change the scratch emitter settings
	 * @param datas {object} the protton emmitter settings to parse
	 */
	public set emitterDatas(datas: any[]) {
		// in case we had one, stop and destroy pevious scratchEmitter
		if (this._scratchEmitter) {
			// stop scratch emitter
			this.stopScratchEmitter();
			this._scratchEmitter.destroy();
		}
		// store in options
		this.options.emitterDatas = datas;
		// parse emitter datas from JSON
		this._scratchEmitter = this._parse(new PixiFollowEmitter(this), datas);
		// sets the resolution :
		this._scratchEmitter.resolution = this.options.resolution || 1;
		// small optimisation : if particle is out of canvas bounds, kill it
		this._scratchEmitter.addBehaviour(new Proton.CrossZone(this._rectZone, "dead"));
		// add it to proton
		this._proton.addEmitter(this._scratchEmitter);
		// start scratch emitter
		this._scratchEmitter.emit();
	}

	/**
	 * sets the reveal emitter datas from JSON to change the auto scratch emitter settings
	 * @param datas {object} the protton emmitter settings to parse
	 */
	public set revealEmitterDatas(datas: any[]) {
		// in case we had one, stop and destroy pevious revealEmitter
		if (this._revealEmitter) {
			// stop reveal emitter
			this.stopRevealEmitter();
			this._revealEmitter.destroy();
		}

		// store in options
		this.options.emitterRevealDatas = datas;

		this._revealEmitter = this._parse(new Proton.BehaviourEmitter(), datas);
		// small visual optimisation : if particle is out of canvas bounds, make it bounce.
		this._revealEmitter.addBehaviour(new Proton.CrossZone(this._rectZone, "bound"));
		// wandering ?
		this._revealEmitter.addSelfBehaviour(new Proton.CrossZone(this._rectZone, "bound"));
		this._revealEmitter.addSelfBehaviour(
			new Proton.RandomDrift(this._rectZone.width * 0.1, this._rectZone.height * 0.1, 0.5)
		);
		// add it to proton
		this._proton.addEmitter(this._revealEmitter);
	}

	/**
	 * Exports the scratch component
	 */
	public export(): any {
		const scratch = Tools.assign(ContainerExporter.exportContainer(this), {
			type: "ui.Scratch",
			content: ContainerExporter.export(this.renderer.content),
			revealRatio: this.options.revealRatio || 0.7,
			invert: this.renderer.invert,
			emitterDatas: this.options.emitterDatas,
			emitterRevealDatas: this.options.emitterRevealDatas,
			alwaysUpdateTransform: this.options.alwaysUpdateTransform,
		});

		// A scratch doesn't have children
		scratch.children = [];

		return scratch;
	}

	/**
	 * Parses the scratch component
	 * @param config the configuration of the scratch component
	 * @param baseUrl the base url of the loader
	 */
	public static parse(config: any, baseUrl: string): Scratch {
		const scratch = new Scratch({
			content: ContainerImporter.import(config.content, baseUrl),
			revealRatio: config.revealRatio,
			invert: config.invert,
			emitterDatas: config.emitterDatas || ScratchPresets.FDJ_SCRATCH_BIG,
			emitterRevealDatas: config.emitterRevealDatas || ScratchPresets.DEFAULT_AUTO,
			autoPath: ScratchPresets.DEFAULT_PATH,
			alwaysUpdateTransform: config.alwaysUpdateTransform,
		});

		// scratch.renderer.content.cacheAsBitmap = true;

		return scratch;
	}
}
