import { Container } from "pixi.js";
import Proton from "proton-js";
import { Tools } from "@gdk/core";
import type { CanvasRenderer, WebGLRenderer } from "pixi.js";

import { ISerializedEmitterComponent, ESerializedEmitterComponentType } from "./types";
import { ProtonSerializer } from "./serialization";
import { ContainerExporter } from "../loading/export/containerExporter";
import type { ISerializedEmitter } from "./types";

export enum EProtonContainerEvent {
	/**
	 * Defines the key of the event raised when a particle has been created.
	 */
	OnParticleCreated = "particleCreated",
	/**
	 * Defines the key of the event raised when a particle is being updated.
	 */
	OnParticleUpdate = "particleUpdate",
}

export class ProtonContainer extends Container {
	/**
	 * Defines the default configuration of particle systems.
	 */
	public static readonly defaultConfig: ISerializedEmitter[] = [
		{
			id: "default-emitter",
			name: "Default Emitter",
			rate: {
				type: "Rate",
				arguments: [
					{ type: "Span", arguments: [10, 15] },
					{ type: "Span", arguments: [10, 15] },
				],
			},
			damping: 0.006,
			initializers: [
				{
					type: "Body",
					arguments: ["scratch/scratch_0.png", 128, 128],
				},
			],
			behaviors: [],
		},
	];

	/**
	 * Defines wether or not the particles emitters should be auto started once they are loaded.
	 */
	public autoStart: boolean = true;

	private _system: Proton;
	private _renderer: Proton.PixiRenderer;

	/**
	 * @hidden
	 */
	public _config: ISerializedEmitter[] = [];

	/**
	 * Constructor.
	 * @param systemConfig defines the base configuration of the container.
	 */
	public constructor(systemConfig?: ISerializedEmitter[]) {
		super();

		this._renderer = new Proton.PixiRenderer(this);
		this._renderer.setColor = true;

		this._system = new Proton();
		this._system.addRenderer(this._renderer);

		this._overrideEvents();

		if (systemConfig) {
			this.loadFromJSON(systemConfig);
		} else {
			this.loadFromJSON(Tools.cloneObject(ProtonContainer.defaultConfig));
			this.startEmitters();
		}
	}

	/**
	 * Renders the scratch using the WebGL renderer.
	 * @param renderer the webgl renderer reference.
	 */
	public renderWebGL(renderer: WebGLRenderer): void {
		super.renderWebGL(renderer);
		this._system.update();
	}

	/**
	 * Renders the scratch using the canvas renderer.
	 * @param renderer the canvas renderer reference.
	 */
	public renderCanvas(renderer: CanvasRenderer): void {
		super.renderCanvas(renderer);
		this._system.update();
	}

	/**
	 * Loads the particles system of the container from the given JSON object.
	 * @param json defines the reference to the JSON object that defines the particles system.
	 */
	public loadFromJSON(json: ISerializedEmitter[]): void {
		// Clear
		if (this._system) {
			this._system.destroy();
		}

		this._system = new Proton();
		this._system.addRenderer(this._renderer);

		// Load
		ProtonSerializer.parseEmitters(json, this._system);
		this._config = json;

		// Start?
		if (this.autoStart) {
			this.startEmitters();
		}
	}

	/**
	 * Appends the particles system of the container from the given JSON object.
	 * @param json defines the reference to the JSON object that defines the new particle system to append.
	 */
	public appendFromJSON(json: ISerializedEmitter[]): void {
		ProtonSerializer.parseEmitters(json, this._system);
		this._config = this._config.concat(json);

		if (this.autoStart) {
			this.startEmitters();
		}
	}

	/**
	 * Gets the particles system reference using Proton.
	 */
	public get system(): Proton {
		return this._system;
	}

	/**
	 * Gets the  renderer used by Proton (here PIXIRenderer).
	 */
	public get renderer(): Proton.PIXIRenderer {
		return this._renderer;
	}

	/**
	 * Gets the current config of all emitters.
	 */
	public get config(): ISerializedEmitter[] {
		return this._config;
	}

	/**
	 * Sets the current config of all emitters.
	 */
	public set config(config: ISerializedEmitter[]) {
		this.loadFromJSON(config);
	}

	/**
	 * Stops all the emitters.
	 */
	public stopEmitters(): void {
		this._system?.emitters.forEach((e) => e.stop());
	}

	/**
	 * Stops the given emitter.
	 */
	public stopEmitter(emitterOrId: Proton.Emitter | string): void {
		this._getEmitterOrId(emitterOrId)?.stop();
	}

	/**
	 * Starts all the emitters in the given mode.
	 * @param mode defines the emission mode for the emitters.
	 */
	public startEmitters(mode?: "once"): void {
		this._system?.emitters.forEach((e) => e.emit(mode));
	}

	/**
	 * Starts the given emitter in the given mode.
	 * @param emitterOrId defines the reference to the emitter or the id of the emitter.
	 * @param mode defines the emission mode for the emitter.
	 */
	public startEmitter(emitterOrId: Proton.Emitter | string, mode?: "once"): void {
		this._getEmitterOrId(emitterOrId)?.emit(mode);
	}

	/**
	 * Returns, if exists, the reference to the Proton emitter.
	 * @param id defines the id of the emitter to get.
	 */
	public getEmitterById(id: string): Proton.Emitter {
		return this._system?.emitters.find((e) => e.id === id) ?? null;
	}

	/**
	 * Returns the index of the given emitter in the array of emitters in the particles system.
	 * @param emitterOrId defines the reference to the emitter or the id of the emitter.
	 */
	public getEmitterIndex(emitterOrId: Proton.Emitter | string): number {
		const emitter = this._getEmitterOrId(emitterOrId);
		if (!emitter) {
			return -1;
		}

		return this._system?.emitters.indexOf(emitter) ?? -1;
	}

	/**
	 * Returns the reference, if exists, of the emitter or the emitter identified by the given Id.
	 */
	private _getEmitterOrId(emitterOrId: Proton.Emitter | string): Proton.Emitter {
		if (typeof emitterOrId === "string") {
			return this.getEmitterById(emitterOrId);
		}

		return emitterOrId;
	}

	/**
	 * Overrides the events of the proton renderer to catch them.
	 */
	private _overrideEvents(): void {
		// Create
		const onParticleCreatedFn = this._renderer.onParticleCreated;
		this._renderer.onParticleCreated = (particle) => {
			this.emit(EProtonContainerEvent.OnParticleCreated);
			onParticleCreatedFn.call(this._renderer, particle);
		};

		// Update
		const onParticleUpdateFn = this._renderer.onParticleUpdate;
		this._renderer.onParticleUpdate = (particle) => {
			this.emit(EProtonContainerEvent.OnParticleUpdate);
			onParticleUpdateFn.call(this._renderer, particle);
		};
	}

	/**
	 * Exports the spine timeline.
	 */
	public export(): any {
		this.children.forEach((c) => (c.doNotExport = true));

		return Tools.assign(ContainerExporter.exportContainer(this), {
			type: "proton.ProtonContainer",
			config: ProtonSerializer.serializeSystem(this._system),
			autoStart: this.autoStart,
		});
	}

	/**
	 * Parses the proton container according to the given configuration.
	 * @param config the serialized data.
	 * @param baseUrl the base url of the loader.
	 * @param container the existing proton container reference.
	 */
	public static parse(config: any, baseUrl: string, container?: ProtonContainer): ProtonContainer {
		if (!container) {
			container = new ProtonContainer([]);
		}

		container.loadFromJSON(config.config);

		if (config.autoStart) {
			container.startEmitters();
		}

		return container;
	}
}
