import { GameCore, Loader, EOrientation } from "@gdk/core";
import { CanvasRenderer, Container } from "pixi.js";
import type { WebGLRenderer, ApplicationOptions, SystemRenderer } from "pixi.js";

import { Application } from "./application";
import { PixiLoader } from "./loader";
import updateTransform from "./container/container";
import { AccessibilityHelper } from "./accessibility/helper";
import { NumberPolyfill } from "./polyfills/number";

NumberPolyfill();

/**
 * Options for game pixi instance creation
 */
export interface IGamePixiOptions extends ApplicationOptions {
	/**
	 * The application reference created by the user.
	 * @example new BabylonApplication(...)
	 */
	application?: Application;
	/**
	 * Wether or not the GameCore instance should create a game div to put canvas in it.
	 */
	createGameDiv?: boolean;
}

export class GamePixi extends GameCore {
	public static instance: GamePixi;

	// Public members
	public application: Application;
	public pixiLoader: PixiLoader;

	public onResize: () => void;
	public onShowLockScreen: () => void;
	public onHideLockScreen: () => void;

	public lockScreenEnabled: boolean = true;

	// Private members
	private _lastRendererWidth: number = 0;
	private _lastRendererHeight: number = 0;

	/**
	 * Constructor
	 * @param with: the renderer width
	 * @param height: the renderer height
	 * @param noWebGL: if force to use canvas renderer
	 * @param options: the Pixi.js application options
	 */
	public constructor(
		width: number,
		height: number,
		noWebGL: boolean,
		options: IGamePixiOptions = {}
	) {
		super(window, null, options.createGameDiv);

		if (!GamePixi.instance) {
			GamePixi.instance = this;
		} else {
			// we should use the logger for that @TODO
			console.warn("Static instance of GamePixi has been overidded");
		}

		// override PIXI.utils.isWebGLSupported method with our isWebGLSupported method
		// utils.isWebGLSupported = this.isWebGLSupported;
		if (!options.resolution) {
			options.resolution = window.devicePixelRatio || 1;
		}

		// Create application context
		this.application = options.application || new Application(width, height, noWebGL, options);

		// define default comportement for touchMove:
		this.application.renderer.plugins.interaction.moveWhenInside = true;

		// Optimizations
		this.application.onRendererResolutionChanged = () => this.resize(innerWidth, innerHeight, true);

		// Configure game-core
		this.canvas = this.application.view;

		// Bind events
		this.onBlur = () => this.application.stop();
		this.onFocus = () => this.application.start();

		window.addEventListener("resize", () => {
			if (this.onResize) {
				this.onResize();
			}
		});

		// this option is enabled by default, and causes a bug on
		// mobiles / tablets regarding the blur / focus events that can no longer
		// be listened
		this.renderer.plugins.interaction.autoPreventDefault = false;

		// Override prototype
		Container.prototype.updateTransform = updateTransform;
		AccessibilityHelper.overrideUpdate();
		AccessibilityHelper.overrideActivate();
		AccessibilityHelper.overrideDeactivate();

		// Register loader
		this.pixiLoader = new PixiLoader();
		Loader.register(this.pixiLoader);
	}

	/**
	 * Returns webgl support
	 * override PIXI.utils.isWebGLSupported method
	 */
	public isWebGLSupported(): boolean {
		// removed failIfMajorPerformanceCaveat:true in contextoptions
		const contextOptions = { stencil: true };

		// copy/paste from PIXI.utils.isWebGLSupported
		try {
			if (!(window as any).WebGLRenderingContext) {
				return false;
			}

			const canvas = document.createElement("canvas");
			let gl =
				(canvas.getContext("webgl", contextOptions) as WebGLRenderingContext) ||
				(canvas.getContext("experimental-webgl", contextOptions) as WebGLRenderingContext);

			const success = Boolean(gl && gl.getContextAttributes().stencil);
			if (gl) {
				const loseContext = gl.getExtension("WEBGL_lose_context");

				if (loseContext) {
					loseContext.loseContext();
				}
			}
			gl = null;

			return success;
		} catch (e) {
			return false;
		}
	}

	/**
	 * Returns the renderer created by PIXI.js
	 */
	public get renderer(): CanvasRenderer | WebGLRenderer {
		return this.application.renderer;
	}

	/**
	 * Returns if the renderer is instance of CanvasRenderer. If not, WebGLRenderer is used
	 */
	public get isCanvasRenderer(): boolean {
		return this.application.renderer instanceof CanvasRenderer;
	}

	/**
	 * Returns the main stage of the PIXI.js application
	 */
	public get stage(): Container {
		return this.application.stage;
	}

	/**
	 * Returns the lock stage (lockscreen) of the PIXI.js application
	 */
	public get lockStage(): Container {
		return this.application.lockStage;
	}

	/**
	 * Starts the game.
	 * @param view Specifies the first view to make appear.
	 * @returns A promise resolved when the game is loaded and started or void for backward compatibility.
	 */
	public start(state?: string): Promise<void> | void {
		this.resize(this.application.screen.width, this.application.screen.height);

		this.application.start();

		// Start application
		return super.start(state);
	}

	/**
	 * Resizes the main stage
	 * @param width the width in pixels
	 * @param height the height in pixels
	 */
	public resize(
		width: number = window.innerWidth,
		height: number = window.innerHeight,
		force?: boolean
	): void {
		if (!force && width === this._lastRendererWidth && height === this._lastRendererHeight) {
			this.application.emitter.emit("resize", { width, height });
			return;
		}

		this.application.resize(width, height);

		// Main stage
		this.stage.width = this.renderer.width / this.renderer.resolution;
		this.stage.height = this.renderer.height / this.renderer.resolution;
		this.stage.scale.set(1, 1);

		// Lock stage
		this.lockStage.width = this.renderer.width / this.renderer.resolution;
		this.lockStage.height = this.renderer.height / this.renderer.resolution;
		this.lockStage.scale.set(1, 1);

		// Check if should show lock stage (lockscreen)
		this.application.shouldLockScreen = false;

		if (this.lockScreenEnabled) {
			// Check orientation and show lock screen
			if (this.settings.orientation === EOrientation.Portrait && width > height) {
				this.application.shouldLockScreen = true;
			} else if (this.settings.orientation === EOrientation.Landscape && width < height) {
				this.application.shouldLockScreen = true;
			}

			if (this.application.shouldLockScreen) {
				this.onShowLockScreen?.();
			} else {
				this.onHideLockScreen?.();
			}
		}

		// Update last dimensions
		this._lastRendererWidth = width;
		this._lastRendererHeight = height;

		this.application.emitter.emit("resize", { width, height });
	}

	/**
	 * Disposes the GamePixi instance. Destroys everything
	 */
	public dispose(): void {
		this.application.destroy();
	}

	/**
	 * Return the game's infos. Should be overidden to add
	 * more informations to log
	 */
	protected async getInfos(): Promise<any> {
		const infos = await super.getInfos();
		infos.envInfos.renderMode = this.isCanvasRenderer ? "Canvas" : "WebGL";
		infos.envInfos.renderSize = { width: this.renderer.width, height: this.renderer.height };
		return infos;
	}
}
