import { Rectangle } from "pixi.js";

import { EAlign } from "./textField";
import type { Text } from "./text";
import type { Word } from "./word";

export class Line {
	public x: number = 0;
	public y: number = 0; // previous line.height + previous line.y + interlignage
	public width: number = 0; // glyphs.width sum;
	public height: number = 0; // Math.max(glyphs.height);
	public maxHeight: number = 0;
	public align: EAlign = EAlign.Left; // how the line should align.
	public words: Word[] = [];
	public baseline: number = 0;
	public leading: number = 0;
	public underline: number = 0;
	public previousLine: Line;
	public baselineOffset: number = 0;
	public text: string = "";

	/**
	 * Constructor
	 * @param composedText: the composed text reference of the line
	 * @param previousLine: the line before "this" one
	 */
	public constructor(composedText: Text, previousLine?: Line) {
		this.align = composedText.align;
		this.previousLine = previousLine;

		if (this.previousLine) {
			this.y = this.previousLine.y + this.previousLine.baseline + composedText.interLine;

			// Update dimensions
			composedText.height += this.previousLine.baseline + composedText.interLine;
			// previousLine.y + previousLine.height + this.Text.interlignage;
			composedText.width = Math.max(composedText.width, this.previousLine.width);
		}
	}

	/**
	 * Add the given word to the line
	 * @param word the word to add
	 */
	public pushWord(word: Word): void {
		word.line = this;
		this.text += (this.words.length > 0 ? " " : "") + word.text;
		this.words.push(word);
		this.width += word.width;
		this.maxHeight = Math.max(this.maxHeight, word.maxHeight);
	}

	/**
	 * Returns the line's bounds
	 */
	public getBounds(): Rectangle {
		return new Rectangle(this.x, this.y - this.baseline, this.width, this.height);
	}
}
