import { Filter, Matrix, TextureMatrix } from "pixi.js";
import type { Sprite, FilterManager, RenderTarget } from "pixi.js";

import maskShaderVS from "./assets/mask.vertex.fx";
import maskShaderPS from "./assets/mask.fragment.fx";

export class Scratch2MaskFilter extends Filter<any> {
	private _maskMatrix: Matrix = new Matrix();

	/**
	 * Constructor
	 * @param resolution the resolution of the filter. Setting this to be lower will lower the quality but increase the performance of the filter.
	 * @param padding the extra padding value for the filter.
	 * @param mask the mask sprite reference (proton output texture)
	 * @param clampValue the clamping value. If 0.0, then clamping is disabled.
	 * @param inverted defines wether or not the scratch is in inverted mode. Means is transparent and reveals its content.
	 */
	public constructor(
		public resolution: number,
		public padding: number,
		public mask: Sprite,
		public clampValue: number = 0.5,
		public inverted: boolean = false
	) {
		super(maskShaderVS, Scratch2MaskFilter._getFragmentShader(clampValue, inverted));
		this.autoFit = true;
		this.padding = padding;
	}

	/**
	 * Applies the filter
	 */
	public apply(
		filterManager: FilterManager,
		input: RenderTarget,
		output: RenderTarget,
		clear: boolean
	): void {
		// Uniforms
		const tex = this.mask.texture;
		if (!tex.valid) {
			return;
		}
		if (!tex.transform) {
			tex.transform = new TextureMatrix(tex, -0.5);
		}
		this.mask.texture.transform.update();

		this.uniforms.filterClamp = true;
		this.uniforms.uMask = tex;
		this.uniforms.otherMatrix = filterManager
			.calculateSpriteMatrix(this._maskMatrix, this.mask)
			.prepend(this.mask.texture.transform.mapCoord);
		if (this.clampValue) {
			this.uniforms.uClamp = this.clampValue;
		}

		filterManager.applyFilter(this, input, output, clear);
	}

	/**
	 * Returns the final shader string according to the given renderer options (clamping, invert, etc.)
	 */
	private static _getFragmentShader(clampValue: number, inverted: boolean): string {
		const fragmentShader: string[] = [maskShaderPS];

		if (clampValue) {
			fragmentShader.unshift("#define CLAMP_ENABLED");
		}
		if (inverted) {
			fragmentShader.unshift("#define INVERTED");
		}

		return fragmentShader.join("\n");
	}
}
