import { Transform, Rectangle } from "pixi.js";
import type { Container } from "pixi.js";

import { DataCache } from "../tools/dataCache";
import { EResize } from "./resizeType";

/**
 * Resize function type alias
 */
export type ResizeType = (child: Container) => void;

export class Resize {
	/**
	 * Docking type as lambda function
	 */
	public resize: ResizeType;

	/**
	 * Used for cache, stores the latest parent bounds.
	 * @hidden
	 */
	public _lastParentBounds: Rectangle = new Rectangle(undefined, undefined, undefined, undefined);
	/**
	 * Used for cache, stores the latest bounds of the resized object.
	 * @hidden
	 */
	public _lastBounds: Rectangle = new Rectangle(undefined, undefined, undefined, undefined);
	/**
	 * Used for cache, to check cache.
	 * @hidden
	 */
	public _cache: DataCache<Resize> = new DataCache(this);

	/**
	 * Constructor.
	 * @param resize the resize function to apply. Can be a custom function.
	 */
	public constructor(resize: ResizeType) {
		this.resize = resize;
	}

	/**
	 * Sets the resize forced dirty. Especially used when implemented custom/animated resize.
	 */
	public setDirty(): void {
		this._cache._forcedDirty = true;
	}

	/**
	 * Returns wether or not the resize is dirty. If yes, the resize function should be applied.
	 * @param parentBounds the potental new parent bounds rectangle.
	 * @param bounds the current bounds of the resized object.
	 */
	public isDirty(parentBounds: Rectangle, bounds: Rectangle): boolean {
		this._cache.addRectangle("_lastParentBounds", parentBounds);
		this._cache.addRectangle("_lastBounds", bounds);
		return this._cache.checkDirty();
	}

	/**
	 * Exports the resize configuration as a JSON.
	 */
	public export(): any {
		let resizeFunctionName: string;
		switch (this.resize) {
			case Resize.cover:
				resizeFunctionName = "cover";
				break;
			case Resize.contain:
				resizeFunctionName = "contain";
				break;
			case Resize.fitContain:
				resizeFunctionName = "fitContain";
				break;
			case Resize.fill:
				resizeFunctionName = "fill";
				break;
		}

		return {
			resize: resizeFunctionName,
		};
	}

	/**
	 * Parses the given resize configuration and returns a new reference.
	 * @param config the exported configuration of the resize object.
	 */
	public static parse(config: any, ref?: Resize): Resize {
		let type: ResizeType = null;

		if (typeof config === "number") {
			// Backward compatibility, transform number to Resize object.
			// TODO: remove in 2.0.0.
			switch (config) {
				case EResize.Cover:
					type = this.cover;
					break;
				case EResize.Contain:
					type = this.contain;
					break;
				case EResize.FitContain:
					type = this.fitContain;
					break;
				case EResize.Fill:
					type = this.fill;
					break;
			}
		} else {
			type = Resize[config.resize];
		}

		if (!ref) {
			return new Resize(type);
		}

		ref.resize = type;
		return ref;
	}

	// Static members
	public static _transform: Transform = new Transform();
	public static _parentTransform: Transform = new Transform();
	public static _parentBounds: Rectangle = new Rectangle();
	public static _bounds: { width: number; height: number } = { width: 0, height: 0 };

	/**
	 * Resizes the element to cover its parent according to its width/height pair.
	 */
	public static readonly cover = (child: Container) => {
		const ratio = Math.max(
			(((Resize._parentBounds.width + 1) / Resize._bounds.width) * Resize._transform.scale.x) /
				Resize._parentTransform.scale.x,
			(((Resize._parentBounds.height + 1) / Resize._bounds.height) * Resize._transform.scale.y) /
				Resize._parentTransform.scale.y
		);
		if (!isNaN(ratio)) {
			(child.transform as Transform).scale.set(ratio, ratio);
		}
	};

	/**
	 * Resizes the element to be contained in its parent according to its width/height pair.
	 */
	public static readonly contain = (child: Container) => {
		const ratio = Math.min(
			((Resize._parentBounds.width / Resize._bounds.width) * Resize._transform.scale.x) /
				Resize._parentTransform.scale.x,
			((Resize._parentBounds.height / Resize._bounds.height) * Resize._transform.scale.y) /
				Resize._parentTransform.scale.y
		);
		if (!isNaN(ratio)) {
			(child.transform as Transform).scale.set(ratio, ratio);
		}
	};

	/**
	 * Resize the element to fill the parent container, breaking the element's ratio
	 */
	public static readonly fill = (child: Container) => {
		const calcX =
			((Resize._parentBounds.width / Resize._bounds.width) * Resize._transform.scale.x) /
			Resize._parentTransform.scale.x;
		const calcY =
			((Resize._parentBounds.height / Resize._bounds.height) * Resize._transform.scale.y) /
			Resize._parentTransform.scale.y;
		(child.transform as Transform).scale.set(calcX, calcY);
	};

	/**
	 * Resizes the element to be contained in its parent according to its width/height pair.
	 * Fit contain means that all children of the element will be docked according the element's parent bounds instead
	 * of the element's bounds. Typically used only to fit contain views.
	 */
	public static readonly fitContain = (child: Container) => {
		const ratio = Math.min(
			((Resize._parentBounds.width / Resize._bounds.width) * Resize._transform.scale.x) /
				Resize._parentTransform.scale.x,
			((Resize._parentBounds.height / Resize._bounds.height) * Resize._transform.scale.y) /
				Resize._parentTransform.scale.y
		);
		if (!isNaN(ratio)) {
			(child.transform as Transform).scale.set(ratio, ratio);
		}

		if (child.parent) {
			(child as any)._width = child.parent.dockingBounds
				? child.parent.dockingBounds.width
				: Resize._parentBounds.width;
			(child as any)._height = child.parent.dockingBounds
				? child.parent.dockingBounds.height
				: Resize._parentBounds.height;
		}
	};

	/**
	 * Resizes the element to be contained in its parent according to its width/height pair.
	 * Similar to fitContains except that an upper and lower parent's area will be ignore
	 * Typically used for top and bottom menu bar
	 * @param topSafeAreaHeight height of the top safe area, in pixel
	 * @param bottomSafeAreaHeight height of the bottom safe area, in pixel
	 * @param totalAreaHeight total height of the area, in pixel
	 */

	public static readonly boundedFitContain = (
		topSafeAreaHeight: number,
		bottomSafeAreaHeight: number,
		totalAreaHeight: number
	) => {
		topSafeAreaHeight = topSafeAreaHeight / totalAreaHeight;
		bottomSafeAreaHeight = bottomSafeAreaHeight / totalAreaHeight;

		return (child: Container) => {
			const parentHeight =
				Resize._parentBounds.height * (1 - (bottomSafeAreaHeight + topSafeAreaHeight));
			const ratio = Math.min(
				(Resize._parentBounds.width / Resize._bounds.width) * Resize._transform.scale.x,
				(parentHeight / Resize._bounds.height) * Resize._transform.scale.y
			);

			(child.transform as Transform).position.y = Resize._parentBounds.height * topSafeAreaHeight;

			if (!isNaN(ratio)) {
				(child.transform as Transform).scale.set(ratio, ratio);
			}
			if (child.parent) {
				(child as any)._width = child.parent.dockingBounds
					? child.parent.dockingBounds.width
					: Resize._parentBounds.width;
				(child as any)._height = child.parent.dockingBounds
					? child.parent.dockingBounds.height
					: parentHeight;
			}
		};
	};

	/**
	 * Resizes the element to be contained in its parent according to its width/height pair.
	 * Similar to cover except that an upper and lower parent's area will be ignore
	 * Typically used for top and bottom menu bar
	 * @param topSafeAreaHeight height of the top safe area, in pixel
	 * @param bottomSafeAreaHeight height of the bottom safe area, in pixel
	 * @param totalAreaHeight total height of the area, in pixel
	 * @param mainArea main container including safe area, used to follow scaleXY evolution
	 * @param safeHeight height of the top and bottom area in pixel
	 */

	public static readonly boundedCover = (
		topSafeAreaHeight: number,
		bottomSafeAreaHeight: number,
		totalAreaHeight: number,
		area: Container
	) => {
		return (child: Container) => {
			const totalSafeArea = bottomSafeAreaHeight + topSafeAreaHeight;
			const parentHeight =
				Resize._parentBounds.height > Resize._parentBounds.width
					? Resize._parentBounds.height - totalSafeArea * area.scaleXY
					: Resize._parentBounds.height * (1 - totalSafeArea / totalAreaHeight);

			const ratio = Math.max(
				((Resize._parentBounds.width / Resize._bounds.width) * Resize._transform.scale.x) /
					Resize._parentTransform.scale.x,
				((parentHeight / Resize._bounds.height) * Resize._transform.scale.y) /
					Resize._parentTransform.scale.y
			);

			if (!isNaN(ratio)) {
				(child.transform as Transform).scale.set(ratio, ratio);
			}
		};
	};
}
