import { loaders, loader, spine } from "pixi.js";
import type { Texture } from "pixi.js";

enum EAssetType {
	Spine,
	Spritesheet,
}

export class SpineSpritesheetParser {
	private static spritesheets: any[] = [];
	private static spineRessources: any[] = [];
	private static attachements: any[] = [];
	private static typesThatRequireLoading: string[] = ["region", "mesh", "linkedmesh"];

	/**
	 * The SpineSpritesheetParser middleware function
	 * @param resource {PIXI.loaders.Resource} the ressource currently loaded (provided by the loader)
	 * @param next {() => any} the function that need to be called on middleware complete
	 */
	public static middleware(resource: loaders.Resource, next: () => any): any {
		// let imageRessource;
		if (!this.isSpineJson(resource)) {
			if (this.isJson(resource) && resource.textures) {
				// console.log(`PIXI SPINE LOADER ${resource.name} SPRITESHEET`);

				SpineSpritesheetParser.spritesheets[resource.name] = resource;

				resource.onAfterMiddleware.add(() => {
					this.onSpritesheetLoaded(resource);
				});
			}

			return next();
		} else {
			this.onSpineLoaded(resource, next);
		}
	}

	/**
	 * will output an array of missing sprite name
	 */
	public static listMissingSprites(): string[] {
		const missingArray = [];
		this.spineRessources.forEach((ressource) => {
			if (!ressource.complete) {
				ressource.missing.forEach((missing) => {
					missingArray.push(missing.imagePath);
				});
			}
		});
		return missingArray;
	}

	private static isImage(resource: loaders.Resource): boolean {
		return resource.type === loaders.Resource.TYPE.IMAGE;
	}

	private static isJson(resource: loaders.Resource): boolean {
		return resource.type === loaders.Resource.TYPE.JSON;
	}

	private static isSpineJson(resource: loaders.Resource): boolean {
		return resource.data && this.isJson(resource) && Boolean(resource.data.bones);
	}

	private static getAttachements(
		spineRessource: loaders.Resource,
		success: (textures: spine.core.Map<Texture>) => void,
		error: (missing: any) => void
	): void {
		const textures: spine.core.Map<Texture> = {};
		const missingTextures = [];

		for (const skinName in spineRessource.data.skins) {
			if (!spineRessource.data.skins.hasOwnProperty(skinName)) {
				continue;
			}
			const skin = spineRessource.data.skins[skinName];
			for (const attachementID in skin) {
				if (!skin.hasOwnProperty(attachementID)) {
					continue;
				}
				const childAttachement = skin[attachementID];
				for (const name in childAttachement) {
					if (!childAttachement.hasOwnProperty(name)) {
						continue;
					}
					const attachmentDatas = childAttachement[name];
					if (this.requireExternalAsset(attachmentDatas)) {
						const attachementName = this.getValue(attachmentDatas, "name", name);
						const imagePath = this.getValue(attachmentDatas, "path", attachementName);
						const attachment = this.getAttachement(imagePath);
						if (attachment) {
							textures[imagePath] = attachment;
						} else {
							missingTextures.push({ imagePath, attachementName, attachmentDatas });
						}
					}
				}
			}
		}

		if (missingTextures.length <= 0) {
			success(textures);
		} else {
			error(missingTextures);
		}
	}

	private static requireExternalAsset(attachmentDatas: any): boolean {
		const type = this.getValue(attachmentDatas, "type", "region");
		return SpineSpritesheetParser.typesThatRequireLoading.indexOf(type) > -1;
	}

	private static getValue(map: any, prop: string, defaultValue: any): any {
		return map[prop] !== undefined ? map[prop] : defaultValue;
	}

	private static addTexture(textureID: string, texture: Texture): void {
		this.attachements[textureID] = texture;
	}

	private static getAttachement(attachementName: string): Texture {
		return this.attachements[attachementName];
	}

	private static buildSpineData(
		spineRessource: loaders.Resource,
		atlas: spine.core.TextureAtlas
	): void {
		const metadata = spineRessource.metadata || {};

		const metadataSkeletonScale = metadata ? metadata.spineSkeletonScale || 1 : 1;

		const attachementLoader = new spine.core.AtlasAttachmentLoader(atlas);
		const spineJsonParser = new spine.core.SkeletonJson(attachementLoader);
		spineJsonParser.scale = metadataSkeletonScale;
		const skeletonData = spineJsonParser.readSkeletonData(spineRessource.data);

		spineRessource.spineData = skeletonData;
	}

	private static onSpineLoaded(resource: loaders.Resource, next: () => any): void {
		const spritesheetsToLoad = [];

		if (Array.isArray(resource.metadata.atlasId)) {
			resource.metadata.atlasId.forEach((atlasId) => {
				spritesheetsToLoad.push(atlasId);
			});
		} else {
			spritesheetsToLoad.push(resource.metadata.atlasId);
		}

		// create the atlas
		const atlas: spine.core.TextureAtlas = new spine.core.TextureAtlas();

		this.spineRessources.push({
			resource,
			next,
			atlas,
			toLoad: spritesheetsToLoad,
			complete: false,
		});

		this.onAssetLoaded(EAssetType.Spine);
	}

	private static onSpritesheetLoaded(resource: loaders.Resource): void {
		for (const key in resource.textures) {
			if (!resource.textures.hasOwnProperty(key)) {
				continue;
			}
			this.addTexture(
				key.indexOf(".") !== -1 ? key.substr(0, key.lastIndexOf(".")) : key,
				resource.textures[key]
			);
		}

		this.onAssetLoaded(EAssetType.Spritesheet);
	}

	private static onAssetLoaded(type: EAssetType): void {
		this.spineRessources.forEach((spineAsset) => {
			const spineRessource = spineAsset.resource;
			if (!spineAsset.complete) {
				const metadataAtlas = new spine.core.TextureAtlas();
				this.getAttachements(
					spineRessource,
					(textures: spine.core.Map<Texture>) => {
						metadataAtlas.addTextureHash(textures, true);
						this.buildSpineData(spineRessource, metadataAtlas);
						delete spineAsset.missing;
						spineAsset.complete = true;
						spineAsset.next();
					},
					(missing: any) => {
						spineAsset.missing = missing;
					}
				);
			}
		});
	}

	/**
	 * FIX IE Function .name
	 * @param fn {Function} the function you need to get the name from.
	 */
	private static getFunctionName(fn: string): string {
		return (fn.toString().match(/function (.+?)\(/) || [, ""])[1];
	}

	/**
	 * will find atlasParser middleware, and will override it with spineSpritesheetParset
	 */
	public static overrideAtlasParser(): void {
		// get loader middlewares
		const pixiMiddleware = (loaders.Loader as any)._pixiMiddleware;
		const afterMiddleware = (loader as any)._afterMiddleware;

		for (let i: number = 0, len = pixiMiddleware.length; i < len; i++) {
			const func = pixiMiddleware[i];
			const functionName = this.getFunctionName(func);

			// search for atlasParser
			if (functionName === "atlasParser") {
				PIXI.loaders[`SpineSpritesheetParser`] = SpineSpritesheetParser;

				// proxy function
				const spineSpritesheetParser = () => {
					return (resource: loaders.Resource, next: () => any) => {
						return SpineSpritesheetParser.middleware(resource, next);
					};
				};

				// replace atlasParser width spineSpritesheetParser
				pixiMiddleware[i] = spineSpritesheetParser;
				afterMiddleware[i] = spineSpritesheetParser;
			}
		}
	}
}
