import { Point } from "pixi.js";
import type { DisplayObject } from "pixi.js";

import { Helpers } from "./helpers";

export interface IDraggEventOptions {
	target: DisplayObject; // the element that will listen the dragg event
	canvas: HTMLCanvasElement; // canvas (required to listen dragOutSide)
	dragThreshold?: number; // the minimum dragging value before considering it a valid dragg.
	dragRestrictAxis?: string; // the axis where dragging is allowed
	stopPropagation?: boolean; // should this stop propagation on source event.
	preventDefault?: boolean; // should this prevent default on source event.
}

export interface ISwipeEventData {
	start: Point;
	end: Point;
	offset: Point;
	elapsed: number;
	pixelsPerSecond: Point;
	target: DisplayObject;
}

export class DraggEvent {
	public static currentScrollTargetID: string = "currentScrollTarget";

	public onDragStart: (e: PIXI.interaction.InteractionEvent) => void;
	public onDragMove: (e: PIXI.interaction.InteractionEvent, offset: Point, position: Point) => void;
	public onDragEnd: (e: PIXI.interaction.InteractionEvent) => void;
	public onPress: (
		e: PIXI.interaction.InteractionEvent,
		isPressed: boolean,
		position: Point
	) => void;

	public stopPropagation: boolean = true;
	public preventDefault: boolean = true;

	private _id: number;
	private _bound: boolean;
	private _cancel: boolean;
	private _dragging: boolean;
	private _start: Point;
	private _offset: Point;
	private _mouse: Point;
	private _movementX: number;
	private _movementY: number;
	private _options: IDraggEventOptions;
	private _target: DisplayObject;
	private _canvas: HTMLCanvasElement;
	private _startTime: number;

	// private previousScrollTarget: any;
	/**
	 * create an drag event listener from given options
	 * will listen mouseDown or touchstart on target,
	 * then listen move event and dispatch delta until mouseUp or touchEnd
	 */
	public constructor(options?: IDraggEventOptions) {
		this._options = options;
		this._target = options.target;
		this._canvas = options.canvas;

		this._bound = false;
		this._start = new Point();
		this._offset = new Point();
		this._mouse = new Point();
		this._movementX = 0;
		this._movementY = 0;
		this._cancel = false;
		this._dragging = false;

		if (this._options.stopPropagation === false) {
			this.stopPropagation = false;
		}

		if (this._options.preventDefault === false) {
			this.preventDefault = false;
		}

		const pOnDragStart = (e: PIXI.interaction.InteractionEvent) => {
			this._id = e.data.identifier;

			if (!this._bound && !e.stopped) {
				this._startTime = Date.now();
				this._start.copy(e.data.global);

				// FIX: addEventListener to canvas to handle mouveOutside target
				this._canvas.addEventListener("mousemove", pOnDragMove);
				this._canvas.addEventListener("touchmove", pOnDragMove);

				this._target.on("mouseup", pOnDragEnd);
				this._target.on("mouseupoutside", pOnDragEnd);
				this._target.on("touchend", pOnDragEnd);
				this._target.on("touchendoutside", pOnDragEnd);
				this._target.on("touchcancel", pOnDragEnd);

				// FIX: pixi mouseupoutside
				document.addEventListener("pointerup", pOnStageMouseUp);

				this._bound = true;

				if (this.onPress) {
					this.onPress(e, true, this._start);
				}

				if (this.stopPropagation) {
					e.stopPropagation();
				}
			}
		};

		const pOnStageMouseUp = (event) => {
			pOnDragEnd(event);
		};

		const pOnDragEnd = (e) => {
			if (e.data) {
				if (e.data.identifier !== this._id) {
					return;
				}
			}

			if (this._bound) {
				this._canvas.removeEventListener("mousemove", pOnDragMove);
				this._canvas.removeEventListener("touchmove", pOnDragMove);

				this._target.removeListener("mouseup", pOnDragEnd);
				this._target.removeListener("mouseupoutside", pOnDragEnd);
				this._target.removeListener("touchend", pOnDragEnd);
				this._target.removeListener("touchendoutside", pOnDragEnd);
				this._target.removeListener("touchcancel", pOnDragEnd);

				document.removeEventListener("pointerup", pOnStageMouseUp);

				this._dragging = false;
				this._bound = false;

				this.onDragEnd(e);
				this._emitSwipe();

				if (this.onPress) {
					this.onPress(e, false, this._start);
				}
			}
		};

		const pOnDragMove = (event) => {
			let point: Point;

			// i don't need to do anything since i'm stopped/prevented.
			if (event.defaultPrevented) {
				return;
			}

			// i stop / prevent propagation on the mouse / touch Event
			// as it is easier than doing so on interactionEvent
			if (this.stopPropagation) {
				event.stopPropagation();
			}
			if (this.preventDefault) {
				event.preventDefault();
			}

			if (event.touches && event.touches.length > 0) {
				point = new Point(event.touches[0].clientX, event.touches[0].clientY);
			} else {
				point = new Point(event.clientX, event.clientY);
			}

			this._mouse.copy(point);
			this._offset.set(this._mouse.x - this._start.x, this._mouse.y - this._start.y);
			if (!this._dragging) {
				this._movementX = Math.abs(this._offset.x);
				this._movementY = Math.abs(this._offset.y);
				if (
					(this._movementX === 0 && this._movementY === 0) ||
					Math.max(this._movementX, this._movementY) < this._options.dragThreshold
				) {
					return;
				}
				if (this._options.dragRestrictAxis !== undefined) {
					this._cancel = false;
					if (this._options.dragRestrictAxis === "x" && this._movementY > this._movementX) {
						this._cancel = true;
					} else if (this._options.dragRestrictAxis === "y" && this._movementY <= this._movementX) {
						this._cancel = true;
					}
					if (this._cancel) {
						pOnDragEnd(event);
						return;
					}
				}
				if (this.onDragStart) {
					this.onDragStart(event);
				}
				this._dragging = true;
			}
			this.onDragMove(event, this._offset, this._mouse);
		};

		const startEvent = () => {
			this._target.on("mousedown", pOnDragStart);
			this._target.on("touchstart", pOnDragStart);
		};

		startEvent();
	}

	private _emitSwipe(): void {
		const elapsed: number = Date.now() - this._startTime;
		const pixelsPerSecond: Point = new Point(
			(1000 * this._offset.x) / elapsed,
			(1000 * this._offset.y) / elapsed
		);

		Helpers.bubbleEmit(this._target, "SwipeEvent", {
			elapsed,
			start: this._start,
			end: this._mouse,
			offset: this._offset,
			pixelsPerSecond,
		});
	}
}
