import { Rectangle } from "pixi.js";

import { EGlyphType } from "./glyph";
import type { Glyph } from "./glyph";
import type { Line } from "./line";

export class Word {
	public x: number = 0;
	public y: number = 0; // previous line.height + previous line.y + interlignage
	public width: number = 0; // glyphs.width sum;
	public maxHeight: number = 0;
	public glyphs: Glyph[] = []; // all glyphs in that line.
	public text: string = "";
	public nextGlyph: Glyph = null;
	public line: Line;
	public baseline: number = 0;

	/**
	 * Constructor
	 * @param previousWord the word before "this" one
	 */
	public constructor(previousWord?: Word) {
		// let lastX = 0;

		if (previousWord) {
			this.x += previousWord.x + previousWord.width;

			if (previousWord.nextGlyph && previousWord.nextGlyph.type === EGlyphType.Space) {
				this.x += previousWord.nextGlyph.width;
			}
		}
	}

	/**
	 * Add the given glyph to the word
	 * @param glyph the glyph to add
	 */
	public pushGlyph(glyph: Glyph): void {
		glyph.word = this;
		this.glyphs.push(glyph);
		this.width += glyph.width;
		this.maxHeight = Math.max(this.maxHeight, glyph.maxHeight);
		this.baseline = Math.max(this.baseline, glyph.ascender);
		this.text += glyph.text;
	}

	/**
	 * Returns the bounds of the word
	 */
	public getBounds(): Rectangle {
		return new Rectangle(
			this.line.x + this.x,
			this.line.y - this.line.baseline + this.y,
			this.width,
			this.line.height
		);
	}
}
