import { Texture, utils } from "pixi.js";

import SCRATCH_0 from "./assets/scratch0.png";
import SCRATCH_2 from "./assets/scratch2.png";
import AUTO_MASK_0 from "./assets/grattage_mask_00.png";
import AUTO_MASK_1 from "./assets/grattage_mask_01.png";
import AUTO_MASK_2 from "./assets/grattage_mask_02.png";
import AUTO_MASK_3 from "./assets/grattage_mask_03.png";
import PARTICLES_DEFAULTS from "./assets/default.png";

interface IAssetMap {
	[index: string]: string;
}

export class ScratchAssets {
	private static _imageCache: any = [];
	/**
	 * get texture
	 * @param assetID ID of the texture you want to have
	 */
	public static get(assetID: string): Texture {
		const raw = this.getRaw(assetID);
		if (!utils.TextureCache[assetID] && raw) {
			const texture = Texture.fromImage(raw);
			// We need to change the cacheId as the raw is a base64 string.
			// Can't retrieve texture from "assetID" after it has been exported except if you manually replace the ids.
			texture.baseTexture.imageUrl =
				texture.baseTexture.textureCacheIds[0] =
				texture.textureCacheIds[0] =
					assetID;
			if (texture) {
				Texture.addToCache(texture, assetID);
			}
		}

		return utils.TextureCache[assetID];
	}

	/**
	 * get raw (string) data from embeded assets
	 * @param assetID the asset id
	 */
	public static getRaw(assetID: string): string {
		return this._embededAssets[assetID];
	}

	/**
	 * get image from embeded assets
	 * @param assetID the asset id
	 */
	public static getImage(assetID: string): HTMLImageElement {
		if (this._imageCache[assetID]) {
			return this._imageCache[assetID];
		} else {
			const texture = this.get(assetID);
			if (texture) {
				return texture.baseTexture.source as HTMLImageElement;
			} else {
				return null;
			}
		}
	}

	/**
	 * will init ScratchAssets cache.
	 */
	public static initCache(): void {
		// get Images and add them to cache
		if (Object.keys(this._imageCache).length <= 0) {
			for (const key in this._embededAssets) {
				this._imageCache[key] = this.getImage(key);
			}
		}
	}

	/**
	 * Clears the ScratchAssets cache. Typically used only in playground or editor.
	 */
	public static clearCache(): void {
		this._imageCache = {};
	}

	private static _embededAssets: IAssetMap = {
		"scratch/scratch_0.png": SCRATCH_0,
		"scratch/scratch_2.png": SCRATCH_2,
		"scratch/grattage_mask_00.png": AUTO_MASK_0,
		"scratch/grattage_mask_01.png": AUTO_MASK_1,
		"scratch/grattage_mask_02.png": AUTO_MASK_2,
		"scratch/grattage_mask_03.png": AUTO_MASK_3,
		"particles/default.png ": PARTICLES_DEFAULTS,
	};
}
