import type {
	Rectangle,
	Point,
	ObservablePoint,
	RoundedRectangle,
	Ellipse,
	Circle,
	Polygon,
} from "pixi.js";

import type {
	IProjectRectangle,
	IProjectPoint,
	IProjectRoundedRectangle,
	IProjectEllipse,
	IProjectCircle,
	IProjectPolygon,
} from "../model";

export class CommonExporter {
	/**
	 * Exports the given rectangle
	 * @param rect the rectangle to export
	 */
	public static exportRectangle(rect: Rectangle): IProjectRectangle {
		if (!rect) {
			return null;
		}

		return {
			x: rect.x,
			y: rect.y,
			type: rect.type,
			width: rect.width,
			height: rect.height,
		};
	}

	/**
	 * Exports the given rounded rectangle
	 * @param rect the rounded rectangle to export
	 */
	public static exportRoundedRectangle(rect: RoundedRectangle): IProjectRoundedRectangle {
		if (!rect) {
			return null;
		}

		return {
			x: rect.x,
			y: rect.y,
			type: rect.type,
			width: rect.width,
			height: rect.height,
			radius: rect.radius,
		};
	}

	/**
	 * Exports the given ellipse
	 * @param ellipse the ellipse to export
	 */
	public static exportEllipse(ellipse: Ellipse): IProjectEllipse {
		if (!ellipse) {
			return null;
		}

		return {
			x: ellipse.x,
			y: ellipse.y,
			type: ellipse.type,
			width: ellipse.width,
			height: ellipse.height,
		};
	}

	/**
	 * Exports the given circle
	 * @param circle the circle to export
	 */
	public static exportCircle(circle: Circle): IProjectCircle {
		if (!circle) {
			return null;
		}

		return {
			x: circle.x,
			y: circle.y,
			type: circle.type,
			radius: circle.radius,
		};
	}

	/**
	 * Exports the given polygon
	 * @param polygon the polygon to export
	 */
	public static exportPolygon(polygon: Polygon): IProjectPolygon {
		if (!polygon) {
			return null;
		}

		return {
			type: polygon.type,
			points: polygon.points,
		};
	}

	/**
	 * Exports the given point
	 * @param point the point to export
	 */
	public static exportPoint(point: Point | ObservablePoint): IProjectPoint {
		if (!point) {
			return null;
		}

		return {
			x: point.x,
			y: point.y,
		};
	}
}
