import { Tools } from "@gdk/core";
import type { interaction } from "pixi.js";

import { SpineTimelineButton } from "./button";
import { SpineTimelineRadioButton } from "./radioButton";
import { SpineTimelineCheckButton } from "./checkButton";
import type { ISpineTimelineButtonAnimations } from "./button";
import type { IStyles } from "../ui";

/**
 * @experimental
 * Defines all possible animations for spine button and the related names.
 */
export interface ISpineTimelineToggleButtonAnimations extends ISpineTimelineButtonAnimations {
	select: string;
	deselect: string;
	overSelected: string;
	overSelectedLoop: string;
	outSelected: string;
	selectedLoop: string;
	upSelected: string;

	// For radio button
	overNotSelected: string;
	overNotSelectedLoop: string;
	outNotSelected: string;
	notSelected: string;
	notSelectedLoop: string;
	notSelectedDown: string;
	notSelectedDownLoop: string;
	downSelected: string;
	downSelectedLoop: string;
	unselectedToIdle: string;
	noSelectedUp: string;
	selectedToUnselected: string;
}

/**
 * @experimental
 * Defines the new SpineButton working as a toggle button.
 * On pointer up, events are sent to notify if selected or unselected.
 * @example myToggleButton.on("selected", () => { ... });
 * @example myToggleButton.on("unselected", () => { ... });
 */
export class SpineTimelineToggleButton extends SpineTimelineButton {
	/**
	 * Defines all possible animations for the spine button and their related names in the spine file.
	 */
	public animationsNames: ISpineTimelineToggleButtonAnimations;
	/**
	 * Sets or gets wether or not looping on "overSelectedLoop" is required.
	 */
	public loopOnOverSelected: boolean = true;
	/**
	 * Sets or gets wether or not looping on "selectedLoop" is required.
	 */
	public loopOnSelected: boolean = true;
	/**
	 * Sets or gets wether or not looping on "overnotselectedLoop" is required.
	 */
	public loopOnOverNotSelected: boolean = true;
	/**
	 * Sets or gets wether or not looping on "notselectedLoop" is required.
	 */
	public loopOnNotSelected: boolean = true;
	/**
	 * Sets or gets wether or not looping on "downSelected" is required.
	 */
	public loopOnDownSelected: boolean = true;

	private _selected: boolean = false;

	/**
	 * Constructor.
	 * @param assetId the id of the spine datas in the core-loader.
	 * @param styles the styles of the buttons.
	 */
	public constructor(assetId: string, styles: IStyles) {
		super(assetId, styles);

		Tools.assign<ISpineTimelineButtonAnimations, ISpineTimelineToggleButtonAnimations>(
			this.animationsNames,
			{
				select: "selected",
				deselect: "unselected",

				selectedLoop: "selectedLoop",
				overSelected: "selected_over",
				overSelectedLoop: "selected_overLoop",
				outSelected: "selected_out",
				downSelected: "selected_down",
				downSelectedLoop: "selected_downLoop",
				upSelected: "selected_up",
				selectedToUnselected: "selected_to_unselected",

				notSelected: "unselected",
				notSelectedLoop: "unselectedLoop",
				overNotSelected: "unselected_over",
				overNotSelectedLoop: "unselected_overLoop",
				outNotSelected: "unselected_out",
				notSelectedDown: "unselected_down",
				notSelectedDownLoop: "unselected_downLoop",
				noSelectedUp: "unselected_up",
				unselectedToIdle: "unselected_to_idle",
			}
		);

		this.on("added", () => {
			if (
				this.parent instanceof SpineTimelineRadioButton ||
				this.parent instanceof SpineTimelineCheckButton
			) {
				this.parent.addButton(this);
			}
		});

		this.on("removed", () => {
			if (
				this.parent instanceof SpineTimelineRadioButton ||
				this.parent instanceof SpineTimelineCheckButton
			) {
				this.parent.removeButton(this);
			}
		});
	}

	/**
	 * Registers the event on pointer down.
	 * @override
	 */
	protected onPointerDown(): void {
		if (!this.state.hasAnimation(this.animationsNames.down)) {
			return;
		}

		this.on("pointerdown", () => {
			this._isDown = true;
			if (this._selected) {
				this.playAnimation([
					{ name: this.animationsNames.downSelected },
					{ name: this.animationsNames.downSelectedLoop, repeat: -1 },
				]);
			} else {
				this.playAnimation([
					{ name: this.animationsNames.down },
					{ name: this.animationsNames.downLoop, repeat: -1 },
				]);
			}
		});
	}

	/**
	 * Registers the event on pointer over.
	 */
	protected onPointerOver(): void {
		this.on("pointerover", (ev: interaction.InteractionEvent) => {
			this._isOver = true;
			if (this._isDown) {
				return;
			}

			if (this._selected) {
				this.playAnimation([
					{ name: this.animationsNames.overSelected },
					{ name: this.animationsNames.overSelectedLoop, repeat: this.loopOnOverSelected ? -1 : 0 },
				]);
			} else {
				this.playAnimation([
					{ name: this.animationsNames.over },
					{ name: this.animationsNames.overLoop, repeat: this.loopOnOver ? -1 : 0 },
				]);
			}

			this._trackPointerId = ev.data.identifier;
		});
	}

	/**
	 * Registers the event on pointer out.
	 * @override
	 */
	protected onPointerOut(): void {
		this.on("pointerout", () => {
			this._isOver = false;
			if (this._isDown) {
				return;
			}

			if (this._selected) {
				this.playAnimation([
					{ name: this.animationsNames.outSelected },
					{ name: this.animationsNames.selectedLoop, repeat: this.loopOnSelected ? -1 : 0 },
				]);
			} else {
				this.playAnimation([
					{ name: this.animationsNames.out },
					{ name: this.animationsNames.idleLoop, repeat: this.loopOnIdle ? -1 : 0 },
				]);
			}
		});
	}

	/**
	 * Registers the event on pointer up.
	 * @override
	 */
	protected onPointerUp(): void {
		this.on("pointerup", () => {
			if (!this._isDown) {
				return;
			}
			this._isDown = false;

			this.selected = !this._selected;
		});
	}

	/**
	 * Registers the event on pointer up outside.
	 * @override
	 */
	protected onPointerUpOutside(): void {
		this.on("pointerupoutside", () => {
			this._isDown = false;

			if (!this._selected) {
				this.playAnimation([
					{ name: this.animationsNames.idle },
					{ name: this.animationsNames.idleLoop, repeat: this.loopOnIdle ? -1 : 0 },
				]);
			} else {
				this.playAnimation([
					{ name: this.animationsNames.select },
					{ name: this.animationsNames.selectedLoop, repeat: this.loopOnIdle ? -1 : 0 },
				]);
			}
		});
	}

	/**
	 * Gets wether or not the toggle button is pushed.
	 */
	public get selected(): boolean {
		return this._selected;
	}

	/**
	 * Sets wether or not the toggle button is pushed.
	 */
	public set selected(selected: boolean) {
		if (this._selected === selected) {
			return;
		}
		this._setSelected(selected);
	}

	/**
	 * Sets the button's state (selected or unselected).
	 * @param selected wether or not the button is selected.
	 */
	private _setSelected(selected: boolean): void {
		this._selected = selected;

		if (selected) {
			if (this._isOver) {
				this.playAnimation([
					{ name: this.animationsNames.select },
					{ name: this.animationsNames.overSelected },
					{ name: this.animationsNames.overSelectedLoop, repeat: this.loopOnOverSelected ? -1 : 0 },
				]);
			} else {
				this.playAnimation([
					{ name: this.animationsNames.select },
					{ name: this.animationsNames.selectedLoop, repeat: this.loopOnSelected ? -1 : 0 },
				]);
			}
			this.emit("selected");
		} else {
			if (this._isOver) {
				this.playAnimation([
					{ name: this.animationsNames.deselect },
					{ name: this.animationsNames.over },
					{ name: this.animationsNames.overLoop, repeat: this.loopOnOver ? -1 : 0 },
				]);
			} else {
				this.playAnimation([
					{ name: this.animationsNames.deselect },
					{ name: this.animationsNames.idleLoop, repeat: this.loopOnIdle ? -1 : 0 },
				]);
			}
			this.emit("unselected");

			// We need to free the trackedPointers object to avoid old "pointerout" events.
			if (this._trackPointerId !== null) {
				delete this.trackedPointers[this._trackPointerId];
				this._trackPointerId = null;
			}
		}
	}

	/**
	 * Exports the spine timeline toggle button.
	 */
	public export(): any {
		return Tools.assign(super.export(), {
			type: "spine.SpineTimelineToggleButton",
			loopOnSelected: this.loopOnSelected,
			loopOnOverSelected: this.loopOnOverSelected,
		});
	}

	/**
	 * Parses the spine timeline toggle button according to the given configuration.
	 * @param config the serialized data.
	 * @param baseUrl the base url of the loader.
	 * @param button the existing button reference.
	 */
	public static parse(
		config: any,
		baseUrl: string,
		button?: SpineTimelineToggleButton
	): SpineTimelineToggleButton {
		// Create button
		if (!button) {
			button = new SpineTimelineToggleButton(config.assetId, config.styles);
		}

		SpineTimelineButton.parse(config, baseUrl, button);

		button.loopOnSelected = config.loopOnSelected;
		button.loopOnOverSelected = config.loopOnOverSelected;

		return button;
	}
}
