import { TweenMax } from "gsap";
import { accessibility } from "pixi.js";

import { accessibilityUpdate, activateUpdate, deactivateUpdate } from "../container/accessibility";

export class AccessibilityHelper {
	/**
	 * Overrides the update function of the AccessibilityManager class of Pixi.JS.
	 * This override is mandatory to get accessibility working with resize and docking
	 * systems.
	 */
	public static overrideUpdate(): void {
		(accessibility.AccessibilityManager.prototype as any).update = accessibilityUpdate;
	}

	/**
	 * Overrides the activate option to trigger an event on the activation of the accessibility.
	 * This is mandatory to set to visible any Containers that needs to be accessible.
	 * If the element is not visible it won't be accessible.
	 */
	public static overrideActivate(): void {
		(accessibility.AccessibilityManager.prototype as any).activate = activateUpdate;
	}

	/**
	 * Overrides the deactivate option to trigger an event on the deactivation of the accessibility.
	 */
	public static overrideDeactivate(): void {
		(accessibility.AccessibilityManager.prototype as any).deactivate = deactivateUpdate;
	}

	/**
	 * Creates a new notification DOM element that will be read by the OS narrator on the fly.
	 * @param message defines the message to be read by the OS narrator.
	 * @param delay defines the time expressed in seconds to wait until the notification is removed from the DOM.
	 */
	public static async createNotification(message: string, delay: number = 1): Promise<void> {
		const noticication = document.createElement("p");
		noticication.setAttribute("role", "alert");

		const notificationText = document.createTextNode(message);
		noticication.appendChild(notificationText);

		document.body.appendChild(noticication);

		return new Promise<void>((resolve) => {
			TweenMax.delayedCall(delay, () => {
				document.body.removeChild(noticication);

				resolve();
			});
		});
	}
}
