import { TweenMax } from "gsap";
import { Loader } from "@gdk/core";
import type { Howl } from "howler";

import { ScratchAssets } from "./scratchAssets";
import type { Scratch } from "./scratch";

export interface IScratchSounds {
	scratch: IScratchSoundsInstance;
	reveal: IScratchSoundsInstance;
	win?: IScratchSoundsInstance;
	lose?: IScratchSoundsInstance;
}

export interface IScratchSoundsInstance {
	id?: number;
	name?: string;
	instance?: Howl;
	solo?: boolean;
}

export enum ESounds {
	Scratch = "scratch",
	Reveal = "reveal",
	Win = "win",
	Lose = "lose",
}

export class ScratchSolver {
	private _scratchs: Scratch[];
	private _currentScratch: number;
	private _numberOfPasses: number;
	private _scratchsCount: number;
	private _maxSimultaneousUpdates: number;
	private _maxUpdateRatio: number;
	private _sounds: IScratchSoundsInstance[] = [];
	private _oneClick: boolean = false;
	private _particulesCount: number = 0;

	/**
	 * will be instanciated by get instance
	 */
	private constructor() {
		this._scratchs = [];
		this.maxUpdateRatio = 0.75;

		TweenMax.ticker.addEventListener("tick", () => {
			this._update();
		});

		ScratchAssets.initCache();

		this.benchmark();
	}

	private benchmark(loopCount: number = 2, performanceModeTrigger: number = 1000): void {
		// if i'm already in performance mode i don't need to test my performance
		if (this._oneClick) {
			return;
		}
		// 100 iterations took 270ms on surface book 2 and 2500ms at 6x slowdown
		const canvas = document.createElement("canvas");
		canvas.width = 512;
		canvas.height = 256;

		const context = canvas.getContext("2d");
		const startTime = Date.now();

		for (let loop = 0; loop < loopCount; loop++) {
			// clear canvas
			context.clearRect(0, 0, canvas.width, canvas.height);
			// draw lots of things on canvas
			for (let i = 0; i < canvas.width; i += 10) {
				for (let j = 0; j < canvas.height; j += 10) {
					context.fillStyle = "#FF00FF";
					context.fillRect(i, j, 5, 5);
				}
			}
			// get pixels
			const imgData = context.getImageData(0, 0, canvas.width, canvas.height);
			// manipulate pixels
			for (let i = 0; i < imgData.data.length; i += 4) {
				imgData.data[i] = imgData.data[i + 1] = imgData.data[i + 2] = 255;
				imgData.data[i + 3] = 255 - imgData.data[i + 3];
			}
			// store pixels
			context.putImageData(imgData, 0, 0, 0, 0, canvas.width, canvas.height);
		}

		const elapsed = Date.now() - startTime;
		console.info(
			`[SCRATCH SOLVER] benchmark took ${elapsed.toFixed(2)} ms to perform ${loopCount} iterations`
		);
		if (elapsed > performanceModeTrigger) {
			console.info(`[SCRATCH SOLVER] MODE PERFORMANCE`);
			this._modePerformance();
		}
	}

	/**
	 * resets all registered scrach
	 */
	public resetAll(): void {
		this._scratchs.forEach((scratch) => {
			scratch.reset();
		});
	}

	/**
	 * register a new scratch
	 */
	public add(scratch: Scratch): void {
		this._scratchs.push(scratch);
		scratch.renderer.oneClick = this._oneClick;
		this._updateMaxSimultaneousUpdates();
	}

	/**
	 * update handler
	 */
	private _update(): void {
		// if i'm already in performance mode i don't need to test my performance
		if (this._oneClick) {
			return;
		}

		const startTime: number = performance.now();
		const avaitUpdate = this._getActiveScratchs(this._maxSimultaneousUpdates);

		this._numberOfPasses = avaitUpdate.length;
		this._scratchsCount = this._numberOfPasses - 1;

		for (let i = 0; i < this._numberOfPasses; i++) {
			// adaptat update ratio if update exeed 35 ms
			const shouldContinue = this._adaptativeScratchRatio(startTime);

			if (shouldContinue) {
				if (this._currentScratch < this._scratchsCount) {
					this._currentScratch++;
				} else {
					this._currentScratch = 0;
				}

				avaitUpdate[this._currentScratch].update();
			} else {
				return;
			}
		}
	}

	private _adaptativeScratchRatio(startTime: number): boolean {
		const endTime: number = performance.now();
		const elapsed: number = endTime - startTime;
		if (elapsed >= 40) {
			this.maxUpdateRatio = this._maxUpdateRatio * 0.75;

			if (this._maxSimultaneousUpdates <= 1) {
				console.warn(
					"WARNING: ULTRA LOW PERFORMANCE SWITCHING TO ONE CLICK REVEAL (" +
						this._maxSimultaneousUpdates +
						" UPDATES)"
				);
				this._modePerformance();
			} else {
				console.warn(
					"WARNING: " +
						this._numberOfPasses +
						" scratchs updates took " +
						elapsed.toFixed(2) +
						" ms adapting scratch ratio to " +
						(this._maxUpdateRatio * 100).toFixed(2) +
						" % (will now perform only " +
						this._maxSimultaneousUpdates +
						" scratchs updates)"
				);
			}

			return false;
		} else {
			return true;
		}
	}

	/**
	 * will compute maxSimultaneousUpdates from max update ratio
	 */
	private _updateMaxSimultaneousUpdates(): void {
		this._maxSimultaneousUpdates = Math.max(
			1,
			Math.round(this._scratchs.length * this._maxUpdateRatio)
		);
	}

	/**
	 * will switch to performance mode
	 */
	private _modePerformance(): void {
		if (!this._oneClick) {
			this._oneClick = true;
			this._scratchs.forEach((scratch) => {
				scratch.modePerformance();
			});
		}
	}

	/**
	 * returns active scratchs
	 */
	private _getActiveScratchs(limit: number): Scratch[] {
		const avaitUpdate: Scratch[] = [];

		this._particulesCount = 0;

		this._scratchs.forEach((scratch) => {
			this._particulesCount += scratch.particulesCount;
			if (avaitUpdate.length <= limit) {
				if (scratch.active) {
					avaitUpdate.push(scratch);
				}
			}
		});

		return avaitUpdate;
	}

	private _playSound(sound: IScratchSoundsInstance): void {
		if (!sound.solo || sound.id === -1) {
			sound.id = sound.instance.play();
		} else if (sound.id !== -1) {
			if (!sound.instance.playing(sound.id)) {
				sound.id = sound.instance.fade(0, 1, 200, sound.id).play(sound.id);
			}
		}
	}

	private _stopSound(sound: IScratchSoundsInstance): void {
		if (sound.instance.playing(sound.id)) {
			sound.instance.fade(0, 1, 400, sound.id);
		}
	}

	/**
	 * define maxUpdate ratio
	 */
	public set maxUpdateRatio(value: number) {
		this._maxUpdateRatio = value;
		this._updateMaxSimultaneousUpdates();
	}

	private static _instance: ScratchSolver;

	/**
	 * get singleton instance
	 */
	public static get instance(): ScratchSolver {
		return this._instance || (this._instance = new this());
	}

	/**
	 * get all registered scratchs
	 */
	public static get scratchs(): Scratch[] {
		return this.instance._scratchs;
	}

	/**
	 * Register scratch sound
	 * @param sounds sound to register
	 */
	public static registerSounds(sounds: IScratchSounds): void {
		if (sounds.scratch) {
			this.instance._sounds[ESounds.Scratch] = {
				id: -1,
				instance: Loader.get<Howl>(sounds.scratch.name),
				solo: sounds.scratch.solo !== undefined ? sounds.scratch.solo : true,
			};
		}

		if (sounds.reveal) {
			this.instance._sounds[ESounds.Reveal] = {
				id: -1,
				instance: Loader.get<Howl>(sounds.reveal.name),
				solo: sounds.reveal.solo !== undefined ? sounds.reveal.solo : true,
			};
		}

		if (sounds.win) {
			this.instance._sounds[ESounds.Win] = {
				id: -1,
				instance: Loader.get<Howl>(sounds.win.name),
				solo: sounds.win.solo !== undefined ? sounds.win.solo : true,
			};
		}

		if (sounds.lose) {
			this.instance._sounds[ESounds.Lose] = {
				id: -1,
				instance: Loader.get<Howl>(sounds.lose.name),
				solo: sounds.lose.solo !== undefined ? sounds.lose.solo : true,
			};
		}
	}

	/**
	 * Play scratch sound
	 * @param soundName name of the sound to play
	 */
	public static playSound(soundName: string): void {
		const sound = this.instance._sounds[soundName];
		if (sound) {
			if (soundName === ESounds.Win) {
				this.instance._stopSound(this.instance._sounds[ESounds.Lose]);
				this.instance._playSound(sound);
			} else if (soundName === ESounds.Lose) {
				const soundWind = this.instance._sounds[ESounds.Win];
				if (!soundWind.instance.playing(soundWind.id)) {
					this.instance._playSound(sound);
				}
			} else {
				this.instance._playSound(sound);
			}
		}
	}
}
