import { Texture, DisplayObject } from "pixi.js";

import { elementToPath } from "../../tools";
import type { IProjectTexture } from "../model";

function originsToString(origins: Array<DisplayObject | string>): string {
	return origins
		.map((origin) => (origin instanceof DisplayObject ? elementToPath(origin) : origin))
		.join(" / ");
}

export class TextureImporter {
	/**
	 * Import a texture from the given configuration
	 * @param config the texture's configuration
	 * @param baseUrl the base url of the loader
	 */
	public static importTexture(
		config: IProjectTexture,
		baseUrl: string,
		...origins: Array<DisplayObject | string>
	): Texture {
		if (!config.imageUrl) {
			// we should use logger for that @TODO
			const prefix = origins.length > 0 ? originsToString(origins) + " : " : "";
			console.error(`${prefix}no URL defined for texture. Empty texture was used.`);
			return Texture.WHITE;
		}

		const url = config.imageUrl.replace(baseUrl, "");
		try {
			const texture = Texture.fromFrame(url);
			return texture;
		} catch (e) {
			// we should use logger for that @TODO
			const prefix = origins.length > 0 ? originsToString(origins) + " : " : "";
			console.error(
				`${prefix}texture at ${url} could not be loaded: ${e.message}. Empty texture was used.`
			);
			return Texture.WHITE;
		}
	}
}
