import type { Timeline, ETimelineType } from "./timeline";
import type { TimelineAnimation } from "./timelineAnimation";

/**
 * All enum types for timeline events
 */
export enum ETimelineEventsType {
	All = "all",
	Start = "start",
	Play = "play",
	Pause = "pause",
	Resume = "resume",
	Complete = "complete",
	Update = "update",
	Repeat = "repeat",
	Loop = "loop",
	ReversePlay = "reverse_play",
	ReverseLoop = "reverse_loop",
	ReverseComplete = "reverse_complete",
	AnimationEvent = "event",
}

/**
 * Represents a timeline callback
 */
export interface ITimelineCallback {
	type: ETimelineEventsType;
	emitter: ETimelineType;
	callback: (e?: TimelineEvent) => void;
}

export class TimelineEvent {
	// Private members
	private _emitter: TimelineAnimation | Timeline;
	private _type: ETimelineEventsType;
	private _params?: any;

	/**
	 * Constructor
	 * @param emitter the event emitter
	 * @param type the event type
	 * @param params the event's params
	 */
	public constructor(
		emitter: TimelineAnimation | Timeline,
		type: ETimelineEventsType,
		params?: any
	) {
		this._type = type;
		this._emitter = emitter;
		this._params = params;
	}

	/**
	 * Returns the event type
	 */
	public get type(): ETimelineEventsType {
		return this._type;
	}

	/**
	 * Returns the emitter
	 */
	public get emitter(): TimelineAnimation | Timeline {
		return this._emitter;
	}

	/**
	 * Returns the event params
	 */
	public get params(): any {
		return this._params;
	}

	/**
	 * Format toString() for ease of use
	 */
	public toString(): {
		emitter: TimelineAnimation | Timeline;
		type: ETimelineEventsType;
		params?: any;
	} {
		if (this.params !== null && this.params !== undefined) {
			return { emitter: this._emitter, type: this.type, params: this.params };
		} else {
			return { emitter: this._emitter, type: this.type };
		}
	}
}

// Spine Timeline Complete Event
export class TimelineCompleteEvent extends TimelineEvent {
	public constructor(emitter: Timeline | TimelineAnimation, params?: any) {
		super(emitter, ETimelineEventsType.Complete, params);
	}
}
