import { TextField } from "./textField";
import { IStyle, IStyles, EAlign } from "./textStyle";
import type { ITextFieldOptions } from "./textField";

export { IStyle, IStyles, EAlign };

export class TextArea extends TextField {
	/**
	 * Constructor
	 * @param text the text of the text field
	 * @param style the style of the text field
	 * @param canvas the optional existing canvas
	 */
	public constructor(
		text: string,
		styles: IStyle | IStyles,
		options: ITextFieldOptions = {},
		canvas?: HTMLCanvasElement
	) {
		super(text, styles, options, canvas);
	}
}
