import { Container, Point } from "pixi.js";
import type { CanvasRenderer, WebGLRenderer, interaction } from "pixi.js";

export interface ICustomPointerOptions {
	debug: boolean;
}

/**
 * Custom pointer configuration
 * TODO : add other optionnal properties (onMove...)
 */
export interface ICustomPointerConfig {
	element: Container;
	pivot?: Point;
	scale?: number;
	allowGlobalInteractionEvents?: boolean;
	onMove?: (target: Container, datas: IPointerMoveEvent) => void;
	onDown?: (target: Container, datas: IPointerEvent) => void;
}

export interface IPointerEvent {
	position: Point;
	localPosition: Point;
	data: interaction.InteractionData;
}

export interface IPointerMoveEvent extends IPointerEvent {
	previous: Point;
	delta: Point;
	distance: number;
	time: number;
	speed: number;
	angle: {
		radian: number;
		degree: number;
	};
}

export class CustomPointer extends Container {
	/**
	 * callback called when the pointer moved Over a target
	 */
	public onOver: (target: Container) => void;
	/**
	 * callback called when the pointer moved Out a target
	 */
	public onOut: (target: Container) => void;
	/**
	 * callback called when the pointer is pressed on a target
	 */
	public onDown: (target: Container, datas: IPointerEvent) => void;
	/**
	 * callback called when the pointer is released
	 */
	public onUp: (target: Container) => void;
	/**
	 * callback called when the pointer moved over a target
	 */
	public onMove: (target: Container, datas: IPointerMoveEvent) => void;
	/**
	 * Sets wether or not the native pointer icon should be hidden or not
	 */
	public hideNativePointer: boolean = true;
	/**
	 * Sets wether or not global interaction events are allowed or are closed to the current target container.
	 */
	public allowGlobalInteractionEvents: boolean = true;

	/**
	 * method called when the pointer need to be shown
	 * should be overriten
	 */
	public onShow: () => void = () => {
		this.visible = true;
	};

	/**
	 * method called when the pointer need to be hidden
	 * should be overriten
	 */
	public onHide: () => void = () => {
		this.visible = false;
	};

	private _pointerIcon: Container;
	private _renderer: CanvasRenderer | WebGLRenderer;
	private previousPosition: Point = new Point();
	private _targets: Container[] = [];
	private _currentTarget: Container;
	private previousTime: number;
	private _degrees: number = 180 / Math.PI;

	/**
	 * Constructor
	 * @param config rendering options for custom pointer
	 */
	public constructor(config?: ICustomPointerConfig) {
		super();

		this.interactive = this.interactiveChildren = false;
		this.previousTime = -1;

		if (config) {
			this.setConfig(config);
		}

		this.onHide();
	}

	/**
	 * sets the pointer icon scale and pivot (and more)
	 * @param config the new configuration of the custom pointer
	 */
	public setConfig(config: ICustomPointerConfig): void {
		if (this._pointerIcon) {
			this.removeChild(this._pointerIcon);
		}

		this._pointerIcon = config.element;
		this._pointerIcon.pivot = config.pivot || new Point(0, 0);
		this._pointerIcon.scaleXY = config.scale || 1;
		this._pointerIcon.rotation = 0;
		this.rotation = 0;
		this.onMove = config.onMove || null;
		this.onDown = config.onDown || null;
		if (config.allowGlobalInteractionEvents !== undefined) {
			this.allowGlobalInteractionEvents = config.allowGlobalInteractionEvents;
		}
		this.addChild(this._pointerIcon);
	}

	/**
	 * Resets the custom pointer
	 */
	public reset(): void {
		this.currentTarget = null;
	}

	/**
	 * override PIXI renderCanvas Method to get the renderer;
	 * @param renderer {CanvasRenderer} the renderer i want to get
	 */
	public renderCanvas(renderer: CanvasRenderer): void {
		super.renderCanvas(renderer);
		this.renderer = renderer;
	}

	/**
	 * override PIXI renderWebGL Method to get the renderer;
	 * @param renderer {WebGLRenderer} the renderer i want to get
	 */
	public renderWebGL(renderer: WebGLRenderer): void {
		super.renderWebGL(renderer);
		this.renderer = renderer;
	}

	/**
	 * add a target to watch and where the pointer need's to appear.
	 * @param target the target to watch.
	 */
	public addTarget(target: Container): void {
		const index = this._targets.indexOf(target);

		if (index === -1) {
			this._targets.push(target);

			if (this.hideNativePointer) {
				target.cursor = "none";
			}

			target.on("pointerover", this._onOver, this);
			target.on("touchmove", this._onOver, this);
			target.on("pointerout", this._onOut, this);
			target.on("pointerdown", this._onDown, this);
			target.on("pointerup", this._onUp, this);
			if (!this.allowGlobalInteractionEvents) {
				target.on("pointermove", this._onMove, this);
			}
		}
	}

	/**
	 * remove a target from the watch list
	 * @param target the target to remove.
	 */
	public removeTarget(target: Container): void {
		const index = this._targets.indexOf(target);

		if (index > -1) {
			const removedTarget = this._targets.splice(index, 1)[0];

			if (this.hideNativePointer) {
				removedTarget.cursor = "default";
			}

			removedTarget.off("pointerover", this._onOver, this);
			removedTarget.off("touchmove", this._onOver, this);
			removedTarget.off("pointerout", this._onOut, this);
			removedTarget.off("pointerdown", this._onDown, this);
			removedTarget.off("pointerup", this._onUp, this);
			if (!this.allowGlobalInteractionEvents) {
				removedTarget.off("pointermove", this._onMove, this);
			}
		}
	}

	public set globalPointerVisibility(pValue: boolean) {
		if (this._renderer) {
			const currentMode = pValue ? "inherit" : "none";
			this._renderer.plugins.interaction.cursorStyles.default = currentMode;
			this._renderer.plugins.interaction.setCursorMode(currentMode);
		}
	}

	private set renderer(renderer: CanvasRenderer | WebGLRenderer) {
		if (this._renderer !== renderer) {
			this._renderer = renderer;
			if (this.allowGlobalInteractionEvents) {
				this._renderer.plugins.interaction.on("pointermove", this._onMove, this);
				this._renderer.plugins.interaction.on("touchmove", this._onMove, this);
			}
		}
	}

	private _onDown(e: interaction.InteractionEvent): void {
		if (this.onDown) {
			this.currentTarget = this._getTargetFromInteration(e);
			const position = new Point(e.data.global.x, e.data.global.y);
			this.onDown(this.currentTarget, {
				localPosition: this.currentTarget
					? e.data.getLocalPosition(this.currentTarget, position)
					: null,
				data: e.data,
				position,
			});
		}
	}

	private _onUp(e: interaction.InteractionEvent): void {
		if (this.onUp) {
			this.onUp(this.currentTarget);
		}
	}

	private _onOut(e: interaction.InteractionEvent): void {
		this.onHide();
		if (this.onOut) {
			this.onOut(this.currentTarget);
		}
	}

	private _onOver(e: interaction.InteractionEvent): void {
		this.currentTarget = this._getTargetFromInteration(e);
		if (this.onOver) {
			this.onOver(this.currentTarget);
		}
		this.onShow();
	}

	private _radian2degrees(rad: number): number {
		return rad * this._degrees;
	}

	private _getTargetFromInteration(e: interaction.InteractionEvent): Container {
		const currentOverTarget = e.currentTarget as Container;

		if (this._targets.indexOf(currentOverTarget) > -1) {
			return currentOverTarget;
		} else {
			return null;
		}
	}

	private _onMove(e: interaction.InteractionEvent): void {
		this.currentTarget = this._getTargetFromInteration(e);

		if (this.onMove) {
			const currentTime = performance.now();
			const position = new Point(e.data.global.x, e.data.global.y);

			if (this.previousTime < 0) {
				this.previousTime = currentTime;
				this.previousPosition.copy(position);
			}

			const time = currentTime - this.previousTime;
			const previous = this.previousPosition;
			const localPosition = this.currentTarget
				? e.data.getLocalPosition(this.currentTarget, position)
				: null;
			const delta = new Point(position.x - previous.x, position.y - previous.y);
			const magnitude = delta.x * delta.x + delta.y * delta.y;
			const radian = Math.atan2(delta.y, delta.x); // angle maths
			const degree = this._radian2degrees(radian); // convert in degrees
			const distance = Math.sqrt(magnitude);
			const speed = distance / time;

			this.position.set(position.x, position.y);

			this.onMove(this.currentTarget, {
				previous,
				position,
				localPosition,
				delta,
				distance,
				time,
				speed,
				angle: {
					degree,
					radian,
				},
				data: e.data,
			});

			this.previousPosition.set(position.x, position.y);
			this.previousTime = currentTime;
		}

		if (this.currentTarget) {
			if (this.hideNativePointer) {
				this.currentTarget.cursor = "none";
			}
			if (!this.currentTarget.interactive) {
				this.onHide();
			}
		}
	}

	private set currentTarget(value: Container) {
		this._currentTarget = value;

		if (this._currentTarget) {
			this.parent.addChild(this);
		}
	}

	private get currentTarget(): Container {
		return this._currentTarget;
	}
}
