import { Container, Rectangle, Texture, mesh, Sprite, Graphics } from "pixi.js";
import { Tools } from "@gdk/core";
import type { DisplayObject, BaseTexture, CanvasRenderer, WebGLRenderer } from "pixi.js";

import { DockingBounds } from "../../../container/dockingBounds";
import { ContainerExporter } from "../../../loading/export/containerExporter";
import { CommonExporter } from "../../../loading/export/commonExporter";
import { TextureExporter } from "../../../loading/export/textureExporter";
import { ContainerImporter } from "../../../loading/import/containerImporter";
import { CommonImporter } from "../../../loading/import/commonImporter";

export interface ISize {
	width: number;
	height: number;
}

export interface INineSliceContainerOptions {
	backgroundTexture:
		| string
		| number
		| HTMLImageElement
		| HTMLCanvasElement
		| HTMLVideoElement
		| BaseTexture;
	slice?: Rectangle | number;
	padding?: Rectangle | number;
	minSize?: ISize;
}

export class NineSliceContainer extends Container {
	public minSize: ISize;
	public innerDockingBounds: DockingBounds = null;

	private _background: mesh.NineSlicePlane;
	private _container: Container;
	private _padding: Rectangle;
	private _slice: Rectangle;
	private _previousSize: ISize = { width: -1, height: -1 };

	private _innerBounds: Rectangle = new Rectangle();
	private _boundCached: number = performance.now() - 1000;

	/**
	 * constructor
	 * @param options the nine slice container options :
	 * {
	 *   backgroundTexture:string | number | HTMLImageElement | HTMLCanvasElement | HTMLVideoElement | BaseTexture;
	 *   slice?: Rectangle | number;
	 *   padding?: Rectangle | number;
	 *   minSize?: Size;
	 * }
	 */
	public constructor(options: INineSliceContainerOptions) {
		super();

		this.slice = options.slice ? options.slice : 0;
		this.padding = options.padding ? options.padding : 0;
		this.background = options.backgroundTexture;
		this.minSize = options.minSize
			? options.minSize
			: {
					width: this._slice.x + this._slice.width,
					height: this._slice.y + this._slice.height,
			  };

		this._container = new Container();
		this._container.setName("Contents");
		this._container.doNotExport = true;
		super.addChild(this._container);

		this.dockingBounds = new DockingBounds(1, 1);
	}

	/**
	 * Render text with WebGL
	 * @param renderer the pixi.js renderer (WebGLRenderer)
	 * @override PIXI.Sprite.renderWebGL
	 */
	public renderWebGL(renderer: WebGLRenderer): void {
		super.renderWebGL(renderer);
		this._updateSize();
	}

	/**
	 * Render text with Canvas renderer
	 * @param renderer the pixi.js renderer (CanvasRenderer)
	 * @override PIXI.Sprite._renderCanvas
	 */
	public renderCanvas(renderer: CanvasRenderer): void {
		super.renderCanvas(renderer);
		this._updateSize();
	}

	/**
	 * add some contents
	 * @param child the child to add
	 */
	public addContent<T extends DisplayObject>(child: T): T {
		return this._container.addChild(child);
	}

	/**
	 * add some contents at given index
	 * @param child the child to add
	 * @param index the index to add at
	 */
	public addContentAt<T extends DisplayObject>(child: T, index: number): T {
		return this._container.addChildAt(child, index);
	}

	/**
	 * get content at given index
	 * @param index the index where the content should be.
	 */
	public getContentAt<T extends DisplayObject>(index: number): T {
		return this._container.getChildAt(index) as T;
	}

	/**
	 * get content by name
	 * @param name the name of the content to get.
	 */
	public getContentByName<T extends DisplayObject>(name: string): T {
		return this._container.getChildByName(name) as T;
	}

	/**
	 * get the index of provided content
	 * @param child the content we need need the index of.
	 */
	public getContentIndex(child: DisplayObject): number {
		return this._container.getChildIndex(child);
	}

	/**
	 * the padding between the background and the content
	 * @param value could be a rectangle or a number
	 */
	public set padding(value: Rectangle | number) {
		if (typeof value === "number") {
			this._padding = new Rectangle(value, value, value, value);
		} else {
			this._padding = value;
		}
	}

	/**
	 * @returns the padding between the background and the content
	 */
	public get padding(): Rectangle | number {
		return this._padding;
	}

	/**
	 * the nine slice settings
	 * @param value could be a rectangle or a number
	 */
	public set slice(value: Rectangle | number) {
		if (typeof value === "number") {
			this._slice = new Rectangle(value, value, value, value);
		} else {
			this._slice = value;
		}

		if (this._background) {
			this._background.leftWidth = this._slice.x;
			this._background.topHeight = this._slice.y;
			this._background.rightWidth = this._slice.width;
			this._background.bottomHeight = this._slice.height;
		}
	}

	/**
	 * @returns the nine slice settings
	 */
	public get slice(): Rectangle | number {
		return this._slice;
	}

	/**
	 * the background texture
	 * @param texture {string | number | HTMLImageElement | HTMLCanvasElement | HTMLVideoElement | BaseTexture}
	 */
	public set background(
		texture: string | number | HTMLImageElement | HTMLCanvasElement | HTMLVideoElement | BaseTexture
	) {
		if (this._background) {
			this.removeChild(this._background);
		}

		this._background = new mesh.NineSlicePlane(
			Texture.from(texture),
			this._slice.x,
			this._slice.y,
			this._slice.width,
			this._slice.height
		);
		this._background.doNotExport = true;

		super.addChildAt(this._background, 0);
	}

	/**
	 * Returns the background nine slice mesh
	 */
	public getBackground(): mesh.NineSlicePlane {
		return this._background;
	}

	/**
	 * return the container children
	 */
	public get contents(): DisplayObject[] {
		return this._container.children;
	}

	/**
	 * return the container
	 */
	public get container(): Container {
		return this._container;
	}

	private _getInnerBounds(force?: boolean): Rectangle {
		if (force || performance.now() - this._boundCached > 1000) {
			if (this.innerDockingBounds) {
				this._innerBounds.width = this.innerDockingBounds.width;
				this._innerBounds.height = this.innerDockingBounds.height;
			} else {
				let maxHeight = 0;
				let maxWidth = 0;

				this._container.children.forEach((child: DisplayObject) => {
					if (child instanceof Sprite || child instanceof Graphics) {
						maxHeight = Math.max(maxHeight, child.y + child.height);
						maxWidth = Math.max(maxWidth, child.x + child.width);
					} else {
						maxHeight = Math.max(maxHeight, child.y + child[`_height`]);
						maxWidth = Math.max(maxWidth, child.x + child[`_width`]);
					}
				});

				this._innerBounds.height = Math.ceil(maxHeight);
				this._innerBounds.width = Math.ceil(maxWidth);
			}

			this._boundCached = performance.now();
		}

		return this._innerBounds;
	}

	/**
	 * update background size according content size + padding
	 */
	private _updateSize(): void {
		const bounds = this._getInnerBounds();

		// if size changed
		if (bounds.width !== this._previousSize.width || bounds.height !== this._previousSize.height) {
			const paddedWidth = Math.round(bounds.width + this._padding.x + this._padding.width);
			const paddedHeight = Math.round(bounds.height + this._padding.y + this._padding.height);

			if (this.minSize.width > paddedWidth) {
				this._background.width = this.minSize.width;
				this._container.x = Math.round(this._background.width - bounds.width) * 0.5;
			} else {
				this._background.width = paddedWidth;
				this._container.x = this._background.x + this._padding.x;
			}

			if (this.minSize.height > paddedHeight) {
				this._background.height = this.minSize.height;
				this._container.y = Math.round(this._background.height - bounds.height) * 0.5;
			} else {
				this._background.height = paddedHeight;
				this._container.y = this._background.y + this._padding.y;
			}

			// update the hit area
			if (this.interactive) {
				this.hitArea = new Rectangle(0, 0, this._background.width, this._background.height);
			}

			this.dockingBounds.setBounds(this._background.width, this._background.height);

			// store the current size for later test
			this._previousSize = {
				width: bounds.width,
				height: bounds.height,
			};
		}
	}

	/**
	 * Exports the nine slice container
	 */
	public export(): any {
		const slice =
			typeof this._slice === "number" ? this.slice : CommonExporter.exportRectangle(this._slice);
		const padding =
			typeof this._padding === "number"
				? this._padding
				: CommonExporter.exportRectangle(this._padding);

		return Tools.assign(ContainerExporter.exportContainer(this), {
			type: "ui.NineSliceContainer",
			options: {
				backgroundTexture: TextureExporter.exportTexture(this._background.texture).imageUrl,
				slice,
				padding,
				minSize: this.minSize,
			},
			content: this._container.children.map((c) => ContainerExporter.export(c as Container)),
		});
	}

	/**
	 * Parses the nine slice container
	 * @param config the serialized data
	 * @param baseUrl the base url of the loader
	 */
	public static parse(config: any, baseUrl: string): NineSliceContainer {
		const slice =
			typeof config.options.slice === "number"
				? config.options.slice
				: CommonImporter.importRectangle(config.options.slice);
		const padding =
			typeof config.options.padding === "number"
				? config.options.padding
				: CommonImporter.importRectangle(config.options.padding);

		const container = new NineSliceContainer({
			backgroundTexture: config.options.backgroundTexture,
			minSize: config.options.minSize,
			slice,
			padding,
		});
		config.content.forEach((c) => container.addContent(ContainerImporter.import(c, baseUrl)));

		return container;
	}
}
