import { Point } from "pixi.js";
import type { DisplayObject } from "pixi.js";

export interface IMouseWheelEventOptions {
	target: DisplayObject; // the element that will listen the wheel event
}

export class MouseWheelEvent {
	public static LINE_HEIGHT: number = 100;
	public static PAGE_HEIGHT: number = 1;

	// mouse wheel event handler
	public onMouseWheel: (e: PIXI.interaction.InteractionEvent, delta: Point) => void;

	// private id: number;
	private _bound: boolean;
	private _preventDefault: boolean;
	private _delta: Point;
	private _options: IMouseWheelEventOptions;
	private _target: DisplayObject;
	private _previousWheelTarget: any;

	/**
	 * create an mouse wheel event listener from given options
	 * will listen mouseOver on target, then listen wheel event and dispatch delta until mouseOut
	 */
	public constructor(options?: IMouseWheelEventOptions) {
		this._options = options;
		this._target = this._options.target;

		this._preventDefault = false;
		this._bound = false;
		this._delta = new Point();

		this._startEvent();
	}

	/**
	 * get current mouse wheel target (the one mouse is over)
	 */
	private get _currentWheelTarget(): any {
		return window[MouseWheelEvent.currentWheelTargetID];
	}

	/**
	 * set current mouse wheel target
	 */
	private set _currentWheelTarget(value: any) {
		window[MouseWheelEvent.currentWheelTargetID] = value;
	}

	private _startEvent(): void {
		const mousewheelHandler = (e) => {
			this._onMouseWheel(e);
		};

		this._target.on("pointerover", (e: PIXI.interaction.InteractionEvent) => {
			if (!this._bound) {
				document.addEventListener("wheel", mousewheelHandler, { passive: !this._preventDefault });
				this._setPreviousWheelTarget();
				this._bound = true;
				e.stopPropagation();
			}
		});

		this._target.on("pointerout", (e: PIXI.interaction.InteractionEvent) => {
			if (this._bound) {
				document.removeEventListener("wheel", mousewheelHandler, false);
				this._resetPreviousWheelTarget();
				this._bound = false;
			}
		});
	}

	private _setPreviousWheelTarget(): void {
		if (!this._previousWheelTarget) {
			this._previousWheelTarget = this._currentWheelTarget;
			this._currentWheelTarget = this;
		}
	}

	private _resetPreviousWheelTarget(): void {
		if (this._previousWheelTarget) {
			this._currentWheelTarget = this._previousWheelTarget;
			this._previousWheelTarget = null;
		}
	}

	private _isCurrentWheelTarget(): boolean {
		return this._currentWheelTarget === this;
	}

	private _onMouseWheel(e: any): void {
		if (this._isCurrentWheelTarget()) {
			if (this._preventDefault) {
				e.preventDefault();
			}

			let pX = 0;
			let pY = 0;

			if (e.deltaMode) {
				// FIREFOX
				pX = e.deltaX * MouseWheelEvent.LINE_HEIGHT;
				pY = e.deltaY * MouseWheelEvent.LINE_HEIGHT;
			} else {
				// OTHERS
				pX = e.deltaX * MouseWheelEvent.PAGE_HEIGHT;
				pY = e.deltaY * MouseWheelEvent.PAGE_HEIGHT;
			}

			this._delta.set(pX, pY);
			this.onMouseWheel(e, this._delta);
		}
	}

	public static currentWheelTargetID: string = "currentWheelTarget";
}
