import Proton from "proton-js";
import { Sprite, RenderTexture, Container, SCALE_MODES, WRAP_MODES } from "pixi.js";
import type { SystemRenderer, WebGLRenderer, Texture, CanvasRenderer } from "pixi.js";

import { ScratchAssets } from "./scratchAssets";
import { CustomMaskFilter } from "./customMaskFilter";

/**
 * Scratch renderer options
 */
export interface IScratchRendererOptions {
	content: Container | Sprite;
	revealRatio: number;
	revealCompleteRatio: number;
	invert: boolean;
	clamp: boolean;
	resolution: number;
	alwaysUpdateTransform: boolean;
}

export class ParticlesMaskRenderer extends Proton.CanvasRenderer {
	public name: string;
	public output: Container;
	public updated: boolean;
	public revealRatio: number;
	public revealed: boolean = false;
	public revealComplete: boolean = false;
	public pixiRenderer: SystemRenderer;
	public stopped: boolean = false;
	public onReveal: () => void;
	public onRevealComplete: () => void;
	public onScratch: () => void;
	public onFirstRenderComplete: () => void;

	public element: HTMLCanvasElement;
	public oneClick: boolean = false;

	public content: Container | Sprite;
	public clampValue: number = 128;

	private _scratchStep: number = 0;
	private _scratchDistance: number = 0;
	private _protonCanvas: HTMLCanvasElement;
	private _protonContext: CanvasRenderingContext2D;
	private _outputCanvas: HTMLCanvasElement;
	private _outputContext: CanvasRenderingContext2D;
	private _rendertexture: RenderTexture;
	private _isWebgl: boolean;
	// private baseRendertexture: BaseRenderTexture;
	private _outputTexture: RenderTexture | Texture;
	private _resolution: number = 0.5;
	private _revealRatio: number;
	private _revealCompleteRatio: number;
	private activeParticulesCount: number = 0;
	private _canvasCompositeOperation: GlobalCompositeOperation = "source-in";
	private _resetting: boolean = false;
	private scratchStepDistance: number = 20;
	private _debugDiv: HTMLDivElement;

	private _invert: boolean = true;
	private _clamp: boolean = false;
	private _mask: Sprite = null;
	private _alwaysUpdateTransform: boolean;

	public isDirty: boolean = true;

	/**
	 * will instanciate a proton canvas and will use it to draw mask
	 * @param options mask renderer settings
	 */
	public constructor(options: IScratchRendererOptions) {
		super(document.createElement("canvas"));

		this._revealRatio = options.revealRatio;
		this._revealCompleteRatio = options.revealCompleteRatio;
		this._resolution = options.resolution;
		this._alwaysUpdateTransform = options.alwaysUpdateTransform;

		this._protonCanvas = this.element;
		this._protonCanvas.id = "protonCanvas";
		this._protonCanvas.width = options.content.width * this._resolution;
		this._protonCanvas.height = options.content.height * this._resolution;
		this._protonContext = this._protonCanvas.getContext("2d");

		this._outputCanvas = document.createElement("canvas") as HTMLCanvasElement;
		this._outputCanvas.id = "outputCanvas";
		this._outputCanvas.width = options.content.width * this._resolution;
		this._outputCanvas.height = options.content.height * this._resolution;
		this._outputContext = this._outputCanvas.getContext("2d");

		this._debugDiv = document.createElement("div");
		this._debugDiv.appendChild(this._protonCanvas);
		this._debugDiv.appendChild(this._outputCanvas);
		this._debugDiv.style.position = "fixed";
		this._outputCanvas.style.left = `${this._outputCanvas.width}px`;

		this.content = options.content;
		const scaleMode = SCALE_MODES.LINEAR;
		this._rendertexture = RenderTexture.create(
			options.content.width,
			options.content.height,
			scaleMode,
			this._resolution
		);

		this._outputTexture = RenderTexture.fromCanvas(this._outputCanvas, scaleMode);

		this.scratchStepDistance = Math.round(
			Math.sqrt(
				this._outputCanvas.width * this._outputCanvas.width +
					this._outputCanvas.height * this._outputCanvas.height
			) / 8
		);

		this.output = new Container();
		this.output.renderable = false;
		this.output.name = "Output";
		this.output.doNotExport = true;

		this.clamp = options.clamp;
		this.invert = options.invert;
		this.name = "MaskRenderer";
	}

	/**
	 * Gets wether or not the particle mask render is set to invert mode
	 */
	public get invert(): boolean {
		return this._invert;
	}

	/**
	 * Sets wether or not the particle mask render is set to invert mode
	 */
	public set invert(value: boolean) {
		this._invert = value;
		this._canvasCompositeOperation = value ? "source-out" : "source-in";

		if (this._isWebgl) {
			this.output.filters = [new CustomMaskFilter(this._mask, this)];
		}
	}

	/**
	 * Gets wether or not clamping is enabled
	 */
	public get clamp(): boolean {
		return this._clamp;
	}

	public set clamp(value: boolean) {
		this._clamp = value;
		if (this._isWebgl) {
			this.output.filters = [new CustomMaskFilter(this._mask, this)];
		}
	}

	/**
	 * Resizes the renderer (this and output canvas)
	 * @param width the new width
	 * @param height the new height
	 */
	public resize(width: number, height: number): void {
		super.resize(width, height);

		this._outputCanvas.width = width;
		this._outputCanvas.height = height;

		this._rendertexture.resize(width, height, false);
	}

	/**
	 * on particle update callback (emited by Proton)
	 */
	public onParticleUpdate(particle: Proton.particle): void {
		super.onParticleUpdate(particle);
		this.updated = true;
	}

	/**
	 * on proton update callback (emited by Proton)
	 */
	public onProtonUpdate(): void {
		if (this.updated && !this.stopped) {
			this.isDirty = true;
			this._renderOutput();
			this._updateImageData(1);

			if (!this._resetting) {
				this.onScratch();
			}
		} else {
			this.isDirty = false;
		}

		this._resetting = false;
		this.updated = false;
	}

	/**
	 * canvas render
	 */
	public renderCanvas(renderer: CanvasRenderer): void {
		if (!this.pixiRenderer) {
			this.output.renderable = false;
			this.pixiRenderer = renderer;
			this._isWebgl = false;
			const sprite = new Sprite(this._outputTexture);
			sprite.scaleXY = 1 / this._resolution;

			this.output.addChild(sprite);
			this._firstRender();
		}
	}

	/**
	 * webgl render
	 */
	public renderWebGL(renderer: WebGLRenderer): void {
		if (!this.pixiRenderer) {
			this.output.renderable = true;
			this.pixiRenderer = renderer;
			this._isWebgl = true;
			this.output.addChild(this.content);
			this._outputTexture.baseTexture.wrap = true;
			this._outputTexture.baseTexture.wrapMode = WRAP_MODES.CLAMP;

			this._mask = new Sprite(this._outputTexture);
			this._mask.scaleXY = 1 / this._resolution;
			this.output.addChild(this._mask);
			this.output.filters = [new CustomMaskFilter(this._mask, this)];
			this._firstRender();

			// rebuild filterArea
			this.cacheAsBitmap = false;
		}
	}

	/**
	 * will perform a render of the output context
	 */
	public renderOutput(): void {
		const targetWidth: number = this._protonCanvas.width;
		const targetHeight: number = this._protonCanvas.height;
		// render source content
		(this.pixiRenderer as CanvasRenderer).render(
			this.content,
			this._rendertexture,
			true,
			null,
			false
		);
		// first part of mask trick
		this._copyImageData();
		// second part of mask trick
		this._outputContext.globalCompositeOperation = "source-over";
		this._outputContext.drawImage(
			this._rendertexture.baseTexture.source,
			0,
			0,
			targetWidth,
			targetHeight,
			0,
			0,
			targetWidth,
			targetHeight
		);

		this._outputTexture.update();
	}

	/**
	 * clear canvas and textures (reset image datas)
	 */
	public clear(): void {
		this._protonContext.clearRect(0, 0, this._protonCanvas.width, this._protonCanvas.height);
		this._outputContext.clearRect(0, 0, this._outputCanvas.width, this._outputCanvas.height);

		// FIX/IE:for IE i need to Fill output with white.
		this._outputContext.fillStyle = "#FFFFFF";
		this._outputContext.fillRect(0, 0, this._outputCanvas.width, this._outputCanvas.height);

		this._outputTexture.update();
	}
	/**
	 * on particle created callback (emited by Proton)
	 */
	public onParticleCreated(particle: any): void {
		// this.activeParticulesCount++;
	}

	/**
	 * on particle death callback (emited by Proton)
	 */
	public onParticleDead(particle: any): void {
		// this.activeParticulesCount--;
	}

	/**
	 * will force reveal (fill, render, pixeldetection, events...)
	 */
	public forceReveal(): void {
		this._fill();
		this._renderOutput();
		this._updateImageData(0);
	}

	/**
	 * stops the renderer
	 */
	public stop(): void {
		this.updated = false;
		this.revealRatio = 0;
		this.stopped = true;
	}

	/**
	 * will perform a frame by frame update
	 * @param distance the distance traveled by the pointer since last updateFame.
	 */
	public updateFrame(distance: number): void {
		this._scratchDistance += distance;
		if (this._scratchDistance > this.scratchStepDistance) {
			this._scratchDistance = 0;
			if (this._scratchStep < 4) {
				const image = ScratchAssets.getImage(`scratch/grattage_mask_0${this._scratchStep}.png`);

				if (image) {
					this._protonContext.drawImage(
						image,
						0,
						0,
						image.height,
						image.height,
						0,
						0,
						this._protonCanvas.width,
						this._protonCanvas.height
					);

					this._renderOutput();
					this._scratchStep++;
				}
			} else {
				this.forceReveal();
			}
		}
	}

	/**
	 * resets the renderer
	 */
	public reset(): void {
		this.revealed = false;
		this.revealComplete = false;
		this._resetting = true;
		this._scratchDistance = 0;
		this._scratchStep = 0;

		this.clear();
		this._updateImageData(0);
		this._renderOutput();
	}

	public set cacheAsBitmap(value: boolean) {
		if (!this.output.filters && !this._isWebgl) {
			return;
		}

		if (value) {
			// removing the filterArea fix the cacheAsBitmap
			this.output.filterArea = null;
		} else {
			// it will rebuild the filterArea and reassign it to the output.
			// Filter
			const filter = this.output.filters[0];
			filter.resolution = this.pixiRenderer.resolution;
			// Filter area
			this.output.filterArea = this._mask.getBounds(true);
			this.output.filterArea.width = (this.output.filterArea.width >> 0) - filter.padding;
			this.output.filterArea.height = (this.output.filterArea.height >> 0) - filter.padding;
		}

		this.output.cacheAsBitmap = value;
	}

	public get cacheAsBitmap(): boolean {
		return this.output.cacheAsBitmap;
	}

	/**
	 * render output texture
	 */
	private _renderOutput(): void {
		if (!this.pixiRenderer) {
			return;
		}

		const targetWidth: number = this._protonCanvas.width;
		const targetHeight: number = this._protonCanvas.height;

		if (!this._isWebgl) {
			// Pixi.js doesn't clear canvas event if "true" is set to "clear" when calling .render
			// Clear it manually but can generate problems in future if pixi.js awakes :)
			const canvasRenderTarget = (this._rendertexture.baseTexture as any)._canvasRenderTarget;
			if (canvasRenderTarget) {
				canvasRenderTarget.clear();
			}

			// render source content
			(this.pixiRenderer as CanvasRenderer).render(
				this.content,
				this._rendertexture,
				true,
				null,
				!this._alwaysUpdateTransform
			);
			// first part of mask trick
			this._copyImageData();
			// second part of mask trick
			this._outputContext.globalCompositeOperation = this._canvasCompositeOperation;
			this._outputContext.drawImage(
				this._rendertexture.baseTexture.source,
				0,
				0,
				targetWidth,
				targetHeight,
				0,
				0,
				targetWidth,
				targetHeight
			);
		} else {
			// first part of mask trick
			this._copyImageData();
			// update output texture
			this._outputTexture.update();
		}
	}

	/**
	 * image data copy (faster than drawing or rendering canvas)
	 */
	private _copyImageData(): void {
		const targetWidth: number = this._protonCanvas.width * (1 / this._resolution);
		const targetHeight: number = this._protonCanvas.height * (1 / this._resolution);

		const imgData = this._protonContext.getImageData(0, 0, targetWidth, targetHeight);

		this._outputContext.putImageData(
			imgData,
			0,
			0,
			0,
			0,
			this._protonCanvas.width,
			this._protonCanvas.height
		);
	}

	/**
	 * first render after creation
	 */
	private _firstRender(): void {
		this._renderOutput();
		this.onFirstRenderComplete.call(this);
		this.output.renderable = true;
	}

	/**
	 * fill canvas and textures when complete
	 */
	private _fill(): void {
		this._resetting = true;
		this._protonContext.fillStyle = "#FFFFFF";
		this._protonContext.fillRect(0, 0, this._protonCanvas.width, this._protonCanvas.height);
	}

	/**
	 * copy image data and test if
	 */
	private _updateImageData(channel: number): void {
		const imgData = this._protonContext.getImageData(
			0,
			0,
			this._protonCanvas.width,
			this._protonCanvas.height
		);
		let drawn: number = 0;

		const dataLength = imgData.data.length;
		const pixelChanels = 4;
		const pixelToTest = 10 * pixelChanels;
		const toDraw = dataLength / pixelToTest;

		for (let i = 0; i < dataLength; i += pixelToTest) {
			if (imgData.data[i + channel] > 128) {
				drawn++;
			}
		}

		this.revealRatio = drawn / toDraw;

		this.completionTest();
	}

	/**
	 * test if reveal is complete
	 */
	private completionTest(): void {
		if (!this.revealed && this.revealRatio > this._revealRatio) {
			this.revealed = true;

			if (this.onReveal) {
				this.onReveal.call(this);
			}
		}

		if (!this.revealComplete && this.revealRatio >= this._revealCompleteRatio) {
			this.revealComplete = true;

			this._fill();
			this._renderOutput();

			if (this.onRevealComplete) {
				this.onRevealComplete.call(this);
			}
		}
	}
}
