import type { Container, interaction } from "pixi.js";

interface IEventReceiverObject {
	/**
	 * Defines the name of the event.
	 */
	eventName: string;
	/**
	 * Defines the name of the property being marked @on("...").
	 */
	propertyKey: string | symbol;
}

/**
 * Returns the event receiver class by taking the @EventReceiver decorated class as argument.
 * @param ctor the constructor reference to extend.
 */
const getEventReceiverClass = <T extends new (...args: any[]) => Container>(ctor: T): T => {
	return class extends ctor {
		public constructor(...ctorArgs: any[]) {
			super(...ctorArgs);

			const events = getEventReceiverObjects(ctor);
			if (!events) {
				return;
			}

			events.forEach((e) => {
				this.on(e.eventName, (...eventArgs: any[]) => {
					this[e.propertyKey](...eventArgs);
				});
			});

			// Clean
			delete (ctor as any).__Events;
		}
	};
};

/**
 * Returns the event receiver objects array to be stored in the given constructor.
 * @param ctor the constructor reference where to store the events.
 */
const getEventReceiverObjects = <T extends new (...args: any[]) => Container>(
	ctor: T
): IEventReceiverObject[] => {
	(ctor as any).__Events = (ctor as any).__Events || [];
	return (ctor as any).__Events;
};

/**
 * Tags the given class as event receiver to use @on decorators.
 */
export function EventReceiver(): any {
	return <T extends new (...args: any[]) => Container>(ctor: T): T => {
		return getEventReceiverClass(ctor);
	};
}

/**
 * Tags the given function to be called on the container receives the given event.
 * @param eventName the name of the event to listen before calling
 */
export function onEvent(eventName: interaction.InteractionEventTypes | string): any {
	return (target: any, propertyKey: string | symbol): any => {
		if (typeof target[propertyKey] !== "function") {
			throw new Error(
				`Property "${propertyKey.toString()}" is not a function. Please use event decorators only on functions.`
			);
		}

		const events = getEventReceiverObjects(target.constructor);
		events.push({ eventName, propertyKey });
	};
}
