/**
 * Polyfills for Number
 */
export function NumberPolyfill(): void {
	/**
	 * Is Integer
	 */
	if (!Number.isInteger) {
		Number.isInteger = function isInteger(value: number): boolean {
			if (value === undefined || value === null || typeof value !== "number") {
				return false;
			}

			return value.toString().indexOf(".") === -1;
		};
	}
}
