import { Amount, Tools } from "@gdk/core";
import { Point } from "pixi.js";
import type { ICurrencyOptions } from "@gdk/core";
import type { Container } from "pixi.js";

import { TextField } from "../baseui";
import { Timeline } from "../../spine";
import type { IStyle, IStyles } from "../baseui";
import type { ITimelineOptions, ITimelineAnimationOptions } from "../../spine";

export interface ISpineLogoParams extends ITimelineOptions {
	stake?: {
		// define stake options
		id?: string; // the id of the stake container in the spine animation
		style?: IStyle | IStyles; // the style(s) for the textfield
		value?: Amount | string; // the stake value (either Amount or string)
		options?: ICurrencyOptions; // Amount.toString() currency options
		offset?: Point; // the offset applyed to the stake textfield
	};
}

export class SpineLogo extends Timeline {
	// the container for the stake (image and text)
	public stakeContainer: Container;
	// the stake textfield
	public stakeTextfield: TextField;
	// store the SpineLogo parametres default: { minimumFractionDigits: 0, currencyDisplay: "symbol" }
	public parameters: ISpineLogoParams;

	// store the stake value for possible later use
	private _stakeValue: Amount | string;

	/**
	 * Create a SpineLogo
	 * @param parameters {ISpineLogoParams} the spine logo settings inherrited from Timeline
	 * the only mendatory parametre is the spine animation "assetId".
	 */
	public constructor(parameters: ISpineLogoParams) {
		// spine logo is paused by default.
		parameters.paused = parameters.paused ? parameters.paused : true;
		parameters.animations = parameters.animations || SpineLogo.DEFAULT_SEQUENCE;

		super(parameters);

		this.parameters = Tools.assign(
			{
				stake: {
					id: "stake",
					style: {
						default: {},
					},
					options: {
						minimumFractionDigits: 0,
						currencyDisplay: "symbol",
					},
					offset: new Point(),
				},
			},
			parameters
		);

		// get the requested slot container
		this.stakeContainer = this.getSlotContainerByName(this.parameters.stake.id);
		// if the requested slot container exist
		if (this.stakeContainer) {
			// create the stake textfield
			this.stakeTextfield = new TextField("", this.parameters.stake.style);
			this.stakeTextfield.doNotExport = true;

			// put the textfield inside
			this.setSlotAttachementByName(this.parameters.stake.id, this.stakeTextfield);
		}

		this.stake = this.parameters.stake.value;
	}

	/**
	 * will start the logo animation
	 */
	public show(): void {
		this.play(0);
	}

	/**
	 * set stake value
	 * @param value{Amount|string} the value can be an Amount or a string
	 * if the stake is undefined or null the stake container will be hidden.
	 */
	public set stake(value: Amount | string) {
		this._stakeValue = value;

		// i have a stake and a textfield
		if (value && this.stakeTextfield) {
			if (this._stakeValue instanceof Amount) {
				this.stakeTextfield.text = this._stakeValue.toString(this.parameters.stake.options);
			} else {
				this.stakeTextfield.text = this._stakeValue;
			}

			// todo: fix that scale bug.
			this.stakeTextfield.scaleY = -1;
			// align stake textfield
			this.stakeTextfield.x = this.parameters.stake.offset.x + this.stakeTextfield.width * -0.5;
			this.stakeTextfield.y =
				this.parameters.stake.offset.y * -1 + this.stakeTextfield.height * -0.5;

			this.stakeContainerVisibility = true;
		} else {
			this.stakeContainerVisibility = false;
		}
	}

	public get stake(): Amount | string {
		return this._stakeValue;
	}

	/**
	 * set the stake container visibility
	 */
	public set stakeContainerVisibility(value: boolean) {
		if (this.stakeContainer) {
			this.stakeContainer.renderable = value;
		}
	}

	/**
	 * Spine logo default animation sequence
	 * start with "enter" followed by 2 "idle" and finish with an "exit"
	 */
	public static DEFAULT_SEQUENCE: ITimelineAnimationOptions[] = [
		{ name: "enter", label: "enter" },
		{ name: "idle", label: "idle", repeat: 2 },
		{ name: "exit", label: "exit" },
	];

	/**
	 * Spine logo default animation sequence for home
	 * start with "enter", then loop on idle. Exit has to be called after.
	 */
	public static DEFAULT_HOME_SEQUENCE: ITimelineAnimationOptions[] = [
		{ name: "enter", label: "enter " },
		{ name: "idle", label: "idle", loop: true },
		{ name: "exit", label: "exit" },
	];

	/**
	 * Exports the current game logo
	 */
	public export(): any {
		const parameters = Tools.assign({}, this.parameters, {
			skin: this.skeleton.skin ? this.skeleton.skin.name : undefined,
			stake: {
				value: this._stakeValue,
				style: this.stakeTextfield ? this.stakeTextfield.styles : undefined,
			},
		});

		return Tools.assign(super.export(), {
			type: "spine.SpineLogo",
			parameters,
		});
	}

	/**
	 * Parses the spine logo
	 * @param config the serialized data
	 * @param baseUrl the base url of the loader
	 */
	public static parse(config: any, baseUrl: string, spineLogo?: SpineLogo): SpineLogo {
		if (!spineLogo) {
			spineLogo = new SpineLogo(config.parameters);
		}

		Timeline.parseSlotContainers(config, baseUrl, spineLogo);

		return spineLogo;
	}
}
