import { Container, Graphics, Rectangle, Point, Sprite } from "pixi.js";
import { TweenMax, Cubic } from "gsap";
import { Tools } from "@gdk/core";
import type { DisplayObject, WebGLRenderer, CanvasRenderer } from "pixi.js";

import { DockingBounds } from "../../../container/dockingBounds";
import { ContainerExporter } from "../../../loading/export/containerExporter";
import { ContainerImporter } from "../../../loading/import/containerImporter";
import { DraggEvent } from "./draggEvent";
import { MouseWheelEvent } from "./mouseWheelEvent";
import { ScrollBar } from "./scrollBar";
import { Helpers } from "./helpers";

export interface IScrollContainerOptions {
	scrollX?: boolean; // Enable horizontal scrolling
	scrollY?: boolean; // Enable vertical scrolling
	dragScrolling?: boolean; // Enable mousedrag scrolling
	wheelScrolling?: boolean; // Enable mousewheel scrolling
	softness?: number; // softness of scrolling
	radius?: number; // corner radius of clipping mask
	expandMask?: number; // mask expand (px)
	showDebug?: boolean; // show debug information
	width?: number; // container width
	height?: number; // container height
}

export enum EScrollbarAlign {
	Before,
	After,
}

export class ScrollContainer extends Container {
	public expandMask: number; // mask expand (px)
	public radius: number; // corner radius of clipping mask
	public innerContainer: Container; // the contents container
	public innerBounds: Rectangle; // the bounds of contents
	public scrollBars: ScrollBar[]; // list of scrollbars.
	public innerDockingBounds: DockingBounds; // content viewport

	private _width: number;
	private _height: number;
	private _lastWidth: number = -1;
	private _lastHeight: number = -1;
	private _innerMask: Graphics;
	private _debugMask: Graphics;
	private _shouldScrollX: boolean;
	private _shouldScrollY: boolean;

	private _scrollX: boolean;
	private _scrollY: boolean;
	private _dragScrolling: boolean;
	private _wheelScrolling: boolean;
	private _softness: number;
	private _animating: boolean;
	private _scrolling: boolean;
	private _boundCached: number;

	private _containerStart: Point;
	private _targetPosition: Point;
	private _lastPosition: Point;
	// pixi.js properties
	private Position: Point;
	private Speed: Point;
	private _stop: boolean;
	private _lastTime: number;
	private _canvas: HTMLCanvasElement;
	private _showDebug: boolean;
	private _positionTween: TweenMax;

	private _draggScroll: DraggEvent;
	private _wheelScroll: MouseWheelEvent;

	/**
	 * create a ScrollContainer element with specified options
	 */
	public constructor(options?: IScrollContainerOptions) {
		super();

		options = options || {};
		this._scrollX = options.scrollX !== undefined ? options.scrollX : false;
		this._scrollY = options.scrollY !== undefined ? options.scrollY : true;
		this._dragScrolling = options.dragScrolling !== undefined ? options.dragScrolling : true;
		this._wheelScrolling = options.wheelScrolling !== undefined ? options.wheelScrolling : true;
		this._softness =
			options.softness !== undefined ? Math.max(Math.min(options.softness || 0, 1), 0) : 0.5;
		this.radius = options.radius || 0;
		this.expandMask = options.expandMask || 0;
		this.width = options.width || 0;
		this.height = options.height || 0;
		this._showDebug = options.showDebug || false;
		this._animating = false;
		this._scrolling = false;
		this.scrollBars = [];
		this._boundCached = performance.now() - 1000;

		this.initialize();
	}

	/**
	 * attach a scrollbar to that scroll container
	 * @param scrollBar {ScrollBar} the scrollbar to attach.
	 * @param align {Align} the alignement (Before or After) of that scrollbar.
	 */
	public attachScrollbar(
		scrollbar: ScrollBar,
		align: EScrollbarAlign = EScrollbarAlign.After
	): void {
		if (scrollbar.vertical) {
			if (align === EScrollbarAlign.After) {
				scrollbar.x = this._width + scrollbar.margin.x;
				scrollbar.y = this.y;
			} else {
				scrollbar.x = this.x - scrollbar.width - scrollbar.margin.width;
				scrollbar.y = this.y;
			}
		} else {
			if (align === EScrollbarAlign.After) {
				scrollbar.x = this.x;
				scrollbar.y = this._height + scrollbar.margin.y;
			} else {
				scrollbar.y = this.y;
				scrollbar.y = this.y - scrollbar.height - scrollbar.margin.height;
			}
		}

		this.scrollBars.push(scrollbar);
		super.addChild(scrollbar);
	}

	/**
	 * override container.addChild() method to prevent adding child oustide of innerContainer.
	 * @param child {T extends DisplayObject} the child to addChild in innerContainer
	 * @param additionalChildren {DisplayObject[]} (optional) additional children to addChild in innerContainer
	 */
	public addChild<T extends DisplayObject>(child: T, ...additionalChildren: DisplayObject[]): T {
		const argumentsLength = arguments.length;
		if (argumentsLength > 1) {
			for (let i = 0; i < argumentsLength; i++) {
				this.addChild(arguments[i]);
			}
		} else {
			this.innerContainer.addChild(child); // add children in the container that has scrolling
			this.getInnerBounds(true); // make sure bounds is updated instantly when a child is added
		}

		return child;
	}

	/**
	 * will scroll to given ratio
	 * @param vertical {boolean} set if it's scrolling on vertical axis
	 * @param ratio {number} the ratio to scroll to 0 to 1
	 * @param duration {number} (default 1) the time in seconds it take to scroll
	 * @param ease {gsap.Ease} (default Cubic.easeInOut) the easing function used to scroll.
	 */
	public scrollTo(
		vertical: boolean,
		ratio: number,
		duration: number = 1,
		ease: gsap.Ease = Cubic.easeInOut
	): void {
		if (duration <= 0) {
			this._forceScrollTo(vertical, ratio);
		} else {
			const bounds = this.getInnerBounds();
			const axis = Helpers.getAxis(vertical);

			const targetPosition = Helpers.Cap(
				-((bounds[axis.size] - this[axis.size]) * Helpers.Cap(ratio, 0, 1)),
				-bounds[axis.size],
				0
			);

			if (this._scrollX && !vertical) {
				this._positionTween = TweenMax.to(this.Position, duration, {
					x: targetPosition,
					ease,
				});
			} else if (this._scrollY && vertical) {
				this._positionTween = TweenMax.to(this.Position, duration, {
					y: targetPosition,
					ease,
				});
			}
		}
	}

	/**
	 * will scroll to given position
	 * @param position {Point} the position to scroll to
	 * @param duration {number} (default 1) the time in seconds it take to scroll
	 * @param ease {gsap.Ease} (default Cubic.easeInOut) the easing function used to scroll.
	 */
	public scrollToPosition(
		position: Point,
		duration: number = 1,
		ease: gsap.Ease = Cubic.easeInOut
	): void {
		const bounds = this.getInnerBounds();

		if (this._scrollX && position.x !== this.innerContainer.x) {
			const x = Helpers.Cap(position.x, 0, bounds.width);
			const total = bounds.width - this._width;
			const ratio = Helpers.Cap(position.x / total, 0, 1);

			this.scrollTo(false, ratio, duration, ease);
		}

		if (this._scrollY && position.y !== this.innerContainer.y) {
			const y = Helpers.Cap(position.y, 0, bounds.height);
			const total = bounds.height - this._height;
			const ratio = Helpers.Cap(position.y / total, 0, 1);

			this.scrollTo(true, ratio, duration, ease);
		}
	}

	/**
	 * Render text with WebGL
	 * @param renderer the pixi.js renderer (WebGLRenderer)
	 * @override PIXI.Sprite.renderWebGL
	 */
	public renderWebGL(renderer: WebGLRenderer): void {
		super.renderWebGL(renderer);
		if (!this._canvas) {
			this._canvas = renderer.view;
			this.initDragScroll();
		}
		this.update();
	}

	/**
	 * Render text with Canvas renderer
	 * @param renderer the pixi.js renderer (CanvasRenderer)
	 * @override PIXI.Sprite._renderCanvas
	 */
	public renderCanvas(renderer: CanvasRenderer): void {
		super.renderCanvas(renderer);
		if (!this._canvas) {
			this._canvas = renderer.view;
			this.initDragScroll();
		}
		this.update();
	}

	/**
	 * get the height
	 */
	// remove this comment when allow-override-transient-properties is included in typescript
	// @ts-ignore
	public get height(): number {
		return this._height;
	}

	/**
	 * set the height
	 * @param value {number} the new height
	 */
	// remove this comment when allow-override-transient-properties is included in typescript
	// @ts-ignore
	public set height(value: number) {
		this._height = value;
	}

	/**
	 * get the width
	 */
	// remove this comment when allow-override-transient-properties is included in typescript
	// @ts-ignore
	public get width(): number {
		return this._width;
	}

	/**
	 * set the width
	 * @param value {number} the new width
	 */
	// remove this comment when allow-override-transient-properties is included in typescript
	// @ts-ignore
	public set width(value: number) {
		this._width = value;
	}

	/**
	 * Returns the inner bounds (from inner container)
	 * @param force to to calculate inner bounds
	 */
	public getInnerBounds(force?: boolean): Rectangle {
		if (force || performance.now() - this._boundCached > 1000) {
			if (this.innerDockingBounds) {
				this.innerBounds.height = this.innerDockingBounds.height;
				this.innerBounds.width = this.innerDockingBounds.width;
			} else {
				let maxHeight = 0;
				let maxWidth = 0;

				this.innerContainer.children.forEach((child: DisplayObject) => {
					if (child instanceof Sprite || child instanceof Graphics) {
						maxHeight = Math.max(maxHeight, child.y + child.height);
						maxWidth = Math.max(maxWidth, child.x + child.width);
					} else {
						maxHeight = Math.max(maxHeight, child.y + child[`_height`]);
						maxWidth = Math.max(maxWidth, child.x + child[`_width`]);
					}
				});

				this.innerBounds.height = Math.ceil(maxHeight);
				this.innerBounds.width = Math.ceil(maxWidth);
			}

			this._shouldScrollX = this.innerBounds.width > this._width && this._scrollX;
			this._shouldScrollY = this.innerBounds.height > this._height && this._scrollY;
			this._boundCached = performance.now();
		}

		return this.innerBounds;
	}

	protected initialize(): void {
		this.innerContainer = new Container();
		this.innerBounds = new Rectangle();
		this._innerMask = new Graphics();
		this._debugMask = new Graphics();

		if (this._showDebug) {
			super.addChildAt(this._debugMask, 0);
		}

		super.addChild(this._innerMask);
		super.addChild(this.innerContainer);

		this.innerContainer.mask = this._innerMask;
		this.innerContainer.name = "Inner Container";

		this._innerMask.name = "Inner Mask";

		if (this._scrollX || this._scrollY) {
			this.initScrolling();
		}
	}

	protected update(): void {
		if (this._lastWidth !== this._width || this._lastHeight !== this._height) {
			const of = this.expandMask;

			const origin = -of;
			const pWidth = this._width + of * 2;
			const pHeight = this._height + of * 2;

			if (this._showDebug) {
				this.redrawMask(true, origin, pWidth, pHeight);
			}

			this.redrawMask(false, origin, pWidth, pHeight);

			this._lastWidth = this._width;
			this._lastHeight = this._height;
		}

		this.setScrollPosition();
	}

	protected setScrollPosition(speed?: Point): void {
		if (speed) {
			this.Speed = speed;
		}

		if (!this._animating) {
			this._animating = true;
			this._lastPosition.copy(this.innerContainer.position);
			this._targetPosition.copy(this.innerContainer.position);
		}
	}

	protected updateScrollPosition(delta: number): void {
		if (this._scrollX || this._scrollY) {
			this._stop = true;
			const bounds = this.getInnerBounds();
			if (this._shouldScrollX) {
				this.updateDirection(bounds, "x", delta);
			}
			if (this._shouldScrollY) {
				this.updateDirection(bounds, "y", delta);
			}
			if (this._scrolling || this._animating) {
				this.updateScrollBars();
			}
			if (this._stop) {
				this._animating = false;
			}
		}
	}

	protected updateDirection(bounds: Rectangle, direction: string, delta: number): void {
		let min;
		if (direction === "y") {
			min = Math.round(Math.min(0, this._height - bounds.height));
		} else {
			min = Math.round(Math.min(0, this._width - bounds.width));
		}

		if (!this._scrolling && Math.round(this.Speed[direction]) !== 0) {
			this._targetPosition[direction] += this.Speed[direction];

			this.Speed[direction] = Helpers.Lerp(
				this.Speed[direction],
				0,
				(5 + 2.5 / Math.max(this._softness, 0.01)) * delta
			);
			if (this._targetPosition[direction] > 0) {
				this._targetPosition[direction] = 0;
			} else if (this._targetPosition[direction] < min) {
				this._targetPosition[direction] = min;
			}
		}

		// elastic bounce
		if (
			!this._scrolling &&
			Math.round(this.Speed[direction]) === 0 &&
			(this.innerContainer[direction] > 0 || this.innerContainer[direction] < min)
		) {
			const target = this.Position[direction] > 0 ? 0 : min;
			this.Position[direction] = Helpers.Lerp(
				this.Position[direction],
				target,
				(5 + 2.5 / Math.max(this._softness, 0.01)) * delta
			);
			this._stop = false;
		} else if (this._scrolling || Math.round(this.Speed[direction]) !== 0) {
			// moving
			if (this._scrolling) {
				this.Speed[direction] = this.Position[direction] - this._lastPosition[direction];
				this._lastPosition.copy(this.Position);
			}
			if (this._targetPosition[direction] > 0) {
				this.Speed[direction] = 0;
				this.Position[direction] =
					250 * this._softness * (1 - Math.exp(this._targetPosition[direction] / -500));
			} else if (this._targetPosition[direction] < min) {
				this.Speed[direction] = 0;
				this.Position[direction] =
					min -
					250 * this._softness * (1 - Math.exp((min - this._targetPosition[direction]) / -500));
			} else {
				this.Position[direction] = this._targetPosition[direction];
			}
			this._stop = false;
		}

		this.innerContainer.position[direction] = Math.round(this.Position[direction]);
	}

	protected updateScrollBars(): void {
		this.scrollBars.forEach((scrollbar: ScrollBar) => {
			scrollbar.alignToContainer();
		});
	}

	protected initScrolling(): void {
		this._containerStart = new Point();
		this._targetPosition = new Point();
		this._lastPosition = new Point();
		this.Position = new Point();
		this.Speed = new Point();
		this._stop = false;

		this._lastTime = performance.now();

		TweenMax.ticker.addEventListener("tick", () => {
			this.onTick();
		});
	}

	protected onTick(): void {
		const newTime = performance.now();
		const elapsed = newTime - this._lastTime;
		this.updateScrollPosition(elapsed / 1000);
		this._lastTime = newTime;
	}

	protected initDragScroll(): void {
		const target = this;

		target.buttonMode = target.interactive = this._dragScrolling || this._wheelScrolling;

		// Drag scroll
		if (this._dragScrolling && !this._draggScroll) {
			this._draggScroll = new DraggEvent({
				target,
				canvas: this._canvas,
				dragThreshold: 1,
			});

			this._draggScroll.onDragStart = (e: PIXI.interaction.InteractionEvent) => {
				if (!this._scrolling && (this._shouldScrollX || this._shouldScrollY)) {
					this._containerStart.copy(this.innerContainer.position);
					this.Position.copy(this.innerContainer.position);
					this._scrolling = true;
					this.setScrollPosition();
					this.emit("dragStart", e);
					this._stopTween();
				}
			};

			this._draggScroll.onDragMove = (e: PIXI.interaction.InteractionEvent, offset: Point) => {
				if (this._shouldScrollX) {
					this._targetPosition.x = this._containerStart.x + offset.x;
				}
				if (this._shouldScrollY) {
					this._targetPosition.y = this._containerStart.y + offset.y;
				}
			};

			this._draggScroll.onDragEnd = (e: PIXI.interaction.InteractionEvent) => {
				if (this._scrolling && (this._shouldScrollX || this._shouldScrollY)) {
					this._scrolling = false;
					this.emit("dragEnd", e);
				}
			};
		}

		// wheel scroll
		if (this._wheelScrolling && !this._wheelScroll) {
			const scrollSpeed = new PIXI.Point();
			this._wheelScroll = new MouseWheelEvent({ target });
			this._wheelScroll.onMouseWheel = (e: PIXI.interaction.InteractionEvent, delta: Point) => {
				if (this._scrollX && (this._shouldScrollX || this._shouldScrollY)) {
					scrollSpeed.set(-delta.y * 0.2, -delta.x * 0.2);
				} else if (this._shouldScrollX || this._shouldScrollY) {
					scrollSpeed.set(-delta.x * 0.2, -delta.y * 0.2);
				}
				this.setScrollPosition(scrollSpeed);
				this._stopTween();
			};
		}

		this.updateScrollBars();
	}

	private _forceScrollTo(vertical: boolean, ratio: number): void {
		const bounds = this.getInnerBounds();
		const axis = Helpers.getAxis(vertical);
		let targetPosition: number;

		if (this._scrollX && !vertical) {
			targetPosition = -((bounds.width - this._width) * ratio);
		}
		if (this._scrollY && vertical) {
			targetPosition = -((bounds.height - this._height) * ratio);
		}

		this.Position[axis.position] =
			this._targetPosition[axis.position] =
			this.innerContainer.position[axis.position] =
				targetPosition;
	}

	private _stopTween(): void {
		if (this._positionTween) {
			this._positionTween.kill();
		}
	}

	private redrawMask(isDebug: boolean, origin: number, width: number, height: number): void {
		const target: Graphics = isDebug ? this._debugMask : this._innerMask;

		target.clear();
		target.lineStyle(0);
		target.beginFill(0x00ff00, 0);

		// fix pixi mask
		target.drawRect(origin - 1, origin - 1, 1, 1);
		target.endFill();

		if (target === this._debugMask) {
			target.lineStyle(1, 0x00ff00, 1);
			target.drawRect(0, 0, this._width, this._height);
		} else {
			target.beginFill(0x00ff00, 1);
		}

		if (this.radius === 0) {
			target.drawRect(origin, origin, width, height);
		} else {
			target.drawRoundedRect(origin, origin, width, height, this.radius);
		}

		target.endFill();
	}

	/**
	 * Export the scroll container
	 */
	public export(): any {
		const scrollBars = [];
		this.scrollBars.forEach((sb) => {
			sb.doNotExport = false;
			scrollBars.push(sb.export());
			sb.doNotExport = true;
		});

		const serializationObject = Tools.assign(ContainerExporter.exportContainer(this), {
			type: "ui.ScrollContainer",
			innerContainer: ContainerExporter.export(this.innerContainer),
			innerDockingBounds: this.innerDockingBounds ? this.innerDockingBounds.export() : undefined,
			scrollBars,

			options: {
				scrollX: this._scrollX,
				scrollY: this._scrollY,
				dragScrolling: this._dragScrolling,
				wheelScrolling: this._wheelScrolling,
				softness: this._softness,
				radius: this.radius,
				expandMask: this.expandMask,
				showDebug: this._showDebug,
				width: this._width,
				height: this._height,
			},
		});

		serializationObject.children = [];

		return serializationObject;
	}

	/**
	 * Parses the scroll container
	 * @param config the serialized data
	 * @param baseUrl the base url of the loader
	 * @param container the existing scroll container
	 */
	public static parse(config: any, baseUrl: string, container?: ScrollContainer): ScrollContainer {
		if (!container) {
			container = new ScrollContainer(config.options);
		}

		container.innerContainer = ContainerImporter.importContainer(
			config.innerContainer,
			baseUrl,
			container.innerContainer
		);
		if (config.innerDockingBounds) {
			container.innerDockingBounds = DockingBounds.parse(config.innerDockingBounds);
		}

		if (config.scrollBars) {
			config.scrollBars.forEach((sb) => ScrollBar.parse(sb, baseUrl, container));
		}

		container.getInnerBounds(true);

		return container;
	}
}
