import { Tools } from "@gdk/core";
import { Container } from "pixi.js";
import type { interaction } from "pixi.js";

import { ContainerExporter } from "../loading/export/containerExporter";
import { ContainerImporter } from "../loading/import/containerImporter";
import type { SpineTimelineToggleButton } from "./toggleButton";

/**
 * @experimental
 * Defines the new SpineButton working as a push button.
 * On pointer up, events are sent to notify the selected or unselected button.
 * @example myCheckButton.on("selected", (selectedButton) => { ... });
 * @example myCheckButton.on("unselected", () => { ... });
 * @example myCheckButton.selectedButtons.forEach((sb) => { ... });
 */
export class SpineTimelineCheckButton extends Container {
	/**
	 * Stores the current list of the available buttons.
	 */
	public readonly buttons: SpineTimelineToggleButton[] = [];
	/**
	 * Defines the maximum count of selectable buttons.
	 */
	public maxSelectedButtonsCount: number = 2;

	private _selectedButtons: SpineTimelineToggleButton[] = [];

	/**
	 * Constructor.
	 */
	public constructor() {
		super();
	}

	/**
	 * Adds the given button to be controlled by the radio button.
	 * @param button the button to add and controlled by the radio button.
	 */
	public addButton(button: SpineTimelineToggleButton): SpineTimelineToggleButton {
		const index = this.buttons.indexOf(button);
		if (index !== -1) {
			return;
		}

		this.buttons.push(button);
		this.addChild(button);

		this._bindEvents();
		return button;
	}

	/**
	 * Removes the given button from the available buttons controlled by the radio button.
	 * @param button the button reference to remove.
	 */
	public removeButton(button: SpineTimelineToggleButton): boolean {
		const index = this.buttons.indexOf(button);
		if (index === -1) {
			return false;
		}

		this.buttons.splice(index, 1);
		this.removeChild(button);

		return true;
	}

	/**
	 * Removes all the available buttons.
	 */
	public removeAllButtons(): void {
		while (this.buttons.length) {
			this.removeButton(this.buttons[0]);
		}
	}

	/**
	 * Returns the currently selected button reference.
	 */
	public get selected(): SpineTimelineToggleButton[] {
		return this._selectedButtons;
	}

	/**
	 * Sets the currently selected button.
	 */
	public set selected(selected: SpineTimelineToggleButton[]) {
		if (!selected.length) {
			// Deselect all
			this.buttons.forEach((b) =>
				b.playAnimation([
					{ name: b.animationsNames.idle },
					{ name: b.animationsNames.idleLoop, repeat: b.loopOnIdle ? -1 : 0 },
				])
			);

			this._selectedButtons = [];
		} else {
			// Deselect delta
			this._selectedButtons.forEach((sb) => {
				if (selected.indexOf(sb) !== -1) {
					return;
				}

				sb.playAnimation([
					{ name: sb.animationsNames.deselect },
					{ name: sb.animationsNames.notSelectedLoop, repeat: sb.loopOnNotSelected ? -1 : 0 },
				]);

				this._selectedButtons.splice(this._selectedButtons.indexOf(sb), 1);
			});

			// Select
			selected.forEach((s) => this._selectedButtons.indexOf(s) === -1 && this.onPointerUp(s, true));
		}
	}

	/**
	 * Resets the check button to select none.
	 */
	public reset(): void {
		this.selected = [];
	}

	/**
	 * Binds all needed events to control the overall buttons animation states.
	 */
	private _bindEvents(): void {
		this.buttons.forEach((b) => {
			b.removeAllListeners("pointerover");
			b.removeAllListeners("pointerout");
			b.removeAllListeners("pointerdown");
			b.removeAllListeners("pointerup");
			b.removeAllListeners("pointerupoutside");
		});

		this.buttons.forEach((b) => {
			b.on("pointerover", (ev: interaction.InteractionEvent) => {
				this.onPointerOver(b);
				b._trackPointerId = ev.data.identifier;
			});
			b.on("pointerout", () => this.onPointerOut(b));
			b.on("pointerdown", () => this.onPointerDown(b));
			b.on("pointerup", () => this.onPointerUp(b));
			b.on("pointerupoutside", () => this.onPointerUpOutside(b));
		});
	}

	/**
	 * Registers the event on pointer over for the given button.
	 * @param button the button to configure the over event.
	 */
	protected onPointerOver(button: SpineTimelineToggleButton): void {
		if (button._isDown) {
			return;
		}

		button._isOver = true;
		const existing = this._selectedButtons.find((sb) => sb === button);

		if (existing === button) {
			// Over selected
			button.playAnimation([
				{ name: button.animationsNames.overSelected },
				{
					name: button.animationsNames.overSelectedLoop,
					repeat: button.loopOnOverSelected ? -1 : 0,
				},
			]);
		} else if (!this._selectedButtons.length) {
			// Over no selected button
			button.playAnimation([
				{ name: button.animationsNames.over },
				{ name: button.animationsNames.overLoop, repeat: button.loopOnOver ? -1 : 0 },
			]);
		} else {
			// Over not selected
			button.playAnimation([
				{ name: button.animationsNames.overNotSelected },
				{
					name: button.animationsNames.overNotSelectedLoop,
					repeat: button.loopOnOverNotSelected ? -1 : 0,
				},
			]);
		}
	}

	/**
	 * Registers the event on pointer out.
	 * @param button the button to configure the out event.
	 */
	protected onPointerOut(button: SpineTimelineToggleButton): void {
		if (button._isDown) {
			return;
		}

		button._isOver = false;
		const existing = this._selectedButtons.find((sb) => sb === button);

		if (existing === button) {
			// Out selected if exists
			button.playAnimation([
				{ name: button.animationsNames.outSelected },
				{ name: button.animationsNames.selectedLoop, repeat: button.loopOnSelected ? -1 : 0 },
			]);
		} else if (!this._selectedButtons.length) {
			// Out not selected
			button.playAnimation([
				{ name: button.animationsNames.out },
				{ name: button.animationsNames.idleLoop, repeat: button.loopOnIdle ? -1 : 0 },
			]);
		} else {
			// Basic out
			button.playAnimation([
				{ name: button.animationsNames.outNotSelected },
				{ name: button.animationsNames.notSelectedLoop, repeat: button.loopOnNotSelected ? -1 : 0 },
			]);
		}
	}

	/**
	 * Registers the event on pointer down.
	 * @param button the button to configure the pointer down event.
	 */
	protected onPointerDown(button: SpineTimelineToggleButton): void {
		button._isDown = true;

		const existing = this._selectedButtons.find((sb) => sb === button);

		if (existing === button) {
			button.playAnimation([
				{ name: button.animationsNames.downSelected },
				{
					name: button.animationsNames.downSelectedLoop,
					repeat: button.loopOnDownSelected ? -1 : 0,
				},
			]);
		} else if (!this._selectedButtons.length) {
			button.playAnimation([
				{ name: button.animationsNames.down },
				{ name: button.animationsNames.downLoop, repeat: button.loopOnDown ? -1 : 0 },
			]);
		} else {
			// Select button
			button.playAnimation([
				{ name: button.animationsNames.notSelectedDown },
				{ name: button.animationsNames.notSelectedDownLoop, repeat: button.loopOnDown ? -1 : 0 },
			]);
		}
	}

	/**
	 * Registers the event on pointer up outside.
	 * @param button the button to configure the pointer up event.
	 */
	protected onPointerUpOutside(button: SpineTimelineToggleButton): void {
		button._isDown = false;

		const existing = this._selectedButtons.find((sb) => sb === button);

		if (!this._selectedButtons.length) {
			button.playAnimation([
				{ name: button.animationsNames.idle },
				{ name: button.animationsNames.idleLoop, repeat: button.loopOnIdle ? -1 : 0 },
			]);
		} else if (button === existing) {
			button.playAnimation([
				{ name: button.animationsNames.upSelected },
				{ name: button.animationsNames.selectedLoop, repeat: button.loopOnIdle ? -1 : 0 },
			]);
		} else {
			button.playAnimation([
				{ name: button.animationsNames.noSelectedUp },
				{ name: button.animationsNames.notSelectedLoop, repeat: button.loopOnIdle ? -1 : 0 },
			]);
		}
	}

	/**
	 * Registers the event on pointer up.
	 * @param button the button to configure the pointer up event.
	 * @param force defines wether or not the button state should be forced.
	 */
	protected onPointerUp(button: SpineTimelineToggleButton, force?: boolean): void {
		if (!button._isDown && !force) {
			return;
		}
		button._isDown = false;

		const existing = this._selectedButtons.find((sb) => sb === button);

		if (!existing) {
			// Check limit
			if (this.maxSelectedButtonsCount === this._selectedButtons.length) {
				button.playAnimation([
					{ name: button.animationsNames.outNotSelected },
					{ name: button.animationsNames.notSelectedLoop, repeat: button.loopOnSelected ? -1 : 0 },
				]);
				return;
			}

			if (!this._selectedButtons.length) {
				this.buttons.forEach(
					(b) =>
						b !== button &&
						b.playAnimation([
							{ name: button.animationsNames.notSelected },
							{
								name: button.animationsNames.notSelectedLoop,
								repeat: button.loopOnNotSelected ? -1 : 0,
							},
						])
				);
			}

			if (button._isOver) {
				button.playAnimation([
					{ name: button.animationsNames.select },
					{ name: button.animationsNames.overSelected },
					{ name: button.animationsNames.overSelectedLoop, repeat: button.loopOnSelected ? -1 : 0 },
				]);
			} else {
				button.playAnimation([
					{ name: button.animationsNames.select },
					{ name: button.animationsNames.selectedLoop, repeat: button.loopOnSelected ? -1 : 0 },
				]);
			}

			// Register button as selected
			this._selectedButtons.push(button);

			this.emit("selected", button);
		} else if (existing) {
			// Unselect
			this._selectedButtons.splice(this._selectedButtons.indexOf(button), 1);

			if (!this._selectedButtons.length) {
				this.buttons.forEach(
					(b) =>
						b !== button &&
						b.playAnimation([
							{ name: button.animationsNames.unselectedToIdle },
							{ name: button.animationsNames.idleLoop, repeat: button.loopOnIdle ? -1 : 0 },
						])
				);

				if (button._isOver) {
					button.playAnimation([
						{ name: button.animationsNames.over },
						{ name: button.animationsNames.overLoop, repeat: button.loopOnIdle ? -1 : 0 },
					]);
				} else {
					button.playAnimation([
						{ name: button.animationsNames.idle },
						{ name: button.animationsNames.idleLoop, repeat: button.loopOnIdle ? -1 : 0 },
					]);
				}
			} else {
				// We need to free the trackedPointers object to avoid old "pointerout" events.
				if (button._trackPointerId !== null) {
					delete button.trackedPointers[button._trackPointerId];
					button._trackPointerId = null;
				}

				if (button._isOver) {
					button.playAnimation([
						{ name: button.animationsNames.selectedToUnselected },
						{ name: button.animationsNames.overNotSelected },
						{
							name: button.animationsNames.overNotSelectedLoop,
							repeat: button.loopOnOverNotSelected ? -1 : 0,
						},
					]);
				} else {
					button.playAnimation([
						{ name: button.animationsNames.selectedToUnselected },
						{
							name: button.animationsNames.notSelectedLoop,
							repeat: button.loopOnOverNotSelected ? -1 : 0,
						},
					]);
				}
			}

			this.emit("unselected", button);
		}
	}

	/**
	 * Exports the spine timeline radio button.
	 */
	public export(): any {
		const buttons = this.buttons.map((b) => {
			b.doNotExport = true;
			return b.export();
		});

		return Tools.assign(ContainerExporter.exportContainer(this), {
			type: "spine.SpineTimelineCheckButton",
			buttons,
			maxSelectedButtonsCount: this.maxSelectedButtonsCount,
		});
	}

	/**
	 * Parses the spine timeline according to the given configuration.
	 * @param config the serialized data.
	 * @param baseUrl the base url of the loader.
	 * @param button the existing button reference.
	 */
	public static parse(
		config: any,
		baseUrl: string,
		button?: SpineTimelineCheckButton
	): SpineTimelineCheckButton {
		// Create timeline
		if (!button) {
			button = new SpineTimelineCheckButton();
			button.maxSelectedButtonsCount = config.maxSelectedButtonsCount ?? 2;
		}

		config.buttons.forEach((bc) => {
			const b = ContainerImporter.import(bc, baseUrl) as SpineTimelineToggleButton;
			button.addButton(b);
		});

		return button;
	}
}
