import { TweenMax } from "gsap";
import { Logger, Tools, ButtonClick, tracker } from "@gdk/core";
import type { ITrackableObject } from "@gdk/core";
import type { interaction } from "pixi.js";

import { SpineTimeline } from "./timeline";
import { TextField } from "../ui";
import type { IStyles } from "../ui";
import type { ISpineTimelineSequence, ISpineTimelineAnimation } from "./types";

/**
 * @experimental
 * Defines an object defining a range in interval [min, max]
 */
export interface ISpineTimelineButtonRange {
	/**
	 * The minimum value of the range.
	 */
	min: number;
	/**
	 * The maximum value of the range.
	 */
	max: number;
}

/**
 * @experimental
 * Defines all possible animations for spine button and the related names.
 */
export interface ISpineTimelineButtonAnimations {
	enter?: string;
	exit?: string;
	disable?: string;
	disableLoop?: string;
	idle?: string;
	idleLoop?: string;
	over?: string;
	overLoop?: string;
	out?: string;
	down?: string;
	downLoop?: string;
	up?: string;
	callToAction?: string;
}

/**
 * @experimental
 * Defines the new SpineButton that uses the newly created SpineTimeline features.
 * This class implements the features of a simple/classic button. See SpineTimelinePushButton etc.
 * for more advanced buttons.
 */
export class SpineTimelineButton extends SpineTimeline implements ITrackableObject {
	/**
	 * Defines the optional name of the object to be tracked.
	 * If unset, a default value will be taken by the tracker. For example,
	 * the name of the object (button, scratch, etc.).
	 */
	public trackingName: string = "";

	/**
	 * Labels dictionary.
	 */
	public labels: { [index: string]: TextField } = {};
	/**
	 * Styles object used by the labels to define the labels styles.
	 */
	public styles: IStyles;
	/**
	 * Sets or gets wether or not looping on "idleLoop" is required.
	 */
	public loopOnIdle: boolean = false;
	/**
	 * Sets or gets wether or not looping on "overLoop" is required.
	 */
	public loopOnOver: boolean = false;
	/**
	 * Sets or gets wether or not looping on "downLoop" is required.
	 */
	public loopOnDown: boolean = false;
	/**
	 * Sets or gets wether or not looping on "disableLoop" is required.
	 */
	public loopOnDisable: boolean = false;
	/**
	 * Defines all possible animations for the spine button and their related names in the spine file.
	 */
	public animationsNames: ISpineTimelineButtonAnimations = {
		enter: "enter",
		exit: "exit",
		disable: "disable",
		disableLoop: "disableLoop",
		idle: "idle",
		idleLoop: "idleLoop",
		over: "over",
		overLoop: "overLoop",
		out: "out",
		down: "down",
		downLoop: "downLoop",
		up: "up",
		callToAction: "calltoaction",
	};

	/**
	 * Sets or gets wether or not the button is down/pushed.
	 * @hidden
	 */
	public _isDown: boolean = false;
	/**
	 * Sets or gets wether or not the pointer is over the button.
	 * @hidden
	 */
	public _isOver: boolean = false;

	private _label: string = "";
	private _enabled: boolean = false;
	private _sequence: ISpineTimelineSequence = { animations: [], loop: false, doNotExport: true };

	private _callToActionEnabled: boolean = false;
	private _callToActionDelay: ISpineTimelineButtonRange = { min: 5, max: 5 }; // 5 seconds
	private _callToActionTimer: TweenMax = null;

	/**
	 * @hidden
	 */
	public _trackPointerId: number = null;

	/**
	 * Constructor.
	 * @param assetId the id of the spine datas in the core-loader.
	 * @param styles the styles of the buttons.
	 */
	public constructor(assetId: string, styles: IStyles) {
		super(assetId);
		this.styles = styles;

		this._bindEvents();
		this._createLabels();
		this.addSequence("events", this._sequence);

		// Enabled by default
		this.enabled = true;

		this.callToActionEnabled = this.state.hasAnimation(this.animationsNames.callToAction);
	}

	/**
	 * Gets wether or not the button is enabled.
	 */
	public get enabled(): boolean {
		return this._enabled;
	}

	/**
	 * Sets wether or not the button is enabled. If "true", the enabling animation will be played.
	 */
	public set enabled(value: boolean) {
		if (value === this._enabled) {
			return;
		}

		this.reset();

		this._enabled = value;
		this.interactive = value;
		this.buttonMode = value;

		if (value) {
			this.playAnimation([
				{ name: this.animationsNames.idle },
				{ name: this.animationsNames.idleLoop, repeat: this.loopOnIdle ? -1 : 0 },
			]);
		} else {
			this.playAnimation([
				{ name: this.animationsNames.disable },
				{ name: this.animationsNames.disableLoop, repeat: this.loopOnDisable ? -1 : 0 },
			]);
		}
	}

	/**
	 * Gets the current label of the button.
	 */
	public get label(): string {
		return this._label;
	}

	/**
	 * Sets the current label of the button.
	 */
	public set label(value: string) {
		this._label = value;
		this.updateLabels();
	}

	/**
	 * Gets wether or not the call to action feature is enabled.
	 */
	public get callToActionEnabled(): boolean {
		return this._callToActionEnabled;
	}

	/**
	 * Sets wether or not the call to action feature is enabled.
	 */
	public set callToActionEnabled(enabled: boolean) {
		if (enabled === this._callToActionEnabled) {
			return;
		}
		this._callToActionEnabled = enabled;

		this.resetCallToAction();
	}

	/**
	 * Gets the current call to action delay in seconds.
	 */
	public get callToActionDelay(): ISpineTimelineButtonRange {
		return this._callToActionDelay;
	}

	/**
	 * Sets the current call to action delay in seconds.
	 */
	public set callToActionDelay(delay: ISpineTimelineButtonRange) {
		if (this._callToActionDelay === delay) {
			return;
		}
		this._callToActionDelay = delay;

		this.resetCallToAction();
	}

	/**
	 * Shows the button by playing the "enter" animation.
	 */
	public async show(): Promise<void> {
		await this.playDirect(this.animationsNames.enter);
		this.playDirect(this.animationsNames.idleLoop, this.loopOnIdle);
	}

	/**
	 * Hides the button by playing the "exit" animation.
	 */
	public hide(): Promise<void> {
		return this.playDirect(this.animationsNames.exit);
	}

	/**
	 * Updates the labels and centers the pivot.
	 */
	public updateLabels(): void {
		for (const l in this.labels) {
			if (!this.labels[l]) {
				continue;
			}
			const label = this.labels[l];
			label.text = this._label;
			label.styles.default = Tools.assign({}, this.styles.default, this.styles[label.name] || {});
			label.update();
			label.pivot.set(label.width * 0.5, -label.height * 0.5);
		}
	}

	/**
	 * Resets the button.
	 */
	public reset(): void {
		if (this._isOver) {
			this.emit("pointerout");
		}

		this._isOver = false;
		this._isDown = false;

		this._sequence.animations = [];

		// We need to free the trackedPointers object to avoid old "pointerout" events.
		if (this._trackPointerId !== null) {
			delete this.trackedPointers[this._trackPointerId];
			this._trackPointerId = null;
		}
	}

	/**
	 * Binds all the events of the button.
	 */
	private _bindEvents(): void {
		this.interactive = true;
		this.buttonMode = true;

		this.onPointerOver();
		this.onPointerOut();
		this.onPointerDown();
		this.onPointerUp();
		this.onPointerUpOutside();
	}

	/**
	 * Registers the event on pointer down.
	 */
	protected onPointerDown(): void {
		this.on("pointerdown", () => {
			this._isDown = true;
			this.playAnimation([
				{ name: this.animationsNames.down },
				{ name: this.animationsNames.downLoop, repeat: this.loopOnDown ? -1 : 0 },
			]);
			this.resetCallToAction();
		});
	}

	/**
	 * Registers the event on pointer over.
	 */
	protected onPointerOver(): void {
		this.on("pointerover", (ev: interaction.InteractionEvent) => {
			if (!this._isDown && !this._isOver) {
				this.playAnimation([
					{ name: this.animationsNames.over },
					{ name: this.animationsNames.overLoop, repeat: this.loopOnOver ? -1 : 0 },
				]);
			}

			this._isOver = true;
			this._trackPointerId = ev.data.identifier;

			this.resetCallToAction();
		});
	}

	/**
	 * Registers the event on pointer out.
	 */
	protected onPointerOut(): void {
		this.on("pointerout", () => {
			if (!this._isDown && this._trackPointerId) {
				this.playAnimation([
					{ name: this.animationsNames.out },
					{ name: this.animationsNames.idleLoop, repeat: this.loopOnIdle ? -1 : 0 },
				]);
			}

			this._isOver = false;

			this.resetCallToAction();
		});
	}

	/**
	 * Registers the event on pointer up.
	 */
	protected onPointerUp(): void {
		this.on("pointerup", () => {
			if (!this._isDown) {
				return;
			}
			this._isDown = false;

			this.trackClick();

			this.playAnimation([
				{ name: this.animationsNames.up },
				{ name: this.animationsNames.overLoop, repeat: this.loopOnOver ? -1 : 0 },
			]);
			this.resetCallToAction();
		});
	}

	/**
	 * Registers the event on pointer up outside.
	 */
	protected onPointerUpOutside(): void {
		this.on("pointerupoutside", () => {
			this._isDown = false;
			this.playAnimation([
				{ name: this.animationsNames.up },
				{ name: this.animationsNames.idleLoop, repeat: this.loopOnIdle ? -1 : 0 },
			]);
			this.resetCallToAction();
		});
	}

	/**
	 * Tracks the click event
	 */
	protected trackClick(): void {
		tracker.track(new ButtonClick(this.trackingName || this.getName()));
	}

	/**
	 * Configures and plays the button's sequence using the given animations array.
	 * @param animations the animations to play.
	 */
	public playAnimation(animations: ISpineTimelineAnimation[]): Promise<void> {
		// Clean unexisting animations
		for (let i = 0; i < animations.length; i++) {
			const a = animations[i];
			if (!this.state.hasAnimation(a.name)) {
				animations.splice(i, 1);
				i--;
			}
		}

		// Play!
		this._sequence.animations = animations;
		return this.play("events");
	}

	/**
	 * Creates all the labels.
	 */
	private _createLabels(): void {
		// Create labels
		this.slotContainers.forEach((s) => {
			if (s.name.indexOf("label") === -1) {
				return;
			}

			const labelStyle = s.name === "label" ? "default" : s.name.replace("label_", "");
			if (!this.styles[labelStyle]) {
				Logger.getLogger().warn(`SpineTimelineButton: No style found for label "${s.name}"`);
			}

			const style = Tools.assign({}, this.styles.default, this.styles[labelStyle] || {});

			const label = new TextField(this._label, style);
			label.name = labelStyle;
			label.scale.y = -1;
			label.doNotExport = true;
			this.labels[labelStyle] = label;
			this.getSlotContainerByName(s.name).addChild(label);
		});

		// Update!
		this.updateLabels();
	}

	/**
	 * Inits the call to action timers by killing the previous timer.
	 */
	protected resetCallToAction(): void {
		if (this._callToActionTimer) {
			this._callToActionTimer.kill();
			this._callToActionTimer = null;
		}

		if (this._callToActionEnabled) {
			const delay =
				this._callToActionDelay.min +
				Math.random() * (this._callToActionDelay.max - this._callToActionDelay.min);
			this._callToActionTimer = TweenMax.delayedCall(delay, () => {
				if (!this.enabled || this.currentAnimation !== this.animationsNames.idleLoop) {
					return this.resetCallToAction();
				}

				const anim = this.state.hasAnimation(this.animationsNames.callToAction)
					? this.animationsNames.callToAction
					: "CTA";
				if (!this.state.hasAnimation(anim)) {
					Logger.getLogger().warn(`No animation found for Call to Action. Button "${this.name}"`);
				}

				this.playAnimation([
					{ name: anim },
					{ name: this.animationsNames.idleLoop, repeat: this.loopOnIdle ? -1 : 0 },
				]);
				this.resetCallToAction();
			});
		}
	}

	/**
	 * Exports the spine timeline button.
	 */
	public export(): any {
		return Tools.assign(super.export(), {
			type: "spine.SpineTimelineButton",
			animationsNames: this.animationsNames,
			loopOnIdle: this.loopOnIdle,
			loopOnDown: this.loopOnDown,
			loopOnOver: this.loopOnOver,
			loopOnDisable: this.loopOnDisable,
			styles: this.styles,
			label: this._label,
			callToActionDelay: this._callToActionDelay,
			callToActionEnabled: this._callToActionEnabled,
			trackingName: this.trackingName,
		});
	}

	/**
	 * Parses the spine timeline button according to the given configuration.
	 * @param config the serialized data.
	 * @param baseUrl the base url of the loader.
	 * @param button the existing button reference.
	 */
	public static parse(
		config: any,
		baseUrl: string,
		button?: SpineTimelineButton
	): SpineTimelineButton {
		// Create button
		if (!button) {
			button = new SpineTimelineButton(config.assetId, config.styles);
		}

		SpineTimeline.parse(config, baseUrl, button);
		if (config.label) {
			button.label = config.label;
		}

		// BC, check if previously serialized.
		if (config.callToActionDelay) {
			button.callToActionDelay = config.callToActionDelay;
		}

		button.callToActionEnabled = config.callToActionEnabled;

		// BC, check if previously serialized.
		if (config.animationsNames) {
			button.animationsNames = Tools.assign(button.animationsNames, config.animationsNames);
		}

		if (config.loopOnIdle !== undefined) {
			button.loopOnIdle = config.loopOnIdle;
		}
		if (config.loopOnDown !== undefined) {
			button.loopOnDown = config.loopOnDown;
		}
		if (config.loopOnOver !== undefined) {
			button.loopOnOver = config.loopOnOver;
		}
		if (config.loopOnDisable !== undefined) {
			button.loopOnDisable = config.loopOnDisable;
		}

		button.trackingName = config.trackingName || "";

		return button;
	}
}
