import Proton from "proton-js";

/**
 * Defines the signature of a custom function used by the emitter parser.
 */
export type CustomProtonFunction = (...args: any[]) => any;

export class ProtonUtils {
	/**
	 * Defines all the available custom functions used to return custom components for the current emitter being parsed.
	 */
	public static readonly customFunctions: { [propType: string]: CustomProtonFunction } = {};

	/**
	 * Adds a the given custom function used to parse custom components for the current emitter being parsed.
	 * @param propertyType the property type to check before calling the given custom function.
	 * @param fn the function to add to the available custom functions.
	 * @returns true if added, false if already exists.
	 * @see .customFunctions
	 */
	public static addCustomFunction(propertyType: string, fn: CustomProtonFunction): boolean {
		if (!this.customFunctions[propertyType]) {
			this.customFunctions[propertyType] = fn;
			return true;
		}

		return false;
	}

	/**
	 * Removes the given function from the custom functions used while parsing emitter.
	 * @param propertyType the function to remove from the custom functions.
	 * @returns true if removed, false if not found.
	 */
	public static removeCustomFunction(propertyType: string): boolean {
		if (this.customFunctions[propertyType]) {
			delete this.customFunctions[propertyType];
			return true;
		}

		return false;
	}

	/**
	 * Returns
	 * @param propertyType the property type (i.e Span, Time, etc.) to build.
	 * @param args the arguments of the property.
	 */
	public static getProperty<T>(propertyType: string, args: any): T {
		const ctor = Proton[propertyType] || this.customFunctions[propertyType];
		if (ctor) {
			const finalArgs = this.getValue(args);
			if (Array.isArray(finalArgs)) {
				return new ctor(...finalArgs);
			}

			return new ctor(finalArgs);
		} else {
			throw new Error(
				`Proton.${propertyType} doesn't exists and no custom function was registered.`
			);
		}
	}

	/**
	 * Returns the value of the given arguments (string, number, object or array).
	 * @param args the arguments of the property.
	 */
	public static getValue<T>(args: any): T {
		switch ((typeof args).toLowerCase()) {
			case "string":
				if (args.toLowerCase() === "infinity") {
					return Infinity as any;
				}
				return args;
			case "number":
				return args;
			default:
				// Object or array
				if (Array.isArray(args)) {
					return args.map((a) => this.getValue(a)) as any;
				}

				return this.getObject(args);
		}
	}

	/**
	 * Returns the object according to the given arguments.
	 * @param args the arguments of the property.
	 */
	public static getObject<T>(args: any): T {
		if (args === null) {
			return undefined;
		}
		if (Object.keys(args).length === 0) {
			return undefined;
		}

		const result = {};

		for (const k in args) {
			if (!args.hasOwnProperty(k)) {
				continue;
			}

			const key = k.toLowerCase();
			if (key === "span" || key === "s") {
				return this.getSpan(args[k]);
			}
			if (key === "zone" || key === "z") {
				return this.getProperty(args[k].type, args[k].args);
			}
			if (key === "vector2d") {
				return this.getProperty("Vector2D", args[k]);
			}
			result[key] = this.getValue(args[k]);
		}

		return result as T;
	}

	/**
	 * Returns a Proton Span according to the given span arguments.
	 * @param args the arguments of the span (array or number or object).
	 */
	public static getSpan(args: any): Proton.Span {
		if (Array.isArray(args)) {
			return this._setSpanValue(...args);
		}

		return this._setSpanValue(args);
	}

	/**
	 * Overrides the Proton.Util.setSpanValue function of Proton to enable b and c to be equal to 0.
	 */
	private static _setSpanValue(a?: any, b?: any, c?: any): Proton.Span {
		if (a === undefined) {
			return new Proton.Span(0, 1);
		}

		if (a instanceof Proton.Span) {
			return a;
		}

		if (b === undefined) {
			return new Proton.Span(a);
		}

		if (c === undefined) {
			return new Proton.Span(a, b);
		}

		return new Proton.Span(a, b, c);
	}
}
