import type { DisplayObject, interaction } from "pixi.js";

import type { GamePixi } from "../../../gamePixi";

export class Helpers {
	/**
	 * Linearly interpolates between start and stop by amt
	 * @param start the start value
	 * @param stop the end value
	 * @param amt the interpolation value between the two values
	 */
	public static Lerp(start: number, stop: number, amt: number): number {
		if (amt > 1) {
			amt = 1;
		} else if (amt < 0) {
			amt = 0;
		}
		return start + (stop - start) * amt;
	}

	/**
	 * will release an interaction event in interaction manager.
	 * @param e the pixi interaction event to release
	 */
	public static releaseInteraction(e: interaction.InteractionEvent): void {
		const game: GamePixi = window[`game`]; // GamePixi.instance;
		game.application.renderer.plugins.interaction.releaseInteractionDataForPointerId(
			e.data.identifier
		);
	}

	/**
	 * will emit an event with bubbling
	 * @param displayObject the display object at the origin of event.
	 * @param eventString event name
	 * @param eventData event datas.
	 */
	public static bubbleEmit(
		displayObject: DisplayObject,
		eventString: string,
		eventData: any
	): void {
		eventData.target = displayObject;
		eventData.type = eventString;

		let bubbleTarget = displayObject;
		while (bubbleTarget && !eventData.stopped) {
			bubbleTarget.emit(eventString, eventData);
			if (bubbleTarget[eventString]) {
				bubbleTarget[eventString](eventData);
			}
			bubbleTarget = bubbleTarget.parent;
		}
	}

	/**
	 * Returns num rounded with decimals
	 * @param num number to round
	 * @param decimals number of decimals
	 */
	public static Round(num: number, decimals: number): number {
		const pow = Math.pow(10, decimals);
		return Math.round(num * pow) / pow;
	}

	/**
	 * Cap value between min and max
	 * @param value value to cap
	 * @param min min value
	 * @param max max value
	 */
	public static Cap(value: number, min: number, max: number): number {
		if (value <= min) {
			return min;
		} else if (value >= max) {
			return max;
		} else {
			return value;
		}
	}

	/**
	 * Returns an object of properties name depending of vertical parameter
	 * @param vertical vertical or horizontal
	 */
	public static getAxis(vertical: boolean): {
		position: string;
		size: string;
		_position: string;
		_size: string;
	} {
		const position = vertical ? "y" : "x";
		const size = vertical ? "height" : "width";

		return { position, size, _position: `_${position}`, _size: `_${size}` };
	}

	/**
	 * Convert number to hexadecimal string
	 * @param c number to convert
	 */
	public static componentToHex(c: number): string {
		const hex = c.toString(16);
		return hex.length === 1 ? `0${hex}` : hex;
	}

	/**
	 * Convert rgb values to hexadecimal string
	 * @param r red value
	 * @param g green value
	 * @param b blue value
	 */
	public static rgbToHex(r: number, g: number, b: number): string {
		const hashTag = "#";
		return hashTag + this.componentToHex(r) + this.componentToHex(g) + this.componentToHex(b);
	}

	/**
	 * Convert rgb values to number
	 * @param r red value
	 * @param g green value
	 * @param b blue value
	 */
	public static rgbToNumber(r: number, g: number, b: number): number {
		return r * 65536 + g * 256 + b;
	}

	/**
	 * Convert number to rgb components
	 * @param c number to convert
	 */
	public static numberToRgb(c: number): { r: number; g: number; b: number } {
		return {
			r: Math.floor(c / (256 * 256)),
			g: Math.floor(c / 256) % 256,
			b: c % 256,
		};
	}

	/**
	 * convert hexadecimal value to rgb components
	 * @param hex hexadecimal values
	 */
	public static hexToRgb(hex: string | number): { r: number; g: number; b: number } {
		if (hex === null) {
			hex = 0xffffff;
		}

		if (!isNaN(hex as number)) {
			return this.numberToRgb(hex as number);
		}

		// Expand shorthand form (e.g. "03F") to full form (e.g. "0033FF")
		const shorthandRegex = /^#?([a-f\d])([a-f\d])([a-f\d])$/i;
		hex = (hex as string).replace(shorthandRegex, (m, r, g, b) => {
			return r + r + g + g + b + b;
		});

		const result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
		return result
			? {
					r: parseInt(result[1], 16),
					g: parseInt(result[2], 16),
					b: parseInt(result[3], 16),
			  }
			: null;
	}
}
