import { Point, Transform, utils } from "pixi.js";
import type { Container, interaction } from "pixi.js";

import { isLeftPointerTap } from "../../tools";

export class DragEvent2 extends utils.EventEmitter {
	/**
	 * Sets or gets wether or not the dragging event is enabled.
	 */
	public enabled: boolean = true;

	private _targetContainer: Container;

	private _dragDown: boolean = false;
	private _transform: Transform = new Transform();
	private _lastPosition: Point = new Point(0, 0);
	private _lastPointerPosition: Point = new Point(0, 0);

	private _offsets: Point = new Point(0, 0);

	private _pointerDownListener: (event: interaction.InteractionEvent) => void = null;

	private _mouseDragListener: (this: Document, ev: MouseEvent) => void = null;
	private _touchDragListener: (this: Document, ev: TouchEvent) => void = null;
	private _mouseUpListener: (this: Document, ev: MouseEvent) => void = null;
	private _touchEndListener: (this: Document, ev: TouchEvent) => void = null;

	/**
	 * Constructor.
	 * @param targetContainer the target container where events should be listened.
	 */
	public constructor(targetContainer: Container = null) {
		super();

		this.targetContainer = targetContainer;
	}

	/**
	 * Gets wether or not the user is dragging the current target container.
	 */
	public get dragDown(): boolean {
		return this._dragDown;
	}

	/**
	 * Gets the current target container events are listened on.
	 */
	public get targetContainer(): Container {
		return this._targetContainer;
	}

	/**
	 * Sets the current target container events are listened on.
	 */
	public set targetContainer(container: Container) {
		if (this._targetContainer === container) {
			return;
		}

		if (this._targetContainer) {
			this._targetContainer.removeListener("pointerdown", this._pointerDownListener);
		}

		this._targetContainer = container;

		if (this._targetContainer) {
			this._bindEvents();
		}
	}

	/**
	 * Binds the events of the target container to listen/emit drag events.
	 */
	private _bindEvents(): void {
		this._targetContainer.on(
			"pointerdown",
			(this._pointerDownListener = (ev: interaction.InteractionEvent) => {
				if (!isLeftPointerTap(ev)) {
					return;
				}

				this._dragDown = true;
				this._refreshEventPosition(ev.data.originalEvent, this._lastPointerPosition);
				if (this._targetContainer.docking) {
					this._lastPosition.set(this._targetContainer.docking.x, this._targetContainer.docking.y);
				} else {
					this._lastPosition.copy(this._targetContainer.position);
				}

				if (this.enabled) {
					this.emit("dragStart", ev);
				}

				ev.data.originalEvent.target.addEventListener(
					"mousemove",
					(this._mouseDragListener = (oev) => this._drag(oev))
				);
				ev.data.originalEvent.target.addEventListener(
					"touchmove",
					(this._touchDragListener = (oev) => this._drag(oev))
				);
				document.addEventListener("mouseup", (this._mouseUpListener = (oev) => this._dragEnd(oev)));
				document.addEventListener(
					"touchend",
					(this._touchEndListener = (oev) => this._dragEnd(oev))
				);
			})
		);
	}

	/**
	 * Drag event. Called on mousemove or touchmove.
	 */
	private _drag(ev: MouseEvent | TouchEvent): void {
		if (!this._dragDown) {
			return;
		}

		this._transform.setFromMatrix(this._targetContainer.worldTransform);

		const newPos = this._refreshEventPosition(ev);
		const dx = (newPos.x - this._lastPointerPosition.x) / this._transform.scale.x;
		const dy = (newPos.y - this._lastPointerPosition.y) / this._transform.scale.y;

		this._offsets.set(this._lastPosition.x + dx, this._lastPosition.y + dy);
		if (this.enabled) {
			this.emit("drag", this._offsets);
		}
	}

	/**
	 * Drag end event. Called on mouseup or toucheend.
	 */
	private _dragEnd(ev: MouseEvent | TouchEvent): void {
		this._dragDown = false;
		this._removeTemporaryEvents(ev);

		const newPos = this._refreshEventPosition(ev);
		const dx = (newPos.x - this._lastPointerPosition.x) / this._transform.scale.x;
		const dy = (newPos.y - this._lastPointerPosition.y) / this._transform.scale.y;

		this._offsets.set(this._lastPosition.x + dx, this._lastPosition.y + dy);

		if (this.enabled) {
			this.emit("dragEnd", this._offsets);
		}
	}

	/**
	 * Refreshes the event position (absolute).
	 */
	private _refreshEventPosition(
		ev: MouseEvent | TouchEvent,
		point: Point = new Point(0, 0)
	): Point {
		if (ev instanceof MouseEvent) {
			point.set(ev.clientX, ev.clientY);
		} else if (ev.touches.length > 0) {
			point.set(ev.touches[0].clientX, ev.touches[0].clientY);
		}

		return point;
	}

	/**
	 * Removes the temporary events used to listen drag events on canvas.
	 */
	private _removeTemporaryEvents(ev: MouseEvent | TouchEvent): void {
		ev.target.removeEventListener("mousemove", this._mouseDragListener);
		ev.target.removeEventListener("touchmove", this._touchDragListener);
		document.removeEventListener("mouseup", this._mouseUpListener);
		document.removeEventListener("touchend", this._touchEndListener);
	}
}
